.card-services-item {
    position: relative;
    display: block;
    width: 100%;
    padding: 15px 20px;
    font-size: 15px;
    cursor: pointer;
    @include border(bottom);

    @include md-block() {
        flex-wrap: wrap;
    }

    @include sm-block() {
        flex-wrap: nowrap;
    }

    @include xs-block() {
        flex-wrap: wrap;
        padding: 15px 10px;
    }

    @include i7-block() {
        padding: 10px;
    }

    @include min(sm) {
        &:hover {
            background-color: $color-bg--hover;
        }
    }

    &:last-child {
        border-bottom: 0;
    }

    &__top {
        display: flex;
        justify-content: space-between;

        @include xs-block() {
            align-items: flex-start;
        }
    } //.card-services-item__top

    &__left {
        // padding-left: 20px;
        position: relative;
        min-width: 190px;
        @include flexColumn;

        @include xs-block() {
            // padding-left: 25px;
            flex: 1;
            width: auto;
            margin-right: 10px;
        }
    } //.card-services-item__left

    &__right {
        flex-shrink: 0;
        align-items: flex-end;
        margin-right: 25px;
        margin-left: auto;

        @include flexColumn;
    } //.card-services-item__right

    &__middle {
        flex: 1;
        margin-left: 30px;
        @include flexCenter;

        @include max-screen(1024) {
            justify-content: center;
            order: 3;
            width: 100%;
            margin-bottom: 5px;
            margin-left: 0;
        }

        @include sm-block() {
            order: initial;
            margin-bottom: 0;
            margin-left: 30px;
        }

        @include xs-block() {
            order: 3;
            margin: 10px 0 5px;
        }
    } //.card-services-item__middle

    &__bottom {
        display: flex;
        margin-top: 10px;
        margin-bottom: 10px;
        // margin-left: 20px;

        [data-text-container-btn] {
            align-self: flex-start;
            margin-top: 5px;
            font-size: 13px;
            text-align: left;
        } //.card-services-item__bottom [data-text-container-btn]

        &.mb-0 {
            margin-bottom: 0;
        } //.card-services-item__bottom.mb-0

        &--column {
            flex-direction: column;
        } //.card-services-item__bottom--column
    } //.card-services-item__bottom

    &__title {
        color: $color-text--dark;
        font-size: 15px;
        font-weight: 500;

        span {
            & + span {
                margin-left: 10px;

                &::before {
                    content: '\f067';

                    margin-right: 10px;
                    color: $color-text--light;
                    font-size: 13px;
                    @include fa('regular');
                }
            }
        }
    } //.card-services-item__title

    &__box {
        display: flex;
        align-items: center;

        .button-link {
            margin-right: 10px;
        }
    } //.card-services-item__box

    &__example {
        width: 65px;

        & + .card-services-item__example {
            margin-left: 1px;
        }
    } //.card-services-item__example

    &__action {
        @include flexCenter;

        &_done {
            display: none;

            .card-services-item {
                &__text {
                    color: $gray--light;
                    font-size: 13px;
                    font-style: italic;
                    font-weight: 400;
                } //.card-services-item__action_done .card-services-item__text

                &__btn {
                    background-color: $color-success;
                    // box-shadow: 0 2px 4px 0 rgba($color-success--light, 0.1), 0 0 4px 0 rgba($color-success--light, 0.1);
                    @include boxShadow($color-success);

                    &::before {
                        @include checkIcon($top: 6px, $left: 9px);
                    }

                    &:hover {
                        &::before {
                            color: #fff;
                        }
                    }
                } //.card-services-item__action_done .card-services-item__btn
            } //.card-services-item__action_done .card-services-item
        } //.card-services-item__action_done

        &_del {
            display: none;

            .card-services-item {
                &__text {
                    color: $gray--light;
                    font-size: 13px;
                    font-style: italic;
                    font-weight: 400;
                }

                &__btn {
                    border: 2px solid $color-accent;
                    @include boxShadow($color-accent);
                    // box-shadow: none;

                    &::before {
                        content: '';
                        display: block;
                        width: 65%;
                        height: 2px;
                        border-radius: 35px;
                        background-color: $color-accent;
                        @include centre;
                    }
                } //.card-services-item__action_del .card-services-item__btn
            }
        } //.card-services-item__action_del
    } //.card-services-item__action

    &__time {
        color: $color-text--light;
        font-size: 14px;
        font-weight: 300;
    } //.card-services-item__time

    &__info-block {
        position: absolute;
        top: 2px;
        left: -4px;
    } //.card-services-item__info-block

    &__text {
        margin-right: 8px;
        color: $color-text;
        font-size: 16px;
        font-weight: 500;

        span {
            margin-right: 5px;
            color: $gray--light;
            font-size: 14px;
            font-weight: 300;
        } //.card-services-item__text span
    } //.card-services-item__text

    &__btn {
        position: absolute;
        top: 15px;
        right: 10px;
        display: inline-flex;
        justify-content: center;
        width: 25px;
        height: 25px;
        padding: 0;
        border: 0;
        border-radius: 50%;
        background-color: #fff;
        @include boxShadow;

        @include xs-block() {
            top: 10px;
        }

        .icon {
            width: 12px;
            height: 12px;
            margin: auto;
            fill: $color-accent;
        } //.card-services-item__btn .icon
    } //.card-services-item__btn

    &__desc {
        position: relative;
        display: inline-block;
        width: 100%;
        margin-top: 10px;
        margin-bottom: 10px;
        padding: 10px 15px;
        padding-right: 45px;
        border-radius: $border-radius;
    } //.card-services-item__desc

    &__desc-hide {
        position: absolute;
        top: 0;
        right: 0;
    } //.card-services-item__desc-hide

    [data-text-container] {
        font-size: 13px;
        line-height: 1.3;
    } //.card-services-item [data-text-container]

    [data-text-container-btn] {
        color: $color-text--light;
    } //.card-services-item [data-text-container-btn]

    &.is-booked {
        @include min(sm) {
            &:hover {
                .card-services-item__action {
                    &_done {
                        display: none !important;
                    }

                    &_del {
                        display: flex;
                    }
                }
            }
        }
    } //.card-services-item.is-booked

    &.is-disabled {
        @include itemDisabled;
    } //.card-services-item.is-disabled

    &--combo {
        display: block;
    } //.card-services-item--combo
} //.card-services-item
