.studio-item {
    @include sm-block() {
        position: relative;
    }

    &:not(.studio-item--header) {
        background-color: #fff;
        @include border(top);

        @include min(sm) {
            cursor: pointer;

            &:hover,
            &:active {
                background-color: $color-gray--hover;

                .studio-item {
                    &__controls {
                        visibility: visible;
                        opacity: 1;
                    }
                }
            }
        }

        &:last-child {
            @include xs-block() {
                @include border(bottom);
            }
        }
    }

    &__inner {
        color: $gray;
        @include col;
        @include size(4);
        @include size-sm(12);
        @include flexCenter;

        @include max(sm) {
            flex-wrap: wrap;
            align-items: flex-start;
            font-size: 15px;
            font-weight: 500;

            &:last-child {
                margin-bottom: 0;
            }
        }

        @include xs-block() {
            margin: 0;
        }

        &:nth-child(2) {
            @include size-md(5);
            @include size-sm(12);

            @include max(sm) {
                margin-bottom: 0;
                margin-left: 7px;

                .text {
                    color: $gray--darkest;
                    font-size: 14px;
                    font-weight: 300;
                }
            }

            @include xs-block() {
                margin-left: -3px;
            }
        }

        &:nth-child(3) {
            @include size-md(3);
            @include size-sm(12);

            @include max(sm) {
                .studio-item__link {
                    display: none;
                }
            }
        }
    } //.studio-item__inner

    &__title {
        display: block;
        width: 100%;
        margin-bottom: $gutter / 2;
        color: $gray--darkest;
        font-size: 15px;
        font-weight: 600;

        @include min(sm) {
            display: none;
        }
    } //.studio-item__title

    &__icon {
        width: 16px;
        height: 16px;
        margin-top: 2px;
        margin-right: 5px;
        fill: $gray--light;
        color: $gray--lighten;
    } //.studio-item__icon

    &__preview-list {
        @include max(sm) {
            margin-top: 10px;
        }
    } //.studio-item__preview-list

    &__controls {
        right: 0;
        z-index: z(fixed);
        visibility: hidden;
        opacity: 0;
        margin-left: auto;
        @include centre('y');

        @include max(sm) {
            visibility: visible;
            opacity: 1;
            display: block;
            margin-left: 0;

            .control {
                &__item {
                    margin: 0;
                }
            }

            .info-block {
                margin: 0;
                padding: 10px;
                // position: absolute;
                // top: 5px;
                // right: 0;
                // z-index: z(fixed) - 1;

                &::before {
                    transform: rotate(0);
                }

                &.is-active {
                    z-index: z(fixed);
                }
            }
        }
    } //.studio-item__controls

    &__photo {
        color: $gray;
        font-weight: 500;

        &:hover {
            color: $gray--darkest;
            text-decoration: none;

            .btn-link__icon {
                fill: $gray--darkest;
            }
        }

        .btn-link {
            &__icon {
                position: relative;
                top: -1px;
                width: 16px;
                height: 16px;
                margin-right: 5px;
                fill: $gray--light;
                @include tr;
            }
        }
    } //.studio-item__photo

    &__link {
        position: absolute;
        top: 0;
        right: 52px;
        bottom: 0;
        left: 0;
        outline: none;

        @include min(xs) {
            display: none;
        }
    } //.studio-item__link

    &__user {
        .info-block {
            padding: 0;
        }

        .bb-dropdown {
            &:hover {
                .bb-dropdown {
                    &__list {
                        top: 22px;
                    }
                }
            }

            &__list {
                top: 32px;
            }
        }
    } //.studio-item__user

    &__address {
        @include xs-block() {
            margin-left: -2px;
        }
    } //.studio-item__address

    &--header {
        // background-color: $color-bg--light;
        background-color: #fff;

        @include max(sm) {
            display: none;
        }

        .text {
            color: $gray--darkest;
            font-size: 15px;
            font-weight: 500;
        }

        .studio-item__inner {
            margin-bottom: 0;
        }

        .row {
            padding: 16px 15px 14px 25px;
        }
    } //.studio-item--header

    .row {
        position: relative;
        margin: 0;
        padding: 17px 15px 16px;

        @include max(sm) {
            padding: 10px 15px;
        }

        @include xs-block() {
            padding: 12px 15px;
        }
    } //.studio-item .row

    [class^='col'] {
        @include flexCenter;

        @include xs-block() {
            margin: 0;
        }
    } //.studio-item [class^='col']

    .user {
        &__inner {
            @include flexCenter;
        } //.studio-item .user__inner

        &__img {
            width: 50px;
            height: 50px;
            margin-right: $gutter;

            @include max(sm) {
                left: 10px;
                width: 40px;
                height: 40px;
                margin-right: 0;
                @include centre('y');
            }
        } //.studio-item .user__img

        &__name {
            color: $gray;
            font-weight: 500;
            line-height: 1.5;
        } //.studio-item .user__name

        &__color {
            position: absolute;
            top: 0;
            left: 0;
            width: 4px;
            height: 100%;

            @each $theme, $color in $text-colors {
                &.#{$theme} {
                    background-color: $color;
                }
            }
        } //.studio-item .user__color
    } //.studio-item .user

    .info-block {
        margin: 0 0 0 2px;

        &::before {
            margin-left: 2px;

            @include max(sm) {
                margin-left: 0;
            }
        }
    } //.studio-item .info-block

    .color-line {
        position: absolute;
        top: 0;
        left: 0;
    } //.studio-item .color-line
} //.studio-item
