.catalog-item {
    position: relative;
    margin-bottom: 15px;
    padding: 20px 20px 0;
    @include box;

    @include xs-block() {
        flex-direction: column;
        margin: 0 -10px 15px;
        padding: 0;
        border-bottom: 1px solid $color-border--lighten;
        border-radius: 0;
        box-shadow: none;
    }

    &:hover {
        @include min(xs) {
            // box-shadow: 1px 1px 10px rgba($color-accent, 0.6);
            box-shadow: 0 2px 4px 0 rgba($color-accent, 0.2),
                0 0 4px 0 rgba($color-accent, 0.2);
        }

        .catalog-item {
            &__buttons {
                visibility: visible;
                opacity: 1;
            }
        }

        .properties {
            &__icon {
                fill: $color-text;
            }
        }
    }

    &:last-child {
        margin-bottom: 0;
    }

    &__wrap {
        position: relative;
        @include flexColumn;
    } //.catalog-item__wrap

    &__inner {
        margin-bottom: 13px;
        @include flex;

        @include xs-block() {
            flex-wrap: wrap;
            margin-bottom: 0;
        }
    } //.catalog-item__inner

    &__img {
        position: relative;
        width: 350px;
        max-width: 350px;
        margin-right: $gutter;
        overflow: hidden;

        @include sm-block() {
            border-radius: $border-radius $border-radius 0 0;
        }

        @include xs-block() {
            width: auto;
            max-width: 100%;
            margin-right: 0;
            border-radius: 0;
        }
    } //.catalog-item__img

    &__text {
        display: flex;
        flex-direction: column;
        width: calc(100% - 370px);

        @include md-block() {
            padding: 10px 15px;
        }

        @include sm-block() {
            margin-left: 10px;
            padding: 0;
        }

        @include xs-block() {
            width: 100%;
            margin-left: 0;
            padding-bottom: 8px;
        }
    } //.catalog-item__text

    &__header {
        position: relative;
        @include flexCenter;

        @include xs-block() {
            padding: 5px 10px 10px;
        }

        &-text {
            flex: 1;
        }
    } //.catalog-item__header

    &__avatar {
        width: 40px;
        height: 40px;
        margin-right: 10px;
        overflow: hidden;
        border-radius: 50%;
        @include min-screen(768) {
            display: none;
        }
    } //.catalog-item__avatar

    &__title {
        margin-top: 0;
        margin-bottom: 5px;
        color: #000;
        font-size: 18px;
        font-weight: 500;
        font-weight: 400;
        @include sm-block() {
            order: 2;
            margin-bottom: 10px;
        }
        @include xs-block() {
            margin-bottom: 5px;
            font-size: 16px;
            font-weight: 500;
        }
    } //.catalog-item__title

    &__address {
        margin-bottom: 15px;
        @include sm-block() {
            order: 3;
            margin-bottom: 5px;
            font-size: 13px;
            line-height: 1.2;
        }
        @include xs-block() {
            margin-bottom: 0;
        }

        .place-text {
            &__address {
                &::before {
                    left: 4px;
                    color: #d5d5d5;
                }
            }
        }
    } //.catalog-item__address

    &__info {
        position: relative;
        margin-bottom: 10px;
        @include flexCenter;
        @include sm-block() {
            order: 1;
            margin-bottom: 0;
            padding: 5px 0;
        }
        @include xs-block() {
            padding: 5px 10px;
        }
    } //.catalog-item__info

    &__rating {
        margin-right: 40px;
    } //.catalog-item__rating

    &__properties {
        @include sm-block() {
            margin-left: auto;
        }
    } //.catalog-item__properties

    &__desc {
        position: relative;
        display: block;
        max-height: 48px;
        margin-bottom: 20px;
        overflow: hidden;
        color: #000;
        font-size: 13px;
        font-weight: 300;
        line-height: 1.3;

        @include sm-block() {
            margin-bottom: 5px;
        }

        @include xs-block() {
            display: none;
        }

        // .fa {
        //     position: absolute;
        //     right: 26px;
        //     bottom: -3px;
        //     background-color: #fff;
        // }

        .btn-link {
            position: absolute;
            right: 0;
            bottom: 0;
            background-color: #fff;
        } //.catalog-item__desc .btn-link
    } //.catalog-item__desc

    &__bottom {
        display: flex;
        align-items: center;
        margin-top: auto;
    } //.catalog-item__bottom

    &__buttons {
        visibility: hidden;
        opacity: 0;
        display: flex;
        align-items: center;
        margin-left: auto;
        @include tr;
        @include sm-block() {
            visibility: visible;
            opacity: 1;
            justify-content: center;
            margin-left: 0;
            padding: 10px 0;
            border-top: 1px solid $color-border--lightest;
        }
        @include max(i5) {
            visibility: visible;
            opacity: 1;
            margin-bottom: 10px;

            .icon {
                display: block;
            }
        }

        .button-animate {
            font-size: 14px;

            &:first-child {
                margin-right: 15px;
            }
        }
    } //.catalog-item__buttons

    &__btn {
        &-subscribe {
            margin-left: auto;
            padding: 3px 15px 4px;
            font-size: 13px;
            font-weight: 300;
        }
    } //.catalog-item__btn

    &__fav {
        position: absolute;
        top: -6px;
        right: -6px;
        z-index: 3;
        display: inline-flex;
        align-items: center;
        width: 35px;
        height: 35px;
        border: 0;
        background-color: transparent;
        color: $gray--lighten;
        cursor: pointer;
        @include tr;

        @include xs-block() {
            top: -2px;
            right: 5px;
        }

        &::before {
            margin: auto;
            font-size: 21px;
            line-height: 21px;
        }
    } //.catalog-item__fav

    &__who {
        color: #000;
        font-size: 14px;
        font-style: italic;
        font-weight: 600;
        @include flexCenter;

        .num {
            margin-left: auto;
            color: $gray--light;

            @include xs-block() {
                width: 100%;
                text-align: right;
            }

            small {
                font-size: 14px;
                font-style: italic;
            }
        }
    } //.catalog-item__who

    &--small {
        display: block;
        width: 300px;
        margin: 0;
        padding: 0;
        // border: 0;
        @include flexColumn;

        @include xs-block() {
            border-radius: $border-radius;
            box-shadow: $bxs-box;
        }

        &:last-child {
            margin-bottom: auto;
        }

        .catalog-item {
            &__wrap {
                flex-direction: row;
            }

            &__inner {
                flex-direction: column;
                margin-bottom: 0;
            }

            &__img {
                width: 100%;
                max-width: 100%;
                margin-right: 0;
                border-radius: $border-radius $border-radius 0 0;
            }

            &__header {
                padding: 10px 10px 0;
            }

            &__fav {
                top: 2px;
                right: 5px;
            }

            &__text {
                width: 100%;
                margin-left: 0;
                // border-left: 1px solid $color-border--lightest;
                // border-right: 1px solid $color-border--lightest;
            }

            &__price {
                // border-left: 1px solid $color-border--lightest;
                // border-right: 1px solid $color-border--lightest;
                // border-bottom: 1px solid $color-border--lightest;
            }

            &__avatar {
                display: block;
            }

            &__info {
                margin-bottom: 0;
                padding: 5px 10px;
            }

            &__title {
                font-size: 15px;
            }

            &__address {
                margin-bottom: 5px;
                font-size: 13px;
                line-height: 1.2;
            }

            &__desc {
                display: none;
            }

            &__rating {
                margin-right: 0;
            }

            &__properties {
                margin-left: auto;
            }

            &__buttons {
                visibility: visible;
                opacity: 1;
                justify-content: center;
                padding: 10px;
                border-top: 1px solid $color-border--lightest;
                @include flexCenter;
            }
        }

        .price {
            &__item {
                padding: 8px 10px;
            }
        }

        .button-animate {
            padding: 5px 20px;
        }
    } //.catalog-item--small

    &--lk {
        padding: $gutter;
        @include xs-block() {
            padding: $gutter/2;
        }

        .page-title {
            &__wrap {
                @include xs-block() {
                    min-height: initial;
                }
            }
        }

        .catalog-item {
            @include xs-block() {
                &__header,
                &__info {
                    padding: 0;
                }
            }

            &__info {
                @include xs-block() {
                    flex-wrap: wrap;
                }
            }

            &__inner {
                margin-bottom: 0;
            }

            &__title {
                font-weight: 500;
            }

            &__text {
                width: 100%;
            }

            &__desc {
                max-height: initial;
                margin-bottom: $gutter/2;
                overflow: auto;
                font-size: 14px;
                @include xs-block() {
                    display: block;
                    margin-top: $gutter/2;
                }
            }

            &__address {
                margin-bottom: 0;
                @include md-block() {
                    order: initial;
                }
            }

            &__who {
                @include xs-block() {
                    flex-wrap: wrap;

                    .num {
                        width: 100%;
                        text-align: right;
                    }
                }
            }
        }

        .price {
            border-top: 0;

            &__num {
                font-size: 15px;

                i {
                    font-size: 12px;
                }
            }
        }

        .properties {
            margin-left: $gutter/2;
            @include xs-block() {
                width: 100%;
                margin-left: 0;

                &__list {
                    flex-wrap: wrap;
                }
            }

            &__item {
                position: relative;
                width: auto;
                height: auto;
                color: $gray--darkest;
                font-size: 14px;
                font-style: italic;
                font-weight: 600;
                cursor: default;
                @include xs-block() {
                    &:first-child {
                        &::before {
                            display: none;
                        }
                    }
                }

                &::before {
                    content: '/';
                    margin-right: $gutter/2;
                }
            }
        }

        .place-text {
            display: inline-block;
            font-style: italic;
            font-weight: 600;

            &__metro {
                display: inline-block;
            }
        }
    } //.catalog-item--lk

    .price {
        &__list {
            padding-bottom: 4px;
        }

        &__time {
            @include sm-block() {
                display: none;
            }
        }
    }
} //.catalog-item

.properties {
    &__list {
        display: flex;
        align-items: center;
    } //.properties__list

    &__item {
        justify-content: center;
        width: 25px;
        height: 25px;
        line-height: 25px;
        cursor: help;
        @include flexCenter;

        & + & {
            margin-left: 5px;
            @include xs-block() {
                margin-left: 5px;
            }
        }

        &_procent,
        &_sale {
            .icon {
                width: 13px;
                height: 13px;
            }
        }

        &_car {
            .icon {
                width: 20px;
                height: 20px;
            }
        }

        .icon {
            display: block;
            width: 23px;
            height: 23px;
            margin: auto;
            fill: #000;
        }

        i {
            color: #000;
            font-size: 20px;
            line-height: 1;
        }

        .bb-dropdown__list {
            left: -5px;
            border: 0;
            background-color: rgba($color-btn--darken, 0.95);
            color: #fff;
            font-size: 13px;

            &::before {
                top: -5px;
                width: 10px;
                height: 10px;
                border: 0;
                border-top-right-radius: 2px;
                background-color: rgba($color-btn--darken, 0.95);
            }
        } //.properties__item.bb-dropdown__list
    } //.properties__item

    &__title {
        font-size: 13px;
    } //.properties__title
} //.properties

.price {
    &__list {
        flex-direction: column;
        @include flex;
    } //.price__list

    &__item {
        position: relative;
        padding: 8px 0;
        border-top: 1px solid $color-border--lightest;
        @include flexCenter;

        @include xs-block() {
            padding: 8px 10px;
        }
    } //.price__item

    &__box {
        width: 100%;
        @include flexCenter;
    } //.price__box

    &__inner {
        width: 50%;
        @include text-overflow;
        @include flexCenter;

        &:last-child {
            text-align: right;
        }
    } //.price__inner

    &__num {
        margin-left: auto;
        color: $gray--darkest;
        font-size: 16px;
        font-weight: 500;
        text-align: right;

        &_wrap {
            margin-left: auto;

            .price__num {
                margin-left: 15px;
            }
        }

        &_old {
            color: $color-text--light;
            font-size: 14px;
            text-decoration: line-through;

            i {
                font-size: 12px !important;
            } //.price__num_old i

            .icon {
                width: 9px;
                height: 9px;
            } //.price__num_old .icon
        } //.price__num_old

        i {
            color: $color-icon;
            font-size: 12px;
        } // .price__num i

        span {
            margin-right: 3px;
            color: $gray--light;

            font-size: 14px;
        }
    } //.price__num

    &__text {
        margin-right: 15px;
        color: $color-text--darkest;
        font-size: 14px;
        white-space: nowrap;
    } //.price__text

    &__time {
        color: $color-text--light;
        font-size: 14px;
        font-weight: 300;
        @include flexCenter;

        i {
            margin-right: 7px;
            font-size: 13px;
        }

        .icon {
            width: 13px;
            height: 13px;
            margin-right: 7px;
        }
    } //.price__time
} //.price

.place-text {
    position: relative;
    color: #000;
    font-size: 14px;
    font-weight: 300;

    &__wrap {
        @include flexCenter;
        @include xs-block() {
            flex-wrap: wrap;
        }

        &:last-child {
            .place-text__box {
                margin-bottom: 0;
            }
        }

        .place-text__box {
            &:first-child {
                width: 60%;
                @include xs-block() {
                    width: 100%;
                    margin-bottom: 0;
                }
            }

            &:last-child {
                width: 40%;
                @include xs-block() {
                    width: 100%;
                }
            }
        }
    } //.place-text__metro

    &__box {
        margin-bottom: $gutter;

        .place-text {
            margin-bottom: 0;
        }
    }

    &__metro {
        position: relative;
        display: block;
    } //..place-text__metro

    &__address {
        display: flex;
        flex-wrap: wrap;
        margin-right: 30px;

        span {
            margin-right: 5px;
        }
    } //.place-text__address

    &--icon {
        padding-left: 30px;
    } //.place-text--icon
} //.place-text
