.tab {
    &__inner {
        padding: $gutter;

        .request-item {
            @include sm-block() {
                width: 100%;
            }
        }
    } //.tab__inner

    &__titles {
        position: relative;
        border-bottom: 1px solid $color-border--lighten;
        @include flexCenter;

        &--scroll {
            overflow-x: auto;
            overflow-y: hidden;
        } //.tab__titles--scroll

        &--big {
            .tab {
                &__title {
                    &.ui-tabs-active {
                        .tab__link {
                            color: $color-text;
                        }
                    }
                }

                &__link {
                    color: #bfbfbf;
                    font-size: 13px;
                    font-weight: 600;
                    text-transform: uppercase;
                }
            }
        } //.tab__titles--big
    } //.tab__titles

    &__title {
        width: 100%;
        margin: 0 10px -1px;
        outline: none;
        text-align: center;
        white-space: nowrap;

        &:hover {
            &:not([data-slide-line]) {
                @include min(xs) {
                    .tab__link {
                        border-bottom: 3px solid lighten($gray--dark, 40%);
                    }
                }
            }
        } //.tab__title:hover

        &:first-child {
            margin-left: 0;
        } //.tab__title:first-child

        &:last-child {
            margin-right: 0;
        } //.tab__title:last-child

        &:not([data-slide-line]) {
            .tab__link {
                border-bottom: 3px solid transparent;
            }

            &.is-active,
            &.ui-state-active {
                .tab {
                    &__link {
                        border-bottom: 3px solid $gray--dark;
                    } //.tab__title.ui-state-active .tab__link
                }
            }
        }

        &.is-active,
        &.ui-state-active {
            .tab {
                &__icon {
                    fill: $color-accent;
                    color: $color-accent;

                    &.icon {
                        margin-top: -1px;
                    }
                } //.tab__title.ui-state-active .tab__icon
            } //.tab__title.ui-state-active .tab
        } //.tab__title.ui-state-active
    } //.tab__title

    &__link {
        display: flex;
        align-items: center;
        justify-content: center;
        padding: 5px 30px 6px;
        transition: none;
        // border-bottom: 3px solid transparent;
        outline: none;
        color: $color-text;
        cursor: pointer;
        @include nosel;

        @include xs-block() {
            justify-content: flex-start;
            padding: 5px 0 6px;
        }

        &:hover {
            color: $color-text;
        }

        span {
            display: none;
        }
    } //.tab__link

    &__icon {
        margin-right: 7px;
        color: $color-icon;
        font-size: 14px;
    } //.tab__icon

    &__content {
        display: none;
        padding: 15px 30px;
    } //.tab__content

    &__line {
        position: absolute;
        bottom: 0;
        left: 0;
        display: block;
        height: 2px;
        transition: all 0.5s;
        border-radius: 5px;
        background: $color-accent;
    } //.tab__line

    &--two {
        .tab {
            &__titles {
                justify-content: center;
            }

            &__title {
                width: auto;
                margin: 0 0 -1px;

                &:not(.ui-state-active) {
                    &:hover,
                    &.ui-state-hover {
                        .tab__link {
                            border-bottom: 1px solid $color-border--lighten;
                            border-color: #e8e8e8 #e8e8e8 transparent;
                            background-color: #e8e8e8;

                            @include max(xs) {
                                border-color: transparent;
                                background-color: transparent;
                            }
                        }
                    }
                }

                &.is-active,
                &.ui-state-active {
                    .tab__link {
                        border-bottom: 1px solid #fbfbfb;
                        border-top-color: $color-border--lighten;
                        border-right-color: $color-border--lighten;
                        border-left-color: $color-border--lighten;
                    }

                    .tab__icon {
                        color: $color-accent;
                    }
                }
            }

            &__link {
                width: auto;
                border: 1px solid;
                border-top-left-radius: 3px;
                border-top-right-radius: 3px;
                border-color: transparent;
                font-size: 15px;
                font-weight: 500;

                @include max(xs) {
                    padding: 5px 10px;
                }
            }
        }
    } //.tab--two

    &--transform {
        .tab {
            &__titles {
                @include xs-block() {
                    flex-direction: column;
                    border: 0;
                }
            } //.tab--transform .tab__titles

            &__title {
                @include xs-block() {
                    position: relative;
                    width: 100%;
                    margin: 0 0 30px;
                    padding: 0;
                    border-bottom: 1px solid $color-border--light;
                    text-align: left;

                    &::after {
                        content: '\f105';
                        right: 0;
                        color: $color-icon;
                        font-size: 21px;
                        @include fa('light');
                        @include centre('y');
                    }

                    &:hover {
                        .tab__link {
                            border-color: transparent;
                        }
                    }
                }
            } //.tab--transform .tab__title

            &__link {
                @include xs-block() {
                    padding: 8px 0 5px;
                    border: 0;
                    color: $gray--darken;
                }

                span {
                    font-size: 12px;
                    font-weight: 400;
                    line-height: 1.4;

                    @include xs-block() {
                        display: block;
                    }
                } //.tab--transform .tab__link span
            } //.tab--transform .tab__link

            &__inner {
                padding: 0;
                background-color: #fff;

                @include max(xs) {
                    padding: 15px;
                }
            } //.tab--transform .tab__inner
        } //.tab--transform .tab
    } //.tab--transform
} //.tab
