.firstscreen {
    position: relative;
    display: flex;
    align-items: center;
    justify-content: center;
    height: 100vh;
    overflow: hidden;

    @include xs-block() {
        height: calc(100vh - 100px);
        padding: 0;
    }

    @include i5-block() {
        height: 100vh;
    }

    @include min(sm) {
        height: 500px;
    }

    @include min(md) {
        height: 90vh;
        max-height: 700px;
    }

    &__inner {
        position: relative;
        z-index: 2;
        display: flex;
        color: #fff;

        @include sm-block() {
            flex-wrap: wrap;
            justify-content: center;
        }

        @include xs-block() {
            width: 100%;
        }

        @include i5-block() {
            padding: 0 10px;
        }

        @include min(md) {
            width: 100%;
            max-width: 1200px;
            text-align: left;
        }
    } // .firstscreen__inner

    &__wrapper {
        display: flex;
        flex-direction: column;
        justify-content: flex-start;

        @include xs-block() {
            width: 100%;
        }

        @include min(i7) {
            align-items: baseline;
            justify-content: center;
            max-width: 585px;
        }
    } // .firstscreen__wrapper

    &__left {
        position: relative;
        box-sizing: content-box;
        align-self: center;
        padding: 0 50px;
        @include flexColumn;

        @include sm-block {
            z-index: 2;
        }
        @include xs-block() {
            padding: 20px 10px;
        }
    } //.firstscreen__left

    &__right {
        align-items: center;
        justify-content: center;
        padding-right: 50px;
        text-align: center;
        @include flexColumn;

        @include md-block() {
            padding: 0;
        }

        @include sm-block() {
            width: 100%;
            background-color: $color-bg--lightest;

            .offer {
                margin-right: 0;
                margin-left: 0;
                background-color: transparent;
            }
        }

        @include xs-block() {
            padding: 40px 0;
        }
    } //.firstscreen__right

    &__search {
        margin: 0 auto 60px 0;

        @include xs-block() {
            margin-bottom: 40px;

            .bb-input {
                .icon-search {
                    display: none !important;
                }
            }
        }

        .search {
            margin-bottom: 0;

            &__hint {
                right: 0;
                left: 0;
                border-radius: $border-radius;
            }

            i {
                display: none;

                &.fa-search {
                    display: none !important;
                }
            }
        }
    } //.firstscreen__search

    &__title {
        margin-top: 30px;
        margin-bottom: 20px;
        color: #fff;
        font-size: 20px;
        font-weight: 500;
        line-height: 1.4;
        text-shadow: 0 3px 6px rgba(0, 0, 0, 0.6);
        text-transform: uppercase;
        text-transform: uppercase;

        @include sm-block() {
            text-align: center;
        }

        @include xs-block() {
            width: 100%;
        }

        @include min(i7) {
            margin-top: 0;
            font-size: 27px;
        }

        @include min(sm) {
            font-size: 40px;
        }

        @include min(md) {
            width: 100%;
            margin-bottom: 30px;
            font-size: 50px;
            line-height: 1.3;
        }
    } // .firstscreen__title

    &__subtitle {
        // margin-bottom: 50px;
        color: #fff;
        font-size: 22px;
        font-weight: 300;
        line-height: 1.4;
        @include textShadow;

        @include md-block() {
            font-size: 18px;
        }

        @include sm-block() {
            // margin-bottom: 30px;
            text-align: center;
        }

        @include xs-block() {
            width: 100%;
        }
    } // .firstcreen__subtitle

    &__footnote {
        font-size: 14px;
        font-weight: 300;
        text-shadow: 0 3px 6px rgba(0, 0, 0, 0.6);

        @include min(md) {
            display: none;
        }
    } // .firstscreen__footnote

    &__btn {
        align-self: center;
        // width: 100%;
        // max-width: 320px;
        margin-top: 50px;
        box-shadow: 0 3px 6px rgba(0, 0, 0, 0.15);
        font-size: 17px;

        @include min(md) {
            align-self: auto;
            // max-width: 200px;
        }

        @include sm-block() {
            margin-bottom: 30px;
        }

        @include xs-block() {
            width: 100%;
        }

        &:hover {
            transform: none;
            box-shadow: inherit;
        }
    } // .firstscreen__btn

    &__bg {
        position: absolute;
        top: 0;
        right: 0;
        bottom: 0;
        left: 0;
        z-index: 0;
        display: block;
        width: auto;
        height: 100%;
        transform: translateZ(-1px);

        @include xs-block() {
            right: auto;
            left: 50%;
            transform: translateZ(-1px) translateX(-50%);
        }
    } // .firstscreen__bg

    &__container {
        justify-content: center;
        @include flexCenter;
    } // .firstscreen__container

    .form {
        @include max(md) {
            margin-left: 30px;
        }
    } //.firstscreen .form

    .mouse-icon {
        position: absolute;
        bottom: 60px;
        left: 50%;
        transform: translateX(-50%);

        @include sm-block() {
            bottom: 20px;
        }

        @include xs-block() {
            bottom: 30px;
        }
    } //.firstscreen .mouse-icon

    .inform-block {
        @include sm-block() {
            align-items: center;
            justify-content: center;
        }

        &__title {
            color: #fff;
            font-size: 40px;
            text-align: left;

            @include sm-block() {
                font-size: 34px;
                line-height: 1.4;
                text-align: center;
                text-transform: uppercase;
            }
        } //.firstscreen .inform-block__title

        &__text {
            color: #fff;
            font-size: 18px;
            font-weight: 300;

            @include sm-block() {
                text-align: center;
            }
        } //.firstscreen .inform-block__text

        &__btn {
            &.btn-default {
                box-shadow: none;
            } //.firstscreen .inform-block__btn.btn-default
        } //.firstscreen .inform-block__btn
    } //.firstscreen .inform-block

    // .btn-default {
    //     box-shadow: 0 2px 2px 0 rgba(0, 0, 0, 0.24);
    // } //.firstscreen .btn-default

    &.instagram-border {
        @include min(md) {
            max-height: 1055px;
        }
    }

    &--toner {
        &::before {
            content: '';
            position: absolute;
            top: 0;
            right: 0;
            bottom: 0;
            left: 0;
            z-index: 1;
            display: block;
            background-color: rgba(0, 0, 0, 0.5);
        }
    }

    &--market {
        @include min(md) {
            height: 100%;
            min-height: 500px;
            max-height: 100%;
        }

        .firstscreen {
            &__bg {
                width: 100%;
            }
        }
    }
} //.firstscreen

.firstscreen-img {
    @include cover;

    @each $page, $desctop-img,
        $mobile-img
            in (
                home,
                '../files/img/bg/home@2.jpg',
                '../files/img/bg/home@1.jpg'
            ),
        (
            registration,
            '../files/img/bg/onepage@2.jpg',
            '../files/img/bg/onepage@1.jpg'
        ),
        (
            hero,
            '../files/img/onepage/icon/firstscreen-hero.jpg',
            '../files/img/bg/onepage@1.jpg'
        )
    {
        &--#{$page} {
            background-image: url(#{$desctop-img});

            @if $mobile-img {
                @include xs-block() {
                    background-image: url(#{$mobile-img});
                }
            }
        }
    }
} //.firstscreen-img
