.move-block {
    position: fixed;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    z-index: z(popup);
    display: flex;
    flex-direction: column;
    overflow: hidden;
    border-radius: 0;
    background-color: #fff;

    @include md-block() {
        // opacity: 0;
        // transform: translate(0, 100vh);
        transition: transform 0.3s ease-in-out, opacity 0.3s ease-in-out;
    }

    @include xs-block() {
        top: -70px;
        padding-bottom: 70px;
        transform: translateY(70px);
        // z-index: z(popup);
        // transform: translate(0, 100vh);
    }

    &:not(.is-open) {
        // opacity: 0;
        // visibility: hidden;
        // position: absolute;
        width: 1px;
        height: 1px;
        margin: -1px;
        padding: 0;
        // overflow: hidden;
        // clip: rect(0 0 0 0);
        transform: translateY(50%);
        border: 0;
        border-radius: 0;
        // top: 100%;
        pointer-events: none;
        touch-action: none;
        // white-space: nowrap;
        // clip-path: inset(50%);

        @include xs-block() {
            transform: translateY(100%);
        }
    } //.move-block:not(.is-open)

    &__header {
        position: relative;
        flex-shrink: 0;
        justify-content: center;
        height: 50px;
        padding: 0 20px;
        color: $gray--darkest;
        font-size: 18px;
        @include border(bottom);
        @include flexCenter;

        @include xs-block() {
            padding: 0 10px;
            font-size: 16px;
            font-weight: 500;
        }

        &_title {
            @extend %styleBlockHeader;
        } //.move-block__header_title

        .bb-select {
            width: auto;
            @include centre;

            select {
                width: auto;
                padding: 0;
                border: 0;
            }
        }

        &.no-border {
            border: 0;
        } //.move-block__header.no-border

        .select2-container--default {
            &.select2-container--open {
                .select2-selection--single {
                    border-bottom: 1px solid;
                }
            }

            .select2-selection--single {
                border-bottom: 0;

                .select2-selection__arrow b {
                    top: 0;
                }

                .select2-selection__rendered {
                    font-size: 17px;
                }
            }
        }
    } //.move-block__header

    &__body {
        // pointer-events: auto;
        flex-grow: 1;
        width: 100%;
        border-radius: 0 0 $border-radius $border-radius;
        background-color: $color-bg--lightest;
        @include scroll;
        @include flexColumn;

        &.tab__inner {
            @include xs-block() {
                padding: 15px;
            }
        } //.move-block__body.tab__inner

        &.bg--white {
            background-color: #fff;
        } //.move-block__body.bg--white

        &.no-scroll {
            overflow: inherit;
        } //.move-block__body.no-scroll

        &.has-offset {
            padding-right: 10px;
            padding-left: 10px;
        } //.move-block__body.has-offset
    } //.move-block__body

    &__inner {
        padding: 10px;
        background-color: #fff;
    } //.move-block__inner

    &__in {
        height: 100%;
        padding: 0 10px;
        overflow-y: auto;
        @include md-block() {
            padding: 10px;
        }
        @include xs-block() {
            padding: 0 10px;
        }
    } //.move-block__in

    &__footer {
        @include footerBtn;

        .text {
            display: block;
            margin-bottom: 10px;
            font-size: 15px;
            text-align: center;
        } //.move-block__footer .text

        &.no-border {
            border: 0;
        } //.move-block__footer.no-border

        &.no-offset {
            padding: 0;
        } //.move-block__footer.no-offset
    } //.move-block__footer

    &__title {
        margin-bottom: 25px;

        span {
            font-size: 26px;
            font-weight: 500;

            @include min(xs) {
                display: none;
            }
        }
    } //.move-block__title

    &__close {
        @include btnBlockClose;
    } //.move-block__close

    &__done {
        right: 15px;
        height: 50px;
        transform: translate(0, -50%);
        font-size: 12px;
        font-weight: 600;
        line-height: 50px;
        text-transform: uppercase;
        @include centre('y');

        @include xs-block() {
            right: 10px;
            padding: 0;
            font-size: 12px;
        }

        &:hover {
            transform: translate(0, -50%);
            text-decoration: none;
        }
    } //.move-block__done

    &__btn {
        &--done {
            @extend .move-block__done;
        } //.move-block__btn--done
    } //.move-block__btn

    &__back {
        left: 15px;
        @include centre('y');

        &:hover {
            @include tY(-50%);

            .icon {
                fill: $color-icon--hover;
            }
        }

        .icon {
            width: 21px;
            height: 21px;
            fill: $color-icon;
        } //.move-block__back .icon
    } //.move-block__back

    &__item {
        min-height: 50px;
        margin-bottom: 10px;

        &:first-child {
            margin-top: 10px;
        }

        &--btn {
            font-size: 16px;
            font-weight: 500;
            cursor: pointer;
        } //.move-block__item--btn
    } //.move-block__item

    &__box {
        @include visibleBlock;
    } //.move-block__box

    &__checkbox {
        margin-bottom: 20px;

        @include xs-block() {
            margin-bottom: 35px;
        }
    } //.move-block__checkbox

    &__icon {
        width: 22px;
        height: 22px;
        margin-right: 10px;
    } //.move-block__icon

    &__date {
        font-weight: 500;
    } //.move-block__date

    &__validate {
        position: absolute;
        top: -65px;
        right: 10px;
        left: 10px;
        display: none;
        text-align: center;
    } //.move-block__validate

    &.is-open {
        // z-index: z(popup);
        // display: flex;
        @include md-block() {
            opacity: 1;
            transform: translateY(70px) translateX(0);
        }

        // .move-block__footer,
        // .move-block__header {
        //     // pointer-events: none;
        //     // touch-action: none;

        //     button,
        //     a {
        //         pointer-events: auto;
        //         touch-action: all;
        //     }
        // }

        .service-select {
            &__list {
                height: 100%;
                @include scroll;
            } //.move-block.is-open .service-select__list
        }
    } //.move-block.is-open

    &.is-close {
        @include xs-block() {
            transform: translateY(100vh) !important;
        }
        // opacity: 0 !important;

        &.move-block--small {
            transform: translateY(50%) !important;
        }
    } //.move-block.is-close

    &.is-overhead {
        z-index: 1011 !important;
    } //.move-block.is-overhead

    &.has-category {
        .service-select {
            &__list {
                overflow-y: hidden;
            } //.move-block.has-category .service-select__list

            &__picker {
                height: 100%;
                overflow-y: auto;
                @include tX(0);
            } //.move-block.has-category .service-select__picker
        } //.move-block.has-category .service-select
    } //.move-block.has-category

    &.has-add-btn {
        .move-block__body {
            margin-top: 55px;
            padding-top: 10px;
        }
    } //.move-block.has-add-btn

    &--no-scroll {
        .move-block {
            &__body {
                overflow-y: hidden;
            }
        }
    } //.move-block--no-scroll

    &--mobile {
        .move-block {
            &__header {
                padding: 0;

                .bb-input {
                    width: 100%;
                }

                .select2-container {
                    height: 50px;
                    padding: 0 15px;
                    line-height: 50px;
                }

                .select2-container .select2-selection--single {
                    height: 49px;
                    line-height: 52px;

                    .select2-selection__rendered {
                        height: 50px;
                        padding-right: 0;
                        line-height: 52px;
                        text-align: center;

                        span {
                            position: relative;

                            &::after {
                                content: '\f107';
                                margin-left: 5px;
                                @include fa('light');
                            }
                        }
                    }

                    .select2-selection__arrow b {
                        display: none;
                    }
                }

                .select2-container--default.select2-container--open.select2-container--below
                    .select2-selection--single:hover {
                    border: 0;
                } //.move-block--mobile .move-block__header .select2-container--default.select2-container--open.select2-container--below .select2-selection--single:hover
            } //.move-block--mobile .move-block__header
        } //.move-block--mobile

        .user {
            cursor: pointer;

            &__phone {
                color: $gray--light;
            }

            &__icon {
                right: 0;
                padding: 10px 8px;
                color: $color-text;
                font-size: 20px;
                @include centre('y');
            } //.move-block--mobile .user__icon
        } //.move-block--mobile .user

        .aplication-item {
            margin-top: 10px;
            padding: 15px 20px;
            border-radius: 0;
            box-shadow: none;
            @include border(top);
            @include border(bottom);

            @include xs-block() {
                padding: 16px;
            }

            &__row {
                justify-content: center;
                margin-bottom: 15px;
            }

            &__col {
                @include size-sm(12);

                & + .aplication-item__col {
                    margin-top: 10px;
                }
            }

            &__service {
                margin-bottom: 0;
            }

            &__btn {
                &--edit {
                    margin: auto;

                    @include xs-block() {
                        padding: 5px 0;
                        font-size: 14px;
                    }
                } //.move-block .aplication-item__btn--edit
            } //.move-block .aplication-item__btn
        } //.move-block--mobile .aplication-item

        .aplication-info {
            // padding: 15px 10px;

            &__wrap {
                // position: absolute;
                // top: 0;
                // right: 0;
                // bottom: 123px;
                // left: 0;
                overflow-y: auto;
            } //.move-block--mobile .aplication-info__wrap
        } //.move-block .aplication-info

        .search-client {
            display: block;
            height: 100%;
            background-color: #fff;

            @include xs-block() {
                flex: 1;
            }

            &__form {
                position: absolute;
                top: 50px;
                right: 0;
                left: 0;
                z-index: z(fixed);
                padding: 10px;
                @include flexCenter;

                .search {
                    margin-right: 15px;

                    @include xs-block() {
                        margin-right: 10px;
                    }
                } //.move-block--mobile .search-client__form .search
            } //.move-block--mobile .search-client__form

            &__icon {
                &--add {
                    flex-shrink: 0;
                    width: 40px;
                    height: 40px;
                    // padding: 10px;
                    cursor: pointer;

                    .icon {
                        width: 20px;
                        height: 20px;
                        fill: $color-icon--dark;
                    }
                } //.move-block--mobile .search-client__icon--add
            } //.move-block--mobile .search-client__icon

            .btn {
                display: block;
                margin: 7px auto 0;
                padding: 6px 15px;
                border-radius: 50px;
                background-color: $color-accent;
                box-shadow: 0 2px 15px #a5a5a596;
                color: #fff;
                font-size: 14px;

                &:hover {
                    text-decoration: none;
                }
            } //.move-block--mobile .search-client .btn

            .user {
                padding: 10px;
                @include border(bottom);
            } //.move-block .search-client .user

            .aplication__in {
                height: 100%;
                margin-top: 65px;
                background-color: #fff;
                @include scroll;
            } //.move-block .search-client .aplication__in
        } //.move-block--mobile .search-client

        .new-client {
            z-index: inherit;
            @include visibleBlock;

            &__form {
                min-height: 100%;
                background-color: #fff;
            } //.move-block .new-client__form

            &.is-open {
                z-index: z(popup);
            } //.move-block .new-client.is-open

            .bb-text {
                margin-bottom: 0;

                @include xs-block() {
                    margin-bottom: 25px;
                }
            } //.move-block .new-client .bb-text

            .move-block {
                &__body {
                    background-color: #fff;
                } //.move-block .new-client .move-block__body
            } //.move-block .new-client .move-block

            .select2-container--default {
                .select2-selection--single {
                    @include border(bottom);
                }
            }
        } //.move-block .new-client

        .aplication-success {
            &__user {
                // margin-bottom: 5px;
                padding: 10px;
                border-bottom: 0;
                background-color: #fff;

                .user {
                    &__info-block {
                        flex-shrink: 0;

                        @include xs-block() {
                            margin-left: 0;
                        }
                    }

                    &__text {
                        flex: 1;
                    }
                }
            } //.move-block--mobile .aplication-success__user

            &__tab {
                padding: 0;

                @include xs-block() {
                    position: absolute;
                    top: 63px;
                    right: 0;
                    bottom: 0;
                    left: 0;
                    flex-grow: 1;
                    // height: 100%;
                }

                .tab {
                    background-color: #fff;

                    &__titles {
                        padding: 9px 10px 0;
                        @include border(top);
                    } //.move-block--mobile .aplication-success__tab .tab__titles

                    &__content {
                        // top: 163px;
                        // bottom: auto;
                        flex-grow: 1;
                        background-color: #fff;

                        @include xs-block() {
                            position: absolute;
                            top: 50px;
                            right: 0;
                            bottom: 0;
                            left: 0;
                            @include scroll;
                        }

                        &#aplication-chat {
                            bottom: 0;
                            border-bottom: 0;
                        } //.move-block--mobile .aplication-success__tab .tab__content#aplication-chat

                        &#aplication-history,
                        &#aplication-info {
                            // height: 100%;
                        } //.move-block--mobile .aplication-success__tab .tab__content#aplication-history

                        &#aplication-history {
                            .box-inline__wrap {
                                // position: absolute;
                                // top: 0;
                                // right: 0;
                                // bottom: 123px;
                                // left: 0;
                                // padding: 15px 10px;
                                // overflow-y: auto;
                            }
                        } //.move-block--mobile .aplication-success__tab .tab__content#aplication-history
                    } //.move-block--mobile .aplication-success__tab .tab__content

                    &__inner {
                        padding: 15px;
                    } //.move-block--mobile .aplication-success__tab .tab__inner

                    &__link {
                        justify-content: center;
                        padding: 5px 0 11px;
                    } //.move-block--mobile .aplication-success__tab .tab__link
                } // .aplication-success .tab
            } // .move-block--mobile .aplication-success__tab
        } // .move-block--mobile .aplication-success

        &.aplication-success {
            position: inherit;

            @include max(xs) {
                position: fixed;
            }

            .move-block {
                &__body {
                    overflow: hidden;

                    @include xs-block() {
                        position: relative;
                    }
                } //.aplication-success .move-block__body
            } //.aplication-success .move-block
        } //.move-block .aplication-success

        &.aplication-comments {
            .move-block {
                &__body {
                    background-color: #fff;
                }

                &__inner {
                    padding: 20px 10px;
                }
            }
        } //.move-block--mobile.aplication-comments

        .bb-date {
            width: 100%;
            margin: 0;

            &__title {
                margin-bottom: 0;
            }

            &__input {
                font-size: 16px;

                &:focus {
                    box-shadow: none;
                }
            }
        } //.aplication-success .bb-date

        .select2-container {
            .select2-selection--single {
                height: 37px;
                line-height: 37px;

                .select2-selection__rendered {
                    padding-right: 25px;
                    padding-left: 0;
                    font-size: 16px;
                    line-height: 37px;

                    .custom-select__results {
                        span {
                            &:first-child {
                                flex-grow: 1;
                                @include text-overflow;
                            }

                            &:not(:first-child) {
                                font-size: 14px;
                            }

                            &:nth-child(2) {
                                margin-left: auto;
                            }

                            &:nth-child(3) {
                                margin-right: 5px;
                            }
                        }
                    }
                }
            }
        } //.aplication-success .select2-container

        .select2-container--default
            .select2-selection--single[aria-expanded='false'] {
            &:hover {
                border: 0;
            }
        }

        .select2-container--default {
            &.select2-container--open,
            &.select2-container--open.select2-container--below {
                .select2-selection--single {
                    border-bottom: 1px solid $color-border--lighten;
                }
            }

            .select2-selection--single {
                border-top: 0;
                border-right: 0;
                border-bottom: 1px solid transparent;
                border-left: 0;
                border-radius: 0;
                box-shadow: none;
            }
        } //.aplication-success .select2-container--default

        .select2-container--open .select2-dropdown--below {
            border-right: 0;
            border-left: 0;
        } //.move-block--mobile .select2-container--open .select2-dropdown--below
    } //.move-block--mobile

    &--scroll {
        @include xs-block() {
            height: 100%;
            padding: 0;
            background-color: #fff;

            .move-block {
                &__header {
                    border-bottom: 1px solid transparent;
                    background-color: #fff;
                    @include tr(0.35s, border);

                    &_title {
                        opacity: 0;
                        @include nosel;
                        @include tr();
                    } //.move-block--scroll .move-block__header_title
                } //.move-block--scroll .move-block__header

                &__body {
                    position: static;
                    background-color: #fff;
                } //.move-block--scroll .move-block__body

                &__footer {
                    flex-shrink: 0;
                } //.move-block--scroll .move-block__footer

                &__title {
                    span {
                        width: 100%;
                        font-size: 26px;
                        font-weight: 500;

                        @include xs-block() {
                            color: $gray--darken;
                        }
                    }
                } //.move-block--scroll .move-block__title
            } //.move-block--scroll
        }
    } //.move-block--scroll //Если нужно показывать тайтл только при скроле

    &--small {
        @include min(xs) {
            top: 65px;
            bottom: auto;
            z-index: -1;
            opacity: 0;
            display: flex;
            flex-direction: column;
            width: 100%;
            max-width: 480px;
            min-height: 320px;
            max-height: 80vh;
            margin: auto;
            transform: translateY(50%);
            transition: transform 0.3s ease-in-out, opacity 0.3s ease-in-out;
            border-radius: $border-radius;
            box-shadow: 0 3px 10px 0 rgba(0, 0, 0, 0.25);
            @include box;
        }

        // @include xs-block() {
        //     top: 0;
        //     bottom: 0;
        //     max-width: 100%;
        //     max-height: 100%;
        //     border-radius: 0;
        //     box-shadow: none;
        // }

        &.is-open {
            @include min(xs) {
                z-index: z(popup);
                opacity: 1;
                transform: translateY(0);
            }
        } //.move-block--small.is-open

        .move-block {
            &__header {
                &_title {
                    font-size: 18px;
                    font-weight: 400;
                }
            } //.move-block--small .move-block__header

            &__body {
                @include min(xs) {
                    min-height: 300px;
                }
            } //.move-block--small .move-block__body

            &__inner {
                @include min(xs) {
                    padding: 20px;
                }

                // @include xs-block() {
                //     padding: 15px;
                // }
            } //.move-block--small .move-block__inner

            &__footer {
                @include min(xs) {
                    position: static;
                    flex-shrink: 0;
                    margin-top: auto;
                }

                .button-box {
                    .btn {
                        @include min(xs) {
                            flex-basis: 50%;
                            max-width: 100%;
                        }
                    }
                }
            } //.move-block--small .move-block__footer
        } //.move-block--small .move-block
    } //.move-block--small

    &--flexible {
        top: 50px;
        // max-height: calc(100vh - 50px);
        border-radius: 5px 5px 0 0;
    } //.move-block--flexible

    &--expanded {
        overflow: hidden;

        .l-slide-block {
            top: 0;
            transform: translateX(120%);

            &__header {
                span {
                    font-size: 18px;
                } //.move-block--expanded .l-slide-block__header span
            } //.move-block--expanded .l-slide-block__header

            &.is-open {
                transform: translateX(0);
            }
        } //.move-block--expanded .l-slide-block
    } //.move-block--expanded

    &.is-scrolled {
        .move-block {
            &__header {
                border-color: $color-border--lightest;

                &_title {
                    opacity: 1;
                } //.move-block.is-scrolled .move-block__header_title
            } //.move-block.is-scrolled .move-block__header
        } //.move-block.is-scrolled .move-block
    } //.move-block.is-scrolled

    &.is-invalid {
        .move-block {
            &__item {
                &.is-error {
                    border-bottom: 1px solid $color-danger;
                }
            }

            &__validate {
                display: block;
            }
        }
    } //.move-block.is-invaid

    .user {
        &__name {
            max-width: 35ch;
            font-weight: 500;
            line-height: 1.3;
            word-wrap: break-word;
        }
    } //.move-block .user

    .services {
        margin: 0;
    } //.services

    .item-services {
        &__header {
            background-color: #fff;
        } //.move-block .item-services__header
    } //.move-block .item-services

    .master-picker {
        margin-bottom: 0;
        border-top: 0;
    } //.move-block .master-picker

    .day-picker {
        &__list {
            margin-left: -15px;
            padding-left: 10px;

            @include xs-block() {
                margin-left: 0;
            }
        } //.modal .day-picker__list

        &__item {
            width: 50px;
            margin: 0 10px;

            @include xs-block() {
                width: 40px;
                margin: 0 5px;
            }
        } //.modal .day-picker__item

        &__arrow {
            top: -73px;

            @include xs-block() {
                top: -52px;
            }

            &:hover {
                &:not(.is-disabled) {
                    background-color: transparent;
                }
            }

            &--left {
                left: 10px;

                @include xs-block() {
                    left: 1px;
                }
            }

            &--right {
                right: 23px;

                @include xs-block() {
                    right: 1px;
                }
            }

            .icon {
                fill: $color-accent;
            }
        } //.modal .day-picker__arrow
    } //.move-block .day-picker

    .time-picker {
        &__item {
            justify-content: center;
        } //.move-block .time-picker__item
    } //.move-block .time-picker

    .beautylink {
        @include xs-block() {
            margin-top: 10px;
            padding-bottom: 20px;
        }

        &.beautylink--no-bg {
            .beautylink {
                &__header {
                    padding-top: 0;
                } //.beautylink.beautylink--no-bg .beautylink__header
            }
        } //.move-block .beautylink.beautylink--no-bg
    } //.move-block .beautylink

    &[data-move-block='request-desktop'] {
        .aplication {
            &__left {
                .aplication__inner {
                    padding-top: 27px;
                } //.move-block[data-move-block="request-desktop"] .aplication__left .aplication__inner
            } //.move-block[data-move-block="request-desktop"] .aplication__left
        }
    } //.move-block[data-move-block="request-desktop"]

    &[data-move-block='product-info'] {
        .move-block {
            &__footer {
                padding-top: 6px;
            } //.move-block[data-move-block='product-info'] .move-block__footer
        }
    } //.move-block[data-move-block='product-info']
} //.move-block

.move-block-item {
    position: relative;
    flex-shrink: 0;
    min-width: 50px;
    padding: 10px;
    background-color: #fff;

    @include border(top);
    @include border(bottom);
    @include flexCenter;

    @include max(sm) {
        cursor: pointer;
    }

    &__title {
        display: block;
        margin-bottom: 20px;
        color: $gray--dark;

        &--small {
            font-size: 14px;
        }
    } //.move-block-item__title

    &__client {
        flex-grow: 1;
        padding-left: 10px;

        @include coverdiv;
        @include flexCenter;

        &::after {
            content: '';
            @include fa('light');
            @include centre('y');
        }

        &--selected {
            display: none;
            flex-grow: 1;
        } //.move-block-item__client--selected

        .icon-reset {
            right: 9px;
            @include centre('y');
        }
    } //.move-block-item__client

    &__icon {
        right: 14px;
        right: 11px;
        width: 13px;
        height: 13px;
        margin-left: auto;
        font-size: 18px;

        @include centre('y');

        @include xs-block() {
            font-size: 22px;
        }
    } //.move-block-item__icon

    &.no-border {
        border: 0;
    } //.move-block-item.no-border

    &--column {
        flex-direction: column;
        align-items: flex-start;
    } // .move-block-item--column

    &--client {
        &.is-selected {
            height: 60px;

            .move-block-item__client {
                display: none;

                &--selected {
                    display: flex;
                }
            }
        }
    } // .move-block-item--client

    &--date {
        padding: 0;

        input {
            height: auto;
            padding: 13px;
            border: 0;
            background-color: transparent;
        }
    } // .move-block-item--date

    &--wide {
        padding: 0;

        .select2-container .select2-selection--single {
            height: 50px;
        } // .move-block-item--wide .select2-container .select2-selection--single

        .select2-container
            .select2-selection--single
            .select2-selection__rendered {
            height: 50px;
            padding: 0 25px 0 10px;
        } // .move-block-item--wide.move-block-item--wide .select2-container .select2-selection--single .select2-selection__rendered

        .select2-container--default
            .select2-selection--single
            .select2-selection__arrow
            b {
            top: 20px;
        } //.move-block-item--wide .select2-container--default .select2-selection--single .select2-selection__arrow b
    } // .move-block-item--wide

    .select-color {
        width: 100%;

        select {
            @include max(sm) {
                border: 0;
            }
        }
    } //.move-block-item .select-color

    .select2-container--default
        .select2-selection--single
        .select2-selection__arrow
        b {
        right: 14px;

        &::before {
            font-size: 22px;
        }
    }
} // .move-block-item

.move-block-service {
    width: 100%;

    @include flexCenter;

    &__info {
        flex-grow: 1;
        @include flexColumn;
    } //.move-block-service__info

    &__time {
        font-size: 14px;
    } //.move-block-service__time

    &__title {
        color: $gray--darken;
        font-weight: 500;
    } //.move-block-service__title

    &__duration {
        .text-time {
            font-size: 14px;

            i {
                margin-right: 5px;
                color: $color-icon--light;
            } //.move-block-service__duration .text-time i
        } //.move-block-service__duration .text-time

        .text-master {
            font-size: 15px;
            font-style: italic;
        } //.move-block-service__duration .text-master
    } //.move-block-service__duration

    &__price {
        i {
            margin-left: 5px;
            color: $color-icon--light;
            font-size: 14px;
        } //.move-block-service__price i
    } //.move-block-service__price
} //.move-block-service

.item-info {
    &__title {
        margin-bottom: 5px;
        color: $gray--darkest;
        font-size: 15px;
        font-weight: 500;

        span {
            position: relative;

            & + span {
                &::before {
                    content: '\f067';
                    margin-right: 5px;
                    margin-left: 1px;
                    color: $color-icon;
                    font-size: 11px;
                    @include fa('regular');
                }
            }
        }
    } //.item-info__title

    &__desc {
        font-size: 14px;
        @include flexCenter;
    } //.item-info__desc

    &__time {
        flex-grow: 1;

        i {
            color: $color-icon;
        }
    } //.item-info__time

    &__price {
        i {
            color: $color-icon;
            font-size: 12px;
        } //.item-info__price i
    } //.item-info__price
} //.item-info

#aplication-chat {
    .chat {
        position: absolute;
        top: 0;
        right: 0;
        bottom: 0;
        left: 0;
        padding-bottom: 70px;

        &__footer {
            position: absolute;
            right: 0;
            bottom: 0;
            left: 0;
        } //#aplication-chat .chat__footer
    } //#aplication-chat .chat
} //#aplication-chat
