$enable-grid-classes: true !default;
$max-width: 1200px;

$font-family-sans-serif: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto,
    'Helvetica Neue', Arial, sans-serif, 'Apple Color Emoji', 'Segoe UI Emoji',
    'Segoe UI Symbol' !default;
$font-family-arial: Arial, Textbook New, sans-serif;
$font-family-roboto: Roboto, Arial, Helvetica, sans-serif !default;

$font-size-base: 16px;
$font-family-base: $font-family-roboto !default;
$line-height-base: 1.5;
$font-weight-base: 400;

//Отступы
$base-indent: 20px;
$half-indent: $base-indent / 2;
$section-indent: 50px;

$gutter: $base-indent;
$gutter--small: $gutter / 2;

$base-header-size: 60px; //Размер блоков в хедере на десктопе
$base-header-size--small: 50px; //Размер блоков в хедере на мобилке

$base-btn-height: 42px; //Defaul size for buttons & inputs
$base-btn-height--xs: 44px; //Defaul size for buttons & inputs im 480px

@media screen and (max-width: 480px) {
    $base-btn-height: 44px;
}

$card-indent: 50px;
$card-indent--mobile: 10px;

//Base Variebles
$color-accent: #ff8272;
$color-main: #00dfbf;
$color-third: #fff06b;

$color-muted: #d6d6d6;
$color-primary: #ff8272;
$color-primary--light: #ff9280;
$color-success: #8cc152;
$color-success--light: #79cebb;
$color-info: #5c87de;
$color-info--light: #1d8acb;
$color-danger: #f33155;
$color-danger--light: #f67c93;
$color-warning: #ffc107;

//gray
$gray--darkest: #242626;
$gray--darken: #353737;
$gray--dark: #464646;
$gray: #585a59;
$gray--light: #9b9b9b;
// $gray--lighten: #ecedf1;
$gray--lighten: #eff1f6;
$gray--lightest: #dee3e7;

//text
$color-text--darkest: $gray--darkest;
$color-text--dark: $gray--dark;
$color-text: $gray;
$color-text--light: $gray--light;
$color-text--lighten: $gray--lighten;
$color-text--lightest: $gray--lightest;

$color-text--disabled: #bfbfbf;

//btn
$color-btn--darken: #3e3737;
$color-btn--dark: #6b6464;

//bg
$color-bg--darken: #191919;
$color-bg--dark: #222;
$color-bg: #2f2f2f;
$color-bg--light: #eee;
$color-bg--lighten: #f5f5f5;
$color-bg--lightest: #f7f7f8;
$color-bg--instagram: linear-gradient(
    to bottom left,
    #962fbf 0%,
    #d62976 35%,
    #fa7e1e 70%,
    #ffdc3e 100%
);

$color-gray--hover: #fbfbfb;
$color-bg--hover: #fbfbfb;

//Icon
$color-icon--darken: $gray;
$color-icon--dark: #727272;
$color-icon: #a5a5a5;
$color-icon--light: #cacaca;
$color-icon--hover: $gray;

//border
$color-border--darkest: $gray--darkest;
$color-border--darken: $gray--darken;
$color-border--dark: $gray;
$color-border: #a5a5a5;
$color-border--light: #c7c7c7;
$color-border--lighten: #dedede;
$color-border--lightest: #dee3e7;
$color-border-very-light: #f2f2f2;

$input-color-border: #c7c7c7;
$input-color-border--hover: #b3b3b3;
$input-shadow--hover: 0 2px 4px 0 rgba(0, 0, 0, 0.05),
    0 0 4px 0 rgba(0, 0, 0, 0.05);

$border-radius: 3px;

$color-placeholder: #999;

//Тень
$bxs: 1px 1px 10px #ddd;
$bxs-hover: 1px 1px 10px rgba($color-accent, 0.6);
$bxs-box: 0 2px 4px 0 rgba(0, 0, 0, 0.1), 0 0 4px 0 rgba(0, 0, 0, 0.1);
$bxs-box-two: 0 2px 5px 0 rgba(164, 173, 186, 0.25);
$bxs-with-overlay: -3px 3px 15px rgba(0, 0, 0, 0.2);

//Transition
$trs: all 0.4s ease-in-out;
// $trs-transform: transform 500ms cubic-bezier(0.23, 1, 0.32, 1);
$trs-transform: transform 0.4s ease-in-out;
$trsMenu: all 0.5s cubic-bezier(0.25, 0.8, 0.4, 0.95);

$text-colors: (
    red: red,
    pink: #fe0061,
    purple: #ab00b6,
    blue: #3c4ebc,
    blue--light: #0096fb,
    blue--lightest: #00bfd8,
    green--dark: #098,
    green: #00b341,
    green--light: #c9df00,
    yellow: #ffec00,
    orange: #ff9300,
    accent: $color-accent,
    gray: $gray--dark,
    gray-light: #808080,
    gray-lighten: #a5a5a5,
);

$bg-colors: (
    light: $color-bg--light,
    lighten: $color-bg--lighten,
    lightest: $color-bg--lightest,
    gray-lighten: $gray--lighten,
);

$icon-whatsapp: '\f232';
$icon-viber: '\f409';
$icon-vk: '\f189';
$icon-instagram: '\f16d';

$btn-social-icon: (
    whatsapp: $icon-whatsapp,
    viber: $icon-viber,
    vk: $icon-vk,
    instagram: $icon-instagram,
);

$btn-social-color: (
    whatsapp: #23d366,
    viber: #7d3daf,
    vk: #4a76a8,
);

//button colors
$button-colors: (
    accent: $color-main,
    default: $color-accent,
    success: $color-success,
    error: #f74969,
    error-light: $color-danger--light,
    warning: $color-warning,
    info: $color-info,
    muted: $color-muted,
    gray: $gray--dark,
    gray-light: #808080,
    gray-lightest: $color-bg--lightest,
    input: $input-color-border,
    secondary: $gray--lighten,
    white: #fff,
);

$breakpoints: (
    z: 0,
    i5: 320px,
    i7: 375px,
    i8: 414px,
    xs: 480px,
    sm: 768px,
    md: 1024px,
    mdX: 1280px,
    lg: 1366px,
) !default;

$breakpointsMin: (
    i5: 319px,
    i7: 374px,
    i8: 413px,
    xs: 479px,
    sm: 767px,
    md: 1023px,
    mdX: 1280px,
    lg: 1365px,
) !default;

$container-max-widths: (
    xs: 480px,
    sm: 768px,
    md: 1024px,
    lg: 1366px,
) !default;

//z-index
$z-index: (
    bottom: -9999,
    down: -1,
    default: 1,
    fixed: 10,
    success: 50,
    successBtnClose: 51,
    sticky: 800,
    dropdown: 900,
    overlay: 1000,
    popup: 1010,
    modal: 1020,
) !default;

//Modal Variebles
$modal-border-width: 1px;
$modal-border-color: #e8e8e8;
$modal-border-radius: $border-radius;

$modal-box-shadow: 0 0.25rem 0.5rem rgba(#000, 0.5) !default;

$modal-backdrop-bg: #000 !default;
$modal-content-bg: #fff !default;
$modal-backdrop-opacity: 0.4 !default;
$modal-header-padding: 15px !default;
$modal-inner-padding: 15px !default;

$modal-lg: 800px !default;
$modal-md: 500px !default;
$modal-sm: 300px !default;

$transition-collapse: height 0.35s ease !default;
$transition-fade: opacity 0.15s linear !default;
