.is-android,
.is-ios {
    .hide-mobile {
        display: none !important;
    } //.is-android .hide-mobile, .is-ios .hide-mobile
}

html:not(.is-android),
html:not(.is-ios) {
    .only-mobile {
        display: none !important;
    } //.only-mobile, .only-mobile--flex
}

.is-android {
    .firstscreen.instagram-border {
        height: calc(100vh - 105px);

        @include xs-block() {
            height: calc(100vh - 105px);
        }
    } //.is-android .firstscreen.instagram-border
} //.is-android

.is-ios {
    .wrapper[inert] {
        .wrapper__inner {
            position: relative;
            overflow-y: hidden;
        }
    }

    .firstscreen.instagram-border {
        height: calc(100vh - 125px);

        @include xs-block() {
            height: calc(100vh - 125px);
        }
    } //.is-ios .firstscreen.instagram-border

    // .page-market {
    //     .cart-item__wrap {
    //         max-height: calc(100vh - 50px - 169px);
    //     }
    // } //.is-ios .page-market .order
} //.is-ios

.is-chrome {
    video.section__img {
        filter: contrast(1);
    }
} //.is-chrome video.section__img

*[inert] {
    overflow: hidden;
    pointer-events: none;
    touch-action: none;
}

*[onclick] {
    cursor: pointer;
}
