//Variables for day status color
$day-color-selected: transparent;
$day-color-output: $color-info;
$day-color-not-record: $color-text--disabled;

.flatpickr-calendar {
    &.inline {
        width: 100%;
        border-radius: 0;
        background-color: transparent;
        box-shadow: none;

        .flatpickr-prev-month,
        .flatpickr-next-month {
            @include flexCenter;

            svg {
                width: 18px;
                height: 18px;
                fill: $color-icon--darken;
            }
        } //.flatpickr-calendar.inline .flatpickr-prev-month, .flatpickr-calendar.inline .flatpickr-next-month

        .flatpickr-next-month {
            svg {
                margin-left: auto;
            }
        } //.flatpickr-calendar.inline .flatpickr-next-month
    } //.flatpickr-calendar.inline

    .flatpickr-day {
        color: $color-text--darkest;
        border-radius: $border-radius;
        touch-action: none;
        pointer-events: none;

        &:hover,
        &:focus {
            border-color: $gray--lighten;
            background-color: $gray--lighten;
        } //.flatpickr-calendar .flatpickr-day:hover

        &.today {
            border-color: $color-border;

            &:hover,
            &:focus {
                border-color: $gray--light;
                background-color: $gray--light;
            }
        } //.flatpickr-calendar .flatpickr-day.today

        &.output {
            color: $day-color-output;
            pointer-events: all;
            touch-action: auto;
            font-weight: 500;
        } //.flatpickr-calendar .flatpickr-day.output

        &.not-record {
            color: $day-color-not-record;
            pointer-events: none;

            &:hover {
                border-color: transparent;
                background-color: transparent;
            }
        } //.flatpickr-calendar .flatpickr-day.not-record

        &.selected {
            border-color: $day-color-selected;
            background-color: $day-color-selected;
            color: initial;

            &.output {
                // background-color: $color-danger;
                // border-color: $color-danger;
                color: $day-color-output;
            }
        } //.flatpickr-calendar .flatpickr-day.selected

        &.nextMonthDay {
            touch-action: none;
            pointer-events: none;
        } //.flatpickr-calendar .flatpickr-day.nextMonthDay
    } //.flatpickr-calendar .flatpickr-day

    .flatpickr-custom-month {
    }
} //.flatpickr-calendar
