$trsM: all 0.4s ease-in-out;
$trsD: 0.4s;
$menuActiveColor: $gray--darkest;

.toggle-menu {
    position: relative;
    display: none;
    align-items: center;
    justify-content: center;
    width: 50px;
    height: 50px;
    margin: 0 0 0 -10px;
    padding: 0;
    overflow: hidden;
    transition: background 0.3s;
    border: 0;
    border-radius: 0;
    background-color: transparent;
    box-shadow: none;
    font-size: 0;
    text-indent: -9999px;
    cursor: pointer;
    appearance: none;

    @include max(sm) {
        display: flex;
    }

    @include landscape {
        display: flex;
    }

    &__inner {
        position: relative;
        width: 25px;
        height: 14px;
        margin: 0 auto;
        transform: rotate(0deg);
        transition: 0.5s ease-in-out;
    } //.toggle-menu__inner

    &__icon {
        position: absolute;
        left: 0;
        opacity: 1;
        display: block;
        width: 100%;
        height: 2px;
        transition: 0.3s ease-in-out;
        border-radius: 15px;
        background-color: $color-bg;
        will-change: top, width;

        &:nth-child(1) {
            top: 0;
        }

        &:nth-child(2) {
            top: 6px;
        }

        &:nth-child(3) {
            top: 6px;
        }

        &:nth-child(4) {
            top: 12px;
        }
    } //.toggle-menu__icon

    &--mobile {
        .toggle-menu__icon {
            background-color: #fff;
        }
    } //.toggle-menu--mobile
} //.toggle-menu

.hamburger {
    position: relative;
    justify-content: center;
    width: $base-header-size;
    height: $base-header-size;
    transition-duration: 0.5s;
    @include flexCenter;

    @include min(xs) {
        &.on {
            span {
                transition: 0.5s;

                &::before {
                    transform: translate(0, 2px) rotate(-45deg);
                }

                &::after,
                &::before {
                    // width: 16px;
                    width: 10px;
                }

                &::after {
                    transform: translate(0, -2px) rotate(45deg);
                }

                // &::before {
                //     transform: translate(-3px, 1px) rotate(-45deg);
                // }

                // &::after {
                //     transform: translate(-3px, -1px) rotate(45deg);
                // }
            }
        }
    }

    span {
        position: relative;
        display: block;
        // width: 25px;
        width: 20px;
        height: 2px;
        backface-visibility: hidden;
        transition: all 0.3s;
        border-radius: 15px;
        // margin: 29px auto 0;
        background-color: $color-icon;
        pointer-events: none;

        @include xs-block() {
            background-color: $gray--light;
        }

        &::after,
        &::before {
            content: ' ';
            position: absolute;
            top: 9px;
            left: 0;
            display: block;
            width: 100%;
            height: 2px;
            backface-visibility: hidden;
            transition: all 0.3s;
            border-radius: 15px;
            background-color: $color-icon;

            @include xs-block() {
                background-color: $gray--light;
            }
        }

        &::before {
            top: -6px;
        }

        &::after {
            top: 6px;
        }

        &:hover {
            cursor: pointer;
        }
    } //.hamburger span
} //.hamburger

.nav {
    @include max(sm) {
        position: absolute;
        top: 50px;
        left: 0;
        z-index: 200;
        display: none;
        width: 100%;
        background-color: #fff;
        box-shadow: 1px 5px 10px rgba($gray, 0.4);
    }

    &__list {
        justify-content: center;
        width: 100%;
        text-align: center;
        @include flexCenter;
        @include max(sm) {
            display: flex;
            flex-direction: column;
            width: 100%;
            margin: 0;
            padding: 15px 0;
            text-align: left;
        }
    } //.nav__list

    &__item {
        position: relative;
        display: inline-block;
        @include max(sm) {
            width: 100%;
            margin-right: 0;
            border-top: 1px solid $color-border--lightest;
        }

        &:hover {
            .nav__link {
                background-color: #f1f1f1;
                color: #000;
                @include max(sm) {
                    color: $color-accent;
                }

                &::after {
                    width: 100%;
                }
            }

            .nav-dropdown {
                display: block;
                pointer-events: all;
            }
        }

        &:first-child {
            margin-left: 0;
        }

        &:last-child {
            margin-right: 0;
        }

        &.is-active {
            .nav__link {
                color: #000;
                @include max(sm) {
                    color: $color-accent;
                }

                &::after {
                    width: 100%;
                }
            }
        }
    } //.nav__item

    &__link {
        position: relative;
        display: block;
        padding: 0 20px;
        transition: initial;
        color: $gray--dark;
        font-size: 13px;
        // font-weight: 300;
        letter-spacing: 1px;
        line-height: 40px;
        text-transform: uppercase;

        @include max(md) {
            padding: 0 15px;
        }

        @include max(sm) {
            width: 100%;
            margin: auto;
            text-align: center;

            &::after {
                display: none;
            }
        }
    } //.nav__link

    &--blog {
        margin-left: auto;

        .nav {
            &__item {
                text-align: center;

                &:hover,
                &.is-active {
                    .nav__link {
                        background-color: transparent;
                        background-image: linear-gradient(
                            180deg,
                            $color-third,
                            $color-third
                        );
                        background-repeat: no-repeat;
                        background-position: 4px bottom;
                        background-size: 100% 10px;
                    }
                }

                &:last-child {
                    .nav__link {
                        padding-right: 0;
                    }
                }

                & + .nav__item {
                    margin-left: 30px;
                    @include max(sm) {
                        margin-top: 10px;
                        margin-left: 0;
                    }
                }
            }

            &__link {
                display: inline-block;
                width: auto;
                padding: 0;
                line-height: 1.5;
            }
        }
    } //.nav--blog
} //.nav

.menu {
    position: fixed;
    top: 0;
    bottom: 0;
    left: 0;
    z-index: z(dropdown);
    width: $base-header-size;
    transition: width, ease-in-out 200ms;
    background-color: $color-bg;

    @include xs-block() {
        top: auto;
        bottom: 0;
        width: 100%;
        // border-top: 1px solid $color-border--lightest;
        background-color: #fff;

        [tooltip] {
            &::before,
            &::after {
                display: none;
            }
        }

        .menu {
            &__list {
                border-right: 0;
                background-color: #fff;
            } //.menu.bg--white .menu__list

            &__icon-main {
                width: 40px;
                height: 40px;
                fill: $gray--darkest;
            }

            &__link,
            &__icon {
                background-color: transparent;
                color: $gray--darkest;
            } //.menu.bg--white .menu__icon

            &__link {
                &:hover {
                    .icon {
                        fill: $color-icon;
                    }

                    .menu__text {
                        color: $color-text;
                    }
                }
            } //.menu.bg--white .menu__link

            &__item {
                border-bottom: 0;

                &.is-active {
                    background-color: #fff;

                    .menu {
                        &__link {
                            background-color: transparent;
                        }

                        &__icon {
                            background-color: #fff;
                            color: $color-accent;

                            &::before {
                                @include fa;
                            }
                        }
                    }
                }

                &--last {
                    .menu__link {
                        border-top: 0;
                    }
                }
            } //.menu.bg--white .menu__item

            &__count {
                border-color: #fff;
                color: #fff;
                font-weight: 600;
            } //.menu.bg--white .menu__count
        } //.menu.bg--white
    }

    &::after {
        @include xs-block() {
            content: '';
            position: absolute;
            top: 0;
            right: 0;
            left: 0;
            width: 100%;
            height: 100%;
            border: 0;
            box-shadow: 0 -3px 10px 0 rgba(0, 0, 0, 0.1);
            pointer-events: none;
        }
    }

    &__logo {
        visibility: hidden;
        opacity: 0;
        margin: auto 10px auto 60px;
        transition: transform 0.4s cubic-bezier(0.22, 0.22, 0.02, 1.07),
            opacity 0.35s ease;
        @include flexCenter;
        @include tX(-200%);

        @include xs-block() {
            margin-left: 50px;
        }

        .logo__link {
            color: #fff;
            white-space: nowrap;
        } //.menu__logo .logo__link
    } //.menu__logo

    &__hamburger {
        position: absolute;
        top: 0;
        left: 0;
        z-index: z(default);
        display: inline-flex;
        transition: left ease-out 0.3s;
        background-color: $color-bg;
        cursor: pointer;
        @include baseSize;

        @include xs-block() {
            display: none;
        }

        &:hover {
            span {
                background-color: #fff;

                &::before,
                &::after {
                    background-color: #fff;
                }
            }
        }

        button {
            border: 0;
            background-color: transparent;
        }
    } //.menu__hamburger

    &__top {
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: $base-header-size;
        @include baseSize;
        @include flexCenter;

        @include xs-block() {
            display: none;
        }
    } //.menu__top

    &__list {
        position: relative;
        top: $base-header-size;
        height: calc(100% - #{$base-header-size});
        background-color: $color-bg;
        @include flexColumn;

        @include xs-block() {
            top: 0;
            flex-direction: row;
            justify-content: space-between;
            height: $base-header-size--small;
        }

        @include landscape {
            overflow-y: auto;
            // overflow-x: hidden;
        }
    } //.menu__list

    &__item {
        outline: none;

        @include xs-block() {
            margin: auto;
        }

        &.is-active {
            .menu {
                &__link {
                    background-color: $color-bg--dark;
                } //.menu__item.is-active .menu__link

                &__text {
                    color: #fff;

                    @include xs-block() {
                        color: $color-text--darkest;
                    }

                    @include min(xs) {
                        background-color: $color-bg--dark;
                    }
                } //.menu__item.is-active .menu__text

                &__icon {
                    background-color: $color-bg--dark;
                    color: #fff;

                    @include xs-block() {
                        fill: $menuActiveColor;
                        color: $menuActiveColor;
                    }

                    &::after {
                        display: block;

                        @include xs-block() {
                            display: none;
                        }
                    }

                    .icon {
                        fill: #fff;

                        @include xs-block() {
                            fill: $menuActiveColor;
                            color: $menuActiveColor;
                        }
                    }
                } //.menu__item.is-active .menu__icon
            }
        } //.menu__item.is-active

        &.menu-dropdown--open {
            .menu {
                &__dropdown {
                    @include tX(0);

                    @include xs-block() {
                        @include tY(0);
                    }
                }
            }
        } //.menu__item.menu-dropdown--open

        &--last {
            margin-bottom: auto;
            // position: absolute;
            // bottom: 0;
            // left: 0;
            // right: 0;
        } //.menu__item--last

        &--border {
            .menu__link {
                border-top: 1px solid $color-bg--dark;
                border-bottom: 0;

                @include xs-block() {
                    border-top: 0;
                }
            }
        }

        &--more {
            @include min-screen(481) {
                display: none;
            }

            .menu__link {
                color: #fff;
            }
        } //.menu__item--more
    } //.menu__item

    &__link {
        position: relative;
        justify-content: center;
        width: 100%;
        height: $base-header-size;
        padding-left: $base-header-size;
        @include flexCenter;

        @include xs-block() {
            flex-direction: column;
            width: $base-header-size--small;
            height: $base-header-size--small;
            padding-left: 0;
            border-bottom: 0;
        }

        &:hover {
            @include min(xs) {
                background-color: $color-bg--dark;

                .menu {
                    &__icon {
                        color: #fff;

                        @include min-screen(426) {
                            background-color: $color-bg--dark;
                        }

                        .icon {
                            fill: #fff;
                        }

                        &--data {
                            &::after {
                                animation: none;
                            }
                        }
                    }

                    &__text {
                        color: #fff;
                    }
                }
            }
        }
    } //.menu__link

    &__text {
        visibility: hidden;
        opacity: 0;
        width: 100%;
        transition: transform 0.4s cubic-bezier(0.22, 0.22, 0.02, 1.07),
            opacity 0.35s ease;
        color: $gray--light;

        font-size: 15px;
        white-space: nowrap;
        @include tX(-100%);
        @include flexCenter;

        @include xs-block() {
            bottom: 1px;
            z-index: z(fixed);
            visibility: visible;
            // display: none;
            opacity: 1;
            width: auto;
            color: $gray--light;
            font-size: 9px;
            font-weight: 600;
            text-transform: uppercase;
            @include centre('x');
        }
    } //.menu__text

    &__btn {
        display: flex;
        align-items: center;
        justify-content: center;
        width: 50px;
        height: 50px;
        margin: 0;
        padding: 0;
        border: 0;
        background: transparent;
    } //.menu__btn

    &__icon {
        position: absolute;
        left: 0;
        z-index: z(default);
        justify-content: center;
        width: $base-header-size;
        height: $base-header-size;
        color: $color-icon;
        font-size: 20px;
        @include tr;
        @include flexCenter;
        @include baseSize;

        @include min(xs) {
            background-color: $color-bg;
        }

        &::after {
            content: '';
            position: absolute;
            top: 0;
            left: 0;
            z-index: z(fixed);
            display: none;
            width: 2px;
            height: 100%;
            background: $color-accent;
            @include tr;
        }

        .icon {
            width: 23px;
            height: 23px;
            margin: auto;
            @include tr;

            @include xs-block() {
                margin: 10px auto 20px;
                fill: $gray--light;
                color: $gray--light;
            }

            &.icon-dashboard {
                width: 18px;
                height: 18px;
            } //.menu__icon .icon.icon-dashboard

            &.icon-insta,
            &.icon-cog {
                width: 21px;
                height: 21px;
            }
        } //.menu__icon .icon
    } //.menu__icon

    &__count {
        position: absolute;
        top: 6px;
        left: 23px;
        z-index: z(default);
        animation: pulse 1.5s ease-out infinite;
        border: 2px solid $color-bg;
        background-color: $color-accent;
        font-style: normal;
        @extend %badge;

        @include xs-block() {
            top: 6px;
            right: 2px;
            left: auto;
            animation: none;
            border-color: $color-accent;
            line-height: 1.4;
        }
    } //.menu__count

    &__overlay {
        position: fixed;
        @extend .overlay;

        &.is-visible {
            display: block;
        }
    } //.menu__overlay

    &.is-open {
        width: 220px;

        .menu {
            // &__hamburger {
            //     left: 188px;
            // }

            &__logo {
                visibility: visible;
                opacity: 1;
                @include tX(0);
            } //.menu.is-open .menu__logo

            &__text {
                visibility: visible;
                opacity: 1;
                @include tX(0);
            } //.menu.is-open .menu__text

            &__item {
                &.is-active {
                    .menu__dropdown {
                        @include tX(190px);
                    }
                }
            } //.menu.is-open .menu__item
        }

        [tooltip] {
            &::before,
            &::after {
                display: none;
            }
        }
    } //.menu.is-open

    &.bg--white {
        background-color: #fff;

        .menu {
            &__list {
                background-color: #fff;
                @include border(right);

                @include xs-block() {
                    border-right: 0;
                }
            } //.menu.bg--white .menu__list

            &__icon-main {
                width: 40px;
                height: 40px;
                fill: $gray--darkest;
            }

            &__link,
            &__icon {
                // background-color: #fff;
                background-color: transparent;
                color: $gray--darkest;
            } //.menu.bg--white .menu__icon

            &__link {
                &:hover {
                    @include min(xs) {
                        background-color: #fff;

                        .menu__icon {
                            background-color: #fff;
                            color: #000;
                        }
                    }

                    @include xs-block() {
                        .icon {
                            fill: $color-icon;
                        }

                        .menu__text {
                            color: $color-text;
                        }
                    }
                }
            } //.menu.bg--white .menu__link

            &__item {
                border-bottom: 0;

                &.is-active {
                    background-color: #fff;

                    .menu {
                        &__link {
                            background-color: transparent;
                        }

                        &__icon {
                            background-color: #fff;
                            color: $color-accent;

                            &::before {
                                @include fa;
                            }
                        }
                    }
                }

                &--last {
                    .menu__link {
                        border-top: 0;
                    }
                }
            } //.menu.bg--white .menu__item

            &__count {
                border-color: #fff;
                color: #fff;
                font-weight: 600;
            } //.menu.bg--white .menu__count
        } //.menu.bg--white
    } //.menu.bg--white

    .toggle-menu {
        display: flex;
        width: 60px;
        height: 60px;
    } //.menu .toggle-menu
} //.menu

.mobile-nav {
    position: fixed;
    top: 0;
    left: 0;
    z-index: z(popup);
    width: 300px;
    height: 100%;
    overflow-x: hidden;
    transition: all 0.4s ease-in-out 0s;
    background-color: $color-bg--lightest;
    @include tX(-100%);
    @include scroll;
    @include flexColumn;

    @include i5-block() {
        width: 272px;
    }

    &__inner {
        padding: 10px;
    } //.mobile-nav__inner

    &__header {
        position: relative;
        // background-color: #fff;
        // height: 50px;
        min-height: max-content;
        // padding: 5px 10px;
        overflow: hidden;

        @include flexCenter;

        &:not(.is-authorized) {
            justify-content: center;
            // padding: 20px;

            .header-user {
                display: none;
            }

            .user {
                display: flex;
                width: 100%;
                height: 100%;
                padding: 20px;
                background-color: #fff;
                @include border(bottom);
            }

            .js-mobile-nav--close {
                top: 20px;
            }

            & ~ .mobile-nav__link--last {
                display: none;
            }

            &.nav-header-img {
                & ~ .mobile-nav__link--last {
                    display: flex;
                }
            }
        }

        .header-user {
            &__name {
                color: $color-text;
            }

            &__link {
                color: #fff;
            }
        }

        .user {
            display: none;
            flex-direction: column;
            align-items: center;
            justify-content: center;
        }

        .js-mobile-nav--close {
            position: absolute;
            right: 5px;
            @include centre('y');
        }

        &.is-authorized {
            // border-bottom: 0;
            // background: transparent;

            .header-user {
                padding: 5px 10px;

                &__link {
                    @include flexCenter;
                }
            }

            .mobile-nav__link--enter {
                display: none;
            }

            .js-mobile-nav--close {
                @include centre('y');
            }

            & ~ nav {
                display: block;
            }

            &.nav-header-img {
                .js-mobile-nav--close {
                    top: 0;
                    transform: translate(0);

                    .icon {
                        fill: #fff;
                    }
                }

                & ~ .mobile-nav__link--last {
                    display: none;
                }
            }
        }

        &.nav-header-img {
            height: 25vh;
        }
    } //.mobile-nav__header

    &__title {
        padding: 20px 15px 10px;
        background-color: $color-bg--lightest;
        color: $color-text--light;
        font-size: 13px;
        font-weight: 500;
        text-transform: uppercase;
    } //.mobile-nav__title

    &__list {
        background-color: #fff;
        @include border(top);
        @include border(bottom);
    } //.mobile-nav__list

    &__item {
        border-left: 4px solid transparent;
        box-shadow: none;
        cursor: pointer;

        &:first-child {
            .mobile-nav {
                &__link {
                    border-top: 0;
                }
            }
        }

        &.is-active {
            border-left: 4px solid $color-accent;
            background-color: $color-bg--lightest;

            &:last-child {
                border-bottom-color: transparent;
            }

            .mobile-nav__link {
                border-top: 0;
            }

            & + .mobile-nav__item {
                .mobile-nav__link {
                    border-top: 0;
                }
            }
        } //.mobile-nav__item .is-active

        .mobile-nav {
            &__link {
                border-top: 1px solid $color-border--lightest;
            }
        }

        .counter {
            right: 15px;
            color: #fff;
            @include centre('y');
        } //.mobile-nav__item .counter

        &.bb-accordeon__item {
            border-top: 0;
            border-right: 0;
            border-bottom: 0;

            .bb-accordeon {
                &__content {
                    a {
                        display: block;
                        margin-left: 35px;
                        padding: 7px 0 4px;
                        color: $color-text;
                        font-size: 16px;
                    }
                }
            }
        } //.mobile-nav__item.bb-accordeon__item

        &--no-icon {
            border-left: 0;

            & + .mobile-nav__item {
                border-top: 1px solid $color-border--lightest;

                .mobile-nav__link {
                    border-top: 0;
                }
            }

            .mobile-nav {
                &__link {
                    margin-left: 0;
                    padding-left: 15px;
                } //.mobile-nav__item--no-icon .mobile-nav__link

                &__text {
                    border: 0;
                } //.mobile-nav__item--no-icon .mobile-nav__text
            } //.mobile-nav__item--no-icon .mobile-nav
        } //.mobile-nav__item--no-icon
    } //.mobile-nav__item

    &__link {
        position: relative;
        height: 44px;
        margin-left: 45px;
        border: 0;
        background-color: transparent;
        color: $color-text;
        font-size: 18px;
        @include nosel;
        @include flexCenter;

        @include min(xs) {
            font-size: 16px;
        }

        &::before {
            left: 13px;
            font-size: 18px;
            @include fa('light');
            @include centre('y');
        }

        &:hover {
            color: #000;
        }

        .icon-dashboard {
