.category-services {
    &__item {
        &:first-child {
            .category-services__link {
                border-top: 0;
            }
        }

        .category-services__link {
            @include border(top);
        }

        &.js-viewer-btn {
            padding: 10px;
            @include border(top);
        }
    }

    &__link {
        display: block;
        padding: 10px 0;
        color: $color-text;
    }

    &__btn-view {
        display: flex;
        align-items: center;
        justify-content: center;
        width: 100%;
        border: none;
        background: transparent;
        color: $color-text--light;
    }
}

.nav-dropdown {
    position: absolute;
    top: 40px;
    z-index: z(dropdown);
    display: none;
    min-width: 300px;
    border-bottom: 4px solid $color-accent;
    background: rgba(255, 255, 255, 0.95);
    box-shadow: 3px 10px 20px rgba(0, 0, 0, 0.2);
    pointer-events: none;

    &__list {
        box-sizing: border-box;
        padding: 10px 35px;
        text-align: left;
    } //.nav-dropdown__list

    &__item {
        padding: 6px 0;
    } //.nav-dropdown__item

    &__link {
        display: inline-block;
        color: #000;
        font-size: 13px;
        text-align: left;
        text-decoration: none;
        @include listIcon;

        &::before {
            top: 9px;
        }

        &:hover {
            color: #000;
            text-decoration: underline;
        }
    } //.nav-dropdown__link

    &--left {
        left: 0;
    } //.nav-dropdown--left

    &--right {
        right: 0;
    } //.nav-dropdown--right
} //.nav-dropdown

.menu-dropdown {
    position: absolute;
    top: 0;
    left: $base-header-size;
    z-index: -1;
    width: 188px;
    height: 100%;
    padding: 30px 15px;
    transition: transform ease-out 0.3s;
    border-top: 1px solid #dedede;
    border-right: 1px solid #dedede;
    background-color: #fff;
    will-change: transform;
    @include tX(-100%);

    @include responseWidth;

    @include landscape {
        position: fixed;
        top: $base-header-size + 1;
        height: calc(100% - #{$base-header-size});
        overflow-y: auto;
    }

    @include xs-block() {
        position: fixed;
        top: auto;
        right: 10px;
        bottom: 10px;
        left: 10px;
        z-index: z(modal);
        height: auto;
        padding: 0;
        transition: $trsM;
        border: 0;
        background-color: transparent;
        // border-radius: $border-radius * 2;
        box-shadow: 0;
        @include tX(0);
        @include tY(100vh);
    }

    &__title {
        display: block;
        margin-bottom: 15px;
        padding: 0 15px;
        color: $gray--light;
        font-size: 13px;
        letter-spacing: 1px;
        text-transform: uppercase;

        @include xs-block() {
            margin-bottom: 0;
            padding: 15px;
            border-radius: 12px 12px 0 0;
            background-color: #fff;
            text-align: center;
        }
    } //.menu-dropdown__title

    &__list {
        @include xs-block() {
            overflow: hidden;
            border-radius: 0 0 $border-radius $border-radius;
        }
    } //.menu-dropdown__list

    &__item {
        margin: 1px auto;
        outline: none;
        @include nosel;

        @include xs-block() {
            width: 100%;
            background-color: #fff;

            &:first-child {
                border-top: 1px solid $color-border--lightest;
            }

            &:last-child {
                &:not(.menu-dropdown__item--abort) {
                    border-bottom: 0;
                    border-radius: 0 0 8px 8px;
                }
            }

            &:not(.menu-dropdown__item--abort) {
                margin: 0 auto;
                border-bottom: 1px solid $color-border--lightest;
            }
        }

        &.is-active {
            @include min(xs) {
                .menu-dropdown__link {
                    background-color: lighten($color-accent, 25%);
                    color: $color-accent;
                }
            }
        }

        &--abort {
            margin-top: 8px;
            border: 0;
            border-radius: $border-radius * 4;

            @include min-screen(481) {
                display: none;
            }
        } //.menu-dropdown__item--abort
    } //.menu-dropdown__item

    &__link {
        display: block;
        padding: 7px 15px;
        border-radius: $border-radius;
        color: $gray;
        font-size: 15px;
        @include nosel;

        @include xs-block() {
            padding: 13px 0;
            color: $color-info;
            font-size: 18px;
            text-align: center;

            &:hover {
                background-color: transparent;
                color: $gray;
            }
        }

        &:hover {
            background-color: lighten($color-accent, 25%);
            color: $color-accent;
        }
    } //.menu-dropdown__link
} //.menu-dropdown

.slide-line {
    position: absolute;
    bottom: 0;
    left: 0;
    z-index: 10;
    display: block;
    width: 2px;
    height: 3px;
    transition: $trs;
    border-radius: 35px;
    background: $gray--dark;

    @include xs-block() {
        height: 2px;
    }
} //.slide-line

.add-menu {
    &__btn {
        position: relative;
        z-index: z(dropdown);
        justify-content: center;
        margin: 0;
        padding: 0;
        border: 0;
        outline: none;
        background: none;
        box-shadow: none;
        cursor: pointer;
        @include tr;
        @include flexCenter;

        &:hover,
        &:focus,
        &:active {
            outline: none;
            box-shadow: none;
        }

        .icon {
            width: 38px;
            height: 38px;
            fill: $gray--dark;
        }
    } //.add-menu__btn

    &__dropdown {
        position: fixed;
        top: 0;
        right: 0;
        bottom: 0;
        left: 0;
        z-index: z(fixed);
        display: none;
        background-color: rgba(#fff, 0.9);
    } //.add-menu__dropdown

    &__list {
        position: absolute;
        right: 0;
        bottom: 44px;
        left: 0;
        z-index: 23;
        display: grid;
        grid-auto-rows: 85px;
        grid-template-columns: auto auto;
        align-items: center;
        margin: 0 38px;
        transition-duration: 0.3s;
    } //.add-menu__list

    &__item {
        display: flex;
        justify-content: center;
    } //.add-menu__item

    &__link {
        align-items: center;
        justify-content: center;
        margin: 0;
        border: 0;
        background: none;
        box-shadow: none;
        color: $gray--darken;

        font-size: 14px;
        text-align: center;
        text-transform: capitalize;
        cursor: pointer;
        @include flexColumn;

        &:hover,
        &:focus {
            color: $gray--darken;
        }
    } //.add-menu__link

    &__text {
    } //.add-menu__text

    &__icon {
        width: 22px;
        height: 22px;
        margin-bottom: 5px;
        fill: $gray--dark;
    } //.add-menu__icon

    &.is-open {
        .add-menu {
            &__btn {
                @include rotate(135);

                .icon {
                    fill: $color-accent;
                }
            }

            &__dropdown {
                display: block;
            }
        }
    } //.add-menu.is-open
} //.add-menu

.sub-nav {
    height: 50px;
    @include flexCenter;

    &__list {
        position: relative;
        width: 100%;
        padding: 0 10px;
        @include flexCenter;

        @include xs-block() {
            justify-content: center;
            margin: 0 10px;
            //padding: 0 10px;
            padding: 0;
        }
    } //.sub-nav__list

    &__item {
        margin: 0 15px;
        cursor: pointer;

        @include md-block() {
            &:first-child {
                margin-left: 0;
            }
        }

        @include xs-block() {
            position: relative;
            margin: 0;
        }

        &:hover {
            &::before {
                opacity: 0.5;
                display: block;
            }

            .sub-nav__link {
                opacity: 1;
                // font-weight: 600;
                color: $gray--darken;
            }
        }

        &.is-active {
            &:not([data-slide-line]) {
                .sub-nav__link {
                    &::before {
                        opacity: 1;
                        display: block;
                    }
                }
            }

            .sub-nav__link {
                opacity: 1;
                // font-weight: 600;
                color: $gray--darken;
            }
        }
    } //.sub-nav__item

    &__link {
        position: relative;
        opacity: 0.8;
        height: 50px;
        transition: initial;
        color: #929292;
        font-size: 13px;
        font-weight: 600;
        letter-spacing: 0.3px;
        text-transform: uppercase;
        white-space: nowrap;
        cursor: pointer;
        @include flexCenter;

        @include xs-block() {
            margin: 0 15px;
            padding: 0;
            font-size: 12px;
        }

        &::before {
            content: '';
            position: absolute;
            right: 0;
            bottom: 0;
            left: 0;
            display: none;
            height: 3px;
            border-radius: 35px;
            background-color: $gray--dark;
        }

        &:hover,
        &:focus {
            color: $gray;
        }
    } //.sub-nav__link

    &__badge {
        right: -25px;
        min-width: 20px;
        height: 15px;
        @include centre('y');

        @include xs-block() {
            right: -13px;
        }
    } //.sub-nav__badge

    &--scroll {
        overflow: hidden;

        .sub-nav {
            &__list::-webkit-scrollbar {
                display: none;
            }

            &__list {
                //margin-bottom: -30px;
                //padding-bottom: 30px;
                overflow-x: auto;
                scrollbar-width: none;

                @include xs-block() {
                    justify-content: flex-start;
                }
            } //.sub-nav--scroll .sub-nav__list
        } //.sub-nav--scroll .sub-nav
    } //.sub-nav--scroll
} //.sub-nav

.nav-header-img {
    position: relative;
    width: 100%;
    height: 100%;
    overflow: hidden;

    &::after {
        content: '';
        position: absolute;
        top: 0;
        right: 0;
        bottom: 0;
        left: 0;
        z-index: -1;
        background-color: rgba(#000, 0.5);
