            top: 0;
            right: 0;
            transform: translateY(0);

            .icon {
                fill: #fff;
            }
        }
    }

    &__img {
        position: absolute;
        top: 0;
        bottom: 0;
        left: 50%;
        z-index: -1;
        width: auto;
        min-width: 100%;
        height: auto;
        min-height: 100%;
        transform: translateX(-50%);
    } // .nav-header-img__img

    &__text-box {
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
        margin: 0 auto;
    } // .nav-header-img__text-box

    &__title {
        max-width: 25ch;
        margin-bottom: 10px;
