    &__text {
        max-width: 25ch;
        color: #fff;
        font-size: 14px;
        text-align: center;
    } // .nav-header-img__text

    &__arrow {
        position: absolute;
        width: 40px;
        height: 40px;
        border: 0;
        background: transparent;
        @include centre('y');

