.ajax-back-btn {
    position: absolute;
    top: 0;
    right: 0;
    display: flex;
    align-items: center;
    justify-content: center;
    width: 50px;
    height: 50px;
    border: 0;
    background: transparent;

    &__icon {
        fill: $color-bg;
    }
