@keyframes spin {
    0% {
        transform: translate(-50%, -50%) rotate(0deg) scale(1);
    }

    90% {
        transform: translate(-50%, -50%) rotate(1080deg) scale(1);
    }

    100% {
        transform: scale(0);
    }
}

@keyframes zoomIn {
    0% {
        transform: scale(1);
    }

    50% {
        transform: scale(1.2);
    }

    100% {
        transform: scale(1);
    }
}

@keyframes success {
    from {
        transform: translate(-50%, -50%) rotate(-45deg) scale(0);
    }

    to {
        transform: translate(-50%, -50%) rotate(0) scale(1);
    }
}

@keyframes btsStatusAnimation {
    from {
        transform: translateY(150%);
    }

    to {
        transform: translate(0);
    }
}

@keyframes vibrate {
    0%,
    30%,
    60%,
    85%,
    100% {
        left: 0;
        background-color: $color-danger;
    }

    10%,
    40%,
    90%,
    70% {
        left: -2px;
        background-color: $color-danger;
    }

    20%,
    50%,
    80%,
    95% {
        left: 2px;
        background-color: $color-danger;
    }
}

@mixin resetBtn {
    overflow: hidden;
    border: 0;
    outline: none;
    background-color: transparent;
    cursor: pointer;
}

@keyframes shake {
    10%,
    90% {
        transform: translate3d(-1px, 0, 0);
    }

    20%,
    80% {
        transform: translate3d(2px, 0, 0);
    }

    30%,
    50%,
    70% {
        transform: translate3d(-4px, 0, 0);
    }

    40%,
    60% {
        transform: translate3d(4px, 0, 0);
    }
}

%button {
    position: relative;
    z-index: z(default);
    display: inline-flex;
    align-items: center;
    justify-content: center; //Не раскоментировать
    min-height: $base-btn-height;
    padding: 0 35px;
    transition: background-color 0.12s linear, color 0.12s linear,
        border 0.12s linear;
    border: 0;
    border-radius: $border-radius;
    outline: none;

    font-size: 16px;
    font-weight: 400;
    letter-spacing: 0.3px;
    line-height: 41px;
    text-align: center;
    text-decoration: none;
    cursor: pointer;
    @include nosel;
    @include text-overflow;

    @include xs-block() {
        min-height: $base-btn-height--xs;
        font-size: 17px;
    }

    &.is-disabled {
        transform: none;
        border-color: transparent;
        background-color: $gray--lighten;
        box-shadow: none;
        color: #ccc;
        cursor: not-allowed;
        pointer-events: none;
        touch-action: none;

        &:hover {
            transform: none;
            border-color: transparent;
            background-color: $gray--lighten;
            box-shadow: none;
            color: #ccc;
        }

        .icon {
            fill: #ccc;
            color: #ccc;
        }
    }
} //tamplate btn

@mixin btnChangeColor($btnBg, $btnText) {
    background-color: $btnBg;
    color: $btnText;

    &:hover {
        background-color: $btnBg;
        color: $btnText;

        .button-animate__hover {
            background-color: rgba(255, 255, 255, 0.16);
        }
    }

    .button-animate__hover {
        background-color: rgba(255, 255, 255, 0.36);
    }
} //Change Bg & Color button-animate

@mixin btnTrs($btnBg, $btnText) {
    border: 1px solid $btnBg;
    background-color: transparent;
    color: $btnBg;
    @include tr;

    &:hover {
        background-color: transparent;
        color: $btnText;

        .button-animate__hover {
            background-color: $btnBg;
        }
    }

    .button-animate__hover {
        background-color: $btnBg;
    }
} //Change Bg & Color button-animate transparent

@mixin btnBg($bgc) {
    background-color: $bgc;
    box-shadow: 0 1px 10px $bgc;

    &:hover,
    &:focus,
    &:active {
        background-color: lighten($bgc, 10%);
        box-shadow: 0 1px 10px lighten($bgc, 10%);
    }
}

