.button {
    @extend %button;

    @each $theme, $color in $button-colors {
        &--#{$theme} {
            background-color: $color;
            color: #fff;
        }

        &--#{$theme}:hover,
        &--#{$theme}:focus {
            background-color: lighten($color, 10%);
            color: #fff;
        }

        &--#{$theme}:active {
            background-color: lighten($color, 7%);
        }
    }

    &[disabled] {
        color: #dbdedd;
    }

    &-subscribe {
        padding: 5px 25px;
    } //.button-subscribe

    &-icon {
        position: relative;
        display: inline-flex;
        align-items: center;
        width: 32px;
        height: 32px;
        padding: 0;
        border: 0;
        outline: none;
        background-color: transparent;
        color: $gray--lighten;

        &::before {
            margin: auto;
            font-size: 23px;
            line-height: 23px;
            @include fa('solid');
            @include centre;
        }

        &--fav {
            &::before {
                content: '\f004';
                @include fa('light');
            }

            &.is-checked {
                &::before {
                    color: rgba($color-accent, 0.65);
                    @include fa('solid');
                }
            }
        } //.button-icon--fav

        &--info {
            &::before {
                content: '\f0f3';
                color: $gray;
                font-size: 20px;
            }

            &::after {
                content: attr(data-count);
                position: absolute;
                top: 6px;
                left: 23px;
                animation: pulse 1.5s infinite;
                border: 2px solid #fff;
                background-color: $color-accent;
                color: #000;
                @extend %badge;
                @include max(xs) {
                    animation: none;
                }
                @include max(i5) {
                    left: 16px;
                }
            }

            &:hover,
            &.is-active {
                &::after {
                    animation: none;
                }
            }

            &.is-checked {
                &::before {
                    color: $gray;
                }
            }
        } //.button-icon--info

        &--search {
            &::before {
                content: '\f002';
                color: $gray;
                font-size: 25px;
            }
        } //.button-icon--search
    } //.button-icon

    &-social {
        width: 100%;
        font-size: 14px;
        text-transform: uppercase;

        &::before {
            font-size: 18px;
            line-height: 1;
            @include fa('brand');
        }

        &:hover {
            color: #fff;
        }

        &--whatsapp {
            background-color: #23d366;

            &::before {
                content: '\f232';
            }

            &:hover {
                background-color: darken(#23d366, 10%);
            }
        } //.button-social--whatsapp

        &--viber {
            background-color: #7d3daf;

            &::before {
                content: '\f409';
            }

            &:hover {
                background-color: darken(#7d3daf, 10%);
            }
        } //.button-social--viber

        &--vk {
            background-color: #4a76a8;

            &::before {
                content: '\f189';
            }

            &:hover {
                background-color: darken(#4a76a8, 10%);
            }
        } //.button-social--vk

        &--instagram {
            transition: opacity 0.7s ease-in-out;
            background: linear-gradient(
                135deg,
                #4c68d7 0%,
                #d321a4 50%,
                #ffef63 100%
            );

            &::before {
                content: '\f16d';
            }

            &::after {
                content: '';
                z-index: z(down);
                opacity: 0;
                display: block;
                background: linear-gradient(
                    135deg,
                    #ffef63 0%,
                    #d321a4 50%,
                    #4c68d7 100%
                );
                @include coverdiv;
                @include tr;
            }

            &:hover {
                &::after {
                    opacity: 1;
                }
            }
        } //.button-social--instagram
    } //.button-social

    &-text--up {
        font-size: 15px;
        font-weight: 500;
        text-transform: uppercase;
    } //.button--up

    &--small {
        padding: 4px 25px 5px;
        font-size: 14px;
        @extend %textLight;
    } //.button--small
} //.button

.button-animate {
    overflow: hidden;
    background-color: $color-accent;
    @extend %button;

    &:hover,
    &:active {
        background-color: $color-accent;
        color: #fff;

        .button-animate__hover {
            width: 800px;
            height: 800px;
            background-color: rgba(255, 255, 255, 0.16);
        }
    }

    @each $theme, $color in $button-colors {
        &.button--#{$theme} {
            background-color: $color;

            &:hover {
                background-color: $color;
            }
        }
    }

    &__hover {
        position: absolute;
        z-index: z(down);
        display: block;
        width: 0;
        height: 0;
        transform: translate(-50%, -50%);
        transition: width 1s, height 1s;
        border-radius: 50%;
        background-color: rgba(255, 255, 255, 0.36);
    } //.button-animate__hover

    &__icon {
        left: 15px;
        @include centre('y');

        &::after {
            content: attr(data-count);
            position: absolute;
            top: -6px;
            right: -6px;
            width: 14px;
            height: 14px;
            border-radius: 500px;
            background-color: #000;
            color: #fff;
            font-size: 8px;
            line-height: 13px;
            text-align: center;
        }
    } //.button-animate__icon

    &__count {
        right: 15px;
        @include centre('y');

        .fa {
            font-size: 14px;
        }
    } //.button-animate__icon

    &--transparent {
        padding: 7px 35px;
        @include btnTrs($color-accent, #fff);
    } //.button-animate--transparent

    &--white {
        padding: 6.5px 35px;
        @include btnTrs(#fff, #000);
    } //.button-animate--white

    &--black {
        @include btnTrs($color-btn--darken, #fff);
    } //.button-animate--black

    &--gray {
        color: #000;
        @include btnTrs($gray, #fff);
    } //.button-animate--gray

    &--muted {
        border-radius: $border-radius;
        background-color: $color-muted;
        color: #fff;
        @include tr;

        &:hover,
        &:focus,
        &:active {
            background-color: $color-muted;
            color: #fff;

            .button-animate__hover {
                background-color: lighten($color-muted, 2%);
            }
        }

        .button-animate__hover {
            background-color: $color-muted;
        }
    } //.button-animate--muted

    &--grayN {
        border-radius: $border-radius;
        background-color: $gray;
        color: #fff;
        @include tr;

        &:hover,
        &:focus,
        &:active {
            background-color: $gray;
            color: #fff;

            .button-animate__hover {
                background-color: lighten($gray, 2%);
            }
        }

        .button-animate__hover {
            background-color: $gray;
        }
    } //.button-animate--gray

    &--icon {
        display: inline-flex;
        @include max(xs) {
            padding: 0;
            background-color: transparent;
        }

        .icon {
            width: 18px;
            height: 18px;
            margin: auto;
            fill: $color-accent;
            @include min-screen(480) {
                display: none;
            }
        }

        .button-animate__text {
            @include max(xs) {
                display: none;
            }
        }
    } //.button-animate--icon
} //.button-animate

.button-link {
    padding: 0;
    border: 0;
    outline: none;
    background-color: transparent;
    color: $color-accent;
    font-size: 14px;
    font-weight: 300;
    cursor: pointer;

    &:hover,
    &:focus,
    &:active {
        color: lighten($color-accent, 3%);
        text-decoration: underline;
    }

    @each $theme, $color in $button-colors {
        &--#{$theme} {
            color: $color;

            .icon {
                fill: $color;
            }
        }

        &--#{$theme}:hover,
        &--#{$theme}:focus {
            color: lighten($color, 10%);

            .icon {
                fill: lighten($color, 7%);
            }
        }

        &--#{$theme}:active {
            color: lighten($color, 7%);

            .icon {
                fill: lighten($color, 7%);
            }
        }
    }

    .icon {
        display: inline-block;
        width: 15px;
        height: 15px;
        margin-right: 5px;
        vertical-align: middle;
    }

    span {
        display: inline-block;
        vertical-align: middle;
    }

    &--add {
        &:hover,
        &:focus {
            text-decoration: none;
        }
    }

    &--icon {
        &:hover {
            text-decoration: none;
        }
    }

    &--share {
        color: $gray--light;
    }
} //.button-link

.btn {
    box-shadow: 0 2px 5px 0 #b7b7b7;
    @extend %button;

    &:hover {
        box-shadow: 0 3px 5px 0 #b7b7b7;
        @include tY(-1px);
    } //.btn:hover

    @each $theme, $color in $button-colors {
        &-#{$theme} {
            background-color: $color;
            color: #fff;

            &:hover {
                background-color: darken($color, 3%);
                color: #fff;
            }

            &:active {
                background-color: darken($color, 7%);
                box-shadow: none;
                @include tY(1px);
            }

            &--transparent {
                border: 1px solid $color;
                background-color: transparent;
                box-shadow: none;
                color: $color;

                &:hover {
                    background-color: $color;
                    box-shadow: none;
                    color: #fff;
                }

                .icon {
                    fill: $color;
                    color: $color;
                }
            }

            &.btn--transparent {
                &:hover {
                    box-shadow: none;
                }
            }

            .icon {
                fill: #fff;
            }
        }
    }

    &:active {
        box-shadow: none;
    } //.btn:active

    &:not(:disabled):not(.disabled) {
        cursor: pointer;
    } //.btn:not(:disabled):not(.disabled)

    &__title {
        @include tr;
    } //.btn__title

    &__error {
        position: absolute;
        top: 104%;
        right: 0;
        left: 0;
        display: none;
        width: 100%;
        color: #f00;
        font-size: 13px;
        font-weight: 300;
        line-height: 1;
        text-align: center;
        @include text-overflow;
    } //.btn__error

    &__list {
        position: absolute;
        top: 100%;
        right: 0;
        left: 0;
        visibility: hidden;
        opacity: 0;
        margin: 1px;
        transition: $trsMenu;
        background-color: #fff;
        box-shadow: $bxs-box;
        color: $color-text;
        font-size: 14px;
        @include flexColumn;
    } //.btn__list

    &__action {
        &--sub {
            padding: 10px;
            border: 0;
            outline: none;
            background-color: transparent;
            box-shadow: none;

            &:hover {
                background-color: $color-bg--light;
                color: $gray--darkest;
            }
        }
    } //.btn__action

    &__icon {
        left: 15px;
        width: 18px;
        height: 18px;
        @include centre('y');

        &::after {
            content: attr(data-count);
            position: absolute;
            top: -6px;
            right: -12px;
            min-width: 17px;
            height: 13px;
            background-color: $color-bg--darken;
            color: #fff;
            line-height: 13px;
            @extend %badge;
        }

        .icon {
            width: 18px;
            height: 18px;
            fill: #fff;
            @include centre;
        } //.btn__icon .icon
    } //.btn__icon

    &__count {
        right: 15px;
        @include centre('y');

        i {
            font-size: 12px;
        }
    } //.btn__count

    &-secondary {
        color: $color-text--darkest;

        &:hover {
            color: $color-text--darkest;
        }

        .icon {
            fill: $color-icon;
        }
    } //.btn-secondary

    &-white {
        border: 2px solid #fff;
        background: transparent;
        box-shadow: none;
        color: #fff;

        &:hover {
            background-color: rgba(255, 255, 255, 0.3);
            box-shadow: none;
            color: #fff;

            @include max(md) {
                background-color: transparent;
            }
        }

        &:active {
            transform: none;
            background-color: transparent;
            box-shadow: none;
        }
    } //.btn--link

    &-add {
        box-sizing: border-box;
        padding: 15px;
        border: 1px dashed $color-border--lightest;
        @include flexColumn;

        &__text {
            font-size: 13px;
            font-weight: 300;
        }

        &--small {
            align-items: center;
            justify-content: center;
            width: 100%;
        } //.btn-add--small

        &--photo {
            background-color: #fff;
        }
    } //.btn-add

    &-fixed {
        &[data-position] {
            position: fixed;
            z-index: 300;

            &.is-hide {
                bottom: -100px !important;
            }
        }

        &[data-position='bottom right'] {
            right: 45px;
            bottom: 45px;

            @include max(xs) {
                bottom: 60px;
            }
        }

        &[data-position='bottom left'] {
            bottom: 45px;
            left: 11px;

            @include max(xs) {
                bottom: 60px;
            }
        }
    } //.btn-fixed

    &.is-invalid {
        overflow: initial;

        .btn__error {
            display: block;
        }
    } //.btn.is-invalid

    &.is-loading {
        &::after {
            content: '';
            display: block;
            background-color: transparent;
            background-image: url('../files/assets/loader-3.gif');
            background-repeat: no-repeat;
            background-position: center center;
            background-size: contain;
            @include coverdiv;
        }

        .btn {
            &__title {
                @include tY(-150%);
            }
        }
    } //.btn.is-loading

    &.is-disabled {
        transform: none;
        border-color: transparent;
        background-color: $gray--lightest;
        box-shadow: none;
        color: #a4adba;
        cursor: not-allowed;
    }

    &.is-disable {
        transform: none;
        border-color: transparent;
        box-shadow: none;
        color: $color-icon--light;
    } //.btn.is-disable

    &.w100 {
        width: 100%;
    } //.btn.w100

    &.w50 {
        width: 100%;
        max-width: 180px;
    } //.btn.w50

    &.first {
        margin-right: auto;

        @include max(xs) {
            margin: 0;
        }
    } //.btn.first

    &.bg--white {
        &:focus,
        &:active {
            background-color: #fff;
        }
    } //.btn.bg--white

    input[type='file'] {
        display: none;
    } //.btn input[type='file']

    &--light {
        border: 1px solid $color-border--lightest;
        background-color: #fbfbfb;
        box-shadow: 0 2px 5px rgba(164, 173, 186, 0.2);
        color: $color-text;

        &:hover {
            transform: translateY(-1px) !important;
            box-shadow: 0 2px 5px rgba(164, 173, 186, 0.25);
            color: $color-text;
        }

        &:active {
            background-color: darken(#fbfbfb, 2%);
            box-shadow: none;
            @include tY(1px);
        }
    } //.btn--light

    &--skinny {
        width: 100%;
        height: auto;
        padding: 0 10px;
        font-size: 14px;
        font-weight: 500;
        line-height: 30px;

        @include xs-block() {
            padding: 0 10px;
            font-size: 14px;
        }

        &::before {
            font-size: 16px;
            line-height: 28px;
        }
    } //.btn--skinny

    &--flat {
        box-shadow: none;

        &:hover {
            transform: none;
            box-shadow: none;
        }
    } //.btn--flat

    &--small {
        padding: 5px 20px;
        font-size: 14px;
    } //.btn--small

    &--medium {
        padding: 9px 25px;
    } //.btn--medium

    &--big {
        padding: 15px;
        font-size: 14px;
        font-weight: 500;
        text-transform: uppercase;

        @include xs-block() {
            padding: 17px 15px;
            font-size: 14px;
        }
    } //.btn--big

    &--lg {
        display: block;
        width: 100%;
    } //.btn--lg

    &--expanded {
        overflow: initial;

        &::after {
            content: '\f107';
            right: 9px;
            @include fa('light');
            @include centre('y');
        }

        &[data-position^='top'] {
            .btn {
                &__list {
                    top: -32px;
                }
            }

            &.is-active {
                .btn {
                    &__list {
                        top: -92px;
                        visibility: visible;
                        opacity: 1;
                    }
                }
            }
        }

        &.is-active {
            &::after {
                content: '\f106';
            }

            .btn {
                &__list {
                    visibility: visible;
                    opacity: 1;
                }
            }
        }
    } //.btn--expanded

    &--last {
        margin-left: auto;
    } //.btn--last

    &--back {
        &:hover {
            text-decoration: none;

            i {
                @include tX(-5px);
            }
        }

        i {
            @include tr;
        }
    } //.btn--back

    &--center {
        display: flex;
        justify-content: center;
        text-align: center;
    } //.btn--center

    &--text {
        flex-direction: column;

        .btn {
            &__title {
                line-height: 1.5;
            } //.btn--text .btn__title

            &__subtitle {
                color: $color-text--light;
                font-size: 13px;
                line-height: 1.5;
            } //.btn--text .btn_subtitle
        } //.btn--text .btn
    } //.btn--text

    &--icon {
        position: relative;

        .icon {
            right: 12px;
            margin: 0;
            @include centre('y');
        } //.btn--icon .icon
    } //.btn--icon
} //.btn

.btn-animate {
    min-width: 180px;
    height: $base-btn-height;
    padding: 0;
    overflow: hidden;
    transition: initial;
    line-height: 43px;
    text-align: center;

    @include xs-block() {
        padding: 0 15px;
    }

    &:not(.is-ready) {
        &.is-error,
        &.is-success {
            &::before {
                content: '';
                position: absolute;
                top: 50%;
                left: 50%;
                display: block;
                width: 22px;
                height: 22px;
                margin-top: -10px;
                margin-left: -10px;
                animation: btsStatusAnimation ease-in 0.3s forwards;
            }

            .btn {
                &__title {
                    @include tY(-150%);
                }
            }
        }

        &.is-success {
            &::before {
                background-image: url('data:image/svg+xml;base64,');
            }
        } //.btn-animate:not(.is-ready).is-success

        &.is-error {
            background-color: $color-danger;

            &::before {
                background-image: url('data:image/svg+xml;base64,');
            }
        } //.btn-animate:not(.is-ready).is-error
    } //.btn-animate:not(.is-ready)

    &.is-animate {
        &::before {
            content: '';
            display: block;
            background-color: transparent;
            background-image: url('../files/assets/loader-3.gif');
            background-repeat: no-repeat;
            background-position: center center;
            background-size: contain;
            @include coverdiv;
        }

        .btn {
            &__title {
                display: none;
            }
        }
    } //.btn-animate.is-animate

    &.is-invalid {
        overflow: hidden;
    } //.btn-animate.is-invalid
} //.btn-animate

.btn-outline {
    @each $theme, $color in $button-colors {
        &-#{$theme} {
            max-height: $base-btn-height - 1;
            padding: 0 35px;
            border: 1px solid $color;
            background-color: transparent;
            background-image: none;
            box-shadow: none;
            color: $color;

            @include xs-block() {
                max-height: $base-btn-height--xs;
            }

            &:hover {
                transform: translate(0);
                background-color: $color;
                box-shadow: none;
                color: #fff;

                &.btn--icon {
                    .icon {
                        fill: #fff;
                    }
                }
            }

            &:active {
                border-color: darken($color, 5%);
                background-color: darken($color, 5%);
                box-shadow: none;
                @include tY(0);
            }

            &.btn--icon {
                .icon {
                    fill: $color;
                }
            }
        }
    }

    &-muted {
        border-color: $color-border-very-light;
        background-color: #fff;
        box-shadow: 0 2px 5px 0 rgba(164, 173, 186, 0.25);
        color: $color-text;

        &:hover,
        &:focus {
            background-color: #fff;
            box-shadow: 0 3px 5px 0 rgba(164, 173, 186, 0.25);
            color: $color-text;
        } //.btn-outline-muted:hover

        &:active {
            border-color: lighten($color-muted, 5%);
            background-color: #fff;
            box-shadow: none;
        } //.btn-outline-muted:active
    } //.btn-outline-muted

    &-white {
        &:hover {
            color: #000;
        }
    } //.btn-outline-white

    &-input {
        padding: 9px 10px;
        color: $gray;
        font-size: 16px;
        @include xs-block() {
            font-size: 16px;
        }
    } //.btn-outline-input
} //.btn-outline

.btn-link {
    display: inline-flex;
    // align-items: center;
    // justify-content: center;
    min-height: 0;
    padding: 0;
    border: 0;
    outline: none;
    background-color: transparent;
    box-shadow: none;
    color: $color-accent;

    font-size: 15px;
    letter-spacing: 0;
    line-height: 1;
    cursor: pointer;
    @include tY(0);

    @include xs-block() {
        padding: 0;
        font-size: 15px;
    }

    &:hover {
        box-shadow: none;
        color: darken($color-accent, 8%);
        text-decoration: underline;
        @include tY(0);
    }

    @each $theme, $color in $button-colors {
        &-#{$theme} {
            color: $color !important;
            cursor: pointer;

            &:hover {
                color: darken($color, 7%) !important;
            }

            &:active {
                color: darken($color, 10%) !important;
            }
        }
    }

    i {
        margin-top: -2px;
        margin-right: 7px;
        line-height: 1;
    }

    &-secondary {
        color: $gray--light;

        &:hover {
            color: $gray;
        }
    } //.btn-link-secondary

    &--default {
        text-decoration: underline;

        &:hover {
            text-decoration: none;
        }
    } //.btn-link--default

    &--invite {
        min-height: auto;
        padding: 4px 15px;
        border: 1px solid $color-border--light;
        border-radius: $border-radius;
        color: $color-text;
        font-size: 13px;

        @include xs-block() {
            padding: 4px 15px;
            font-size: 13px;
        }

        &:hover,
        &:focus {
            color: $color-text;
            text-decoration: none;
        }
    } //.btn-link--invite

    &--cancel {
        color: $gray--light;
        font-size: 12px;
        text-transform: uppercase;

        @include xs-block() {
            font-size: 12px;
        }

        &:hover {
            color: $gray--light;
            text-decoration: none;
        }
    } //.btn-link--cancel

    &--save {
        font-size: 12px;
        font-weight: 600;
        text-transform: uppercase;

        @include xs-block() {
            font-size: 12px;
        }

        &:hover {
            text-decoration: none;
        }
    } //.btn-link--save

    &--small {
        font-size: 14px;
    } //.btn-link--small

    &--light {
        font-weight: 300;
    } //.btn-link--light

    input[type='file'] {
        display: none;
    } //.btn--link input[type='file']
} //.btn--link

.btn-icon {
    position: relative;
    display: inline-flex;
    flex-shrink: 0;
    align-items: center;
    width: 32px;
    height: 32px;
    padding: 0;
    border: 0;
    border-radius: $border-radius;
    outline: none;
    background-color: transparent;
    color: $gray--lighten;
    text-align: center;
    cursor: pointer;

    &::before {
        margin: auto;
        font-size: 23px;
        line-height: 23px;
        @include fa('light');
        @include centre;
    }

    &:hover {
        .icon {
            fill: $color-icon--hover;
        }
    }

    @each $theme, $color in $button-colors {
        &--#{$theme} {
            background-color: $color;

            &:hover {
                background-color: lighten($color, 3%);

                .icon {
                    fill: darken($color, 5%);
                }
            }
        }
    }

    i {
        margin: auto;
        color: $color-icon;
        font-size: 21px;
    } //.btn-icon i

    .icon {
        margin: auto;
        @include tr;
    } //.btn-icon .icon

    &--fav {
        justify-content: center;
        @include flexCenter;

        &:hover {
            .icon {
                fill: $color-accent;
            }
        }

        .icon {
            width: 20px;
            height: 20px;
            margin: auto;
            animation: zoomIn 0.5s ease-in-out;
            @include tr;

            &.icon-favorite--filled {
                display: none;
            }
        }

        &.is-checked {
            &:hover {
                .icon {
                    fill: $color-accent;
                }
            }

            .icon {
                &.icon-favorite {
                    display: none;
                }

                &.icon-favorite--filled {
                    display: block;
                    fill: lighten($color-accent, 5%);
                }
            }
        }
    } //.btn-icon--fav

    &--info {
        &::before {
            color: $gray;
            font-size: 20px;
        }

        &::after {
            content: attr(data-count);
            position: absolute;
            top: 6px;
            left: 23px;
            animation: pulse 1.5s infinite;
            border: 2px solid #fff;
            background-color: $color-accent;
            color: #000;
            @extend %badge;
            @include max(xs) {
                animation: none;
            }
            @include max(i5) {
                left: 16px;
            }
        }

        &:hover,
        &.is-active {
            &::after {
                animation: none;
            }
        }
    } //.btn-icon--info

    &--controls,
    &--cog {
        width: $base-btn-height;
        height: $base-btn-height;
        border: 1px solid $color-border-very-light;
        background-color: #fff;
        @include bS;

        &:active {
            box-shadow: none;
        }
    } //.btn-icon--controls

    &--cog {
        .icon-cog {
            width: 25px;
            height: 25px;
        }
    }

    &--search {
        &::before {
            color: $gray;
            font-size: 25px;
        }
    } //.btn-icon--search

    &--large {
        width: 56px;
        height: 56px;

        i,
        span {
            font-size: 20px;
        }

        .icon {
            width: 20px;
            height: 20px;
        }
    } //.btn-icon--large

    &--medium {
        width: 40px;
        height: 40px;
    } //.btn-icon--medium

    &--small {
        .icon {
            width: 20px;
            height: 20px;
        } //.btn-icon--small .icon
    } //.btn-icon--small

    &--big {
        @include baseSize;

        .icon {
            width: 20px;
            height: 20px;
        }
    } //.btn-icon--big

    &--stroke {
        border: 1px solid $color-border--light;
        border-radius: $border-radius;
    } //.btn-icon--stroke

    &--rounded {
        border-radius: 50%;
    } //.btn-icon--rounded

    &--checked {
        background-color: $gray--lighten;
    } //.btn-icon--checked

    &--reverse {
        flex-direction: row-reverse;

        .icon {
            margin-left: 15px;
        } //.btn-icon--reverse .icon
    } //.btn-icon--reverse

    input[type='file'] {
        display: none;
    }

    &[class*='--prev'] {
        .icon {
            &[class*='angle'] {
                @include rotate(180);
            } //.btn-icon[class*='--prev'] .icon[class*=angle]
        }
    } //.btn-icon[class*='--prev']
} //.btn-icon

.btn-social {
    justify-content: center;
    min-width: 92px;
    height: 40px;
    padding: 0 10px;
    color: #fff;
    font-size: 14px;

    @include tr;
    @include flexCenter;

    @include xs-block() {
        padding: 12px 15px;
    }

    &::before {
        font-size: 19px;
        line-height: 1;
        @include fa('brand');
    }

    &:hover {
        color: #fff;
    }

    @each $theme, $color in $btn-social-color {
        &--#{$theme} {
            border-color: $color;
            background-color: $color;

            &:hover {
                border-color: darken($color, 5%);
                background-color: darken($color, 5%);

                .icon {
                    fill: #fff;
                }
            }

            .icon {
                fill: $color;
            }
        }
    }

    @each $theme, $icon in $btn-social-icon {
        &--#{$theme} {
            &::before {
                content: $icon;
                margin-right: 5px;
            }
        }
    }

    @each $theme, $icon in $btn-social-icon {
        &--#{$theme}-outline {
            &::before {
                content: $icon;
            }
        }
    }

    @each $theme, $color in $btn-social-color {
        &--#{$theme}-outline {
            box-shadow: none;

            &::before {
                color: $color;
            }

            &:hover {
                transform: translateY(0);
                border-color: #fff;
                background: $color;
                box-shadow: none;

                &::before {
                    color: #fff;
                }
            }
        }
    }

    &-text {
        text-transform: initial;

        &::before {
            margin-right: 10px;
        }
    } //.btn-social-text

    &-link {
        padding: 0;
        border: 0;
        box-shadow: none;

        &:hover {
            transform: none;
            box-shadow: none;
        }

        @each $theme, $color in $btn-social-color {
            &--#{$theme} {
                color: $color;

                &::before {
                    color: $color;
                }

                &:hover {
                    color: darken($color, 10%);

                    &::before {
                        color: darken($color, 10%);
                    }

                    span {
                        color: darken($color, 10%);
                    }
                }

                span {
                    color: $color;
                }
            }
        }

        @each $theme, $icon in $btn-social-icon {
            &--#{$theme} {
                &::before {
                    content: $icon;
                }
            }
        }

        span {
            font-weight: 500;
        }
    } //.btn-social-link

    span {
        margin-top: 1px;
        margin-left: 10px;
        color: #fff;

        font-size: 16px;
        line-height: 1;
        text-transform: initial;

        @include xs-block() {
            font-size: 17px;
        }
    } //.btn-social span

    & > img {
        width: 18px;
    }

    .text {
        display: none;
    } //.btn-social .text

    .icon {
        fill: #fff;
        @include tr;
    }

    &--outline {
        @each $theme, $color in $btn-social-color {
            border: 1px solid $color-border--lightest;
            box-shadow: none;

            &.btn-social {
                &--#{$theme} {
                    background-color: transparent;

                    &::before {
                        color: $color;
                    }

                    &:hover {
                        transform: translateY(0);
                        border-color: #fff;
                        background: $color;
                        box-shadow: none;

                        &::before {
                            color: #fff;
                        }
                    }
                }

                &--instagram {
                    background: none;

                    &::before {
                        background-image: linear-gradient(
                            60deg,
                            #b900b4,
                            #f50000 50%,
                            #ffd521
                        );
                        color: transparent;
                        -webkit-background-clip: text;
                        background-clip: text;
                    }

                    &::after {
                        display: none;
                    }

                    &:hover {
                        border-color: #fff;
                        background: linear-gradient(
                            60deg,
                            #b900b4,
                            #f50000 50%,
                            #ffd521
                        );
                        box-shadow: none;

                        &::before {
                            background-image: none;
                            color: #fff;
                            background-clip: inherit;
                        }
                    }
                    //background-clip: text;

                    &.bg-light {
                        background-color: #fff;
                    }
                } //.btn-social--outline.btn-social--instagram
            }

            .icon {
                fill: $color;
            }
        }
    } //.btn-social--outline

    &--instagram {
        justify-content: center;
        transition: opacity 0.7s ease-in-out;
        background: linear-gradient(60deg, #ffd521, #f50000 50%, #b900b4);
        color: #fff;
        @include flexCenter;

        &::before {
            content: '\f16d';
            margin-right: 7px;
            color: #fff;
            font-size: 20px;
            line-height: 41px;
            @include fa('brand');
        }

        &::after {
            content: '';
            z-index: z(down);
            opacity: 0;
            display: block;
            background: linear-gradient(60deg, #b900b4, #f50000 50%, #ffd521);
            @include coverdiv;
            @include tr;
        }

        &:hover {
            &::after {
                opacity: 1;
            }
        }
    } //.btn-social--instagram

    &--allone {
        width: 100%;
        margin-left: 0;

        .text {
            display: block;
        }
    } //btn-social--allone

    &-outline {
        background-color: transparent;
        box-shadow: none;

        @each $theme, $color in $btn-social-color {
            &--#{$theme} {
                border: 1px solid $color;
                color: $color;

                &::before {
                    margin-right: 5px;
                    color: $color;
                }

                &:hover {
                    transform: translateY(0);
                    border-color: #fff;
                    background: $color;
                    box-shadow: none;

                    &::before {
                        color: #fff;
                    }
                }
            }
        }

        @each $theme, $icon in $btn-social-icon {
            &--#{$theme} {
                &::before {
                    content: $icon;
                }
            }
        }

        &--instagram {
            border: 1px solid #f75757;
            color: #f75757;

            &::before {
                margin-right: 5px;
                // background-image: linear-gradient(
                //     60deg,
                //     #b900b4,
                //     #f50000 50%,
                //     #ffd521
                // );
                // color: transparent;
                background-clip: text;
            }

            &::after {
                display: none;
            }

            &:hover {
                transform: translateY(0);
                border-color: #fff;
                background-image: linear-gradient(
                    60deg,
                    #b900b4,
                    #f50000 50%,
                    #ffd521
                );
                box-shadow: none;

                &::before {
                    background-image: none;
                    color: #fff;
                    background-clip: inherit;
                }
            }
        }
    } //btn-social-outline
} //.btn-social

.btn-floating {
    box-sizing: content-box;
    display: block;
    width: 56px;
    height: 56px;
    padding-top: 10px;
    transition: bottom 300ms cubic-bezier(0.075, 0.82, 0.165, 1) 1s;
    cursor: pointer;
    // animation: shake 0.82s cubic-bezier(0.36, 0.07, 0.19, 0.97) 3.5s;

    &__list {
        position: absolute;
        right: 0;
        bottom: 66px;
        left: 0;
        visibility: hidden;
        @include tr;

        a {
            transform: scale(0);
        }
    } //.btn-floating__list

    &__item {
        box-sizing: content-box;
        padding-top: 10px;
    } //.btn-floating__item

    &__link {
        border-radius: 50%;
        //Тень не коментировать
        box-shadow: 0 2px 2px 0 rgba(0, 0, 0, 0.14),
            0 3px 1px -2px rgba(0, 0, 0, 0.12), 0 1px 5px 0 rgba(0, 0, 0, 0.2);
        pointer-events: none;
        @include tr();

        &[data-btn-tooltip] {
            &::after {
                content: attr(data-btn-tooltip);
                position: absolute;
                right: 70%;
                bottom: 25%;
                visibility: hidden;
                opacity: 0;
                margin-right: 12px;
                padding: 5px 7px;
                transition: all 0.4s ease-out;
                transition-delay: 0.4s;
                border-radius: $border-radius;
                background: #585858;
                color: #fff;
                font-size: 12px;
                font-weight: 500;
                font-weight: 500;
                white-space: nowrap;
            }
        }
    } //.btn-floating__link

    &__icon {
        fill: #fff;
    } //.btn-floating__icon

    &.fa-enter-active {
        & > .btn-floating__link {
            .icon,
            i {
                transform: rotate(0) translate(-51%, -50%);

                &:not(.icon--first) {
                    visibility: visible;
                    opacity: 1;
                }

                &--first {
                    visibility: hidden;
                    opacity: 0;
                }
            }
        }

        .btn-floating__link {
            pointer-events: auto;

            &[data-btn-tooltip] {
                &::after {
                    right: 100%;
                    visibility: visible;
                    opacity: 1;
                }
            }
        }

        .btn-floating__list {
            visibility: visible;

            .btn-floating__link {
                opacity: 1;
                transform: scale(1);
            }
        }
    } //.btn-floating.fa-enter-active

    &.fa-leave-active {
        .btn-floating__list {
            .btn-floating__link {
                opacity: 0;
                transform: translateY(40px);
            }
        }
    } //.btn-floating.fa-leave-active

    & > .btn-floating__link {
        cursor: pointer;

        .icon,
        i {
            transform: rotate(90deg) translate(-51%, -50%);
            transform-origin: 0 0;
            transition: $trs;
            fill: #fff;
            color: #fff;
            @include centre;

            &:not(.icon--first) {
                visibility: hidden;
                opacity: 0;
            }
        }
    }
} //.btn-floating

.btn-select {
