            &__list {
                display: block;
            } //.btn-select.is-active .btn-select__list

            &__overlay {
                display: block;
            } //.btn-select.is-active .btn-select__overlay
        } //.btn-select.is-active .btn-select
    } //.btn-select.is-active

    &--fixed {
        position: fixed;
        top: 50px;
        right: 0;
        left: 0;
        z-index: z(fixed);
    } //.btn-select--fixed
} //.btn-select

.button-box {
    display: flex;
    justify-content: center;
    text-align: center;

    &:not(.button-box--column) {
        & > :not(:only-child) + .btn,
        & > :not(:only-child) + .btn-link {
