        color: #fff;
        font-size: 13px;
        text-align: center;
        @extend %textLight;
    } //.buttons-box__text

    &__title {
        align-self: flex-start;
        margin: 5px 0;
        font-size: 14px;

        &--center {
            align-self: center;
        }
    } //.buttons-box__title

    .btn {
        padding: 0 30px;

        &--small {
            min-height: 30px;
            padding: 0 15px;
            line-height: 30px;
        }
    } //.button-box .btn

    .button-animate {
        @include max(xs) {
            width: 80%;
        }

        & + .button-animate {
            margin-left: 30px;

            @include max(xs) {
                margin-top: 15px;
                margin-left: 0;
            }
        }
    } //.button-box--mobile-column .button-animate

    &--center {
        display: flex;
        justify-content: center;

        .btn {
            &:only-child {
                flex-basis: 100%;
            }

            &:nth-last-child(2):first-child {
                flex-basis: 50%;

                & ~ button,
                & ~ a {
                    flex-basis: 50%;
                }
            }

            &:nth-last-child(3):first-child {
                flex-basis: 33.33%;

                & ~ button,
                & ~ a {
                    flex-basis: 33.33%;
                }
            }
        }

        & > :not(:first-child) {
            margin-left: 10px;
        }

        & > :not(:last-child) {
            margin-right: 10px;
        }
    } //.button-box--center

    &--column {
        align-items: center;
        @include flexColumn;

        .btn {
            & + .btn {
                margin-top: 20px;
                margin-left: 0 !important;

                @include xs-block() {
                    margin-top: 10px;
                }
            } //.buttons-box--column .btn + .btn
        } //.buttons-box--column .btn
    } //.buttons-box--column

    &--btn-p-small {
        .btn {
            margin-top: 0 !important;
            padding: 0 9px;

            @include xs-block() {
                font-size: 15px;
            }
        } //.button-box--btn-p-small .btn
    } //.button-box--btn-p-small

    &--mobile-column {
        @include xs-block() {
            flex-direction: column;
        }

        .btn {
            @include xs-block() {
                width: 100%;
                margin-left: 0;
            }

            & + .btn {
                margin-top: 10px;
                margin-left: 10px;

                @include xs-block() {
                    margin-left: 0;
                }
            } //.button-box--mobile-column .btn + .btn
        } //.button-box--mobile-column .btn
    } //.button-box--mobile-column

    &--connect-box {
        display: none;
        align-items: center;
        justify-content: space-between;

        @include border(top);
        @include xs-block() {
            display: flex;
        }

        & > :not(:first-child):not(.first) {
            margin-left: 0;
            padding: 0 15px;
        }

        .connect-btn {
            display: flex;
            align-items: center;
            justify-content: center;
            width: 50%;
            margin: 10px 0;

            &:nth-child(even) {
                @include border(left);
            }

            .icon {
                width: 30px;
                height: 30px;
                fill: #43425d;
            }
        }
    } //.buttons-box--connect-box
} //.buttons-box

.btn-box {
    display: flex;

    &__info {
        margin-top: 7px;
        font-size: 13px;
    } //.btn-box__info

    &--center {
        align-items: center;
        justify-content: center;
    } //.btn-box--center

    &--column {
        flex-direction: column;
    } //.btn-box--column
} //.btn-box

.btn-reset {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 40px;
    height: 40px;
    border: 0;
    background: transparent;
    cursor: pointer;

    &:hover {
        span {
            background-color: $gray--light;
        }
    }

    span {
        display: flex;
        align-items: center;
        justify-content: center;
        width: 18px;
        height: 18px;
        border-radius: 50%;
        background-color: lighten($gray--light, 13%);

        .icon {
            width: 8px;
            height: 8px;
            fill: #fff;
        } // .btn-reset span .icon
    } // .btn-reset span
} // .btn-reset
