.header {
    background-color: #fff;
    color: $gray--darken;

    &__top {
        position: relative;
        height: $base-header-size;

        @include xs-block() {
            height: $base-header-size--small;
        }
    } //.header__top

    &__bottom {
        height: 40px;

        @include sm-block() {
            display: none;
        }
    } //.header__bottom

    &__inner {
        height: $base-header-size;
        border-bottom: 1px solid $color-border--lightest;
        @include flexCenter;

        @include sm-block() {
            border-bottom: 0;
        }

        @include xs-block() {
            height: $base-header-size--small;
        }

        &--back-arrow {
            padding-left: 30px;

            icon {
                fill: #707070;
            }
        }

        &--order {
            display: flex;
            justify-content: center;

            @include min(md) {
                background-color: #fff;
            }

            .header__title--order {
                flex: 1;
                justify-content: center;
                margin-left: 0;
                padding-right: 65px;
                color: #fff;
                font-weight: 500;
                @include flexCenter;
            }

            .button-link--icon {
                margin-right: auto;
                margin-left: 15px;

                i {
                    font-size: 20px;
                }
            }
        }
    } //.header__inner

    &__back {
        margin-left: 10px;
        padding: 10px;

        .icon {
            width: 20px;
            height: 20px;
            fill: $gray;
        }
    } //.header__back

    &__search {
        z-index: z(fixed);
        width: 100%;
        max-width: 320px;
        @include centre;

        @include xs-block() {
            display: none;
        }
    } //.header__search

    &__city-select {
        margin-left: 20px;

        @include xs-block() {
            display: none;
        }
    } //.header__city-select

    &__select-mobile {
        position: relative;
        max-width: 50px;
        height: 50px;
        padding: 50px 0 0;

        select {
            top: -50px;
            max-width: 50px;
            height: 50px;
            padding: 50px 0 0;
            border: 0;
        }

        .bb-select {
            &__ico {
                width: 22px;
                height: 22px;
                fill: $color-icon;
            } //.header__select-mobile .bb-select__icon
        } //.header__select-mobile .bb-select
    } //.header__select-mobile

    &__title {
        margin-left: 30px;
        font-weight: 600;

        @include md-block() {
            margin-left: 10px;
        }

        @include xs-block() {
            right: 0;
            left: 0;
            width: 50%;
            margin: auto;
            font-size: 12px;
            font-weight: 600;
            letter-spacing: 0.5px;
            text-align: center;
            text-transform: uppercase;

            @include centre('y');
            @include text-overflow;
        }
    } //.header__title

    &__logo {
        margin-top: 4px;
    } //.header__logo

    &--transparent {
        background-color: transparent;

        .toggle-mnu {
            span {
                background-color: #fff;

                &::before {
                    background-color: #fff;
                }

                &::after {
                    background-color: #fff;
                }
            }
        }

        .logo {
            &__link {
                color: #fff;

                &:hover {
                    color: #fff;
                }
            }
        }
    } //.header--transparent

    &--absolute {
        position: absolute;
        top: 0;
        left: 0;
        z-index: z(fixed);
        width: 100%;
    } //.header.bg--absolute

    &.header--fixed {
        position: fixed;
        top: 0;
        left: 0;
        z-index: 201;
        width: 100%;
        transition: transform 0.4s ease-in-out;
    } //.header.header--fixed

    &.bg--dark {
        border-bottom: 0;
        background-color: $color-bg--dark;

        .header {
            &__top {
                background-color: $color-bg--dark;
            } //.header.bg--dark .header__top

            &__bottom {
                background-color: #2f2f2f;
            } //.header.bg--dark .header__bottom

            &__inner {
                border-bottom: 0;
            } //.header.bg--dark .header__inner
        } //.header.bg--dark .header

        .header-user {
            color: $gray--light;

            &::after {
                color: $gray--light;
            }

            &:hover {
                background-color: $color-bg--darken;
            }

            &.is-active {
                background-color: $color-bg--darken;
            }
        } //.header.bg--dark .header-user

        .header-action {
            &__login {
                color: $gray--light;
            } //.header.bg--dark .header-action__login

            &__link {
                color: $color-icon;

                &:hover {
                    &:not(.header-action__link--phone) {
                        background-color: $color-bg--darken;
                        color: #fff;
                    }

                    .header__icon {
                        fill: #fff;
                    } //.page-market .header-action__link:hover .header__icon
                } //.page-market .header.bg--dark .header-action__link:hover
            } //.page-market .header.bg--dark .header-action__link
        } //.header.bg--dark .header-action

        .city-select {
            &__title {
                border-color: $gray--light;
                color: $gray--light;

                &:hover {
                    border-color: $gray--lighten;
                    color: $gray--lighten;
                }
            }
        } //.header.bg--dark .change-city

        .toggle-menu {
            &__icon {
                background-color: #fff;
            } //.header.bg--dark .toggle-menu__icon
        } //.header.bg--dark .toggle-menu

        .request-info {
            &:hover,
            &.is-active {
                background-color: $color-bg--darken;

                .icon {
                    fill: #fff;
                }

                .request-info__icon {
                    color: $gray--light;

                    &::after {
                        border-color: $color-bg--darken;
                    }
                }
            }

            &__icon {
                &::after {
                    border-color: $color-bg--dark;
                }
            } //.header.bg--dark .request-info__icon
        } //.header.bg--dark .request-info

        .nav {
            &__item {
                &.is-active,
                &:hover {
                    .nav__link {
                        background-color: $color-bg--darken;
                        color: #fff;
                    }
                }
            } //.header .bg--dark .nav__item

            &__link {
                color: $gray--lightest;
                font-weight: 400;
            } //.header .bg--dark .nav__link
        } //.header .bg--dark .nav

        .search {
            &__input {
                border: 0;
                background-color: $gray--darken;
                color: $gray--lightest;
                @include tr;

                @include placeholder {
                    color: #d4d4d4;
                }

                &:hover,
                &:focus {
                    border: 0;
                    background-color: #fff;
                    box-shadow: none;
                    color: $gray--dark;
                }

                &:-webkit-autofill,
                &:-webkit-autofill:hover,
                &:-webkit-autofill:focus,
                &:-webkit-autofill:active {
                    box-shadow: 0 0 0 30px $gray--darken inset !important;
                    color: #fff;
                }
            } //.header.bg--dark .search__input

            &__ico {
                fill: $color-accent;
                color: $color-accent;
            } //.header.bg--dark .search__ico
        } //.header.bg--dark .search
    } //.header.bg--dark

    .search {
        display: flex;
        flex: 1;
        height: 35px;
        margin: 0 auto;

        @include sm-block() {
            display: none;
        }

        &__group {
            &--city {
                border-right: 0;
                border-radius: $border-radius 0 0 $border-radius;
            } //.header .search__group--city
        } //.header .search__group

        &__input {
            height: auto;
            padding: 9px 30px 7px 17px;

            font-weight: 400;
        } //.header .search__input

        &__btn {
            height: 35px;
            font-size: 15px;
            line-height: 35px;

            span {
                display: block;
                margin-top: 0;
                line-height: 1;
            } //.header .search__btn span
        } //.header .search__btn
    } //.header .search

    .container {
        @include xs-block() {
            padding-right: 0;
        }
    }
} //.header

.logo {
    display: inline-block;
    width: 130px;

    @include sm-block() {
        margin-right: 15px;
    }

    @include xs-block() {
        position: relative;
        top: 2px;
        margin-top: -2px;
        margin-right: 0;
    }
} //.logo

.header-action {
    margin-left: auto;
    @include flexCenter;

    &__inner {
        @include flexCenter;
    } //.header-action__inner

    &__request-info {
        .request-info {
            @include baseSize;

            &__items {
                max-height: 355px;
                overflow-y: auto;

                @include xs-block() {
                    max-height: 100%;
                    background-color: #fff;
                }
            } //.header-action__request-info .request-info__items
        }
    } //.header-action__request-info

    &__link {
        position: relative;
        justify-content: center;
        margin: auto;

        @include baseSize;
        @include flexCenter;

        &:hover {
            color: $color-accent;

            .icon {
                fill: $color-accent;
            }
        } //.page-market .header-action__link:hover

        .header__icon {
            fill: $color-icon;
        } //.page-market .header-action__link .header__icon

        .tiny,
        .badge {
            position: absolute;
            top: 12px;
            right: 5px;
            height: 15px;
            @extend %badge;
        } //.page-market .header-action__link .tiny

        .icon {
            width: 21px;
            height: 21px;
        } //.page-market .header-action__link .icon

        &--phone {
            width: auto;
            margin-left: 30px;
            color: $color-text--light;
            font-weight: 500;

            @include landscape() {
                display: none;
            }

            &:hover {
                background-color: transparent;
                color: $color-text;
            }
        } //.page-market .header-action__link--phone
    } //.page-market .header-action__link

    &__fav {
        width: 60px;
        height: $base-header-size;
        color: $color-text;

        @include xs-block() {
            width: 50px;
            height: $base-header-size--small;
        }
        @include max(i5) {
            width: 35px;
        }

        &:hover {
            color: $color-accent;
        }
    } //.header-action__fav

    &__search {
        cursor: pointer;
        @include baseSize;
        @include flexCenter;

        @include xs-block() {
            display: none;
        }

        &:hover {
            .icon {
                fill: #fff;
            }
        }

        .btn-icon {
            margin: auto;
        }

        .icon {
            width: 19px;
            height: 19px;
            @include tr;
        }
    } //.header-action__search

    &__enter {
        width: auto;
        height: $base-header-size;
        padding: 0 15px;
        @include flexCenter;

        @include xs-block() {
            width: $base-header-size--small;
            height: $base-header-size--small;
        }

        a {
            margin: auto;
            padding: 0;
            color: $gray--dark;

            &:hover {
                color: $color-accent;
                text-decoration: none;

                .icon {
                    fill: $color-accent;
                }
            }
        } //.header-action__enter a

        span {
            margin-right: 10px;
            font-size: 14px;
            font-weight: 500;

            @include xs-block() {
                display: none;
            }
        } //.header-action__enter span

        .icon {
            width: 24px;
            height: 24px;
            margin: 0;
            fill: $color-icon;
            @include tr;
        } //.header-action__enter .icon
    } //.header-action__enter

    &__city-select {
        @include landscape() {
            display: none;
        }
    } //.header__city-select
} //.header-action

.header-user {
    position: relative;
    justify-content: flex-start;
    height: $base-header-size;
    padding-right: 26px;
    padding-left: 12px;
    color: $gray--dark;
    cursor: pointer;

    @include flexCenter;
    @include iconAngle($width: 8px, $height: 8px);

    @include sm-block() {
        justify-content: center;
        width: $base-header-size;
        height: $base-header-size;
        padding: 0;
        @include flexCenter;
    }

    @include xs-block() {
        justify-content: center;
        width: $base-header-size--small;
        height: $base-header-size--small;
        margin-left: 0;
        padding-left: 0;
    }

    &::before {
        @include xs-block() {
            display: none;
        }
    }

    &:hover {
        .header-user {
            &__name {
                color: $color-accent;
            }
        }
    }

    &__name {
        font-size: 14px;
        font-weight: 500;
        @include tr;

        @include sm-block() {
            display: none;
        }
    } //.header-user__name

    &__img {
        width: 35px;
        height: 35px;
        // margin-left: 15px;
        overflow: hidden;
        border-radius: 50%;

        @include sm-block() {
            margin-left: 0;
        }
    } //.header-user__img

    &__link {
        @include flexCenter;
    } //.header-user__link

    &__desc {
        font-size: 13px;
        font-style: italic;
        font-weight: 400;
    } //.header-user__desc

    &.is-active {
        background-color: $color-gray--hover;

        .bb-dropdown__list {
            top: 60px;
        }
    }

    .bb-dropdown {
        &__list {
            top: 70px;
            min-width: 180px;
            padding: 0;
            border-right: 0;
            border-bottom: 4px solid $color-accent;
            border-left: 0;
            border-top-color: $color-border--lightest;
            box-shadow: 0 6px 20px rgba(#000, 0.3);

            &::before {
                border-bottom-color: $color-border--lightest;
            }

            ul {
                margin: 6px 0 1px;
                padding: 0;
            } //.header-user ul

            li {
                position: relative;

                &:hover {
                    background-color: $color-gray--hover;

                    a {
                        color: #000;
                    }

                    .icon {
                        fill: $gray--darken;
                    }
                }

                &:last-child {
                    &::after {
                        content: '';
                        top: -4px;
                        display: block;
                        width: 84%;
                        height: 1px;
                        background-color: $color-border--light;
                        @include centre('x');
                    }

                    a {
                        margin-top: 7px;
                    }
                }
            } //.header-user li

            a {
                display: block;
                padding: 11px 15px;
                transition: initial;
                color: $color-text;
                font-size: 13px;
            } //.header-user a
        } //.header-user .bb-dropdown__list
    } //.header-user .bb-dropdown

    &[data-dropdown-position^='right'] {
        .bb-dropdown__list {
            @include sm-block() {
                right: 10px;
            }

            &::before,
            &::after {
                right: 38px;

                @include landscape {
                    right: 12px;
                }
            } //.header-user[data-dropdown-position^=right] .bb-dropdown__list::after, .header-user[data-dropdown-position^=right] .bb-dropdown__list::before
        } //.header-user[data-dropdown-position^='right'] .bb-dropdown__list
    } //.header-user[data-dropdown-position^=right]

    &--expanded {
        .bb-dropdown {
            &__list {
                min-width: 250px;
                border-bottom: 0;

                ul {
                    margin: 0;

                    li {
                        &:not(:last-child) {
                            @include border(bottom);
                        }

                        &:last-child {
                            &::after {
                                display: none;
                            }

                            a {
                                margin-top: 0;
                            }
                        }
                    }
                } //.header-user--expanded .bb-dropdown__list ul

                a {
                    display: flex;
                    align-items: center;
                    line-height: 20px;

                    span {
                        @include min(sm) {
                            margin-left: 10px;
                        }
                    }
                } //.header-user--expanded .bb-dropdown__list a

                .user {
                    display: flex;
                    align-items: center;
                    padding: 15px;
                    @include border(bottom);

                    &__avatar {
                        margin-right: 15px;
                        margin-bottom: 0;

                        .user__img {
                            margin-right: 0;
                        }
                    }

                    &__link {
                        padding: 0;
                        color: $color-accent;
                        line-height: 20px;
                    }

                    &__info-block {
                        flex-grow: 1;
                        margin-left: 0;

                        a {
                            display: inline-block;
                            text-align: left;
                        }
                    }
                } //.header-user--expanded .bb-dropdown__list .user
            } //.header-user--expanded .bb-dropdown__list
        } //.header-user--expanded .bb-dropdown
    } //.header-user--expanded
} //.header-user

.request-info {
    position: relative;
    cursor: pointer;
    @include flexCenter;

    &:hover {
        .request-info {
            &__icon {
                .icon {
                    fill: $color-accent;
                    color: $color-accent;
                }
            }
        }
    }

    &__header {
        display: block;
        height: 50px;
        padding: 0 10px;
        color: $gray--darkest;
        font-size: 15px;
        font-weight: 500;
        line-height: 50px;
        @include border(bottom);

        @include xs-block() {
            border-bottom: 0;
        }

        span {
            color: $gray--light;
            font-size: 14px;
        }
    } //.request-info__header

    &__footer {
        @include border(top);

        .btn-link {
            display: block;
            justify-content: center;
            height: 40px;
            padding: 0 10px;
            color: $color-text;
            font-size: 13px;
            line-height: 40px;
            text-align: center;
            @include flexCenter;

            &:hover {
                background-color: #fbfbfb;
                text-decoration: none;
            }
        } //.request-info__footer .btn-link
    } //.request-info__footer

    &__inner {
        align-content: flex-start;
        padding: 10px;
        overflow-x: hidden;
        @include flex;

        .info-block {
            position: absolute;
            top: 10px;
            right: 10px;
        }
    } //.request-info__inner

    &__desc {
        flex: 1;
        margin-right: 35px;
        @include flexColumn;
    } //.request-info__desc

    &__icon {
        margin: auto;
        color: $gray--light;
        font-size: 20px;
        line-height: 1;

        @include tr;
        @include centre;

        &::after {
            content: attr(data-count);
            position: absolute;
            top: -5px;
            left: 7px;
            display: none;
            animation: pulse 1.5s infinite;
            border: 2px solid #fff;
            background-color: $color-accent;
            color: $gray--darkest;
            @extend %badge;

            @include xs-block() {
                animation: none;
            }

            @include i5-block() {
                left: 7px;
            }
        }

        .icon {
            width: 19px;
            height: 19px;
            margin-top: 5px;
            fill: $color-icon;
            @include tr;
        } //.request-info__icon .icon
    } //.request-info__icon

    &__list {
        @include flexCenter;
    } //.request-info__list

    &__items {
        flex: 1;
        overflow-x: hidden;
        background-color: $color-bg--lightest;
        @include smooth-scroll;
        @include flexColumn;
    } //.request-info__items

    &__item {
        position: relative;
        border-bottom: 1px solid $color-border--lightest;
        background-color: #fff;
        cursor: pointer;

        &:hover {
            background-color: #fbfbfb;

            .request-info__action {
                display: block;
            }
        }

        &:first-child {
            @include xs-block() {
                border-top: 1px solid $color-border--lightest;
            }
        }

        &:last-child {
            border-bottom: 0;
        }
    } //.request-info__item

    &__avatar {
        position: relative;
        display: inline-block;
        width: 35px;
        height: 35px;
        margin-right: 10px;
        overflow: hidden;
        border-radius: 50%;
    } //.request-info__avatar

    &__text {
        margin-bottom: 3px;
        color: $color-text;
        font-size: 13px;
        line-height: 1.3;

        strong {
            color: #000;
            font-weight: 500;
            font-weight: 500;
        }
    } //.request-info__text

    &__title {
        display: block;
        color: $color-text--darkest;
        font-size: 14px;
        font-weight: 500;

        &:hover {
            color: $color-text--darkest;
        }
    } //.request-info__title

    &__time {
        display: block;
        color: $gray--light;
        font-size: 13px;
        line-height: 1;
    } //.request-info__time

    &__action {
        display: none;

        @include xs-block() {
            display: block;
        }

        .bb-dropdown__list {
            padding: 5px 0;
        }
    } //.request-info__action

    &__empty {
        display: none !important;
        min-height: 250px;
    } //.request-info__empty

    &.is-empty {
        .request-info {
            &__items,
            &__header,
            &__footer {
                display: none !important;
            }

            &__empty {
                display: flex !important;
            }
        }
    } //.request-info.is-empty

    &.is-checked {
        .request-info {
            &__icon {
                &::after {
                    display: block;
                }
            }
        }
    } //.request-info.is-checked

    &.is-active {
        .request-info {
            &__icon {
                .icon {
                    transform: rotate(10deg);
                }
            }
        }
    } //.request-info.is-active

    &[data-dropdown-position^='right'] {
        &.is-active {
            background-color: $color-gray--hover;

            &::after {
                border-color: $color-gray--hover;
            }

            .request-info__icon {
                color: $gray;

                &::after {
                    border-color: $color-gray--hover;
                }
            }

            & > .bb-dropdown__list {
                top: 59px;
                z-index: 250;

                @include xs-block() {
                    // right: -1px;
                    top: 50px;
                }
            }
        } //.request-info.is-active

        & > .bb-dropdown__list {
            top: 70px;

            @include xs-block() {
                position: fixed;
                top: 70px;
                right: -1px;
                bottom: 0;
            }

            &::before,
            &::after {
                right: 23px;

                @include xs-block() {
                    right: 71px;
                }
            }
        } //.request-info[data-dropdown-position^=right]>.bb-dropdown__list
    } //.request-info[data-dropdown-position^='right']

    & > .bb-dropdown__list {
        top: 70px;
        right: 1px;
        left: auto;
        min-width: 320px;
        max-width: 377px;
        padding: 0;
        @include dropdownResponseWidth;

        @include xs-block() {
            top: 60px;
            right: -11px;
            border-radius: 0;
            box-shadow: none;
        }

        &::before,
        &::after {
            right: 23px;

            @include xs-block() {
                right: 29px;
            }
        } //.request-info .bb-dropdown__list
    } //.request-info .bb-dropdown

    .empty {
        &__text {
            & + .empty__text {
                margin-top: 0;
            } //.request-info .empty__text + .empty__text
        } //.request-info .empty__text

        &__icon {
            width: 40px;
            height: 40px;
        } //.request-info .empty__icon
