// ширина картинки
$widthImg: 50px;
// максимальная ширина тайтла
$MaxWidthColumWithoutImg: 300px;
// растояние между картинкой и тайтлом
$marginImgToColum: 10px;
// ширина заголовка таблицы для тайтла
$widthColumsBig: $widthImg + $MaxWidthColumWithoutImg + $marginImgToColum;
// ширина маленьких колонок
$widthColumSmall: 150px;

.cabinet-product {
    &__item {
        position: relative;
        display: flex;
        flex-wrap: nowrap;
        align-items: center;
        min-width: 100%;
        border-bottom: 1px solid $color-border--lightest;
        background-color: #fff;

        &:last-child {
            border-bottom: 0;
        } // .cabinet-product__item:last-child

        .table-name {
            flex-grow: 1;
            padding: 15px 0;
            text-align: center;
        } // .cabinet-product__item .table-name

        .bb-checkbox {
            &__box {
                left: 15px;
            } // .cabinet-product__item .bb-checkbox__box
        } // .cabinet-product__item .bb-checkbox

        .market-product-item {
            // margin-left: 20px;

            // @include xs-block() {
            //     margin-left: 10px;
            // }

            &--horizon {
                min-height: 70px;
                border-bottom: 0;

                @include xs-block() {
                    padding: 15px 0;
                }

                .market-product-item {
                    &__title {
                        flex-shrink: 0;
                        align-items: flex-start;
                        justify-content: flex-start;
                        // margin: 0 15px 0 0;
                        margin-left: 0;
                        font-weight: 500;
                        text-overflow: clip;
                        white-space: normal;
                    } // .cabinet-product .market-product-item--horizon .market-product-item__title

                    &__desc {
                        & > .market-product-item__box {
                            flex-basis: 18%;
                            align-self: center;
                            @include xs-block() {
                                align-self: flex-start;
                            }

                            .market-product-item__title {
                                max-width: 18ch;
                            }
                        }

                        & > .market-product-item__options {
                            flex-grow: 3;
                        }
                    }

                    &__img {
                        @include i5-block() {
                            max-width: 50px;
                        }
                    }
                } // .cabinet-product .market-product-item--horizon
            } // .cabinet-product .market-product-item--horizon
        } // .cabinet-product__item .market-product-item

        .control {
            margin-right: 15px;
        } // .cabinet-product__item .control
    } // .cabinet-product__item

    &__sort {
        position: absolute;
        left: 15px;
        cursor: grab;

        & ~ .market-product-item {
            margin-left: 50px;
        }
    }

    &.box {
        @include xs-block() {
            margin: 0 -10px;
            border-right: 0;
            border-left: 0;
            border-radius: 0;
        }
    } //.cabinet-product.box
} // .cabinet-product

.market-product-item {
    position: relative;
    display: inline-block;
    width: 100%;
    margin-bottom: $base-indent * 2;
    background-color: #fff;
    color: $color-text;
    // cursor: pointer;

    &:hover {
        color: $color-text;
    }

    &__wrap {
        @include row-flex();

        @include xs-block() {
            @include u-row-flex($gutter--small);
        }

        .market-product-item {
            @include col();
            @include size(4);
            @include size-xs(6);

            @include xs-block() {
                margin-bottom: $gutter;
                @include u-col($gutter--small);
                @include u-size(6, 12, $gutter--small);
            }
        }

        &.column-4 {
            .market-product-item {
                @include size(3);
                @include size-xs(6);
            }
        }

        &.column-5 {
            .market-product-item {
                @include size(2.4);
                @include size-xs(6);
            }
        }
    } //.market-product-item__wrap

    &__info {
        display: flex;
        flex: 1;
    } //.market-product-item__info

    &__category {
        width: 100%;
        color: $color-info--light;
        text-decoration: underline;
        cursor: pointer;
        @include text-overflow;
    } //.market-product-item__category

    &__img {
        position: relative;
        margin-bottom: 10px;
        padding: 0;
        overflow: hidden;
        // border: 1px solid $color-border--lightest;
        border-radius: $border-radius;

        img {
            min-width: 100%;
        }

        &._loading {
            min-height: 200px;
        }

        .btn-icon {
            position: absolute;
            top: 5px;
            right: 5px;
            z-index: 3;

            @include xs-block() {
                top: auto;
                bottom: 5px;
            }
        }
    } //.market-product-item__img

    &__fav {
        position: absolute;
        top: 12px;
        right: 15px;
        z-index: 10;
        color: #000;
    } //.market-product-item__fav

    &__more {
        position: absolute;
        top: 100%;
        left: 0;
        z-index: 10;
        display: none;
        width: 100%;
        padding: 10px;
        border-top: 0;
        border-radius: $border-radius;
        background-color: #fff;

        @include xs-block() {
            display: none;
        }
    } //.market-product-item__more

    &__desc {
        padding: 0 10px;

        @include xs-block() {
            padding: 0;
        }
    } //.market-product-item__desc

    &__title {
        margin: 0;
        color: $gray--dark;
        font-size: 14px;
        font-weight: 400;
        line-height: 18px;

        @include xs-block() {
            align-items: flex-start;
            // margin-bottom: 5px;
            font-size: 15px;
            text-align: left;
        }

        span {
            position: relative;

            font-weight: 400;

            &::before,
            &::after {
                font-family: faL;
            }

            &::before {
                content: '\f100';
            }

            &::after {
                content: '\f101';
            }
        }
    } //.market-product-item__title

    &__price {
        align-self: flex-end;
        margin-bottom: 5px;
        font-size: 18px;
        font-weight: 600;
        white-space: nowrap;

        & + & {
            margin-left: 5px;
        }

        &--sale {
            color: $color-primary;

            // .icon {
            //     fill: $color-primary;
            // }
        } //.market-product-item__price--sale

        &--old {
            margin-bottom: 2px;
            color: $color-text--light;
            font-size: 14px;
            text-decoration: line-through;

            // .icon {
            //     width: 8px;
            //     height: 8px;
            // }
        } //.market-product-item__price--old

        i {
            font-size: 80%;
            font-style: normal;
            font-weight: 400;
        }

        // & > span {
        //     font-size: 15px;
        // }
    } //.market-product-item__price

    &__articul,
    &__color-count {
        margin-left: auto;
    } //.market-product-item__articul

    &__articul {
        margin-top: 0;
        margin-bottom: auto;
        font-size: 13px;
        font-style: italic;
        @include xs-block() {
            margin-left: 0;
        }
    } //.market-product-item__articul

    &__color-count {
        margin-left: auto;
        font-style: normal;
    } //.market-product-item__color-count

    &__color {
        margin-top: 15px;
        margin-bottom: 10px;
    } //.market-product-item__color

    &__btn {
        display: block;
        width: 100%;
        padding: 10px 25px;

        & + & {
            margin-top: 7px;
        }
    } //.market-product-item__btn

    &__box {
        // flex-shrink: 0;
        flex-grow: 1;
        flex-wrap: wrap;
        @include flexCenter;

        .bb-input--select {
            width: 100%;
        }
    } //.market-product-item__box

    &__multiplier {
        width: 7px;
        height: 7px;
        // margin: 0 3px;
    } //.market-product-item__multiplier

    &__del {
        flex-grow: 1;
        color: $color-icon--light;
        @include tr;

        @include xs-block() {
            position: absolute;
            top: 0;
            right: 0;
            visibility: visible;
            opacity: 1;
        }

        &:hover {
            color: $color-icon;
        }
    } //.market-product-item__del

    &__text {
        font-size: 13px;
    } //.market-product-item__text

    &__show-btn {
        background-color: $color-bg--light;
        color: $color-text;
        font-weight: 500;

        &:hover {
            background-color: darken($color-bg--light, 5%) $color-bg--light;
            color: $color-text;
        }
    } //.market-product-item__show-btn

    &__badge-box {
        position: absolute;
        bottom: 5px;
        left: 5px;
    } //.market-product-item__badge-box

    // &--show {
    //     @include landscape() {
    //         width: calc(25% - 20px) !important;
    //     }

    //     .market-product-item {
    //         &__title {
    //             width: 100%;
    //             @include xs-block() {
    //                 width: 100%;
    //             }
    //         } //.market-product-item--show .market-product-item__title

    //         &__desc {
    //             margin: 0;
    //             padding: 0;
    //         } //.market-product-item--show .market-product-item__desc
    //     }
    // } //.market-product-item--show

    &--hover-effect {
        @include min-screen(769) {
            &::before {
                content: '';
                position: absolute;
                top: 0;
                right: 0;
                bottom: 0;
                left: 0;
                z-index: -1;
                opacity: 0;
                display: block;
                transition: opacity 0.35s ease;
                background-color: #fff;
                box-shadow: 0 5px 30px rgba(0, 0, 0, 0.2);
            }
        }

        &.has-hover,
        &:hover {
            z-index: 11;
            color: $color-text;

            @include min-screen(769) {
                &::before {
                    opacity: 1;
                }

                & .market-product-item__img {
                    border: 0;
                }

                & .market-product-slider {
                    visibility: visible;
                    opacity: 1;
                }

                .market-product-item {
                    &__more {
                        display: block;
                        @include landscape() {
                            display: none;
                        }
                    }

                    &__desc {
                        padding: 0 10px;
                    }
                }
            }

            @include landscape() {
                &::before {
                    display: none;
                }

                &:hover,
                &:active,
                &:focus {
                    & .market-product-slider {
                        display: none;
                    }
                }

                .market-product-item {
                    &__img {
                        border: 1px solid $color-border--lightest;
                    }

                    &__more {
                        display: none;
                    }
                }
            }
        }

        .market-product-item {
            &__desc {
                padding: 0 10px;

                @include xs-block() {
                    padding: 0;
                }
            }

            &__more {
                &::before {
                    content: '';
                    position: absolute;
                    top: 0;
                    right: 0;
                    bottom: 0;
                    left: 0;
                    z-index: -1;
                    display: block;
                    background-color: #fff;
                    box-shadow: 0 30px 30px rgba(0, 0, 0, 0.2);
                }
            }
        }
    } //.market-product-item--hover-effect

    &--horizon {
        flex-grow: 1;
        flex-shrink: 0;
        min-height: 60px;
        margin: 0;
        padding: 15px 0;
        @include flexCenter;

        @include sm-block() {
            padding: 20px 15px;
        }

        @include xs-block() {
            flex-wrap: wrap;
            align-items: flex-start;
        }

        &:hover {
            color: $color-text;

            .market-product-item {
                &__del {
                    visibility: visible;
                    opacity: 1;
                } //.market-product-item--horizon:hover .market-product-item__del
            }
        } //.market-product-item--horizon:hover
        // overflow: hidden;
        // padding-right: 10px;
        // border-bottom: 1px solid $gray--lighten;
        // @include border(bottom);

        & + & {
            @include border(top);
        }

        .market-product-item {
            &__title {
                font-size: 16px;

                @include xs-block() {
                    font-weight: 500;
                }
            } //.market-product-item--horizon .market-product-item__title

            &__articul {
                margin-left: 0;
            } //.market-product-item--horizon .market-product-item__articul

            &__box {
                align-items: flex-start;
                justify-content: flex-start;

                @include xs-block() {
                    // width: 100%;

                    & > .btn {
                        width: 100%;
                        margin-top: 10px;
                        // padding: 8px 35px;
                    }
                }

                &--column {
                    flex-direction: column;
                }
            } //.market-product-item--horizon .market-product-item__box

            &__info {
                @include xs-block() {
                    flex-direction: column;
                    width: calc(100% - 75px - 15px);
                }

                .market-product-item__price {
                    align-self: center;
                }
            } //.market-product-item--horizon .market-product-item__info

            &__img {
                align-self: flex-start;
                max-width: 80px;
                margin: 0 15px 0 0;
            } //.market-product-item--horizon .market-product-item__img

            &__image {
                width: 100%;
                border: 0;
            } //.market-product-item--horizon .market-product-item__image

            &__desc {
                display: flex;
                flex-basis: auto;
                flex-grow: 1;
                flex-wrap: wrap;
                justify-content: space-around;
                padding: 0;

                @include min(xs) {
                    padding-right: 10px;
                }

                @include xs-block() {
                    flex-basis: 0;
                    flex-direction: column;
                    justify-content: flex-start;
                }

                & > * {
                    flex: 0 1 100%;
                }

                & > .market-product-item__options,
                & > .market-product-item__box {
                    flex-basis: auto;
                    @include xs-block() {
                        flex-basis: 100%;
                    }
                }

                & > .market-product-item__box {
                    flex-grow: 1;
                }

                & > .market-product-item__options {
                    flex-grow: 2;
                    @include xs-block() {
                        flex: none;
                    }

                    .market-product-item__box {
                        flex-basis: auto;
                        justify-content: flex-start;

                        &:nth-child(even) {
                            justify-content: center;
                            @include xs-block() {
                                justify-content: flex-end;
                            }
                        }
                    }
                }

                &--column {
                    flex-basis: 33%;
                    flex-direction: column;
                    padding: 0;

                    & > * {
                        flex: none;
                        align-self: flex-start;
                    }

                    .market-product-item__box {
                        width: 100%;

                        .market-product-item__title {
                            width: auto;
                        }
                    } //.market-product-item--horizon .market-product-item__desc--column .market-product-item__box

                    & ~ .market-product-item__options {
                        @include xs-block() {
                            margin-top: 10px;
                        }
                    }
                } //.market-product-item--horizon .market-product-item__desc--column
            } //.market-product-item--horizon .market-product-item__desc

            &__options {
                display: flex;
                // flex: 1;
                flex-basis: 33.33%;
                flex-grow: 1;
                align-items: center;
                justify-content: space-between;

                // @include min(xs) {
                //     margin-left: 15px;
                // }

                @include xs-block() {
                    flex-basis: 100%;
                    flex-grow: 0;
                    // margin-top: 10px;
                    // margin-left: 0;
                }

                .market-product-item__box {
                    flex-basis: 0;

                    &:first-child {
                        flex: 1;
                    }

                    &:only-child {
                        flex-basis: 100%;
                        justify-content: flex-end;

                        @include xs-block() {
                            justify-content: flex-start;
                        }
                    } //.market-product-item--horizon .market-product-item__options .market-product-item__box:only-child

                    &:nth-child(even) {
                        justify-content: flex-end;
                    } //.market-product-item--horizon .market-product-item__options .market-product-item__box:nth-child(even)

                    & + .market-product-item__box {
                        // margin-left: 15px;
                    } //.market-product-item--horizon .market-product-item__options .market-product-item__box + .market-product-item__box
                } //.market-product-item--horizon .market-product-item__options .market-product-item__box
            } //.market-product-item--horizon .market-product-item__options

            &__cost {
                font-size: 18px;
                font-weight: 400;
                white-space: nowrap;
            } //.market-product-item--horizon .market-product-item__cost
        } //.market-product-item--horizon .market-product-item
    } //.market-product-item--horizon
} //.market-product-item

.market-product-slider {
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    visibility: hidden;
    opacity: 0;
    overflow: hidden;
    cursor: pointer;

    @include md-block() {
        min-height: 80%;
    }

    @include sm-block() {
        min-height: 80%;
    }

    @include xs-block() {
        display: none;
        min-height: 80%;
    }

    @include landscape() {
        display: none;
    }

    &:hover {
        visibility: visible;
        opacity: 1;
    } //.market-product-slider__item:hover

    &__list {
        position: absolute;
        top: 0;
        right: 0;
        bottom: 0;
        left: 0;
        z-index: 2;
        display: flex;
        align-items: stretch;
        overflow: hidden;
        transition: opacity 0.3s;
        background-size: cover;

        @include md-block() {
            height: 75%;
        }

        @include sm-block() {
            min-height: 75%;
        }

        @include xs-block() {
            min-height: 75%;
            max-height: 75%;
        }
    } //.market-product-slider__list

    &__item {
        display: flex;
        flex-basis: 50%;
        align-items: flex-end;

        &::after {
            content: '';
            display: block;
            width: 100%;
            height: 2px;
            margin: 0 3px;
            transition: backgroud-color 0.3s;
            background-color: rgba(232, 232, 232, 0.8);
        } //.market-product-slider__item

        &:hover {
            &::after {
                background-color: $color-accent;
            }

            .market-product-slider {
                &__image {
                    opacity: 1;
                }

                &__more {
                    display: flex;
                }
            }
        } //.market-product-slider__item:hover

        &:first-child {
            &::after {
                margin-left: 6px;
            }
        } //.market-product-slider__item::after

        &:last-child {
            &::after {
                margin-right: 6px;
            }
        } //.market-product-slider__item:last-child

        &:not(:hover) {
            &:first-child {
                .market-product-slider__image {
                    opacity: 1;
                }
            }
        } //.market-product-slider__item:not(:hover)
    } //.market-product-slider__item

    &__image {
        z-index: -1;
        opacity: 0;
        width: 100%;
        height: 100%;
        background-size: cover;
        @include centre;
    } //.market-product-slider__image

    &__more {
        position: absolute;
        top: 0;
        right: 2px;
        bottom: 0;
        left: 2px;
        z-index: -1;
        display: none;
        align-items: center;
        justify-content: center;
        background-color: rgba(0, 0, 0, 0.5);
        color: #fff;
        font-size: 15px;
        font-weight: 300;
        text-align: center;
    } //.market-product-slider__more
} //.market-product-slider

.product-editing {
    @include xs-block() {
        border-right: 0;
        border-left: 0;
    }

    &__header {
        display: flex;
        align-items: center;
        justify-content: space-between;
        width: 100%;
        padding: 15px;
    } //.product-editing__header

    &__title {
        font-size: 18px;
        font-weight: 500;
    } //.product-editing__title

    &__body {
        width: 100%;
        padding: 15px;

        @include xs-block() {
            padding: 15px 10px;
        }
    } //.product-editing__body

    &__footer {
        display: flex;
        align-items: center;
        justify-content: space-between;
