        padding: 15px;
        @include border(top);
        @include footerBtn;

        // .button-box {
        //     width: 100%;
        //     flex-shrink: 0;
        //     // margin-left: auto; Не раскоментировать

        //     // @include xs-block() {
        //     //     width: 100%;
        //     //     margin-left: 0;
        //     // }

        //     .btn {
        //         min-width: 140px;
        //     }
        // } //.product-editing__footer .button-box
    } //.product-editing__footer

    &__block {
        margin-bottom: 30px;
        // border-bottom: 1px solid $color-border--lighten;

        & + .product-editing__block {
            padding-top: 25px;
            @include border(top);
        } //.product-editing__block+.product-editing__block

        // &:first-child {
        //     padding-top: 0;
        // }

        // &:last-child {
        //     border-bottom: 0;
        // }
    } //.product-editing__block

    &__photo {
        padding: 15px 0 20px;
        border-top: 1px solid $color-border--lighten;
    } //.product-editing__photo
} //.product-editing

.swiper-button-disabled {
    visibility: hidden;
    opacity: 0;
} //.swiper-button-disabled
