%dropdownActive {
    top: 45px;
    // visibility: visible;
    // opacity: 1;
    // display: flex;
    pointer-events: auto;
    filter: none;
} //Active state dropdown__list

%dropdownActiveTop {
    top: auto;
    // visibility: visible;
    // opacity: 1;
    // display: flex;
    pointer-events: auto;
    filter: none;
} //Active state dropdown__list

.bb-dropdown {
    position: relative;

    &__list {
        position: absolute;
        top: 45px;
        left: 1px;
        z-index: z(dropdown);
        visibility: hidden;
        opacity: 0;
        flex-direction: column;
        min-width: 150px;
        max-width: 320px;
        padding: $gutter / 2 $gutter / 1.5;
        transition: all 100ms linear;
        border: 1px solid $color-border--lighten;
        background-color: #fff;
        text-align: left;
        pointer-events: none;
        filter: alpha(opacity=0);
        @include box;
        @include nosel;

        @include xs-block() {
            display: none;
        }

        &::before,
        &::after {
            content: '';
            position: absolute;
            left: 15px;
            width: 0;
            height: 0;
            border: solid transparent;
            pointer-events: none;
        }

        &::before {
            top: -12px;
            margin: 0 -1px;
            border-width: 6px;
            border-bottom-color: $color-border--lighten;
        }

        &::after {
            bottom: 100%;
            border-width: 5px;
            border-bottom-color: #fff;
        }

        &._transform {
            visibility: visible;
            opacity: 1;
            max-width: 100%;
            padding: 0;
            overflow: hidden;
            transform: translateY(100vh);
            transition: $trs;
            border: 0;
            background-color: transparent;
            box-shadow: none;
            text-align: center;
            pointer-events: auto;

            &::before,
            &::after {
                display: none;
            }

            .info-block {
                &__list {
                    margin-bottom: 8px;
                    overflow: hidden;
                    border-radius: $border-radius * 4;
                    background-color: #fff;
                    text-align: center;
                }

                &__item {
                    padding-left: 0;
                    border-top: 0;
                    border-bottom: 1px solid $color-border--lightest;

                    &::before {
                        display: none;
                    }

                    &:last-child {
                        border-bottom: 0;
                    }
                }

                &__link {
                    justify-content: center;
                    padding: 15px 10px;
                    background-color: transparent;
                    color: $color-info;

                    font-size: 19px;
                    text-transform: initial;

                    &.text-color--red {
                        color: #f67c93;
                    }
                }

                &__icon {
                    display: none;
                }
            }
        }

        &._transform_info {
            top: auto;
            bottom: 0;
            visibility: visible;
            opacity: 1;
            width: 100%;
            max-width: 100%;
            max-height: calc(100vh - 50px);
            padding: 15px 20px;
            overflow-y: auto;
            transform: translateY(100vh);
            transition: $trsMenu;
            border: 0;
            border-radius: $border-radius * 2 $border-radius * 2 0 0;
            background-color: #fff;
            pointer-events: auto;

            &::before,
            &::after {
                display: none;
            }

            .info-block {
                &__list {
                    margin-bottom: 8px;
                    overflow: hidden;
                }

                &__text {
                    font-size: 14px;
                }

                &__icon {
                    display: none;
                }
            }
        }

        &.is-visible {
            transform: translateY(0);
        }

        .fa-times {
            position: fixed;
            top: 0;
            right: 0;
            padding: 6px 9px;
        }

        .btn-icon--close {
            position: fixed;
            top: 0;
            right: 0;

            .icon {
                width: 14px;
                height: 14px;
            }
        }
    } //.bb-dropdown__list

    &__close {
        width: 100%;
        padding: 15px 10px;
        border: 0;
        border-radius: $border-radius * 4;
        outline: none;
        background-color: #fff;
        color: $color-info;

        font-size: 18px;
    } //.bb-dropdown__close

    &__overlay {
        @extend .overlay;
    } //.bb-dropdown__overlay

    &.is-active {
        box-shadow: none;

        &:hover {
            transform: none;
            box-shadow: none;
        }

        & > .bb-dropdown__list {
            visibility: visible;
            opacity: 1;
            display: flex;
            @extend %dropdownActive;
            @include min(xs) {
                z-index: z(modal) + 1;
            }
        }

        &.request-info {
            & > .bb-dropdown__list {
                @include min(sm) {
                    height: 45vh;
                }
            }
        }
    } //.bb-dropdown.is-active

    &--hover {
        position: relative;

        .bb-dropdown__list {
            @extend %dropdownActive;
        }

        &.hover {
            .bb-dropdown__list {
                top: 35px;
                visibility: visible;
                opacity: 1;
                display: flex;
            }
        }

        &[data-dropdown-position^='top'] {
            .bb-dropdown__list {
                @extend %dropdownActiveTop;
            }

            &.hover {
                .bb-dropdown__list {
                    bottom: 35px;
                }
            }
        }

        &[data-dropdown-position^='top-right'] {
            .bb-dropdown__list {
                @extend %dropdownActiveTop;
            }

            &.hover {
                .bb-dropdown__list {
                    bottom: 35px;
                }
            }
        }
    }

    &--top {
        // &:hover {
        //     .bb-dropdown {
        //         &__list {
        //             top: -45px;
        //         }
        //     }
        // }

        .bb-dropdown {
            &__list {
                top: -35px;

                &::before {
                    top: auto;
                    bottom: -5px;
                }
            }
        }
    } //.bb-dropdown--top

    &--left {
        .bb-dropdown {
            &__list {
                right: auto;
                left: 0;

                &::before {
                    right: auto;
                    left: 15px;
                }
            }
        }
    } //.bb-dropdown--left

    &--wide {
        .bb-dropdown {
            &__list {
                min-width: 480px;
                @include responseWidth();
            }
        }
    } //.bb-dropdown--small

    &--small {
        .bb-dropdown {
            &__list {
                min-width: 250px;

                p {
                    color: $color-text--light;
                    font-size: 13px;
                    line-height: 1.3;
                }
            } //.bb-dropdown--small .bb-dropdown__list
        } //.bb-dropdown--small .bb-dropdown
    } //.bb-dropdown--small

    &--btn {
        overflow: initial;
        line-height: 39px;

        &.is-active {
            z-index: 1002;
            box-shadow: none;

            // &:hover {
            //     transform: translateY(0);
            // }

            & > .bb-dropdown__list {
                top: 50px;
            }
        }

        .bb-dropdown {
            &__list {
                padding: 0;
                line-height: 28px;

                &::before,
                &::after {
                    display: none;
                }

                .info-block__list {
                    margin-bottom: 0;
                }
            }
        }
    } //.bb-dropdown--btn

    &[data-dropdown-position^='right'] {
        .bb-dropdown {
            &__list {
                right: 0;
                left: auto;

                &::before,
                &::after {
                    right: 14px;
                    left: auto;
                }
            }
        }
    }

    &[data-dropdown-position^='top'] {
        .bb-dropdown {
            &__list {
                top: auto;
                right: auto;
                bottom: 45px;

                &::before,
                &::after {
                    top: 100%;
                    transform: rotateX(180deg);
                }
            }
        }
    } //.bb-dropdown--hover

    &[data-dropdown-position^='top-right'] {
        .bb-dropdown {
            &__list {
                top: auto;
                right: 0;
                bottom: 45px;
                left: auto !important;

                &::before,
                &::after {
                    top: 100%;
                    right: 12px;
                    left: auto;
                    transform: rotateX(180deg);
                }
            }
        }
    }

    &--transform {
        @include max(sm) {
            // position: static;

            .bb-dropdown {
                &__list {
                    position: fixed;
                    top: auto;
                    right: 10px;
                    bottom: 10px;
                    left: 10px;
                    z-index: z(modal);
                    max-width: 100%;
                    padding: 0;
                    overflow: hidden;
                    transition: $trs;
                    border: 0;
                    background-color: transparent;
                    box-shadow: none;
                    text-align: center;
                    @include tY(100vh);

                    &::before,
                    &::after {
                        display: none;
                    }
                }
            }

            .info-block {
                &__list {
                    margin-bottom: 8px;
                    overflow: hidden;
                    border-radius: $border-radius * 4;
                    background-color: #fff;
                    text-align: center;
                }

                &__item {
                    border-top: 0;
                    border-bottom: 1px solid $color-border--lightest;

                    &:last-child {
                        border-bottom: 0;
                    }
                }

                &__link {
                    justify-content: center;
                    padding: 15px 10px;
                    background-color: transparent;
                    color: $color-info;

                    font-size: 18px;
                    text-transform: initial;

                    &.text-color--red {
                        color: #f67c93;
                    }
                }
            }

            &.is-active {
                .bb-dropdown {
                    &__list {
                        @include tY(0);
                    }

                    &__overlay {
                        display: block;
                    }
                }
            }

            &[data-dropdown-position^='right'] {
                .bb-dropdown {
                    &__list {
                        top: auto;
                        right: 10px;
                        bottom: 10px;
                        left: 10px;
                        padding: 0;
                    }
                }
            }
        }
    } //.bb-dropdown--transform
} //.bb-dropdown
