.registration {
    width: 100%;
    min-height: 100vh;
    @include cover;
    @include flexColumn;
    @include sectionOverlay($opa: 0.2);

    @include xs-block() {
        background-image: none !important;
    }

    & > .container {
        margin: auto;

        @include xs-block() {
            margin: 0;
            padding: 0;
        }
    }
} //.registration

.messenger-block {
    padding: 15px;
    background: $color-bg--lightest;
    box-shadow: none;

    &__header {
        position: relative;
        padding: 0;
        border: 0;
        @include flexCenter;

        &::before {
            display: none;
        }
    } //.messenger-block__header

    &__content {
        display: none;
        margin-top: 12px;
    } //.messenger-block__content

    &__icon {
        position: absolute;
        top: 1px;
        left: 0;
    } //.messenger-block__icon

    & + .messenger-block {
        margin-top: 15px;
    }

    &.is-open {
        .messenger-block {
            &__header {
                padding-bottom: 12px;
                @include border(bottom);
            }

            &__content {
                display: block;
            }
        }
    }

    .bb-checkbox {
        width: 100%;
        padding-left: 30px;

        &__title {
            font-weight: 500;
        }

        &__box {
            right: 0;
            left: auto;
            margin-right: 0;

            &::before {
                display: none;
            }
        }
    } //.messenger-block .bb-checkbox
} //.messenger-block
