$input-title-size: 14px;

%isInvalid {
    .bb-input,
    .bb-date {
        &__input {
            border: 1px solid rgba(247, 73, 105, 0.75);
        }

        &__error {
            display: block;
        }
    }

    .bb-text {
        &__field {
            border: 1px solid rgba(247, 73, 105, 0.75);
        }

        &__error {
            display: block;
        }
    }

    .bb-select {
        select {
            border-color: rgba(247, 73, 105, 0.75) !important;
        }
    }

    .bb-input-tip {
        display: block;
        border-color: rgba(247, 73, 105, 0.75);
        background: #fbebee;
        color: $color-danger;

        &__arrow-up {
            &::before {
                border-color: rgba(247, 73, 105, 0.75);
                background: #fbebee;
            }
        }
    }

    .select2-container--default .select2-selection--single {
        border-color: rgba(247, 73, 105, 0.75) !important;
    }
}

%isInfo {
    .bb-input,
    .bb-date {
        &__input {
            border-color: rgba(40, 131, 210, 0.75);
        }

        &__error {
            display: block;
        }
    }

    .bb-text {
        &__field {
            border: 1px solid rgba(247, 73, 105, 0.75);
        }

        &__error {
            display: block;
        }
    }

    .bb-select {
        select {
            border-color: rgba(40, 131, 210, 0.75) !important;
        }
    }

    .bb-input-tip {
        display: block;
        border-color: rgba(40, 131, 210, 0.75);
        background: #ebf5fd;
        color: $color-info;

        &__arrow-up {
            &::before {
                border-color: rgba(40, 131, 210, 0.75);
                background: #ebf5fd;
            }
        }
    }

    .select2-container--default .select2-selection--single {
        border-color: rgba(40, 131, 210, 0.75) !important;
    }
}

%isDisabled {
    .bb-input,
    .bb-select,
    .bb-text {
        &__title {
            color: $gray--lighten;
        }
    }

    .move-select__search {
        &::after {
            color: $gray--lighten;
        }
    }

    input {
        border-color: $gray--lighten;

        &:disabled {
            background-color: $gray--lighten !important;
        }
    }
}

%shadowField {
    @include bS;
    @include border(all, $color-border-very-light);

    &:hover {
        border-color: $color-border--lighten;
    }

    &:focus {
        border-color: $color-border--lighten;
        @include bS;
    }
}

@mixin inputTransform {
    @include xs-block() {
        &::before {
            right: 0;
            color: $color-icon;
        }

        &.is-focus,
        &.is-invalid,
        &.is-info {
            .bb-input-tip {
                background-color: transparent;
            }
        }

        &.is-invalid {
            .bb-input {
                &__input {
                    border-top: 0;
                    border-right: 0;
                    border-left: 0;
                }
            }

            .select2-container {
                .select2-selection--single {
                    height: 37px;
                    line-height: 37px;
                }
            } //.aplication-success .select2-container

            .select2-container--default {
                .select2-selection--single {
                    border-top: 0;
                    border-right: 0;
                    border-bottom: 1px solid transparent;
                    border-left: 0;
                    border-radius: 0;
                    box-shadow: none;
                }
            } //.aplication-success .select2-container--default
        }

        &.is-focus {
            &:not([data-type='selected']) {
                .bb-select {
                    &::before {
                        display: none;
                    }
                }
            }

            .bb-input,
            .bb-text {
                &__title {
                    top: -12px;
                    color: $gray;
                    font-size: 12px;
                }

                select,
                input,
                textarea {
                    border-bottom: 1px solid $color-border--darkest;
                }
            }
        }

        .bb-input-born {
            &__select {
                &.is-focus {
                    .bb-input-born__title {
                        top: -12px;
                        color: $gray;
                        font-size: 12px;
                    }
                }
            }
        }

        .bb-input,
        .bb-input-born,
        .bb-text {
            &__title {
                position: absolute;
                top: 10px;
                z-index: 1;
                margin-bottom: 0;
                box-shadow: none;
                color: $gray--darken;

                font-size: 16px;
                @include tr;
            }

            &__input {
                position: relative;
                z-index: 2;
                padding: 0 30px 0 0;
                border-top: 0;
                border-right: 0;
                border-bottom: 1px solid $color-border--light;
                border-left: 0;
                border-radius: 0;
                background-color: transparent;
            }
        }

        .bb-text {
            &__field {
                position: relative;
                z-index: 2;
                height: 37px;
                min-height: auto;
                padding: 7px 0 5px;
                overflow: hidden !important;
                border-top: 0;
                border-right: 0;
                border-left: 0;
                border-radius: 0;
                background-color: transparent;
                font-size: 16px;
                line-height: 1.4;

                &:focus {
                    border-bottom: 1px solid #000;
                    box-shadow: none;
                }
            }
        }

        .bb-select {
            &::before {
                right: 0;
                font-size: 22px;
            }
        }

        select,
        input,
        textarea {
            padding: 0 30px 0 0;
            border-top: 0;
            border-right: 0;
            border-bottom: 1px solid $color-border--light;
            border-left: 0;
            border-radius: 0;

            &:focus {
                border-bottom-color: $color-border--darkest;
                box-shadow: none;
            }
        }

        select {
            position: relative;
            z-index: 2;
            height: 40px;
            background-color: transparent;
            line-height: 1;
        }

        .bb-input-tip {
            padding: 0;
            border: 0;

            &__arrow-up,
            .fa-times {
                display: none;
            }
        }

        .select2-container {
            .select2-selection--single {
                height: 37px;
                line-height: 37px;

                .select2-selection__rendered {
                    padding-right: 25px;
                    padding-left: 0;
                    font-size: 16px;
                    line-height: 37px;

                    .custom-select__results {
                        span {
                            &:first-child {
                                flex-grow: 1;
                                @include text-overflow;
                            }

                            &:not(:first-child) {
                                font-size: 14px;
                            }

                            &:nth-child(2) {
                                margin-left: auto;
                            }

                            &:nth-child(3) {
                                margin-right: 5px;
                            }
                        }
                    }
                }
            }
        } //.aplication-success .select2-container

        .select2-container--default {
            &.select2-container--open,
            &.select2-container--open.select2-container--below {
                .select2-selection--single {
                    border-color: transparent;
                    border-bottom-color: $color-accent;

                    &:hover {
                        border-color: transparent;
                        border-bottom-color: $color-accent;
                    }
                }
            }

            .select2-selection--single {
                border: 1px solid transparent;
                border-top: 0;
                border-right: 0;
                border-left: 0;
                border-radius: 0;
                box-shadow: none;
            }
        } //.aplication-success .select2-container--default
    }
}

.check-validate {
    &.is-invalid {
        position: relative;
        border-color: rgba(247, 73, 105, 0.75);

        & > .bb-input-tip {
            display: block;
            border-color: rgba(247, 73, 105, 0.75);
            background: #fbebee;
            color: $color-danger;

            .bb-input-tip__arrow-up {
                &::before {
                    border-color: rgba(247, 73, 105, 0.75);
                    background: #fbebee;
                }
            }
        }
    } //.check-validate .is-invalid

    &.is-info {
        position: relative;
        border-color: rgba(40, 131, 210, 0.75);

        & > .bb-input-tip {
            display: block;
            border-color: rgba(40, 131, 210, 0.75);
            background: #ebf5fd;
            color: $color-info;

            .bb-input-tip__arrow-up {
                &::before {
                    border-color: rgba(40, 131, 210, 0.75);
                    background: #ebf5fd;
                }
            }
        }
    } //.check-validate .is-info
} //.check-validate

.bb-input {
    position: relative;
    display: inline-block;

    @include xs-block() {
        width: 100%;
    }

    &::before {
        right: 40px;
        z-index: 1;
        color: $color-icon--light;
        font-size: 12px;
        line-height: 1;
        @include fa('light');
        @include centre('y');
    } //.bb-input::beofer

    &::after {
        position: absolute;
        top: 8px;
    } //.bb-input::after

    &__icon {
        right: 0;
        padding: 5px 10px;
        color: $color-icon--light;
        font-size: 13px;
        line-height: 1;
        cursor: pointer;
        @include centre('y');

        &.fa-eye,
        &.fa-eye-slash {
            padding-right: 5px;
        }

        &--check {
            display: none;

            .icon {
                fill: $color-success;
            }
        } //.bb-input__icon--check
    } //.bb-input__icon

    &__wrapper {
        @include flex;
    } //.bb-input__wrapper

    &__wrap {
        position: relative;

        &_inline {
            display: inline-block;

            & + & {
                margin-left: 30px;
                @include xs-block() {
                    margin-left: 0;
                }
            }
        }

        .bb-input + .bb-input {
            margin-left: $gutter;
        }
    } //.bb-input__wrap

    &__row {
        @include flexCenter;

        @include xs-block() {
            flex-wrap: wrap;
        }

        .bb-input {
            width: calc(50% - 10px);

            @include xs-block() {
                width: 100%;
            }

            & + .bb-input {
                margin-left: 20px;

                @include xs-block() {
                    margin-left: 0;
                }
            } //.bb-input__row .bb-input
        } //.bb-input__row .bb-input

        &--street {
            .bb-input {
                @include xs-block() {
                    & + .bb-input {
                        margin-left: 20px;
                    }
                }

                &:first-child {
                    width: calc(70% - 10px);
                } //.bb-input__row--street:first-child

                &:last-child {
                    width: calc(30% - 10px);
                } //.bb-input__row--street:last-child
            }
        } //.bb-input__row--street
    } //.bb-input__row

    &__title {
        margin-bottom: 5px;
        color: $color-text;
        font-size: $input-title-size;
        white-space: nowrap;
        @include nosel;

        span {
            display: inline-block;
            margin-left: 5px;
        } //.bb-input__title span
    } //.bb-input__title

    &__input {
        @extend %styleInput;
    } //.bb-input__input

    &__box {
        position: relative;
        display: inline-block;
        margin-right: 30px;
        @include xs-block() {
            margin-right: 0;
        }
        @include i5-block() {
            span.js-remove-period {
                position: absolute;
                top: 46%;
                right: -30px;
            }
        }
    } //.bb-input__box

    &__btn {
        border-radius: 0 $border-radius $border-radius 0;
        box-shadow: none;

        @include xs-block() {
            flex-shrink: 0;
            padding: 0 15px;
            font-size: 14px;
        }

        &:hover,
        &:focus {
            transform: none;
            box-shadow: none;
        }
    } //.bb-input__btn

    &__remove {
        position: relative;
        top: 2px;
        width: 16px;
        height: 16px;
        margin-left: 15px;
        fill: $gray--lighten;
        cursor: pointer;
        @include tr;
        @include xs-block() {
            width: 15px;
            height: 15px;
            margin-right: 5px;
            margin-left: 0;
        }

        &:hover,
        &:focus {
            fill: $gray;
        }
    } //.bb-input__remove

    &__devider {
        margin: 0 5px;
        @include flexCenter;
    } //.bb-input__devider

    &__error {
        position: absolute;
        bottom: -15px;
        left: 0;
        display: none;
        width: 100%;
        color: $color-danger;
        font-size: 13px;
        font-weight: 300;
        line-height: 1;
        @include text-overflow;
    } //.bb-input__error

    &__message {
        position: absolute;
        top: 100%;
        right: 0;
        left: 0;
        z-index: z(success);
        padding: 10px 15px;
        border-radius: 0 0 $border-radius $border-radius;
        background-color: #fff;
        box-shadow: $bxs-box;
        font-size: 13px;
        line-height: 1.3;
    } //.bb-input__message

    & > .icon {
        right: 9px;
        padding: 0;
        @include centre('y');

        &:not(.icon-eye):not(.icon-eye--blocked):not(.icon-count-text):not(.icon-close--bold) {
            width: 12px;
            height: 12px;
        }

        &.icon-rub {
            width: 10px;
            height: 10px;
        }

        &.icon-count-text {
            width: 16px;
            height: 16px;
        }
    } //.bb-input .icon

    &.is-invalid {
        @extend %isInvalid;
    } //.bb-input.is-invalid

    &.is-error {
        @extend %isInvalid;

        &::after {
            content: '\f12a';
            position: absolute;
            right: 13px;
            color: $color-danger;
            @include fa('solid');
        }
    } //.bb-input.is-error

    &.is-info {
        @extend %isInfo;
    } //.bb-input.is-info

    &.is-confirm {
        .bb-input {
            &__icon {
                &--check {
                    display: block;
                }
            }
        }
    } //.bb-input.is-complite

    &.is-disabled {
        @extend %isDisabled;
    } //.bb-input.is-disabled

    &.w100 {
        display: flex;
        width: 100%;

        .bb-input {
            &__input {
                width: 100%;
            } //.bb-input.w100 .bb-input__input
        }
    } //.bb-input.w100

    &--title {
        @include flexColumn;

        &::before {
            top: auto;
            right: 10px;
            bottom: 12px;
            transform: translate(0);
        }

        &::after {
            top: 34px;
        }

        .bb-input {
            &__icon {
                top: auto;
                bottom: 8px;
                transform: translate(0);
            } //.bb-input--title .bb-input__icon

            &__info {
                position: absolute;
                right: 5px;
                bottom: 10px;
                left: auto;
                z-index: z(fixed);

                .bb-dropdown__list {
                    min-width: 180px;
                    max-width: 180px;
                    font-size: 14px;
                }
            } //.bb-input--title .bb-input__info
        } //.bb-input--title

        .icon {
            top: auto;
            bottom: 8px;

            &.icon-rub {
                bottom: 10px;
            }

            &.icon-count-text {
                bottom: 3px;
                width: 16px;
                height: 16px;
            }
        }

        .counter-text {
            bottom: 5px;
        } //.bb-input--title .counter-text

        & + .bb-input--title {
            margin-top: 20px;
        } //.bb-input--title + .bb-input--title
    } //.bb-input--title

    &--icon {
        .bb-input {
            &__input {
                padding-right: 32px;
            } //.bb-input--icon .bb-input__input
        }
    } //.bb-input--icon

    &--user {
        &::before {
            content: '\f007';
            right: 12.5px;
        }
    } //.bb-input--user

    &--price {
        width: 110px;

        @include i5-block() {
            width: 100px;
        }

        &::before {
            content: '\f158';
            right: 10px;
        }

        .bb-input {
            &__input {
                width: 100%;
                padding-right: 25px;
            }
        }
    } //.bb-input--price

    &--time {
        &::before {
            content: '\f017';
            right: 10px;
        }

        .bb-input {
            &__input {
                padding-right: 30px;
            }
        }
    } //.bb-input--time

    &--email {
        &::before {
            content: '\f0e0';
            right: 11.5px;
            font-size: 16px;
        }
    } //.bb-input--email

    &--phone {
        &::before {
            content: '\f3cf';
            top: auto;
            right: 13.5px;
            bottom: 11px;
            transform: translate(0);
            font-size: 18px;
            @include fa('solid');
        }
    } //.bb-input--phone

    &--born {
        align-items: flex-start;
        width: 100%;
        max-width: 320px;
        @include flexColumn;

        @include xs-block() {
            max-width: 100%;
        }
    } //.bb-input--born

    &--confirm {
        .bb-input {
            &__error {
                white-space: nowrap;
            }

            &__input {
                padding: 5px;
                font-size: 16px;
            }
        }
    } //.bb-input--confirm

    &--inline {
        border: 0;
        @include flexCenter;

        &.is-invalid {
            .bb-input {
                &__input {
                    border: 0;
                    border-radius: 0;
                }
            }
        }

        .bb-input {
            &__title {
                position: static;
                margin-top: 9px;
                margin-right: $gutter / 2;

                font-size: 16px;
            }

            &__input {
                width: 100%;
            }
        } //.bb-input--inline .bb-input

        .bb-input-tip {
            right: 0;
            left: auto;

            &__arrow-up {
                right: 6px;
                left: auto;
            } //.bb-input--inline .bb-input-tip__arrow-up
        } //.bb-input--inline .bb-input-tip

        .bb-select {
            width: 100%;

            select {
                border: 0;
                text-align: right;
            }
        } //.bb-input--inline .bb-select

        .select2-container--default .select2-selection--single {
            border: 0;
            text-align: right;
        } //.bb-input--inline .select2-container--default .select2-selection--single

        .select2-container--default.select2-container--open.select2-container--below
            .select2-selection--single {
            box-shadow: none;
        } //.bb-input--inline .select2-container--default.select2-container--open.select2-container--below .select2-selection--single
    } //.bb-input--inline

    &--btn {
        width: 100%;
        @include flexCenter;

        .bb-input {
            &__input {
                width: 100%;
                padding-right: 10px;
                border-right: 0;
                border-radius: $border-radius 0 0 $border-radius;
                font-size: 14px;
            } //.bb-input--btn .bb-input__input
        } //.bb-input--btn .bb-input
    } //.bb-input--btn

    &--outline {
        .bb-input {
            &__input {
                border: 0;
            }
        }
    } //.bb-input--outline

    // &--select {
    //     .bb-input-tip {
    //         margin-top: 3px;
    //     }

    //     .select2-search__field {
    //         width: 100% !important;
    //         height: 33px;
    //     }

    //     &-icon {
    //         .select2-container--default
    //             .select2-results__option[aria-selected='true']::after {
    //             display: none;
    //         }
    //     }
    // } //.bb-input--select

    // &--select-round {
    //     width: 100%;

    //     .select2-container--default .select2-selection--single {
    //         height: 35px;
    //         border-radius: 20px;

    //         .select2-selection__rendered {
    //             // margin-top: -3px;
    //             height: 35px;
    //             padding: 0 28px 0 15px;
    //         } //.bb-input--select-round .select2-container--default .select2-selection--single .select2-selection__rendered
    //     } //.bb-input--select-round .select2-container--default .select2-selection--single

    //     .select2-container--default
    //         .select2-selection--single
    //         .select2-selection__arrow
    //         b {
    //         // top: -1px;
    //         right: 12px;
    //     } //.bb-input--select-round .select2-container--default .select2-selection--single .select2-selection__arrow b
    // } //.bb-input--select-round

    &--transform {
        @include inputTransform;

        @include xs-block() {
            margin-bottom: 30px;

            input[type='date']::-webkit-datetime-edit {
                color: transparent;
            }
        }

        .bb-input {
            &__icon {
                @include xs-block() {
                    right: -9px;
                }
            } //.bb-input--transform .bb-input__icon

            &__input {
                @include xs-block() {
                    width: 100%;
                }
            } //.bb-input--transform .bb-input__input
        } //.bb-input--transform

        .icon {
            @include xs-block() {
                right: 0;
            }
        } //.bb-input--transform .icon
    } //.bb-input--transform

    &--fake {
        .bb-input {
            &__title {
                @include xs-block() {
                    top: -12px;
                    font-size: 12px;
                }
            }

            &__input {
                @include flexCenter;
            }
        }
    } //.bb-input--fake

    &--multiselect {
        .select2-container--default
            .select2-selection--multiple
            .select2-selection__rendered
            .select2-search--inline {
            width: 100%;
        }

        .select2-container--default {
            &.select2-container--focus .select2-selection--multiple {
                box-shadow: none;
            }

            .select2-selection--multiple {
                @include sm-block() {
                    padding: 0 10px;
                    border: 1px solid $color-border--light;
                    border-radius: $border-radius;
                }

                .select2-selection__rendered {
                    @include sm-block() {
                        padding: 0;

                        li {
                            position: relative;
                            width: 48.5%;
                            padding-right: 15px;
                            @include text-overflow;
                        }
                    }
                }

                .select2-selection__choice {
                    position: relative;
                    margin: 3px 4px 3px 0;
                    padding: 4px 30px 4px 10px;
                    border: 0;
                    background-color: $gray--lighten;
                    font-size: 14px;
                    font-weight: 500;

                    &__remove {
                        position: absolute;
                        right: 2px;
                        justify-content: center;
                        width: 18px;
                        height: 18px;
                        border-radius: 50%;
                        background-color: $gray--light;
                        color: #fff;
                        font-size: 18px;
                        font-weight: 100;
                        @include flexCenter;

                        &:hover {
                            background-color: darken($gray--light, 10%);
                        }
                    } //.bb-input--multiselect .select2-container--default .select2-selection--multiple .select2-selection__choice__remove
                }
            }
        }

        .select2-container .select2-search--inline {
            width: 100% !important;
        }

        .select2-container--open .select2-dropdown--above {
            border-bottom: 1px solid $color-border--light;
        }
    } //.bb-input--multiselect

    &--phone-code {
        position: relative;
        transition: border 0.3s;
        border: 1px solid $color-border--lighten;
        border-radius: $border-radius;
        @include flexCenter;

        &:hover {
            transition: border 0.3s;
            border: 1px solid $color-border--light;

            .bb-input--phone {
                border-left: 1px solid $color-border--light;
            }
        }

        &.is-focus {
            transition: all 0.3s;
            border: 1px solid $color-border--light;
            box-shadow: $bxs-box;

            .bb-input--phone {
                border-left: 1px solid $color-border--light;
            }
        }

        &.is-invalid {
            border: 1px solid $color-danger;
            border-radius: $border-radius;

            & .bb-input--phone {
                border: 0;
                border-left: 1px solid $color-danger;
            }
        }

        .bb-input {
            position: static !important;
            width: 100%;
            margin: 0 !important;

            &--select {
                .select2-container {
                    min-width: auto;

                    .select2-selection--single .select2-selection__rendered {
                        padding: 0 20px 0 15px;
                    }

                    &--open {
                        .select2-dropdown {
                            &--below {
                                box-shadow: none;
                            }
                        }
                    }

                    &--default {
                        .select2-selection--single {
                            border-top-right-radius: 0;
                            border-bottom-right-radius: 0;
                        }

                        &.select2-container--open {
                            .select2-selection--single,
                            .select2-selection--multiple {
                                box-shadow: none;
                            }
                        }
                    }
                }

                .select2-container--default .select2-selection--multiple,
                .select2-container--default .select2-selection--single {
                    border: 0;
                }
                // Отмена :hover при клике

                .select2-container--default.select2-container--open.select2-container--below
                    .select2-selection--multiple:hover,
                .select2-container--default.select2-container--open.select2-container--below
                    .select2-selection--single:hover {
                    border: 0;
                }

                .custom-select__results {
                    justify-content: center;
                    padding-top: 1px;
                }
            }

            &--code {
                width: 100px !important;
            }

            &--phone {
                transition: border 0.3s;
                border-left: 1px solid $color-border--lighten;

                .bb-input__input {
                    border: 0;

                    &:focus {
                        box-shadow: none;
                    }
                }
            }
        }

        .bb-select {
            &::before {
                z-index: 100;
            }

            select {
                position: relative;
                z-index: 200;
                width: 80px;
                height: 40px;
                padding-right: 79px;
                padding-left: 0;
                border: 0;
            }
        }

        .bb-input--select-value {
            position: absolute;
            top: 0;
            right: 0;
            display: flex;
            align-items: center;
            justify-content: center;
            width: 80px;
            height: 40px;
            padding-right: 15px;
            background: #fff;
            line-height: 1.2;
        }

        .select2-dropdown {
            top: 2px;
            left: -1px;
            width: 100% !important;
        }
    } //.bb-input--phone-code

    &--color {
        .bb-input {
            &__input {
                width: 100%;
                min-width: 50px;
                padding: 9px 10px;
            } //.bb-input--color .bb-input
        }
    } //.bb-input--color

    &--shadow {
        &:hover {
            .select2-container--default .select2-selection--multiple,
            .select2-container--default .select2-selection--single {
                border-color: $color-border-very-light;
            }
        }

        .bb-input {
            &__input {
                @extend %shadowField;
            }
        }

        .bb-select {
            select {
                @extend %shadowField;
            }
        }

        .select2-container--default .select2-selection--multiple,
        .select2-container--default .select2-selection--single {
            @include bS;
            @include border(all, $color-border-very-light);
        }

        .select2-dropdown {
            @include bS;
            @include border(left, $color-border-very-light);
            @include border(right, $color-border-very-light);
            @include border(bottom, $color-border-very-light);
        }
    } //.bb-input--shadow

    &--date-range {
        display: flex;
        align-items: center;
        justify-content: space-between;
        overflow: hidden;

        .bb-input {
            &__date {
                width: 100%;
                overflow: hidden;

                input {
                    width: 100%;
                    max-width: 100%;
                    padding: 5px 10px;
                    white-space: nowrap;
                } //.bb-input--date-range .bb-input__date input
            } //.bb-input--date-range .bb-input__date

            &__separator {
                margin: 0 5px;

                .icon {
                    position: static;
                    top: auto;
                    right: auto;
                    transform: translateY(0);
                }
            } //.bb-input--date-range .bb-input__seporator
        }
    } //.bb-input--date-range

    &--disabled {
        .bb-input {
            &__input {
                border-color: $gray--lighten;
                background-color: $gray--lighten;
                color: $gray--light;
                line-height: 1.4;
            }
        }
    } //.bb-input--disabled

    .icon-selected {
        position: absolute;
        right: 7px;
        bottom: 8px;
        width: 13px;
        height: 13px;
    } //.bb-input .icon-selected
} //.bb-input

.bb-input-row {
    @include flexCenter;

    .bb-input {
        width: calc(50% - 10px);

        & + .bb-input {
            margin-left: 20px;

            @include xs-block() {
                margin-left: 10;
            }
        } //.bb-input-row .bb-input
    } //.bb-input-row .bb-input
} //.bb-input-row

.bb-input-tip {
    position: absolute;
    top: 98%;
    left: 0;
    z-index: z(fixed) - 3;
    display: none;
    width: auto;
    margin-top: 2px;
    padding: 6px 20px 6px 6px;
    border: 1px solid #000;
    border-radius: 2px;
    background: #fff;
    color: #000;
    font-size: 12px;
    cursor: pointer;

    &:hover {
        .icon {
            fill: $color-danger;
        }
    }

    &__arrow-up {
        position: absolute;
        top: -14px;
        left: 13px;
        width: 14px;
        height: 14px;
        overflow: hidden;

        &::before {
            content: '';
            position: absolute;
            top: 11px;
            left: 2px;
            width: 10px;
            height: 10px;

            transform: rotate(45deg);
            border: 1px solid #000;
            background: #fff;
        }
    } //.bb-input-tip__arrow-up

    .icon {
        position: absolute;
        top: 7px;
        right: 5px;
        width: 8px;
        height: 8px;
        fill: $color-danger--light;
        cursor: pointer;
    } //.bb-input-tip .icon

    .fa-times {
        position: absolute;
        top: 5px;
        right: 5px;
        font-size: 10px;
        cursor: pointer;
    } //.bb-input-tip .fa-times

    &.no-close {
        .fa-times,
        .icon {
            display: none;
        }
    } //.bb-input-tip.no-close
} //.bb-input-tip

.bb-input-born {
    width: 100%;
    max-width: 320px;
    @include flexCenter;

    @include xs-block() {
        max-width: 100%;
    }

    &__select {
        width: 100%;
        max-width: 80px;

        & + & {
            margin-left: 20px;
        }

        &--year {
            display: none;
        } //.bb-input-born__select--year

        &--month {
            max-width: 110px;

            @include xs-block() {
                flex: 1;
                max-width: 100%;
            }
        } //.bb-input-born__select--month

        &.is-focus {
            .bb-select {
                &::before {
                    display: none;
                }
            }
        }
    } //.bb-input-born__select

    &__btn {
        margin-left: 20px;
        padding: 6px 0;
        font-size: 14px;
        white-space: nowrap;

        @include xs-block() {
            padding: 11px 0;
            font-size: 14px;
        }

        &:hover {
            text-decoration: none;
        }
    } //.bb-input-born__btn

    &--transform {
        @include inputTransform;

        .bb-input-born {
            &__title {
            }
        }
    } //.bb-input-born--transform

    .select2-container {
        min-width: 80px;
    } //.bb-input-born .select2-container
} //.bb-input-born

.bb-date {
    position: relative;
    display: flex;

    &__wrap {
        display: flex;
        flex-wrap: wrap;
        border-radius: $border-radius;
    } //.bb-date__wrap

    &__title {
        margin-top: 0;
        font-size: $input-title-size;
    } //.bb-date__title

    &__input {
        width: 100%;
        padding-right: 30px;
        @extend %styleInput;

        ._visually-hidden {
            top: auto;
            bottom: 0;
        }
    } //.bb-date__input

    &__icon {
        &--arrow {
            width: 10px;
            height: 10px;
            margin-left: 7px;
            @include rotate(90);
        } //.bb-date__icon--arrow
    } //.bb-date__icon

    &__error {
        position: absolute;
        bottom: -15px;
        left: 0;
        display: none;
        width: 100%;
        color: $color-danger;
        font-size: 13px;
        font-weight: 300;
        line-height: 1;
    } //.bb-date__invalid

    &__btn {
        flex-shrink: 0;
        justify-content: center;
        padding: 0 13px;
        border: 0;
        outline: none;
        background-color: transparent;
        font-size: 15px;
        line-height: 1;
        text-align: center;
        cursor: pointer;
        @include flexCenter;

        &:hover {
            background-color: $color-gray--hover;
        }

        &--prev,
        &--next {
            width: $base-btn-height;
            height: $base-btn-height;
            padding: 0;
            border: 1px solid $input-color-border;

            @include xs-block() {
                width: $base-btn-height--xs;
                height: $base-btn-height--xs;
            }
        }

        &--prev {
            margin-right: -1px;
            border-radius: $border-radius 0 0 $border-radius;

            &:hover {
                border-right: 1px solid $input-color-border--hover;
                border-color: $input-color-border--hover;

                & + .bb-date__btn--today {
                    border-left-color: transparent;
                }
            }

            .icon {
                margin: auto;
                @include rotate(180);
            }
        } //.bb-date__btn--prev

        &--next {
            margin-left: -1px;
            border-radius: 0 $border-radius $border-radius 0;

            &:hover {
                border-color: $input-color-border--hover;
            }
        } //.bb-date__btn--next

        &--today {
            z-index: 2;
            border: 1px solid $input-color-border;

            &:hover {
                border-color: $input-color-border--hover;
            }
        } //.bb-date__btn--today

        i {
            margin: auto;
            font-size: 22px;
        } //.bb-date__btn i

        .icon {
            width: 10px;
            height: 10px;
            fill: $color-icon--dark;
        } //.bb-date__btn .icon

        &.is-disabled {
            border-color: $gray--lightest !important;
            background-color: $gray--lightest !important;
            cursor: not-allowed !important;
            pointer-events: none;
            touch-action: none;

            .icon {
                fill: $color-icon;
            }
        } //.bb-date__btn.is-disabled
    } //.bb-date__btn

    &.is-invalid {
        @extend %isInvalid;
    } //.date-date.is-invalid

    &.is-info {
        @extend %isInfo;
    } //.bb-date.is-info

    &--title {
        @include flexColumn;

        .bb-input__icon {
            top: auto;
            bottom: 8px;
            transform: translate(0);
        } //.bb-date--title .bb-input__icon
    } //.bb-date--title

    &--flex {
        display: inline-flex;
        align-items: center;
    } //.bb-date--flex

    &--expanded {
        .bb-date {
            &__input {
                position: absolute;
                right: 0;
                bottom: 0;
                left: 0;
                z-index: -1;
                width: 100%;
            } //.bb-date--expanded .bb-date__input
        }
    } //.bb-date--expanded

    &--controls {
        display: flex;
        width: 100%;
        max-width: 415px;
        margin: auto;
        overflow: hidden;
        border-radius: $border-radius;
        background-color: #fff;

        .bb-date {
            &__field {
                width: 250px;
                height: $base-btn-height;
                margin-left: -1px;
                padding: 0 16px;
                border: 1px solid $input-color-border;
                font-size: 15px;
                line-height: $base-btn-height - 1;
                text-align: center;
                white-space: nowrap;

                @include max-screen(1200) {
                    width: 100%;
                }

                @include md-block() {
                    padding: 0 8px;
                }

                @include xs-block() {
                    width: 100%;
                    height: $base-btn-height--xs;
                }

                &:hover {
                    border-color: $input-color-border--hover;
                    background-color: $color-gray--hover;
                }
            } //.bb-date--controls .bb-date__field

            &__btn {
                line-height: 1;

                &--prev {
                    @include sm-block() {
                        border-right-width: 0;
                    }

                    @include i5-block() {
                        border-right: 0;
                    }
                } //.bb-date--controls .bb-date__btn--prev

                &--today {
                    @include sm-block() {
                        display: none;
                    }

                    @include xs-block() {
                        display: block;
                        padding: 8px;
                    }

                    @include i5-block() {
                        display: none;
                    }
                } //.bb-date--controls .bb-date__btn--today
            } //.bb-date--controls .bb-date__btn

            &__input {
                left: 47px;
                padding: 0;
                border-top: 0;
                border-bottom: 0;
                border-radius: 0;
                text-align: center;
                cursor: pointer;

                @include xs-block() {
                    width: auto;
                }

                @include i7-block() {
                    // width: 217px;
                }

                @include i5-block() {
                    // width: 203px;
                }

                &:hover {
                    border-color: $color-border--lightest;
                    background-color: $color-bg--lightest;
                }

                &:focus {
                    box-shadow: none;
                }
            } //.bb-date--controls .bb-date__input
        } //.bb-date--controls .bb-date
    } //.bb-date--controls

    &--small {
        @include xs-block() {
            .bb-date {
                &__field {
                    border-radius: $border-radius;
                }

                &__input {
                    @include xs-block() {
                        left: auto;
                        width: auto;
                    }
                }

                &__btn {
                    &--prev,
                    &--next {
                        @include xs-block() {
                            display: none;
                        }
                    } //.date-date--small .bb-date__btn--prev

                    &--today {
                        @include xs-block() {
                            display: none;
                        }
                    } //.date-date--small .bb-date__btn--today
                }
            }
        }
    } //.bb-date--small

    &--shadow {
        @include bS;

        .bb-date {
            &__btn {
                &--prev,
                &--next,
                &--today {
                    @include border(all, $color-border-very-light);
                }

                &--prev {
                    @include border(right);
                } //.bb-date--shadow .bb-date__btn--prev

                &--next {
                    @include border(left);
                } //.bb-date--shadow .bb-date__btn--next

                &--today {
                    @include border(left);
                    @include border(right);
                } //.bb-date--shadow .bb-date__btn--today
            } //.bb-date--shadow .bb-date__btn

            &__field {
                border-left: 0;
                @include border(top, $color-border-very-light);
                @include border(bottom, $color-border-very-light);
                // @include border(left);
                @include border(right);

                &:hover {
                    border-color: $color-border-very-light;
                }
            } //.bb-date--shadow .bb-date__field
        } //.bb-date--shadow .bb-date
    } //.bb-date--shadow
} //.bb-date

.bb-text {
    position: relative;

    &__title {
        display: block;
        @extend .bb-input__title;
    } //.bb-text__title

    &__field {
        min-height: 120px;
        padding: 10px 10px 0;
        border-radius: $border-radius $border-radius 0 0;
    } //.bb-text__field

    &__placeholder {
        position: absolute;
        right: 0;
        bottom: -45px;
        left: 0;
        padding: 10px 15px;
        border: 1px solid $gray--lighten;
        border-top: 0;
        border-radius: 0 0 $border-radius $border-radius;
        color: $gray--light;
        font-size: 13px;
        line-height: 1.2;
    } //.bb-text__placeholder

    &__error {
        bottom: -10px;
        @extend .bb-input__error;
    } //.bb-text__error

    &.is-invalid {
        .bb-text {
            &__field {
                border: 1px solid $color-danger;
            }

            &__error {
                display: block;
            }
        }

        .select2-container--default .select2-selection--single {
            border-color: $color-danger;
        }

        .bb-input-tip {
            top: 96%;
            display: block;
            margin-top: 0;
            border-color: rgba(247, 73, 105, 0.75);
            background: #fbebee;
            color: $color-danger;

            &__arrow-up {
                &::before {
                    border-color: rgba(247, 73, 105, 0.75);
                    background: #fbebee;
                }
            }
        }
    } //.bb-text.is-invalid

    &--light {
        .bb-text {
            &__field {
                height: 35px;
                min-height: auto;
                padding: 5px 0;
                line-height: 1.4;
            }

            &.is-invalid {
                .bb-text {
                    &__field {
                        border-top: 0;
                        border-right: 0;
                        border-left: 0;
                    }
                }
            }
        }
    } //.bb-text--light

    .counter-text {
        bottom: 12px;
    } //.bb-text .counter-text
} //.bb-text

