    } // .bb-input-custom__title

    &__arrow {
        position: absolute;
        top: 10px;
        right: 8px;
        transform: rotateZ(90deg);

        .arrow-select {
            width: 10px;
            height: 10px;
            fill: $gray;
        } // .bb-input-custom__arrow .arrow-select
    } // .bb-input-custom__arrow

    &__list {
        position: absolute;
        top: 100%;
        left: 0;
        z-index: 2;
        display: none;
        width: 100%;
        border: 1px solid $input-color-border;
        border-top: 0;
        border-radius: 0 0 $border-radius $border-radius;
        background-color: #fff;

        &--full-border {
            border-top: 1px solid $input-color-border;
            border-radius: $border-radius $border-radius 0 0;
        }
    } // .bb-input-custom__list

    &__item {
        display: flex;
        align-items: center;
        justify-content: flex-start;
        height: 39px;
        padding: 0 5px 0 10px;
        border-bottom: 1px solid $input-color-border;
        cursor: pointer;

        &:hover {
            background-color: $gray--lightest;
        } // .bb-input-custom__item:hover

        &:last-child {
            border: 0;
        } // .bb-input-custom__item:last-child

        &.in-available {
            .bb-input-custom__stock {
                color: $color-success;
            } // .bb-input-custom__item.in-available .bb-input-custom__stock
        } // .bb-input-custom__item.in-available

        &.not-available {
            .bb-input-custom__stock {
                color: $color-muted;
            } // .bb-input-custom__item.not-available .bb-input-custom__stock
        } // .bb-input-custom__item.not-available

        &.is-ending {
            .bb-input-custom__stock {
                color: $color-danger;
            } // .bb-input-custom__item.is-ending .bb-input-custom__stock
        } // .bb-input-custom__item.is-ending

        .bb-input-custom__title {
            font-size: 14px;
            line-height: 13px;
        } // .bb-input-custom__item .bb-input-custom__title

        .bb-input-custom__color {
            margin-right: 10px;
        } // .bb-input-custom__item .bb-input-custom__color

        span {
            font-size: 13px;

            & + span {
                margin-left: 3px;
            } // .bb-input-custom__item span + span
        } // .bb-input-custom__item span
    } // .bb-input-custom__item

    &__color {
        flex-shrink: 0;
        width: 12px;
        height: 12px;
        margin-right: 5px;
        margin-left: 5px;
        border-radius: 50%;
        background-color: #000;
    } // .bb-input-custom__color

    &__discount {
        margin: 0 3px 0 auto;
        color: $color-danger;
    } // .bb-input-custom__discount

    &__item-inner {
        display: flex;
        flex-direction: column;
    } // .bb-input-custom__item-inner

    &__stock {
        font-size: 12px;
    } // .bb-input-custom__stock

    &.is-open {
        .bb-input-custom {
            &__value {
                border-radius: $border-radius $border-radius 0 0;
            } // .bb-input-custom.is-open .bb-input-custom__value

            &__list {
                display: block;
            } // .bb-input-custom.is-open .bb-input-custom__list

            &__arrow {
                transform: rotateZ(270deg);
            } // .bb-input-custom.is-open .bb-input-custom__arrow
        } // .bb-input-custom.is-open .bb-input-custom
    } // .bb-input-custom.is-open
} // .bb-input-custom

.c-input-code {
    justify-content: center;
    @include flexCenter;

    &__box {
        position: relative;
        overflow: hidden;
        border-radius: 10px;

        &::after {
            content: '';
            position: absolute;
            bottom: 0;
            display: none;
            width: 100%;
            height: 2px;
            background-color: $color-accent;

            @include xs-block() {
                position: relative;
            }
        }

        & + .c-input-code__box {
            margin-left: 12px;
        }

        &.has-focus {
            &::after {
                display: block;
            }
        }
    } //.c-input-code__box

    &__field {
        width: 44px;
        height: 56px;
        padding: 8px;
        transition: none;
        border: 0;
        border-radius: $border-radius * 3;
        background-color: $color-bg--lightest;
        box-shadow: none;
        font-size: 28px;
        font-weight: 500;
        line-height: 1.21;
        text-align: center;

        &:focus {
            font-size: 28px;
        }
    } //.c-input-code__field
} //.c-input-code
