.breadcrumbs {
    padding: 12px 0 14px;

    @include xs-block() {
        display: none;
    }

    &__wrap {
        @include xs-block() {
            background-color: #fff;
        }

        .breadcrumbs {
            padding-top: 0;
            padding-bottom: 28px;

            @include xs-block() {
                display: block;
                margin-right: 10px;
                margin-left: 10px;
                padding: 15px 0;
            }
        }
    } //.breadcrumbs__wrap

    &__list {
        display: flex;
    } //.breadcrumbs__list

    &__item {
        @include iconAngle(
            $width: 8px,
            $height: 8px,
            $right: -13px,
            $direction: right
        );

        &:last-child {
            &::before {
                display: none;
            }

            .breadcrumbs__title {
                color: $gray--dark;
            }
        } //.breadcrumbs__item:last-child

        &:not(:last-child) {
            &:hover {
                .breadcrumbs__title {
                    color: $color-accent;
                }
            }
        } //.breadcrumbs__item:not(&:last-child)

        & + & {
            margin-left: 20px;
        } //.breadcrumbs__item + .breadcrumbs__item
    } //.breadcrumbs__item

    &__link {
        display: inline-block;
        color: $color-accent;
    } //.breadcrumbs__link

    &__title {
        color: $color-text--light;

        font-size: 14px;
    } //.breadcrumbs__title

    &--last-show {
        @include xs-block() {
            display: flex;

            &__item {
                display: none;

                &:last-child {
                    display: block;
                }
            }
        }
    } //.breadcrumbs--last-show
} //.breadcrumbs
