.swiper-container {
    &--fluid {
        @include sm-block() {
            margin-left: 10px;
        }

        &:not(.swiper-container-initialized) {
            @include xs-block() {
                overflow: hidden;
            }

            &[data-slider-slides='2.5'] {
                .swiper-slide {
                    @include xs-block() {
                        flex-basis: 300px;

                        & + .swiper-slide {
                            margin-left: 10px;
                        }
                    }
                } //.swiper-container--fluid:not(.swiper-container-initialized)[data-slider-slides="2.5"] .swiper-slide
            } //.swiper-container--fluid:not(.swiper-container-initialized)[data-slider-slides="2.5"]

            .swiper-wrapper {
                @include xs-block() {
                    margin-bottom: -30px;
                    padding-bottom: 30px;
                    overflow-x: auto;
                    -webkit-overflow-scrolling: touch;
                }
            }

            .swiper-slide {
                @include xs-block() {
                    flex-basis: 128px;
                }
            } //.swiper-container--fluid .swiper-slider
        } //.swiper-container:not(.swiper-container-initialized)

        .swiper-slide {
            height: auto;
            min-height: 100%;
        } //.swiper-container--fluid .swiper-slider
    } //.swiper-container--fluid

    &--hero {
        .swiper-button-next,
        .swiper-button-prev {
            top: 50%;
            opacity: 0.5;
            display: flex;
            align-items: center;
            justify-content: center;
            width: 50px;
            height: 50px;
            margin-top: -25px;
            // box-shadow: 0 2px 4px 0 rgba(0, 0, 0, 0.1);
            // @include bS;
            transition: all 0.12s ease-out;
            border-radius: 50%;
            // background-color: #fff;
            background-color: #ececec;

            &:hover {
                opacity: 0.7;
                transform: none;
                // box-shadow: 0 3px 4px 0 rgba(0, 0, 0, 0.1);
            }

            &:active {
                box-shadow: none;
            }

            .icon {
                width: 15px;
                height: 15px;
                fill: $color-icon--darken;
            }
        } //.swiper-button-next, .swiper-button-prev

        .swiper-button-next {
            right: 20px;

            .icon {
                margin-left: 4px;
            }
        } //.swiper-container--hero .swiper-button-next

        .swiper-button-prev {
            left: 10px;

            .icon {
                margin-right: 4px;
            }
        } //.swiper-container--hero .swiper-button-prev

        &.swiper-container-horizontal {
            .swiper-pagination {
                bottom: 10px;
            }
        } //.swiper-container--hero.swiper-container-horizontal .swiper-pagination
    } //.swiper-container--hero

    .swiper-slide {
        height: auto;
        min-height: 100%;
    } //.swiper-container--fluid .swiper-slider

    .btn-add {
        width: 100%;
        height: 100%;
    } //.swiper-container .btn-add
} //.swiper-container

.swiper-button-next,
.swiper-button-prev {
    top: 50%;
    display: flex;
    align-items: center;
    justify-content: center;
    width: 40px;
    height: 40px;
    margin-top: -20px;
    border: 0;
    border-radius: 50%;
    background-color: #ececec;
    background-image: none;
    // box-shadow: 0 2px 4px 0 rgba(0, 0, 0, 0.1);
    // @include bS;
    transition: transform 0.35s ease-in-out, box-shadow 0.35s ease-in-out;

    &:hover {
        transform: translateY(-2px);
        box-shadow: 0 3px 4px 0 rgba(0, 0, 0, 0.1);
    }

    &:active {
        box-shadow: none;
    }

    .icon {
        width: 23px;
        height: 23px;
        fill: $color-icon--darken;
    }
} //.swiper-button-next, .swiper-button-prev

.swiper-button-prev {
    left: -10px;

    .icon {
        transform: rotateZ(180deg);
        transform-origin: center;
    }
} //.swiper-container .swiper-button-prev

.swiper-button-next {
    right: -10px;
} //.swiper-container

.swiper-pagination {
    &--round {
        .swiper-pagination-bullet {
            width: 9px;
            height: 9px;
            border-radius: 50px;
            background-color: #dcdcdc;

            @include xs-block() {
                width: 7px;
                height: 7px;
            }
        } //.swiper-pagination--round .swiper-pagination-bullet
    } //.swiper-pagination--round
} //.swiper-pagination

.swiper-container-horizontal > .swiper-pagination-bullets,
.swiper-pagination-custom,
.swiper-pagination-fraction {
    bottom: 0;
} // .swiper-container-horizontal>.swiper-pagination-bullets, .swiper-pagination-custom, .swiper-pagination-fraction

.swiper-pagination-bullet {
    width: 25px;
    height: 6px;
    border-radius: $border-radius;
} // .swiper-pagination-bullet

.swiper-pagination-bullet-active {
    background: $gray;
} // .swiper-pagination-bullet-active
