.lesson {
    display: flex;
    flex-direction: column;

    &:not(.lesson--detail) {
        cursor: pointer;
    }

    &__section {
        margin-bottom: $section-indent;

        &--dbl-offset {
            margin-bottom: 0;
            padding: 35px 0;
            @include xs-block() {
                padding: 30px 0;
                // margin-top: 50px;
            }
        }
    } //.lesson__section

    &__wrap {
        @include row-flex;
    } //.lesson__wrap

    &__preview {
        position: relative;
        margin-bottom: 10px;
        overflow: hidden;
        border-radius: $border-radius;
        @include cover;
    } //.lesson__preview

    &__info {
        display: flex;
        flex-wrap: wrap;
    } //.lesson__info

    &__play {
        position: absolute;
        top: 10px;
        right: 10px;
        display: flex;
        align-items: center;
        justify-content: center;
        width: 25px;
        height: 25px;
        border-radius: 50%;
        background-color: rgba(#000, 0.3);
        cursor: pointer;

        .icon-play_arrow {
            fill: #fff;
        }
    } //.lesson__play

    &__title {
        margin-bottom: 5px;
        font-size: 15px;
        font-weight: 500;
        line-height: 1.2;

        @include xs-block() {
            margin-bottom: 5px;
            font-size: 20px;
        }
    } //.lesson__title

    &__subtitle {
        margin-bottom: 20px;
        // margin-left: 10px;
        font-size: 25px;
    } //.lesson__subtitle

    &__tags {
        margin-top: -2px;
        margin-bottom: 3px;

        .list {
            &__item {
                line-height: 1.2;
            }

            &__link {
                color: $color-info;

                font-size: 12px;

                @include xs-block() {
                    font-size: 14px;
                }

                &:hover {
                    color: $color-text;
                }
            } //.lesson__tags .list__link
        } //.lesson__tags .list
    } //.lesson__tags

    &__text {
        margin-bottom: 10px;

        font-size: 13px;

        @include xs-block() {
            font-size: 15px;
        }
    } //.lesson__text

    &__info-item {
        display: flex;
        align-items: center;
        margin-top: 5px;
        margin-right: 20px;

        &:last-child {
            margin-right: 0;
        }

        &:first-child {
            margin-left: 0;
        }

        span {
            margin-left: 5px;
            color: $color-icon;
            font-size: 13px;

            &:first-child {
                margin-left: 0;
            }
        }

        button {
            border: 0;
            background: transparent;
            font-size: 13px;
            text-decoration: underline;
        }
    } //.lesson__info-item

    &__comment {
        margin-top: 50px;
        padding: 35px 0;
        @include xs-block() {
            padding: 30px 0;
            // margin-top: 50px;
        }
    } //.lesson__comment

    &__comment-item {
        &:last-child {
            .item-reviews {
                border-bottom: 0;
            }
        }

        .item-reviews {
            border: 0;
            border-radius: 0;
            background: transparent;
            @include border(bottom);
        }

        .item-reviews__took-away {
            display: block;

            .item-reviews {
                border-bottom: 0;

                &__header {
                    padding-top: 15px;
                    @include border(top);
                }
            }
        }
    } //.lesson__comment-item

    &__comment-title {
        padding: 10px;
        font-size: 20px;
        font-weight: 500;
    } //.lesson__comment-title

    &__comment-btn-all {
        margin-top: 10px;
        border: none;
        background: transparent;
        color: $gray;
        font-size: 15px;

        &:hover,
        &:active {
            text-decoration: underline;
        }
    } //.lesson__comment-btn-all

    &--detail {
        margin-bottom: 0;

        & > .lesson {
            &__preview {
                position: relative;
                display: flex;
                align-items: center;
                height: 480px;
                min-height: 480px;
                margin-bottom: 30px;
                overflow: hidden;
                border-radius: 0;

                @include xs-block() {
                    height: 240px;
                    min-height: 240px;
                    margin-bottom: 20px;
                }

                &::before {
                    content: '';
                    position: absolute;
                    top: 0;
                    right: 0;
                    bottom: 0;
                    left: 0;
                    z-index: 1;
                    background: rgba(#000, 0.3);
                }

                h3 {
                    z-index: 2;
                    max-width: 80vw;
                    margin: 0 auto 10px;
                    margin-bottom: 10px;
                    color: #fff;
                    font-size: 35px;
                    line-height: 1.3;
                    text-align: center;
                    cursor: default;

                    @include xs-block() {
                        font-size: 27px;
                    }
                }

                .lesson__img {
                    position: absolute;
                    z-index: 0;
                    width: auto;
                    min-width: 100%;
                    height: auto;
                    min-height: 100%;
                    max-height: 178%;
                }
            } //.lesson--detail > .lesson__preview

            &__info {
                display: flex;
                flex-wrap: wrap;
                margin-bottom: 20px;
            } //.lesson--detail > .lesson__info

            &__desc {
                h5 {
                    margin-bottom: 10px;

                    font-size: 17px;
                }

                p {
                    margin-bottom: $gutter;
                    font-size: 15px;
                    line-height: 1.5;
                }

                ul,
                ol {
                    margin-bottom: $gutter;
                    padding-inline-start: 30px;

                    @include xs-block() {
                        padding-inline-start: 21px;
                    }

                    &:last-child {
                        margin-bottom: 0;
                    }
                }

                li {
                    font-size: 15px;
                    line-height: 1.5;

                    @include xs-block() {
                        font-size: 16px;
                    }
                }

                ul > li,
                ol > li {
                    margin-top: 7px;
                    list-style: disc;
                }
            } //.lesson--detail .lesson__desc
        } //.lesson--detail > .lesson

        .lesson {
            flex-shrink: 0;
            margin-bottom: 0;
            @include col;
            @include size(3);

            &:first-child {
                @include xs-block() {
                    margin-left: 0;
                }
            }

            &__preview {
                max-height: 200px;
                @include xs-block() {
                }
            } //.lesson--detail .lesson__preview

            &__wrap {
                display: flex;
                flex-wrap: nowrap;

                @include xs-block() {
                    margin: 0;
                    // margin-right: 10px;
                    // margin-bottom: -30px;
                    // margin-left: 10px;
                    // padding-bottom: 30px;
                    // overflow-x: auto;
                    // overflow-y: hidden;
                }

                .lesson {
                    @include xs-block() {
                        width: 80vw;
                        margin-right: 5px;
                        margin-left: 5px;
                    }
                }

                // &-title {
                //     margin-bottom: 20px;
                //     margin-left: 10px;
                //     font-size: 25px;
                // }
            } //.lesson--detail .lesson__wrap

            &__info {
                margin-bottom: 20px;
            } //.lesson--detail .lesson__info
        }
    } //.lesson--detail
} //.lesson

.knowledge-base {
    display: flex;
    flex-direction: column;
    border: 1px solid $color-border--lightest;
    border-radius: $border-radius;

    @include xs-block() {
        border: 0;
    }

    &__firstscreen {
        margin-bottom: 30px;

        @include xs-block() {
            margin-bottom: 25px;
        }

        &.firstscreen {
            height: 240px;

            .firstscreen {
                &__title {
                    z-index: 20;
                    max-width: 16ch;
                    margin-bottom: 0;
                    font-size: 35px;
                    text-align: center;
                    text-shadow: none;

                    @include xs-block() {
                        font-size: 27px;
                    }
                }

                &__bg {
                    width: auto;
                    height: 100%;
                }
            }
        }
    } //.knowledge-base__firstscreen

    &__category {
        margin-bottom: 20px;
        padding: 0 50px;

        @include sm-block() {
            padding: 0 15px;
        }

        @include xs-block() {
            margin-bottom: 25px;
            padding: 0;
        }

        .list {
            @include xs-block() {
                padding: 0 10px;
            }

            &__item {
                @include xs-block() {
                    margin-bottom: 0;

                    &:last-child {
                        margin-right: 15px;
                    }
                }
            } //.knowledge-base__category .list__item
        } //.knowledge-base__category .list
    } //.knowledge-base__category

    &__header {
        display: flex;
        align-items: center;
        justify-content: space-between;
        margin: 30px 0;
        padding: 15px 50px;
        background-color: $color-bg--lightest;
        @include flexCenter;

        @include sm-block() {
            padding: 15px;
        }

        @include xs-block() {
            justify-content: center;
            padding: 15px 10px;
        }

        &--view {
            justify-content: flex-start;
            padding: 50px 48px 20px;
            background-color: transparent;

            @include sm-block() {
                padding: 30px 15px 10px;
            }

            @include xs-block() {
                justify-content: flex-start;
                padding: 5px 10px 10px;
            }

            .knowledge-base__title {
                font-size: 27px;

                @include xs-block() {
                    font-size: 23px;
                }
            }
        } //.knowledge-base__header--view
    } //.knowledge-base__header

    &__title {
        font-size: 20px;
        font-weight: 500;

        // @include sm-block() {
        //     text-transform: uppercase;
        // }
    } //.knowledge-base__title

    &__content {
        padding: 30px 50px;
        @include row-flex;

        @include sm-block() {
            padding: 15px;
        }

        @include xs-block() {
            margin: 0;
            padding: 15px 10px;
        }
    } //.knowledge-base__content

    &__item {
        @include col;
        @include size(3);
        @include size-md(4);
        @include size-sm(6);

        @include xs-block() {
            width: 100%;
            margin: 0;
            margin-bottom: 20px;
        }

        &--line {
            margin-bottom: $gutter;
            padding-bottom: 20px;
            @include row-flex;
            @include size(12);
            @include size-md(12);
            @include size-sm(12);
            @include size-xs(12);
            @include border(bottom);

            &:last-child {
                margin-bottom: 0;
                border-bottom: 0;
            }

            &.lesson {
                flex-direction: row;
                width: 100%;
                margin-right: 10px;
                margin-left: 10px;

                @include xs-block() {
                    flex-direction: column;
                    width: 100%;
                    margin-right: 0;
                    margin-left: 0;
                }

                &:last-child {
                    padding-bottom: 0;
                }
            } //.knowledge-base__item.lesson

            .lesson {
                &__title {
                    font-size: 18px;
                } //.knowledge-base__item .lesson__title

                &__preview {
                    margin-bottom: 0;
                    margin-left: 0 !important;
                    @include col;
                    @include size(3);
                    @include size-sm(4);

                    @include xs-block() {
                        width: 100%;
                        margin-right: 0;
                        margin-bottom: 10px;
                    }
                } //.knowledge-base__item .lesson__preview

                &__desc {
                    margin-right: 0 !important;
                    @include col;
                    @include size(9);
                    @include size-sm(8);

                    @include xs-block() {
                        width: 100%;
                        margin-left: 0;
                    }
                } //.knowledge-base__item .lesson__desc
            } //.knowledge-base__item .lesson
        } //.knowledge-base__item--line
    } //.knowledge-base__item

    &__more {
        width: 100%;
        margin: 0 auto 30px;
    } //.knowledge-base__more

    &__more-btn {
        color: $gray--light;
        font-size: 15px;
        cursor: pointer;

        &:hover {
            color: lighten($gray--light, 10%);
        }
    } //.knowledge-base__more-btn

    .devider {
        margin: 0 0 27px;
    } //.knowledge-base .devider
} //.knowledge-base
