.fancybox-slide--html {
    padding: 5px 5px 0;
}

.fancybox-active {
    height: 100%;
}

.fancybox-toolbar {
    visibility: visible;
    opacity: 1;
}

.fancybox-container {
    z-index: z(modal);

    &:not(.bb-window__wrap) {
        .fancybox-navigation .fancybox-button {
            visibility: visible;
            opacity: 1;
        }
    }
}

.fancybox-is-open .fancybox-bg {
    background-color: rgba($gray--darkest, 0.8);
}

.compensate-for-scrollbar {
    margin-right: 0 !important;
}

.fancybox-slide--html .fancybox-content {
    margin-bottom: 0;
}

.fancybox-show-infobar .fancybox-infobar {
    display: none;
}

.fancybox-can-drag .fancybox-content {
    cursor: default;
}

.fancybox-slide {
    @include bb-scroll;
}

.fancybox-close-small {
    top: 9px;
    right: 9px;
    z-index: z(successBtnClose) - 2;
    width: 25px;
    height: 25px;
    padding: 0;

    &::after {
        top: 7px;
        right: 7px;
        font-size: 23px;
    }

    &:hover {
        &::after {
            background-color: transparent;
        }
    }

    svg {
        width: 25px;
        height: 25px;
    }
}

.fancybox-toolbar {
    top: auto;
    bottom: 0;
}
