* {
    box-sizing: border-box;
    margin: 0;
    padding: 0;
    outline: none;
    -webkit-text-size-adjust: none;
}

html {
    scroll-behavior: smooth;
    box-sizing: border-box;
    height: 100%;
    overflow-x: hidden;
    @include xs-block() {
        background-color: $color-bg--lightest;
        scroll-behavior: smooth;
        -webkit-overflow-scrolling: touch;
    }

    &.is-fixed {
        // overflow: hidden;
        // touch-action: none;

        &:not(.is-ios) {
            overflow: hidden;
        }

        body {
            // position: fixed;
            overflow: hidden;
        }

        // .wrapper,
        // .wrapper__inner {
        //     overflow: hidden;
        //     touch-action: none;
        // }

        // .wrapper {
        //     @include min(xs) {
        //         pointer-events: none;
        //     }

        //     &__inner {
        //         pointer-events: none;
        //     }
        // }
    }

    &.no-touch-events {
        .wrapper {
            touch-action: none;

            @include min(xs) {
                pointer-events: none;
            }

            &__inner {
                touch-action: none;

                @include min(xs) {
                    pointer-events: none;
                }
            }
        }
    }

    &.is-firefox {
        .search__input {
            padding: 7px 30px 8px 10px;
        }
    } //Firefox browser hucks
}

body {
    height: auto;
    // position: relative;
    // height: 100%; //Не раскоментировать
    // width: 100%;
    // min-width: 320px;
    // overflow-x: hidden;
    // overflow-y: auto;
    background-color: transparent;
    color: $color-text;
    // background-color: #fff;
    // font-family: -apple-system,BlinkMacSystemFont,"Segoe UI",Roboto,"Helvetica Neue",Helvetica,Arial,sans-serif;
    font-family: $font-family-base;
    font-size: $font-size-base;
    font-weight: 400;
    line-height: $line-height-base;
    text-rendering: optimizeLegibility;
    -webkit-text-size-adjust: none;
    @include smooth-font;

    @include xs-block() {
        background-color: $color-bg--lightest;
        // background-color: transparent;
        scroll-behavior: smooth;
        -webkit-overflow-scrolling: touch;
        // overflow: hidden;
    }

    &.is-fixed,
    &.fancybox-enabled {
        overflow: hidden;
    }

    &.is-loading {
        .preloader--cabinet {
            display: flex;
        }

        .main {
            visibility: hidden;
            opacity: 0;
        }
    } //body.is-loading

    &.loading {
        .preloader {
            visibility: visible;
            opacity: 1;
            transform: scale(1);
            background-color: #fff;
        }

        .btn-floating {
            bottom: -100px !important;
        }
    } //body.loading

    &.loading-animation {
        .preloader {
            transform: translateY(-200vh);
        }

        .btn-floating {
            bottom: -100px !important;
        }
    } //body.loading-animation

    &.modal-open {
        padding-right: 0 !important;
        overflow: hidden;
        pointer-events: none;
        touch-action: none;

        .wrapper {
            overflow: hidden;
            pointer-events: none;
            touch-action: none;

            &__inner {
                overflow: hidden;
                pointer-events: none;
                touch-action: none;
            }
        }
    } //body.modal-open
} //body

img {
    @extend %img;
}

iframe {
    border: 0;
}

select {
    width: 100%;
    outline: none;
    background-color: transparent;

    option {
        &[disabled] {
            color: $gray--light;
        }
    }
}

textarea {
    width: 100%;
    padding: 10px 15px;
    resize: none;
    // background-color: transparent;
    // @include bb-scroll--thin;
}

label {
    cursor: pointer;

    input[type='file'] {
        display: none;
    }
}

/* Change the white to any color ;) */

input:-webkit-autofill,
input:-webkit-autofill:hover,
input:-webkit-autofill:focus,
input:-webkit-autofill:active {
    -webkit-box-shadow: 0 0 0 30px white inset !important;
}

input,
textarea,
select {
    outline: none;
    box-shadow: none;
    resize: none !important;
    -webkit-appearance: none;
    appearance: none;
    @extend %styleInput;
}

textarea {
    width: 100%;
    min-height: 100px;
    padding: 4px 10px;
    font-size: 14px;
    line-height: 1.25;

    @include max(xs) {
        font-size: 16px;
    }

    @include placeholder {
        line-height: 1.25;
    }

    &:focus {
        font-size: 14px;
    }
}

input[type='num'],
input[type='number'],
input[type='tel'],
input[type='time'],
input[type='date'] {
    -moz-appearance: textfield;
    appearance: none;

    &::-webkit-outer-spin-button,
    &::-webkit-inner-spin-button,
    &::-webkit-calendar-picker-indicator {
        display: none;
        margin: 0;
        -webkit-appearance: none;
    }
}

/*disable auto-zoom on iphone input field focus*/
@media screen and (-webkit-min-device-pixel-ratio: 0) {
    @include max(xs) {
        select:focus,
        textarea:focus,
        input:focus {
            font-size: 16px;
        }
    }
}

