.main {
    flex: 1;
    display: flex;
    flex-direction: column;
    background-color: $color-bg--lightest;

    &.home,
    &.card,
    &.promo {
        background-color: #fff;
    }

    &.promo {
        @include sm-block() {
            padding-top: 0;
        }
    }

    &.home {
        margin-top: -40px;
    }
} //.main

.windows {
    display: none;
} //.windows

.overlay {
    position: fixed;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    z-index: z(overlay);
    display: none;
    background: rgba(47, 47, 47, 0.35);

    @include max(xs) {
        cursor: pointer;
    }

    &:hover {
        .overlay__btn {
            visibility: visible;
            opacity: 1;
        }
    }

    &__btn {
        top: 100px;
        visibility: hidden;
        opacity: 0;
        @include tr;
        @include centre();
    } //.overlay__btn

    &__icon {
        align-items: center;
        justify-content: center;
        width: 55px;
        height: 55px;
        margin: auto auto 15px;
        padding: 10px;
        border-radius: 50%;
        background-color: $color-bg--light;
        box-shadow: 0 3px 20px rgba(0, 0, 0, 0.25);
        cursor: pointer;
        @include tr;
        @include flexColumn;

        &:hover {
            background-color: $color-bg--lightest;

            .icon {
                @include rotate(90);
            }
        }

        span {
            margin-top: 5px;
            color: $gray;
            font-weight: 600;
            font-size: 14px;
            line-height: 1;
            text-transform: uppercase;
        }

        .icon {
            @include tr;
        }

        .fal {
            transform-origin: 50% 50%;
            color: $color-icon;
            font-size: 22px;
            line-height: 1;
            text-align: center;

            &::before {
                line-height: 1;
            }
        }
    } //.overlay__icon

    &__text {
        color: #fff;
        font-weight: 600;
        font-size: 14px;
        letter-spacing: 0.5px;
        text-transform: uppercase;
    } //.overlay__text

    &.is-visible {
        display: block;
    } //.overlay.is-visible

    &.is-overhead {
        z-index: 1010;
    } //.overlay.is-overhead

    &--absolute {
        position: absolute;
    } //.overlay--absolute

    &--dropdown {
        z-index: z(overlay) + 2;
        display: block;

        @include min(md) {
            background-color: transparent;
        }
    } //.overlay--dropdown

    &--popup-select-block {
        z-index: z(modal) + 1;
        display: block;
    } //.overlay--popup-select-block
} //.overlay

.no-overlay {
    &::before {
        display: none !important;
    }
} //.no-overlay

.icon {
    width: 18px;
    height: 18px;
    fill: $color-icon;

    &-controls {
        width: 20px;
        height: 20px;
    } //.icon-controls

    &-rub {
        width: 11px;
        height: 11px;
        fill: $color-icon;
    } //.icon-rub

    &-add {
        position: relative;

        .icon {
            &:not(.icon-plus--filled) {
                position: relative;
                width: 50px;
                height: 50px;
                margin-bottom: 10px;
                fill: $color-icon--light;
            }

            &--big {
                width: 150px;
            } //.widget__icon--big
        } //.icon-add

        .icon-plus--filled {
            position: absolute;
            right: -5px;
            bottom: 7px;
            width: 25px;
            height: 25px;
            margin-bottom: 0;
            fill: $gray;
        } //.icon-add
    } //.icon-add

    &-reset {
        justify-content: center;
        width: 19px;
        height: 19px;
        border-radius: 50%;
        background-color: $gray--light;
        @include flexCenter;

        &:hover {
            background-color: $gray--light;
        } //.icon-reset:hover

        .icon {
            width: 9px;
            height: 9px;
            margin: auto;
            fill: #fff;
        } //.icon-reset .icon
    } //.icon-reset

    &--lg {
        width: 70px;
        height: 70px;
    } //.icon

    &--big {
        width: 100px;
        height: 100px;
    } //.icon

    &--dark {
        fill: $gray--dark;
    } //.icon--dark
} //.icon

.currency {
    // color: $color-icon;
    // font-weight: 500;
    font-size: 0.8em;
    font-weight: inherit;
} //.currency

.loader {
    position: fixed;
    top: 50vh;
    left: 50vw;
    width: 80px;
    height: 80px;
    margin-left: 100px;
    clip: rect(0, 80px, 80px, 40px);
    animation: animateRotation 1.5s linear infinite;

    &::after {
        content: '';
        position: absolute;
        width: 80px;
        height: 80px;
        clip: rect(0, 80px, 80px, 40px);
        animation: animateRotationShadow 1.5s ease-in-out infinite;
        border-radius: 50%;
    }

    &__wrap {
        display: none;
        background-color: rgba(#fff, 0.7);
        @include coverdiv;
    }

    &--scroll {
        bottom: -42px;
        width: 35px;
        height: 35px;
        background-color: transparent;
        background-image: url('../files/assets/loader-1.gif');
        background-repeat: no-repeat;
        background-size: contain;
        @include centre('x');
    } //.loader--scroll
} //.loader

.preloader {
    position: fixed;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    z-index: 10000;
    visibility: hidden;
    opacity: 0;
    transform: scale(0);
    transition: visibility 0.5s, opacity 0.5s, transform 0.5s;
    background-color: transparent;

    &__icon {
        position: fixed;
        top: 50%;
        left: 50%;
        z-index: z(fixed);
        width: 55px;
        height: 55px;
        transform: translate(-50%, -50%);

        path {
            fill: #ececec;
        }
    } //.preloader__icon

    &--cabinet {
        position: fixed;
        top: 0;
        right: 0;
        bottom: 0;
        left: 0;
        z-index: 300;
        visibility: visible;
        opacity: 1;
        display: none;
        transform: scale(1);
        background-color: #fff;

        .preloader__icon {
            width: 35px;
            height: 35px;
            background-image: url('../files/assets/loader-1.gif');
            @include cover;
        }
    } //.preloader--cabinet
} //.preloader
