.search {
    position: relative;
    width: 100%;
    max-width: 585px;
    height: 45px;
    @include flex;
    @include clr;

    @include sm-block() {
        max-width: 100%;
        margin-bottom: 0;
    }

    &__wrap {
        margin-top: 35px;
    } //.search__wrap

    &__group {
        position: relative;
        display: flex;

        &--services {
            flex: 1;
            border-left: 1px solid $color-border--lightest;
            @include xs-block() {
                border-left: 0;

                .search__input {
                    border-radius: $border-radius 0 0 $border-radius;
                }
            }

            .search__input {
                padding-right: 15px;
            }
        }

        &--city {
            flex: 1;

            &::before {
                content: '\f3c5';
                right: 8px;
                color: $gray--lighten;
                font-size: 16px;
                @include fa('light');
                @include centre('y');
            }

            .search__input {
                border-radius: $border-radius 0 0 $border-radius;
            }
        } //.search__group--city
    } //.search__group

    &__input {
        box-sizing: border-box;
        width: 100%;
        height: 45px;
        padding: 10px 30px 14px 10px;
        border: 1px solid $color-bg--light;
        border-radius: 0;
        background-color: $color-bg--light;
        color: #000;

        @include placeholder {
            color: $gray--dark;
            font-size: 14px;
            font-style: italic;
            font-weight: 400;
            font-weight: 300;
        }

        &:hover {
            border-color: $color-bg--light;
        }

        &:focus {
            border-color: $color-border--light;
            background-color: #fff;
            color: $gray--dark;
        }

        &:-webkit-autofill,
        &:-webkit-autofill:hover,
        &:-webkit-autofill:focus,
        &:-webkit-autofill:active {
            box-shadow: 0 0 0 30px #fff inset !important;
            color: $gray--dark;
        }
    } //.search__input

    &__btn {
        height: 45px;
        padding: 0 35px;
        border-radius: 0 $border-radius $border-radius 0;
        outline: none;
        box-shadow: none;
        color: #fff;
        font-size: 17px;
        line-height: 45px;
        text-transform: initial;
        @include xs-block() {
            display: flex;
            padding: 0 20px;

            span {
                display: none;
            }
        }

        @include i5-block() {
            padding: 0 15px;
        }

        &:hover {
            box-shadow: none;
        }
    } //.search__btn

    &__ico {
        right: 10px;
        width: 15px;
        height: 15px;
        fill: $color-icon;
        color: $color-icon;
        font-size: 16px;
        @include centre('y');
    } //.search__ico

    &__clear {
        right: 6px;
        justify-content: center;
        border: 0;
        background-color: transparent;
        @include centre('y');
        @include flexCenter;

        &:hover {
            span {
                background-color: $color-icon;
            }
        }

        span {
            justify-content: center;
            width: 20px;
            height: 20px;
            border-radius: 50%;
            background-color: $color-icon--light;
            @include tr;
            @include flexCenter;
        } //.search__clear span

        .icon {
            position: static;
            top: auto;
            width: 8px;
            height: 8px;
            transform: none;
            fill: #fff;
        } //.search__clear .icon
    } //.search__clear

    &__icon {
        display: none;
        width: 25px;
        height: 25px;
        fill: #fff;
        @include xs-block() {
            display: flex;
            margin: auto;
        }
    } //.search__icon

    form {
        width: 100%;
    } //.search form

    &--sm {
        width: 100%;
        max-width: 320px;

        .search {
            &__input {
                border-radius: 20px;
            }
        }
    } //.search--sm

    &--dark {
        .search {
            &__input {
                border: 0;
                background-color: $gray--darken;
                color: $gray--light;

                @include placeholder {
                    color: $gray--dark;
                }

                &:hover,
                &:focus {
                    border: 0;
                    background-color: #fff;
                    box-shadow: none;
                    color: $gray--dark;
                }

                &:-webkit-autofill,
                &:-webkit-autofill:hover,
                &:-webkit-autofill:focus,
                &:-webkit-autofill:active {
                    box-shadow: 0 0 0 30px $gray--darken inset !important;
                    color: #fff;
                }
            } //.search--dark .search__input

            &__ico {
                fill: $color-accent;
                color: $color-accent;
            }
        }
    } //.search--dark

    .bb-input {
        width: 100%;
    } //.search .bb-input

    form {
        width: 100%;
    } //.search form
} //.search

.search-hint {
    position: absolute;
    top: 110%;
    right: -5px;
    left: -5px;
    z-index: z(dropdown) + 1;
    max-height: 250px;
    overflow-y: auto;
    border-radius: $border-radius * 2;
    background-color: #fff;
    box-shadow: $bxs-box;
    @include bb-scroll;

    &__item {
        &.is-selected {
            background-color: #eee;
        }
    } //.search-hint__item

    &__link {
        display: flex;
        padding: 7px 10px;
        transition: initial;
        @include flexCenter;

        &:hover {
            background-color: $color-bg--light;
        }
    } //.search-hint__link

    &__bade {
        width: 65px;
        margin-right: 5px;
        padding: 1px 5px;
        border-radius: $border-radius;
        color: $color-text;
        font-size: 10px;
        text-align: center;
        text-transform: uppercase;

        &--service {
            background-color: $color-success--light;
            color: #fff;
            @include xs-block() {
                width: 65px;
            }
        } //.search-hint__bade--service

        &--master {
            background-color: $color-primary--light;
            color: #fff;
        } //.search-hint__bade--master

        &--salon {
            background-color: $gray--light;
            color: #fff;
        } //.search-hint__bade--salon

        &--category {
            background-color: $color-info--light;
            color: #fff;
        } //.search-hint__bade--category

        &--location {
            width: auto;
            margin-right: 0;

            i {
                font-size: 14px;
            }
        } //.search-hint__bade--location
    } //.search-hint__bade

    &__text {
        flex: 1;
        color: $color-text;
        font-size: 14px;
        @extend %textLight;
    } //.search-hint__text

    &--cyties {
        .search-hint {
            &__item {
                border-bottom: 1px solid $color-bg--light;

                &:last-child {
                    border-bottom: 0;
                }
            }
        }
    } //.search-hint--cyties
} //.search-hint

.mobile-search {
    position: fixed;
    top: 0;
    left: 0;
    display: none;
    width: 100%;
    height: 100%;
    background-color: #fff;

    &__header {
        padding: 10px 15px 10px 10px;
        border-bottom: 1px solid $color-border--lightest;
        background-color: $color-bg--lighten;
        @include flexCenter;
    } //.mobile-search__header

    &__input {
        position: relative;
        flex: 1;
        height: 35px;
        margin-right: 15px;

        input {
            height: 35px;

            &:not(:placeholder-shown) ~ .bb-input__icon.icon-search {
                display: none;
            }

            &:placeholder-shown ~ .bb-input__icon.icon-close--bold {
                display: none;
            }
        } //.mobile-search__input input
    } //.mobile-search__input

    &__close {
        &:hover,
        &:focus {
            text-decoration: none;
        }
    } //.mobile-search__closse

    .search {
        height: 100%;

        &__group {
            position: relative;
            flex-direction: column;
            width: 100%;
        } //.mobile-search .search__group

        &__hint {
            position: absolute;
            top: 56px;
            right: 0;
            bottom: 0;
            left: 0;
        } //.mobile-search .search__hint

        &__input {
            padding: 10px 23px 10px 10px;
            border: 1px solid $color-border--light;
            border-radius: $border-radius;
            background-color: #fff;

            &:focus {
                border-color: $color-border;
            }
        } //.mobile-search .search__input
    } //.mobile-search .search

    .search-hint {
        top: 0;
        max-height: 100%;
        border: 0;
        box-shadow: none;
    }

    .bb-input {
        &__icon {
            right: 7px;
            width: 12px;
            height: 12px;
            padding: 0;
            // color: $color-accent;
            // fill: $color-accent
        } //.search-mobile .bb-input__icon
    } //.search-mobile .bb-input
} //.search-mobile

.popular-search {
    max-width: 565px;
    margin: 0 auto 50px 0;
    padding: 1px 10px 3px;
    border-radius: $border-radius;
    background: rgba($color-bg--dark, 0.5);

    @include xs-block() {
        display: none;
    }

    &__item {
        display: inline-block;
        margin-right: 10px;
        color: #fff;
        font-size: 12px;
        font-weight: 600;
        text-transform: uppercase;
    } //.popular-search__item

    &__link {
        color: #fff;
        font-weight: 300;
        letter-spacing: 1px;

        &:hover {
            color: #fff;
            text-decoration: underline;
        }
    } //.popular-search__link
} //.popular-search
