$widget-colors: (
    new: $gray--light,
    work: rgb(255, 217, 99),
    done: rgb(151, 204, 100),
    abort: rgb(253, 90, 62),
);

.widget {
    position: relative;
    margin-bottom: $gutter;
    background-color: #fff;
    @include border(all);
    @include flexColumn;

    @include xs-block() {
        // min-height: 105px;
        // margin: 10px 0;
        margin-bottom: $gutter / 2;
    }

    // &:last-child {
    //     margin-bottom: 0;
    // }

    &__wrap {
        align-items: flex-start;
        @include row-flex;

        .widget {
            @include col;
            @include size(3);
            @include size-md(4);
            @include size-sm(6);
            @include size-xs(12);

            &--md {
                @include size(6);
                @include size-md(6);
                @include size-sm(12);
            }
        }

        .card-item {
            @include col;
            @include size(6);
        }
    } //.widget__wrap

    &__row {
        display: flex;
        overflow-y: auto;

        .widget {
            flex-shrink: 0;

            @include xs-block() {
                margin-right: 0;
                border-right: 1px solid $color-border--lightest;
                border-left: 1px solid $color-border--lightest;
            }

            &:first-child {
                margin-left: 10px;
            }

            & + .widget {
                margin-left: 10px;
            }
        } //.widget__row .widget
    } //.widget__row

    &__header {
        position: relative;
        padding: 10px 15px;
        // border-radius: $border-radius $border-radius 0 0;
        color: $gray--darkest;
        font-size: 14px;
        font-weight: 500;
        // @include border(bottom);
        @include flexCenter;

        @include xs-block() {
            padding: 10px;
            border-radius: 0;
        }

        .inline-select {
            margin-left: auto;
        } //.widget__header .inline-select
    } //.widget__header

    &__header-link {
        margin-left: auto;
        font-size: 14px;
        font-weight: 300;

        @include xs-block() {
            font-size: 15px;
        }
    } //.widget__header-link

    &__footer {
        padding: 15px;
        border-top: 1px solid $color-border--lightest;

        .btn {
            width: 100%;
        }
    } //.widget__footer

    &__close {
        right: 2px;
        @include centre('y');

        .icon {
            width: 13px;
            height: 13px;
        }
    } //.widget__close

    &__title {
        display: inline-block;
        color: $gray--darkest;

        @include xs-block() {
            border-bottom: 0;

            font-size: 16px;
        }
    } //.widget__title

    &__subtitle {
        margin-left: auto;
        color: $gray;
    } //.widget__subtitle

    &__body {
        padding: 15px;

        @include xs-block() {
            min-height: auto;
            padding: 10px;
        }

        & + .widget__inner {
            margin-top: 10px;
        }

        &.no-offset {
            padding: 0;
        } //.widget__body.no-offset

        &--scrolled {
            padding-top: 0;
            padding-bottom: 0;

            @include min(sm) {
                max-height: calc(100vh - 385px);
                @include scroll;
            }

            @include xs-block() {
                .request-item {
                    width: 100%;
                    margin-right: 0;
                    margin-left: 0;
                    border-right: 1px solid #dee3e7 !important;
                }
            }

            &--full {
                max-height: calc(100vh - 183px);
                @include scroll;
            }
        } //.widget__body--scrolled

        .cart-form {
            &__inner {
                display: flex;
                flex-wrap: wrap;
                justify-content: space-between;
                padding: 10px 15px;
            }

            &__title {
                display: block;
                padding: 10px 15px;
                color: $gray--darkest;
                font-size: 14px;
                font-weight: 500;
            } //.widget__body .cart-form__title

            &__item {
                flex-grow: 1;
                max-width: calc(50% - 10px);
                margin-bottom: 10px;
                @include mdX-block() {
                    max-width: 100%;
                }
            } //.widget__body .cart-form__item
        } //.widget__body .cart-form

        .order-control {
            display: flex;
            align-items: center;

            &__box {
                display: flex;
                flex-shrink: 0;
                width: 55%;
                // flex-basis: 50%;
                margin-left: auto;
            } //.widget__body .order-control__box

            &__sum {
                // flex-basis: 50%;
                font-weight: 500;
            } //.widget__body .order-control__sum

            &__item {
                flex-basis: auto;
                flex-grow: 1;
                width: calc(50% - 20px);

                select {
                    height: 40px;
                }

                & + .order-control__item {
                    margin-left: 10px;
                } //.widget__body .order-control__item + .order-control__item
            } //.widget__body .order-control__item

            &--xs-column {
                @include xs-block() {
                    flex-direction: column;
                }

                .order-control {
                    &__box {
                        @include xs-block() {
                            flex-direction: column;
                            width: 100%;
                        }
                    } //.widget__body .order-control--xs-column .order-control__box

                    &__sum {
                        @include xs-block() {
                            align-self: flex-end;
                        }
                    } //.widget__body .order-control--xs-column .order-control__sum

                    &__item {
                        @include xs-block() {
                            width: 100%;
                            margin-top: 10px;
                        }

                        & + .order-control__item {
                            @include xs-block() {
                                margin-left: 0;
                            }
                        }
                    } //.widget__body .order-control--xs-column .order-control__item
                }
            } //.widget__body .order-control--xs-column
        } //.widget__body .order-control
    } //.widget__body

    &__inner {
        width: 100%;
        @include flex;

        &--column {
            height: 100%;
            @include flexColumn;

            .bb-checkbox {
                & + .bb-checkbox {
                    margin-top: 20px;
                }
            } //.widget__inner--column .bb-checkbox + .bb-checkbox

            .btn {
                margin-top: auto;
            } //.widget__inner--column .btn
        } //.widget__inner--column

        .widget {
            &__left {
                position: relative;
                width: 50%;

                @include xs-block() {
                    padding: $gutter $gutter / 2;

                    .list {
                        &__item {
                            margin: 0 0 $gutter / 2;

                            &:last-child {
                                margin-bottom: 0;
                            }
                        }
                    }
                }

                .list {
                    &__item {
                        justify-content: flex-end;
                    }

                    &__text {
                        margin-right: 10px;
                        margin-left: 0;
                    }
                }
            } //.widget__body .widget__left

            &__right {
                position: relative;
                width: 50%;
                margin-left: 40px;

                @include xs-block() {
                    padding: $gutter $gutter / 2;
                }

                &::before {
                    content: '';
                    left: -20px;
                    display: block;
                    width: 1px;
                    height: 80%;
                    background-color: $color-bg--light;
                    @include centre('y');

                    @include xs-block() {
                        left: -10px;
                    }
                }
            } //.widget__body .widget__right
        }
    } //.widget__body

    &__scroll {
        max-height: 286px;
        @include flexColumn;

        @include xs-block() {
            max-height: 278px;
        }

        .empty {
            margin: auto;
            padding: 20px 0;
        }

        .widget__items {
            height: 100%;
            overflow-y: auto;
        }
    } //.widget__scroll

    &__items {
        flex: 1;
        min-height: 132px;
        @include flexCenter;

        @include xs-block() {
            min-height: auto;
            padding-bottom: 15px;
        }

        &--column {
            @include flexColumn;

            @include xs-block() {
                margin: 0 10px;
            }

            .widget__item {
                align-items: flex-start;
                width: 100%;
                color: $color-text;
                text-align: left;

                @include xs-block() {
                    padding: 10px 0;
                }

                & + .widget__item {
                    border-top: 1px solid $color-border--lightest;
                }
            } //.widget__item
        } //.widget__items--column

        &--devider {
            .widget__item {
                &:not(:first-child) {
                    position: relative;

                    &::before {
                        content: '';
                        position: absolute;
                        top: 0;
                        left: 0;
                        display: block;
                        width: 1px;
                        height: 100%;
                        background-color: $color-bg--light;
                    }
                }
            }
        } //.widget__items--devider

        &.no-offset {
            margin: 0;
            padding: 0;
        }
    } //.widget__items

    &__item {
        position: relative;
        width: 50%;
        height: 100%;
        padding: 15px;
        border: 0;
        outline: none;
        background-color: transparent;
        color: $color-primary;
        font-size: 15px;
        text-align: center;
        cursor: pointer;
        @extend %textLight;
        @include tr;

        @each $theme, $color in $widget-colors {
            &.is-#{$theme} {
                .widget__status {
                    border-color: $color;
                    color: $color;
                }
            }
        }

        &:first-child {
            &::after {
                display: none;
            }
        }

        &.color-black {
            color: $gray--darken;

            &:hover {
                color: $gray--darken;
            }
        }

        span {
            display: block;
            margin: auto;
            font-size: 35px;
            font-weight: 500;
            line-height: 1;
        }

        img {
            width: 80%;
            padding: 5px 0;
        }

        small {
            display: block;
            margin-top: -3px;
            font-size: 12px;
        }
    } //.widget__itemA

    &__status {
        position: absolute;
        top: 11px;
        right: 10px;
        padding: 0 10px;
        border: 1px solid $color-border--lightest;
        border-radius: 15px;
        font-size: 13px;
    } //.widget__status

    &__text {
        margin-bottom: 15px;
        font-size: 14px;
        font-weight: 300;

        &.mb-0 {
            margin-bottom: 0;
        }
    } //.widget__text

    &__num {
        font-size: 30px;
        font-weight: 500;
    } //.widget__num

    &__icon {
        position: relative;
        width: 50px;
        height: 50px;
        margin-bottom: 10px;
        fill: $color-icon--light;

        &--big {
            width: 150px;
        } //.widget__icon--big

        .icon-plus--filled {
            position: absolute;
            right: -11px;
            bottom: -13px;
            z-index: z(fixed);
            width: 25px;
            height: 25px;
            fill: $gray;
        } //.widget__icon .icon-plus--filled
    } //.widget__icon

    &__btn {
        &:hover {
            &::after {
                display: none;
            }

            & + .widget__item {
                &::after {
                    display: none;
                }
            }

            .widget__btn_top {
                @include tY(-5px);
            }
        }

        &_top {
            display: block;
            width: 28px;
            height: 28px;
            margin: auto;
            fill: $color-accent;
            @include tr;
        } //.box__btn_top
    } //.widget__btn

    &__toggle {
        justify-content: center;
        padding: 20px;
        @include flexCenter;

        @include xs-block() {
            padding: 0 10px 10px;
        }

        .bb-checkbox {
            &__box {
                top: 0;
            }
        }
    } //.widget__toggle

    &__time {
        font-size: 15px;
        font-weight: 500;

        @include max(sm) {
            font-size: 16px;
        }
    } //.widget__time

    &__desc {
        color: $color-text--light;
        font-size: 14px;
        font-style: italic;

        @include max(sm) {
            font-size: 16px;
        }

        span {
            display: block;
            margin: 5px 0 3px;
            color: $color-text;
            font-size: inherit;
            font-weight: 500;

            @include xs-block() {
            }
        }
    } //.widget__desc

    &__result {
        display: flex;
    } //.widget__result

    &__number {
        margin-left: auto;
        color: $gray--dark;
        font-weight: 500;
    } //.widget__number

    &__popup {
        position: fixed;
        top: 50%;
        left: 50%;
        z-index: 50;
        display: none;
        width: 300px;
        background-color: #fff;
        box-shadow: $bxs;
    } //.widget__popup

    &__add-btn {
        width: 100%;
        padding: 15px 8.5px 14px 15px;
        // border-radius: $border-radius;
        background-color: transparent;
        color: $color-text;
        cursor: pointer;
        @include border(all);
        @include flexCenter;

        // @include xs-block() {
        //     margin-top: -10px;
        // }

        &:hover {
            border-color: $color-border--light;

            .icon {
                fill: $color-icon--hover;
            } //.widget__add-btn:hover .icon
        } //.widget__add-btn:hover

        .icon {
            margin-left: auto;
            @include tr;
        } //.widget__add-btn .icon
    } //.widget__add-btn

    &__figure {
        flex-shrink: 0;

        @include col;
        @include size(3);
    } //.widget__figure

    &__bord {
        @include col;
        @include size(6);

        &:only-child {
            width: 100%;
        }

        & + & {
            width: 50%;
            margin-right: 0;
            margin-left: 0;
            padding: 0 10px;

            @include border(left);
        }
    } //.widget__bord

    &__progress-bar {
        width: 50%;
        max-width: 120px;
        margin-left: 10px;
    } //.widget__progress-bar

    &__formula {
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
        margin-left: 20px;

        @include xs-block() {
            margin-left: 10px;
        }

        hr {
            width: 100%;
        }
    } //.widget__formula

    &__box {
        width: 100%;

        &:first-child {
            @include border(top);
        }

        .widget__text {
            @include text-overflow;
        } //.widget__box .widget__text
    } //.widget__box

    &__insta-log {
        margin-top: 15px;
    } //.widget__insta-log

    &.no-offset {
        margin-bottom: 0 !important;
    } //.widget.no-offset

    &.no-border {
        border: 0;
    } //.widget.no-border

    &--shadow {
        // @include bS;
        // // border: 0;
        // border-color: $color-border-very-light;
        // border-radius: $border-radius;
        border: 0;
        @include box;

        .widget {
            &__add-btn {
                // @include bS;
                border: 0;
                // border-radius: $border-radius;
                @include tr;

                &:hover {
                    @include tY(1px);
                }
            } //.widget--shadow .widget__add-btn
        }
    } //.widget--shadow

    &--counter {
        width: 100%;
        margin-bottom: 0;
        overflow: hidden;
        border: 0;
        background: transparent;

        @include xs-block() {
            min-height: auto;
            margin: 0;
            overflow: hidden;
            border: 0;
        }

        .widget {
            &__inner {
                position: relative;
                display: flex;
                flex-direction: column;
                flex-wrap: wrap;
                min-width: 220px;
                min-height: 105px;
                padding: 10px 0;
                background: transparent;

                @include xs-block() {
                    flex-direction: row;
                    flex-wrap: nowrap;
                    margin-bottom: -30px;
                    padding-bottom: 30px;
                    overflow-x: scroll;
                    overflow-y: hidden;
                }
            } //.widget--counter .widget__inner

            &__box {
                width: 100%;
                padding: 15px;
                border: 1px solid $color-border--lighten;
                background-color: #fff;

                @include xs-block() {
                    justify-content: flex-start;
                    min-width: 220px;
                    margin: 0 10px;
                    padding: 10px;

                    &:first-child {
                        margin: 0;
                    }
                }
            } //.widget--counter .widget__box

            &__text {
                margin-bottom: 0;

                font-size: 15px;
            } //.widget--counter .widget__text
        } //.widget--counter

        .icon {
            right: 10px;
            width: 30px;
            height: 30px;

            @include centre('y');
        } //.widget--counter .icon

        .icon-rub {
            position: static;
            top: auto;
            right: auto;
            width: 13px;
            height: 13px;
            transform: translateY(0);
        } //.widget--counter .icon-rub

        &.no-scroll {
            border-right: 0;
            border-left: 0;

            @include xs-block() {
                border-right: 0;
                border-left: 0;
            }

            .widget {
                &__inner {
                    position: relative;
                    display: flex;
                    flex-direction: column;
                    flex-wrap: wrap;
                    min-width: auto;
                    min-height: 105px;
                    padding: 10px 0;
                    background: transparent;

                    @include xs-block() {
                        flex-direction: row;
                        flex-wrap: nowrap;
                        margin-bottom: 0;
                        padding-bottom: 0;
                        overflow: hidden;
                    }
                } //.widget--counter.no-scroll .widget__inner

                &__box {
                    @include xs-block() {
                        justify-content: flex-start;
                        min-width: 100%;
                        margin: 0 10px;

                        &:first-child {
                            margin-left: 0;
                        }
                    }
                } //.widget--counter.no-scroll .widget__box
            } //.widget--counter.no-scroll
        } //.widget--counter.no-scroll
    } //.widget--counter

    &--center {
        align-items: center;
        @include flexColumn;

        .widget {
            &__text {
                text-align: center;
            }
        }
    } //.widget--center

    &--add {
        margin: 0;
        padding: 20px 10px 10px;
        transition: border 0.3s ease-in-out;
        border: 1px dashed $color-border--lightest;

        @include xs-block() {
            margin: 15px 0 0;
            border: 1px dashed $color-border--lightest;
            border-radius: $border-radius;
        }

        &:not(.no-hover) {
            &:hover {
                border-color: $color-border--light;
                cursor: pointer;

                .icon {
                    &:not(.icon-plus--filled) {
                        fill: $gray--light;
                    }
                }
            }
        }

        .icon {
            transition: all 0.3s ease-in-out;
        }
    } //.widget--add

    &--icon {
        .widget {
            &__body {
                @include flexCenter;

                @include xs-block() {
                    flex-wrap: wrap;
                }
            } //.widget--icon .widget__body
        } //.widget--icon

        .icon {
            &__list {
                width: 100%;
                @include flexCenter;

                @include xs-block() {
                    margin-bottom: $gutter;
                }
            } //.widget--icon .icon__list

            &__item {
                width: 50px;
                margin: auto;
                overflow: hidden;
                border-radius: 50%;
            } //.widget--icon .icon__item
        } //.widget--icon .icon

        .btn {
            width: 100%;
            min-width: 160px;

            @include xs-block() {
                min-width: 100%;
            }
        } //.widget--icon .btn
    } //.widget--icon

    &--profile,
    &--table {
        .user {
            display: flex;
            flex-direction: column;
            align-items: center;
            text-align: center;

            &__inner {
                display: flex;
                flex-direction: column;
                align-items: center;
                justify-content: center;
            }

            &--inline {
                flex-direction: row;
                margin-bottom: 0;

                .user {
                    &__avatar {
                        margin-bottom: 0;
                    } //.widget--profile .user__avatar

                    &__img {
                        width: 40px;
                        height: 40px;
                    } //.widget--profile .user__img

                    &__name {
                        width: 100%;

                        font-size: 17px;
                    } //.widget--profile .user__name

                    &__status {
                        width: 100%;
                        text-align: left;
                    } //.widget--profile .user__status

                    &__phone {
                        margin-top: 5px;
                    } //.widget--profile .user__phone

                    &__inner {
                        display: flex;
                        flex-direction: column;
                        align-items: flex-start;
                        justify-content: center;
                        margin-left: 20px;
                    } //.widget--profile .user__inner
                }
            } //.widget--profile .user--inline

            &--inline-mobile {
                @include xs-block() {
                    flex-direction: row;
                    margin-bottom: 0;

                    .user {
                        &__avatar {
                            margin-bottom: 0;
                        } //.widget--profile .user__avatar

                        &__img {
                            width: 40px;
                            height: 40px;
                        } //.widget--profile .user__img

                        &__name {
                            width: 100%;

                            font-size: 17px;
                            text-align: left;
                        } //.widget--profile .user__name

                        &__status {
                            width: 100%;
                            text-align: left;
                        } //.widget--profile .user__status

                        &__phone {
                            margin-top: 5px;
                        } //.widget--profile .user__phone

                        &__inner {
                            display: flex;
                            flex-direction: column;
                            align-items: flex-start;
                            justify-content: center;
                            margin-left: 20px;
                        } //.widget--profile .user__inner
                    }
                }
            } //.widget--profile .user--inline-mobile
        } //.widget--profile .user
    } //.widget--profile, .widget--table

    &--profile {
        @include xs-block() {
            margin: 0 0 10px;
        }

        .widget {
            &__close {
                position: absolute;
                top: 22px;
                right: 2px;
            } //.widget--profile .widget__close

            &__text {
                margin-bottom: 0;
            } //.widget--profile .widget__text
        }

        .progress-block {
            margin-bottom: 25px;
        } //.widget--profile .progress-block

        .btn-link {
            color: $color-text;

            &.contacts-btn {
                font-size: 16px;
                @include i5-block() {
                    font-size: 13px;
                }
            } // .btn-link.contacts-btn

            .icon {
                fill: $color-text;
            }
        }

        .button-box--center {
            .btn {
                font-size: 16px;
            }
        }
    } //.widget--profile

    &--table {
        .subscribers__tbody .user__name {
            font-size: 15px;
        }
    } //.widget--table

    &--transparent {
        border: 0;
        background-color: transparent;

        @include xs-block() {
            border: 0;
        }

        .widget {
            &__header {
                padding-right: 0;
                padding-left: 0;
            }

            &__body {
                padding-right: 0;
                padding-left: 0;
            }

            &__add-btn {
                background-color: #fff;
            }
        }
    } //.widget--transparent

    &--transparent-mobile {
        @include xs-block() {
            border: 0;
            background-color: transparent;
        }

        .widget {
            &__add-btn {
                background-color: #fff;
            }

            &__header {
                @include xs-block() {
                    padding: 10px 0;
                }
            }

            &__body {
                @include xs-block() {
                    padding: 0;
                }

                &--scrolled {
                    border-top: 0;
                    border-bottom: 0;
                }
            }
        }
    } //.widget--transparent-mobile

    &--top-no-offset {
        margin-top: 0 !important;
    } //.widget--top-no-offset

    &--form {
        &.is-success {
            .form-success {
                @include activeSuccess;
            } //.widget--form.is-success .form-success
        } //.widget--form.is-success
    } //.widget--form

    &--btn-only {
        min-height: auto;

        .widget__add-btn {
            @include xs-block() {
                margin-top: 0;
            }
        } //.widget--form .widget__add-btn
    } //.widget--form

    &--no-offset {
        .widget__body {
            padding: 0;
        } //.widget--no-offset .widget__body
    } //.widget--no-offset

    &--header-transform {
        @include xs-block() {
            border-top: 0;
        }

        .widget {
            &__header {
                @include border(bottom);

                @include xs-block() {
                    padding: 20px 15px 10px;
                    font-weight: 400;
                }
            }

            &__title {
                color: $gray;

                @include xs-block() {
                    font-size: 12px;
                    letter-spacing: 0.5px;
                    text-transform: uppercase;
                }
            }
        }
    } //.widget--header-transform

    .info-block {
        margin-left: auto;
        padding: 8px;

        &::before {
            position: relative;
            top: 1px;
        }

        &--right {
            &.is-active,
            &:hover {
                .info-block__dropdown {
                    top: 24px;
                }
            }

            .info-block {
                &__dropdown {
                    top: 33px;
                    right: -5px;
                }
            }
        }

        .bb-dropdown__list {
            left: -2px;
        }

        &[data-dropdown-position^='right'] {
            .bb-dropdown__list {
                right: -2px;
                left: auto;
            }
        }
    } //.widget .info-block

    .list {
        &.list-color--success {
            .list {
                &__icon {
                    color: $color-success;
                }
            }
        }
    } //.widget .list
} //.widget

.widget-box {
    padding: 10px 15px;

    &__title {
        color: $gray--light;
        font-size: 15px;

        &--dark {
            color: $gray;
        } //.widget-box__title--dark
    } //.widget-box__title

    &__text {
        font-weight: 500;

        .status__text {
            color: $color-text;

            font-size: 14px;
        }
    } //.widget-box__text
} //.widget-box

.h50 {
    display: flex;
    flex-direction: column;

    .widget {
        @include min(xs) {
            flex-basis: calc(50% - 30px);
        }
    }
}

.modal {
    .widget {
        @include xs-block() {
            margin: 15px 0;
        }

        &:last-child {
            @include xs-block() {
                margin-bottom: 0;
            }
        }
    } //.modal .widget
} //.modal

.insta-log {
    display: flex;
    align-items: flex-start;
    justify-content: center;

    &__item {
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
        padding: 5px;

        &:not(:first-child) {
            margin-left: 10px;
        }
    } // .insta-log__item

    &__sum {
        font-size: 18px;
        font-weight: 600;
    } // .insta-log__sum

    &__text {
        display: flex;
        justify-content: space-between;
        width: 100%;
        font-size: 15px;
        font-weight: 300;
        text-align: center;
    } // .insta-log__text

    &__status {
        font-weight: 500;

        .icon {
            display: none;
        }

        &.is-top,
        &.is-down {
            .icon {
                display: inline-block;
                width: 10px;
                height: 10px;
            }
        }

        &.is-top {
            color: $color-success;

            .icon {
                fill: $color-success;
            }
        }

        &.is-down {
            color: $color-danger;

            .icon {
                transform: rotateZ(180deg);
                fill: $color-danger;
            }
        }
    } // .insta-log__status

    &--column {
        flex-direction: column;
    } // .insta-log--column
} //.insta-log

.subscribers {
    width: 100%;

    &__thead,
    &__tbody {
        & > table {
            width: 100%;
            table-layout: fixed;
            border-collapse: collapse;
        }

        td {
            width: 75px;
            padding: 10px;

            &:last-child {
                width: 120px;
            }

            &:first-child {
                width: 100%;
                padding-left: 15px;

                @include xs-block() {
                    padding-left: 10px;
                }
            }

            &:not(:first-child) {
                text-align: right;

                .icon {
                    margin-left: auto;
                }
            }
        }
    }

    &__thead {
        td {
            font-size: 15px;
        } // .subscribers__thead td
    } // .subscribers__thead

    &__tbody {
        max-height: 300px;
        overflow-x: hidden;
        overflow-y: auto;

        .user__name {
            max-width: 25ch;
            font-size: 15px;
            @include text-overflow;

            @include md-block() {
                max-width: 16ch;
            }
        } // .subscribers__tbody .user__name
    } // .subscribers__tbody
} //.subscribers

.figure {
    &__img {
        width: 100%;
    } // .figure__img

    &__figcaption {
        display: flex;
        align-items: center;
        justify-content: center;
        padding: 9px;
        @include i5-block() {
            padding: 2px;
        }

        span {
            margin-left: 10px;
        } // .figure__figcaption span
    } // .figure__figcaption
} //.figure
