$o: 10px;

@mixin mobileAlbumItemResize {
    &__inner {
        @include xs-block() {
            @include u-row-flex($o);
        }

        .album-item {
            @include xs-block() {
                margin-bottom: $o;
                @include u-col($o);
                @include u-size(6, 12, $o);
            }
        }
    }
}

.album {
    @include mobileAlbumItemResize;

    @include max(xs) {
        padding: 10px 10px 0;
    }

    &__inner {
        @include row-flex;

        &.has-offset {
            margin-right: 0;
            margin-left: 0;
        }

        .album-item {
            margin-bottom: $gutter;
            @include col;
            @include size(4);
            @include size-sm(6);
        }
    } //.album__inner

    &__empty {
        margin-top: 100px;
    } //.album__empty

    &--full-screen {
        @include mobileAlbumItemResize;

        .album {
            &__inner {
                @include xs-block() {
                    @include u-row-flex($o);
                }

                .album-item {
                    @include size(2);
                    @include size-lg(3);
                    @include size-sm(4);

                    @include xs-block() {
                        margin-bottom: $o;
                        @include u-col($o);
                        @include u-size(6, 12, $o);
                    }

                    @include i7-block() {
                        &__img {
                            border-radius: 0;
                        }
                    }

                    &__info {
                        @include i5-block() {
                            display: none;
                        }
                    }

                    &--icon {
                        @include i7-block() {
                            border-top: 1px solid $color-border--lightest;
                        }
                    }
                } //.album--full-scree .album__inner .album-item
            } //.album--full-scree .album__inner
        }
    } //.album--full-scree
} //.album

.album-wrap {
    &.column-2 {
        @include row-flex;

        .album-item {
            @include col;
            @include size(6);
        }
    } //.album-wrap.column-2
} //.album-wrap

.album-item {
    display: block;
    border-radius: $border-radius;
    color: $color-text;
    cursor: pointer;
    @include box;
    @include tr;
    @include nosel;

    &:hover {
        box-shadow: 1px 1px 10px rgba(255, 130, 114, 0.6);
        color: $color-text;
    }

    &__img {
        position: relative;
        overflow: hidden;
        border-top-left-radius: $border-radius;
        border-top-right-radius: $border-radius;

        @include sectionOverlay;

        &::before {
            display: none;
        }
    } //.album-item__img

    &__desc {
        position: relative;
        padding: $gutter / 2;
    } //.album-item__desc

    &__title,
    &__edit {
        margin-right: 30px;
    }

    &__title {
        margin-bottom: 2px;
        font-size: 15px;
        font-weight: 500;
        @include text-overflow;

        @include xs-block() {
            margin-right: 0;
        }
    } //.album-item__title

    &__edit {
        color: #9a9a9a;
        font-size: 13px;
        font-weight: 300;

        @include xs-block() {
            margin-right: 0;
        }

        span {
            font-size: 12px;
            font-weight: 500;
            font-weight: 500;
        }
    } //.album-item__edit

    &__info {
        margin-top: 10px;
        @include flexCenter;
    } //.album-item__info

    &__btn {
        &--edit {
            position: absolute;
            top: 50%;
            left: 50%;
            opacity: 0;
            display: flex;
            padding: 4px 13px;
            transform: translate(-49%, 0);
            transition: all 0.4s ease-in-out;
            border: 1px solid rgba(#fff, 0.6);
            font-size: 14px;
            font-weight: 300;

            i {
                position: relative;
                top: 3px;
                margin-right: 7px;
            }
        } //.album-item__btn--edit

        &--dell {
            position: absolute;
            top: 0;
            right: 0;
            opacity: 0;
            padding: 5px 10px;
            border: 0;
            outline: none;
            background-color: transparent;
            color: #fff;

            &:hover {
                opacity: 1;
                color: #fff;
            }
        } //.album-item__btn--dell
    } //.album-item__btn

    &__preview {
        margin-right: auto;
    } //.album-item__preview

    &__count {
        position: relative;
        padding: 2px 10px;
        border: 1px solid $color-border--lighten;
        border-radius: 15px;
        color: #9a9a9a;
        font-size: 12px;
        font-weight: 600;
        text-align: center;
        @include flexCenter;

        &::before {
            content: '\f03e';
            left: 8px;
            display: none;
            font-size: 13px;
            @include fa('regular');
            @include centre('y');
        }

        .icon {
            width: 11px;
            height: 11px;
            margin-right: 5px;
        }
    } //.album-item__count

    &__info-block {
        position: absolute;
        top: 6px;
        right: 6px;
        z-index: z(fixed);

        .btn-icon {
            &:hover {
                i {
                    color: $gray--darkest;
                }
            }

            i {
                @include tr;
            }
        }
    } //.album-item__info-block

    &--icon {
        .album-item {
            &__desc {
                border-top: 1px solid $color-border--lightest;
            }

            &__info-block {
                right: 0;
                @include centre('y');

                @include max(xs) {
                    display: none;
                }
            }
        }
    } //.album-item--icon

    &.no-shadow {
        box-shadow: none;

        &:hover {
            box-shadow: none;
        }
    }
} //.album-item

.preview-list {
    @include flexCenter;

    &__item {
        display: block;
        width: 28px;
        height: 28px;
        overflow: hidden;
        border-radius: $border-radius * 2;
        @include cover;

        & + & {
            margin-left: 5px;
        }
    } //.preview-list__item

    &--big {
        .preview-list {
            &__item {
                width: 32px;
                height: 32px;
            }
        }
    } //.preview--big
} //.preview-list
