$select-height: $base-btn-height;
$select-line-height: $base-btn-height - 1;
$select-border-radius: $border-radius !default;
$select-border-color: $color-border--light !default;

.bb-select {
    position: relative;
    cursor: pointer;
    @include iconAngle;

    @include i5-block() {
        width: 132px;
    }

    &__reset {
        right: 0;
        z-index: z(fixed);
        display: block;
        width: 30px;
        height: 40px;
        border: 0;
        background-color: transparent;
        cursor: pointer;
        @include flexCenter;
        @include centre('y');

        &:hover {
            background-color: $gray--light;
        }

        span {
            display: flex;
            align-items: center;
            justify-content: center;
            width: 18px;
            height: 18px;
            margin: auto;
            border-radius: 50%;
            background-color: $gray--light;
            background-color: lighten($gray--light, 13%);
        }

        i {
            font-size: 18px;
        }

        svg {
            width: 8px;
            height: 8px;
            margin: auto;
            fill: #fff;
        }
    } //.bb-select__reset

    &__icon {
        width: 24px;
        height: 24px;
        @include flexCenter;
    } //.bb-select__icon

    &__ico {
        width: 24px;
        height: 24px;
        fill: $gray--dark;
        pointer-events: none;
    } //.bb-select__ico

    select {
        width: 100%;
        padding: 11px 40px 10px 10px;
        color: $gray--darkest;

        font-size: 16px;
        cursor: pointer;
        appearance: none;
        @extend %styleInput;

        &:focus {
            border-color: $input-color-border--hover;
            box-shadow: none;
        }

        &.has-placeholder {
            color: #999;
            font-style: italic;
            font-weight: 400;

            option {
                color: $color-text;
            }
        }
    } //.bb-select select

    option {
        padding: 0 10px;
    } //.bb-select option

    &--icon {
        position: relative;
        display: none;
        align-items: center;
        width: 100%;
        height: 40px;
        background: transparent;
        line-height: 40px;

        &::before {
            display: none;
        }

        & + & {
            margin-left: 10px;
        }

        .bb-select {
            &__icon {
                z-index: z(fixed);
                @include centre();

                .icon-navigation {
                    width: 16px;
                    height: 16px;
                }
            } //.bb-select--icon .bb-select__icon
        }

        select,
        button {
            position: relative;
            z-index: z(fixed) + 1;
            width: auto;
            width: 100%;
            height: 0;
            padding: 38px 0 0;
            background: transparent;

            &:hover,
            &:focus {
                border-color: $input-color-border--hover;
            }
        }

        button {
            padding: 38px 25px 0;
            border-radius: $border-radius;
            @extend %styleInput;
        }
    } //.bb-select--icon

    &--round {
        select {
            padding: 9px 40px 8px 25px;
            border: 0;
            border-radius: 20px;
            @include tr;
        } //.bb-select--round select

        option {
            background-color: #fff;
            color: $gray;
        } //.bb-select--round option

        &.red {
            &::before {
                background-image: $base64-icon-angle--red;
            }

            select {
                background-color: rgba($color-danger, 0.1);
                color: $color-danger;
            }
        } //.bb-select--round.red

        &.green {
            &::before {
                background-image: $base64-icon-angle--green;
            }

            select {
                background-color: rgba($color-success, 0.2);
                color: $color-success;
            }
        } //.bb-select--round.green

        &.gray {
            &::before {
                background-image: $base64-icon-angle;
            }

            select {
                background-color: rgba($gray--light, 0.2);
                color: $gray--light;
            }
        } //.bb-select--round.gray

        &.yellow {
            &::before {
                background-image: $base64-icon-angle--yellow;
            }

            select {
                background-color: rgba($color-warning, 0.2);
                color: $color-warning;
            }
        }

        &.blue {
            &::before {
                background-image: $base64-icon-angle--blue;
            }

            select {
                background-color: rgba($color-info, 0.2);
                color: $color-info;
            }
        }

        .select2-container--default.select2-container--open.select2-container--below
            .select2-selection--multiple,
        .select2-container--default.select2-container--open.select2-container--below
            .select2-selection--single {
            border-radius: 20px;
            border-bottom-left-radius: 20px;
            border-bottom-right-radius: 20px;
        } //.bb-select--round .select2-container--default.select2-container--open.select2-container--below .select2-selection--multiple, .select2-container--default.select2-container--open.select2-container--below .select2-selection--single

        .select2-container--default
            .select2-selection--single
            .select2-selection__arrow
            b {
            top: 14px;
        } //.bb-select--round .select2-container--default .select2-selection--single .select2-selection__arrow b

        .select2-container--default.select2-container--open
            .select2-selection--multiple,
        .select2-container--default.select2-container--open
            .select2-selection--single,
        .select2-container--open .select2-dropdown--below {
            box-shadow: none;
        } //.bb-select--round .select2-container--default.select2-container--open .select2-selection--multiple, .select2-container--default.select2-container--open .select2-selection--single, .select2-container--open .select2-dropdown--below

        .select2-container--open .select2-dropdown--below {
            border-top: 1px solid $color-border--light;
        } //.bb-select--round .select2-container--open .select2-dropdown--below
    } //.bb-select--round

    &.bb-input--transform {
        .bb-select {
            &__reset {
                right: -3px;
            } //.bb-select.bb-input--transform .bb-select__reset
        }
    } //.bb-select.bb-input--transform
} //.bb-select

.select-services {
    .select2-results {
        &__option {
            &[aria-disabled='true'] {
                background-color: $gray--lightest;
                color: $color-text--darkest !important;
                font-size: 12px;
                font-weight: 300;
                text-transform: uppercase;

                .custom-select__results {
                    span {
                        margin-right: 13px;

                        &:nth-child(1) {
                            margin-right: 0;
                        }

                        &:nth-child(2) {
                            margin-right: 12px;
                        }

                        &:nth-child(3) {
                            margin-right: 3px;
                        }
                    }
                }
            }
        } //.select-services .select2-results__option
    } //.select-services .select2-results
} //.select-services

.select-color {
    &__ball {
        display: block;
        width: 20px;
        height: 20px;
        border-radius: 50%;

        @each $theme, $color in $text-colors {
            &.#{$theme} {
                background-color: $color;
            }
        }

        & + p {
            padding-left: 9px;
        }
    } //.select-color__ball

    &__line {
        left: -1px;
        width: 3px;
        height: 90%;
        @include centre('y');

        @each $theme, $color in $text-colors {
            &.#{$theme} {
                background-color: $color;
            }
        }
    } //.select-color__line

    &__marker {
        flex-shrink: 0;
        width: 8px;
        height: 8px;
        margin-right: 10px;
        border-radius: 50%;

        @each $theme, $color in $text-colors {
            &.#{$theme} {
                background-color: $color;
            }
        }
    } //.select-color__marker

    &__item {
        width: 100%;
        height: 100%;
        @include flexCenter;

        p {
            max-width: 240px;
            @include text-overflow;

            @include xs-block() {
                max-width: 100%;
            }
        } //.select-color__item p
    } //.select-color__item

    &--palette {
        width: 110px;

        .select2-dropdown {
            width: 163px !important;
            padding: 5px;
        }

        .select2-results {
            &__options {
                display: flex;
                flex-wrap: wrap;
            }

            &__option {
                padding: 5px;
            }

            &__option[aria-selected='true']::after {
                right: 9px !important;
                color: #fff !important;
                font-size: 11px !important;
                font-weight: 400 !important;
            }

            &__option--highlighted[aria-selected] {
                background-color: transparent;
            }
        }
    } //.select-color--palette
} //.select-color

.custom-select {
    position: relative;

    @include tr;

    &__select {
        position: relative;
        width: 100%;
        height: 40px;

        &::before {
            content: '\f107';
            position: absolute;
            top: 7px;
            right: 10px;
            font-family: 'Font Awesome 5 Pro';
            font-size: 17px;
            font-weight: 300;
        }

        &:hover {
            cursor: pointer;
        }
    } //.custom-select__main

    &__input {
        width: 100%;

        &:hover {
            cursor: pointer;
        }
    } //.custom-select__input

    &__inner {
        width: 100%;
        height: 100%;
        padding-right: 28px;
        padding-left: 10px;

        @include flexCenter;
    } //.custom-select__inner

    &__arrow {
        color: #000;
        font-size: 18px;
    } //.custom-select__arrow

    &.is-active {
        .custom-select-dropdown {
            visibility: visible;
            opacity: 1;
            transform: translate3d(0, 5px, 0);
            box-shadow: $bxs;
            @include tr;

            @include max(md) {
                transform: translate3d(-50%, -50%, 0);
                box-shadow: $bxs-box;
            }
        } //.custom-select.is-active .custom-select-dropdown

        .custom-select {
            &__input {
                border: 1px solid $color-border;
                box-shadow: 0 1px 5px rgb(241, 241, 241);
            } //.custom-select__input

            &__select {
                &::before {
                    content: '\f106';
                }
            } //.custom-select__select
        }
    } //.custom-select.is-active
} //.custom-select

.custom-select-dropdown {
    position: absolute;
    left: 0;
    z-index: z(modal);
    visibility: hidden;
    opacity: 0;
    width: 100%;
    overflow: hidden;
    transform: translate3d(0, -30px, 0);
    border-radius: $border-radius;
    background-color: #fff;

    @include md-block() {
        position: fixed;
        top: 50%;
        left: 50%;
        width: 100%;
        max-width: 60vw;
        height: 100%;
        max-height: 90vh;
        transform: translate3d(-50%, -70%, 0);
        @include tr;
    }

    @include xs-block() {
        max-width: 100vw;
        max-height: 100vh;
        transform: translate3d(-50%, 70%, 0);
        border: 0;
        border-radius: 0;
    }

    &__overlay {
        position: fixed;
        top: 0;
        left: 0;
        z-index: z(overlay);
        display: none;
        width: 100%;
        height: 100%;
        background-color: transparent;

        @include max(md) {
            background-color: rgba(47, 47, 47, 0.35);
        }
    } //.custom-select-dropdown__overlay

    &__block {
        position: relative;
    } //.custom-select-dropdown__block

    &__title {
        padding: 15px 23px 5px;
        font-size: 16px;
        font-weight: 500;
    } //.custom-select-dropdown__title

    &__header {
        position: relative;
        padding: 15px 20px;
        border-bottom: 1px solid $color-border--lightest;
        font-size: 24px;
        font-weight: 300;
        text-align: center;

        @include xs-block() {
            height: 50px;
            padding: 0 10px;

            font-size: 18px;
            line-height: 49px;
        }
    } //.custom-select-dropdown__top

    &__search {
        position: relative;
        padding: 20px;
        border-bottom: 1px solid $color-border--lightest;
        @include tr;

        @include xs-block() {
            padding: 15px 10px;
        }

        input {
            width: 100%;
        } //.custom-select-dropdown__search input

        .bb-input__icon {
            right: 20px;
            bottom: 26px;

            @include xs-block() {
                right: 10px;
                bottom: 7px;
            }
        } //.custom-select-dropdown__search .bb-input__icon

        &.is-fixed {
            box-shadow: $bxs-box;
        } //.custom-select-dropdown__search.is-fixed
    } //.custom-select-dropdown__search

    &__close {
        right: 15px;
        @include centre('y');

        @include xs-block() {
            right: 5px;
        }

        .icon {
            width: 22px;
            height: 22px;

            @include xs-block() {
                width: 20px;
                height: 20px;
            }
        }
    } //.custom-select-dropdown__close

    &__scroll {
        height: 350px;
        overflow-y: auto;

        @include md-block() {
            height: calc(100% - 148px);
        }

        @include xs-block() {
            height: calc(100% - 120px);
        }
    } //.custom-select-dropdown__scroll

    &__input {
        width: 100%;
    } //.custom-select-dropdown__input

    &__left {
        margin-right: 15px;
        font-size: 15px;
        @include text-overflow;
    } //.custom-select-dropdown__left

    &__right {
        position: relative;
        margin-left: auto;
        padding-right: 15px;
        font-weight: 500;

        &::after {
            content: '\f158';
            right: 0;
            margin-left: 3px;
            color: $color-icon;
            font-size: 13px;
            line-height: 1;
            @include fa('light');
            @include centre('y');
        }
    } //.custom-select-dropdown__right

    &__option {
        position: relative;
        padding: 20px;
        border-right: 0;
        border-bottom: 1px solid $color-border--lightest;
        border-left: 0;
        @include flexColumn;

        &:hover {
            background-color: $color-gray--hover;
            cursor: pointer;
        }

        & + & {
            border-top: 0;
        }

        &_top {
            @include flex;
        } //.dropdown__option_top

        &_bottom {
            color: $color-text--light;
            font-size: 14px;
            font-style: italic;
        } //.dropdown__option_bottom
    } //.custom-select-dropdown__option
} //.custom-select-dropdown

.custom-multi-select {
    position: relative;
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    justify-content: flex-start;
    width: 100%;
    margin: 10px 0;
    border: 1px solid $input-color-border;
    border-radius: $border-radius;

    &:hover {
        border: 1px solid $input-color-border--hover;
    } // .custom-multi-select:hover

    &__wrap {
        min-width: 40px;
        margin-bottom: 25px;
    } // .custom-multi-select__wrap

    &__input {
        display: flex;
        flex-wrap: wrap;
        width: 100%;
        padding: 2px 5px;
        color: $color-text--light;
        font-style: italic;
        font-weight: 300;
        cursor: pointer;
    } // .custom-multi-select__input

    &__text {
        width: 100%;
        padding: 5px;

        &:not(:only-child) {
            display: none;
        }
    } // .custom-multi-select__text

    &__search {
        display: inline-block;
        width: 100%;
        height: auto;
        padding: 6px 25px 9px 10px;
        border: 0;

        & ~ .custom-multi-select__input {
            .custom-multi-select__text {
                display: none;
            }
        }
    } // .custom-multi-select__search

    &__emit {
        position: absolute;
        width: 1px;
        height: 1px;
        margin: -1px;
        padding: 0;
        overflow: hidden;
        clip: rect(0 0 0 0);
        border: 0;
        white-space: nowrap;
        clip-path: inset(50%);
    }

    &__list {
        position: absolute;
        top: 99%;
        left: -1px;
        z-index: z(dropdown);
        display: none;
        width: calc(100% + 2px);
        max-height: 40vh;
        overflow-x: hidden;
        border: 1px solid $input-color-border;
        border-radius: 0 0 $border-radius $border-radius;
        background-color: #fff;
        box-shadow: $input-shadow--hover;
        @include scroll;
    } // .custom-multi-select__list

    &__item {
        position: relative;
        display: flex;
        justify-content: center;
        width: 100%;
        min-height: 40px;
        border-bottom: 1px solid $input-color-border;

        &:hover {
            background-color: $color-bg--hover;
        }

        &:last-child {
            border-bottom: 0;
        }

        .bb-checkbox {
            position: static;
            display: flex;
            justify-content: center;
            width: 100%;
            padding-left: 0;

            &:hover {
                .bb-checkbox__box {
                    border: 0;
                }
            } //.custom-multi-select__item .bb-checkbox:hover

            &__title {
                // font-weight: 500;
                font-size: 15px;
                text-align: center;
            } //.custom-multi-select__item .bb-checkbox__title

            &__box {
                right: 0;
                left: auto;
                margin-right: 0;
                border: 0;
                @include centre('y');

                &::before {
                    content: '';
                    top: 3px;
                } //.custom-multi-select__item .bb-checkbox__box::before
            } //.custom-multi-select__item .bb-checkbox__box

            input:not(:checked) ~ .bb-checkbox {
                &__box {
                    border: none;

                    &::before {
                        display: none;
                        // display: block !important;
                    } //.custom-multi-select__item .bb-checkbox__box::before
                }

                &__title {
                    color: $gray--light;
                } //.custom-multi-select__item .bb-checkbox__box::before
            }

            input:checked ~ .bb-checkbox {
                &__title {
                    color: $gray;
                } //.custom-multi-select__item .bb-checkbox input:checked~.bb-checkbox__title

                &__box {
                    border: 0;
                    background-color: transparent;

                    &::before {
                        content: '';
                        display: block !important;
                        border-color: $gray--light;
                        color: $gray--light;
                    } //.custom-multi-select__item .bb-checkbox input:checked~.bb-checkbox__box::before
                } //.custom-multi-select__item .bb-checkbox input:checked~.bb-checkbox__box
            }

            // &.is-checked {
            //     .bb-checkbox {
            //         &__title {
            //             color: $gray--light;
            //         }

            //         &__box {
            //             &::before {
            //                 content: '';
            //                 top: 3px;
            //                 display: block !important;
            //                 border-color: $gray--lighten;
            //                 color: $gray--lighten;
            //             } //.custom-multi-select__item .bb-checkbox__box::before
            //         }
            //     }
            // }
        } //.custom-multi-select__item .bb-checkbox
    } // .custom-multi-select__item

    &__time-box {
        display: flex;
        align-items: center;
        justify-content: space-between;
        margin: 3px;
        padding: 3px 5px;
        border-radius: $border-radius;
        background-color: $gray--lighten;
        font-style: normal;

        &[data-tag-color] {
            padding-left: 12px;
            border-radius: 15px;
        }

        &[data-tag-color='blue'] {
            background-color: lighten($color-info, 30%);

            .custom-multi-select__time {
                color: $color-info;
            }

            .custom-multi-select__btn-remove {
                background-color: $color-info;
            }
        }

        &[data-tag-color='yellow'] {
            background-color: lighten($color-warning, 40%);

            .custom-multi-select__time {
                color: $color-warning;
            }

            .custom-multi-select__btn-remove {
                background-color: $color-warning;
            }
        }

        &[data-tag-color='green'] {
            background-color: lighten($color-success, 40%);

            .custom-multi-select__time {
                color: $color-success;
            }

            .custom-multi-select__btn-remove {
                background-color: $color-success;
            }
        }
    } // .custom-multi-select__time-box

    &__time {
        color: $color-text;

        font-size: 15px;
    } // .custom-multi-select__time

    &__btn-remove {
        display: flex;
        align-items: center;
        justify-content: center;
        width: 16px;
        height: 16px;
        margin-left: 10px;
        padding: 0;
        border: 0;
        border-radius: 50%;
        background-color: $gray--light;

        &:hover {
            background-color: darken($gray--light, 10%);
        } // .custom-multi-select__btn-remove:hover

        &:focus,
        &:active {
            outline: none;
        } // .custom-multi-select__btn-remove:focus
        // .custom-multi-select__btn-remove:active
    } // .custom-multi-select__btn-remove

    &__remove {
        width: 8px;
        height: 8px;
        fill: #fff;
    } // .custom-multi-select__remove

    &.is-invalid {
        .bb-input-tip {
            display: block;
            border-color: rgba(247, 73, 105, 0.75);
            background: #fbebee;
            color: $color-danger;

            &__arrow-up {
                &::before {
                    border-color: rgba(247, 73, 105, 0.75);
                    background: #fbebee;
                }
            }
        }

        &.is-open {
            .bb-input-tip {
                visibility: hidden;
                opacity: 0;
            }
        }
    }

    &.is-open {
        border-radius: $border-radius $border-radius 0 0;
        box-shadow: $input-shadow--hover;

        .custom-multi-select__list {
            display: block;
        } // .custom-multi-select.is-open .custom-multi-select__list
    } // .custom-multi-select.is-open

    &--title {
        margin-top: 26px;
    } // .custom-multi-select.custom-multi-select--title
} // .custom-multi-select

.select-box {
    @include row-flex;

    &__item {
        display: flex;
        flex-direction: column;

        @include col;
        @include size(6);
        @include size-xs(12);

        @include xs-block() {
            & + .select-box__item {
                margin-top: 20px;
            }
        }

        .bb-select {
            width: 100%;
        } //.select-box__item .bb-select

        &--small {
            @include size-xs(6);
        } //.select-box__item--small
    } //.select-box__item
} //.select-box

.city-select {
    padding-left: 15px;
    @include flexCenter;

    &__title {
        position: relative;
        margin-left: 10px;
        border-bottom: 1px dashed $color-border;
        font-size: 13px;
        cursor: pointer;

        &::after {
            content: '';
        }

        &:hover {
            color: $gray--dark;
        }
    } //.city-select__title

    &__icon {
        left: -20px;
        width: 12px;
        height: 12px;
        fill: $gray;
        color: $gray;
        font-size: 13px;
        @include centre('y');
    } //.city-select__icon

    &__list {
        left: -8px;
        max-height: 250px;
        padding: 0;
        border: 0;
    } //.city-select__list

    &__search {
        padding: 10px;
        border-radius: $border-radius $border-radius 0 0;
        background-color: $color-bg--lighten;
        @include border(bottom);

        input {
            padding: 5px 10px;
        }
    } //.city-select__search

    &__item {
        padding: 5px 10px;
        font-size: 14px;
        cursor: pointer;

        &:hover {
            background-color: $color-bg--lighten;
            color: #000;
        }
    } //.city-select__item
} //.city-select

.move-select {
    background-color: #fff;
    @include tr;
    @include flexColumn;

    &__search {
        position: relative;
        @include iconAngle($right: 0, $direction: right);
    } //.move-select__search

    &__field {
        width: 100%;
        height: 37px;
        padding: 0 20px 0 0;
        border-top: 0;
        border-right: 0;
        border-bottom: 1px solid $color-border--light;
        border-left: 0;
        border-radius: 0;
        box-shadow: none;
        font-size: 16px;
        line-height: 1;
        @include text-overflow;

        @include placeholder {
            padding: 0;
        }

        &:focus,
        &:hover {
            border-top: 0;
            border-right: 0;
            border-left: 0;
            border-radius: 0;
            box-shadow: none;
        }
    } //.move-select__field

    &__icon {
        display: none;
        align-items: center;
        justify-content: center;
        width: 50px;
        height: 50px;
        padding: 0;
        border: 0;
        background-color: transparent;
        color: $color-icon;
        @include centre('y');

        &--back {
            left: 0;

            .icon.icon-arrow-long--left {
                width: 15px !important;
                height: 15px !important;
            }
        } //.move-select__icon--back

        &--clear {
            right: 0;

            .icon {
                width: 13px;
                height: 13px;
            }
        } //.move-select__icon--clear

        .icon {
            position: static;
            transform: translate(0);
        }
    } //.move-select__icon

    &__body {
        display: none;
        flex: 1;
        flex-direction: column;
        background-color: #fff;
        @include scroll;
    } //.move-select__body

    &__footer {
        display: none;
        padding: 20px;
        @include border(top);

        @include xs-block() {
            padding: 10px;
        }

        .btn {
            width: 100%;
            min-width: 180px;
            height: 42px;
            margin: auto;
            padding: 0 15px;
            line-height: 42px;

            @include xs-block() {
                padding: 0 15px;
            }
        } //.move-select__footer .btn
    } //.move-select__footer

    &__results {
        height: 100%;
        background-color: #fff;
        cursor: pointer;
        @include flexColumn;
    } //.move-select__result

    &__result {
        padding: 7px 10px;

        & + & {
            @include border(top);
        }

        &.is-selected {
            background-color: $color-bg--lighten;
        }

        .user {
            &__img {
                width: 40px;
                height: 40px;
            }
        }
    } //.move-select__result

    &__empty {
        margin: auto;
    } //.move-select__empty

    &.is-active {
        position: fixed;
        top: 0;
        right: 0;
        bottom: 0;
        left: 0;
        z-index: z(popup);

        .move-select {
            &__body,
            &__footer {
                display: flex;
            } //.move-select__footer

            &__search {
                &::before {
                    display: none;
                }
            } //.move-select.is-active .move-select__search

            &__field {
                height: auto;
                padding: 15px 50px 15px 59px;
                border-bottom: 1px solid $color-border--lighten !important;
            } //.move-select.is-active .move-select__field

            &__icon {
                display: flex;
            } //.move-select__icon
        } //.move-select.is-active
    } //.move-select.is-active
} //.move-select

.rainbow-select {
    width: 100%;
    padding: 20px;
    border-radius: 20px;
    background-color: lighten(#a9b1b8, 20%);
    color: $gray;

    option {
        background-color: #fff;
        color: $gray;
    }
}
