.rating {
    position: relative;
    width: 75px;
    height: 13px;
    background-image: url('../files/assets/rating--gray.svg');
    background-repeat: repeat-x;
    background-size: 75px auto;
    font-weight: 500;

    &__wrapper {
        flex-grow: 1;
        @include flexCenter;

        & + .rating__wrapper {
            margin-top: 7px;
        }
    } //.rating__wrapper

    &__wrap {
        position: relative;
        @include flexCenter; //.rating__wrap_col

        &_col,
        &--column {
            flex-direction: column;
            align-items: flex-start;

            .rating {
                &__reviews {
                    margin-top: 5px;
                }
            }
        }

        .rating {
            &__reviews {
                margin-left: 5px;
            }
        }

        &--column {
            align-items: center;
            justify-content: center;

            .rating {
                margin: auto;
            }
        }
    } //.rating__wrap

    &__inner {
        position: absolute;
        top: 0;
        bottom: 0;
        left: 0;
        height: auto;

        background-image: url('../files/assets/rating.svg');
        background-repeat: no-repeat;
        background-size: 75px auto;
    } //.rating__inner

    &__data {
        justify-content: center;
        @include flexCenter;

        strong {
            margin-right: 7px;
            margin-bottom: 10px;
            color: #000;

            font-size: 32px;
            font-weight: 500;
            line-height: 1;
        }

        span {
            color: $gray--lighten;
            font-size: 18px;
            font-weight: 600;
        }
    } //.rating__data

    &__reviews {
        display: block;
        color: #000;
        font-family: roboto-300, sans-serif;
        font-size: 13px;
        font-style: italic;
    } //.rating__reviews

    &__rev-count {
        position: absolute;
        top: -1px;
        right: -30px;
        color: #000;
        font-size: 12px;
        line-height: 1;
    } //.rating__rev-count

    &__meter {
        // margin-left: 85px;
        flex-grow: 1;
    } //.rating__meter

    &--big {
        width: 130px;
        height: 25px;
        margin: auto;
        background-size: 130px auto;

        .rating {
            &__inner {
                background-size: 130px auto;
            } //.rating--big .rating__inner
        } //.rating--big .rating
    } //.rating--big
} //.rating

.star-rating {
    margin: 0;
    padding: 0;
    border: none;

    &__caption {
        margin-bottom: 0.5em;
        padding: 0;
    } //.star-rating__caption

    &__group {
        position: relative;
        width: 10em;
        height: 2em;
        background-image: url('../files/assets/star_border.svg');
        background-repeat: repeat-x;
        background-size: 2em auto;
    } //.star-rating__group

    &__star {
        position: absolute;
        top: 0;
        left: 0;
        width: 2em;
        height: 2em;
        margin: 0;
        border: none;
        background-color: transparent;
        background-repeat: repeat-x;
        background-size: 2em auto;
        font-size: inherit;
        cursor: pointer;
        appearance: none;

        &:hover,
        &:checked {
            background-image: url('../files/assets/star_fill.svg');
        }

        &:hover ~ .star-rating__star {
            background-image: url('../files/assets/star_border.svg');
        }

        &:focus {
            outline: none;
        }

        &:nth-of-type(1) {
            z-index: 5;
            width: 2em;
        }

        &:nth-of-type(2) {
            z-index: 4;
            width: 4em;
        }

        &:nth-of-type(3) {
            z-index: 3;
            width: 6em;
        }

        &:nth-of-type(4) {
            z-index: 2;
            width: 8em;
        }

        &:nth-of-type(5) {
            z-index: 1;
            width: 10em;
        }
    } //.star-rating__star
} //.star-rating
