.steps {
    @include max(xs) {
        padding: 0 10px;
    }

    &__list {
        @include flexColumn;
    } //.steps__list

    &__item {
        position: relative;
        z-index: z(default);

        &.is-active {
            background-color: $color-bg--lighten;
            color: #000;

            &::before {
                content: '';
                position: absolute;
                top: 0;
                left: 0;
                z-index: 1;
                display: block;
                width: 2px;
                height: 100%;
                background-color: #000;
            }

            .steps__link {
                color: #000;
            }
        }

        &.is-checked {
            .steps__link {
                &::before {
                    content: '\f058';
                    color: $color-success;
                }
            }
        }

        &.is-invalid {
            .steps__link {
                &::before {
                    content: '\f06a';
                    color: $color-danger;
                }
            }
        }
    } //.steps__item

    &__link {
        display: block;
        padding: 7px 10px;
        transition: initial;
        color: $color-text;
        font-size: 15px;
        font-weight: 500;

        &::before {
            content: '';
            right: 10px;
            display: block;
            font-size: 18px;
            @include fa('light');
            @include centre('y');
        }

        &:hover {
            background-color: $color-bg--lighten;
            color: #000;
        }
    } //.steps__link

    &__search {
        padding: 20px;
        border-bottom: 1px solid $color-border--lightest;

        @include xs-block() {
            padding: 15px 10px;
        }

        .search {
            width: 100%;
            max-width: 100%;
            margin: 0;

            &__group {
                flex: 1;
                margin-right: 20px;

                @include xs-block() {
                    margin-right: 10px;
                }
            }

            &__input,
            &__btn {
                background-color: $color-bg--light;
            }

            &__input {
                border: 0;
                border-radius: $border-radius;
            }

            &__btn {
                padding: 0 15px;
            }

            &__icon {
                display: block;
                fill: $color-icon;
            }
        }
    } //.steps__search

    &__back {
        outline: none;

        @include xs-block() {
            padding: 10px 7px 10px 10px;
        }

        &:hover {
            text-decoration: none;
        }

        i {
            margin-right: 0;
            font-size: 18px;
        }
    } //.steps__back

    .profile-editor {
        @include max(xs) {
            margin: 0 -10px 20px;
        }

        &__form {
            @include col;
            @include size(8);
            @include size-xs(12);

            @include xs-block() {
                order: 2;
                width: 100%;
                box-shadow: none;
                @include border(top);
                @include border(bottom);
            }

            &--small {
                .profile-editor {
                    &__inner {
                        max-width: 768px;
                        margin: auto;
                    }
                }
            }
        } //.steps .profile-editor__form

        &__info {
            @include col();
            @include size(4);
            @include size-sm(12);

            @include xs-block() {
                width: 100%;
                margin-bottom: 20px;
                border-radius: 0;
                box-shadow: none;
                @include border(top);
                @include border(bottom);
            }
        } //.steps .profile-editor__info

        &__upload {
            padding: 15px 10px 0;
        } //.steps .profile-editor__upload
    } //.steps .profile-editor

    .services {
        margin: 0;

        &__item {
            border-bottom: 1px solid $color-border--lightest;
            background-color: transparent;

            &:first-child {
