.category-item {
    position: relative;
    align-items: center;
    justify-content: center;
    min-height: 170px;
    margin-bottom: 20px;
    padding-top: 45px;
    cursor: pointer;
    @include box;
    @include flexCenter;

    @include max(i7) {
        min-height: 160px;
    }
    @include max(i5) {
        min-height: 170px;
    }

    @include col;
    @include size(3);
    @include size-md(4);
    @include size-sm(6);
    @include size-i5(12);

    &:hover {
        box-shadow: $bxs-hover;
    }

    &__wrap {
        padding: 20px 20px 0;
        @include row-flex;
    } //.category-item__wrap

    &__icon {
        top: 35px;
        width: 45px;
        height: 45px;
        @include centre('x');
    } //.lk-service__icon

    &__buttons {
        padding: 17px 20px 20px;
        border-top: 1px solid $color-border--lightest;
        text-align: center;
    }

    &.is-checked {
        &:hover {
            .bb-checkbox {
                &__dell {
                    display: block;
                }
            }
        }
    }

    .bb-checkbox {
        &__title {
            margin: auto;
        }

        &__box {
            position: absolute;
            top: 10px;
            right: 10px;
        }
    }
} //.category-item

.steps-categoty {
    .bb-checkbox {
        position: relative;
        padding: 20px 50px;

        @include max(xs) {
            padding: 20px 10px 20px 35px;
        }

        &:hover {
            .bb-checkbox {
                &__icon {
                    fill: $color-accent;
                }
            }
        }

        &__box {
            left: 20px;
            @include centre('y');

            @include max(xs) {
                left: 10px;
            }
        } //.steps-categoty .bb-checkbox__box

        &__title {
            position: relative;
            top: 1px;
        }

        &__icon {
            right: 20px;
            width: 22px;
            height: 22px;
            fill: $color-icon--darken;
            @include centre('y');

            @include max(xs) {
                right: 10px;
            }
        } //.steps-categoty .bb-checkbox__icon

        & + .bb-checkbox {
            border-top: 1px solid $color-border--lightest;
        }
    } //.steps-categoty .bb-checkbox
} //.steps-categoty
