.devider {
    display: block;
    flex-shrink: 0;
    width: 100%;
    height: 1px;
    background-color: $color-bg--light;
    font-size: 14px;
    line-height: 1.7;
    text-align: center;

    @each $theme, $color in $text-colors {
        &--#{$theme} {
            &::before,
            &::after {
                border-color: $color;
            }

            .devider {
                &__icon {
                    fill: $color;
                    color: $color;
                }
            }
        }
    }

    &--icon {
        position: relative;
        justify-content: center;
        height: auto;
        background-color: transparent;
        @include flexCenter;

        &::before,
        &::after {
            content: '';
            display: block;
            width: 100%;
            height: 1px;
            background-color: $color-icon;
        }

        .devider {
            &__icon {
                width: 120px;
                height: 40px;
                margin: $gutter * 1.5;
                fill: $color-icon;
                color: $color-icon;
                font-size: 42px;
            }
        }
    } //.devider--icon

    &--with-text {
        position: relative;
        display: table;
        height: auto;
        overflow: hidden;
        background-color: transparent;
        font-size: 14px;
        line-height: 1;
        white-space: nowrap;
        @extend %textLight;

        &::before,
        &::after {
            content: '';
            position: relative;
            top: 0.5em;
            display: table-cell;
            width: 45%;
            border-top: 1px solid $gray--light;
        }

        &::before {
            right: 1.5%;
        }

        &::after {
            left: 1.5%;
        }
    } //.devider--with-text

    &--vertical {
        position: relative;
        width: auto;
        height: 100%;
        overflow: hidden;

        &::before,
        &::after {
            content: '';
            position: relative;
            top: 0.5em;
            left: 50%;
            display: block;
            width: 1px;
            height: 100%;
            min-height: 80px;
        }

        &::before {
            top: -15%;
        }

        &::after {
            top: 15%;
        }
    } //.devider--vertical

    &--transform {
        margin: 0 50px;

        @include xs-block() {
            position: relative;
            display: table;
            width: 100%;
            margin: 30px 0;
            overflow: hidden;
            white-space: nowrap;
        }

        &::before,
        &::after {
            @include xs-block() {
                position: relative;
                top: 0.8em;
                left: auto;
                display: table-cell;
                width: 42%;
                height: initial;
                min-height: initial;
                border-top: 1px solid $input-color-border;
                background-color: transparent;
            }
        }

        &::before {
            @include xs-block() {
                left: 0;
            }
        }

        &::after {
            @include xs-block() {
                right: 0;
                left: auto;
            }
        }
    } //.devider--transform
} //.devider

.title-decorator {
    position: relative;
    z-index: 0;

    &::after {
        content: '';
        position: absolute;
        top: 50%;
        left: 50%;
        z-index: -1;
        width: 150px;
        height: 40px;
        margin-left: -75px;
        background: $base64-icon-header-decorator;
        background-repeat: no-repeat;
        background-position: 0 0;
        background-size: contain;
    }
} //.title-decorator

.badge {
    @extend %badge;

    &--medium {
        padding: 3px 7px;
        font-size: 10px;
        font-weight: 600;
        text-transform: uppercase;
    } //.badge--medium
} //.badge

.bg {
    @each $theme, $color in $button-colors {
        &--#{$theme} {
            background-color: $color;
        }
    }

    @each $theme, $color in $bg-colors {
        &--#{$theme} {
            background-color: $color;

            &.bg-hide {
                @each $screen in lg, md, sm, xs {
                    @include max(#{$screen}) {
                        &--#{$screen} {
                            @include max($screen) {
                                background-color: transparent;
                            }
                        }
                    }
                }
            }
        }
    }
} //.bg

