.section {
    position: relative;
    padding: $section-indent 0;

    @include xs-block() {
        padding: $section-indent / 5 * 4 0 $section-indent;
    }

    &__title {
        margin-bottom: 35px;
        color: $gray--darkest;
        font-size: 32px;
        font-weight: 500;
        text-align: center;

        &.title-decorator {
            margin-bottom: 50px;
        } //.section__title.title-decorator

        &--left {
            text-align: left;
        } //.section__title--left
    } //.section__title

    &__btn {
        display: block;
        margin: 30px auto 0;
        @include xs-block() {
            min-width: 80vw;
        }

        &--solo {
            max-width: max-content;
            margin: 0 auto;

            @include xs-block() {
                margin: 0 auto 20px;
            }
        } //.section__btn--solo
    } //.section__btn

    &__img {
        width: 45%;
        margin: 10px auto;
        @include xs-block() {
            order: 0;
            width: 90%;
        }
    } //.section__img

    &__inner {
        justify-content: center;
        @include flexCenter;

        @include xs-block() {
            flex-direction: column;
        }

        &--rivers {
            @include min(xs) {
                flex-direction: row-reverse;
            }

            .inform-block--align-left {
                @include min(xs) {
                    padding-left: 50px;
                }
            }
        }
    } //.section__img-bg

    &__img-bg {
        position: absolute;
        top: 45px;
        left: 135px;
        display: block;
        width: auto;
        height: 150%;
        transform: rotateZ(-30deg);

        @include md-block() {
            top: 45px;
            left: 20px;
        }
    } //.section__img-bg

    &__img-bg-2 {
        position: absolute;
        top: 65px;
        left: 410px;
        display: block;
        width: auto;
        height: 120%;
        transform: rotateZ(-30deg);
        @include md-block() {
            top: -20px;
            left: 245px;
        }
    } //.section__img-bg-2

    &__info {
        display: flex;
        align-items: center;
        justify-content: space-between;
        width: 100%;
        padding: 15px;
        border-radius: $border-radius;

        @include xs-block() {
            padding: 15px;
            border-radius: 0;
        }

        .btn {
            flex-shrink: 0;
            margin-left: 20px;
            padding: 0 22px;
        }
    } //.section__info

    &-gradient {
        position: relative;
        background: $color-bg, url('../files/icons/svg/bg-pattern-pink.svg');
        background-size: 125px;

        &::before,
        &::after {
            content: '';
            position: absolute;
            top: -22px;
            right: 0;
            left: 0;
            display: block;
            height: 32px;
            background: url('../files/icons/svg/wave-white.svg');
            background-size: cover;
        } //.section--gradient::before

        &::after {
            content: '';
            top: auto;
            bottom: -22px;
            transform: rotate(180deg);
        } //.section--gradient::after

        &--gray {
            background-color: $gray--lightest;
            background-image: url('../files/icons/svg/bg-pattern-gray.svg');
            background-size: 124px;
        } //.section--gradient--gray
    } //.section--gradient

    &--no-bg-mobile {
        @include xs-block() {
            background-color: transparent !important;
            background-image: none !important;
        }
    } //.section--p-small

    &--skew-box {
        background-color: $color-bg--lighten;

        @include xs-block() {
            margin-top: 50px;
            margin-bottom: 50px;
        }

        &::before,
        &::after {
            content: '';
            position: absolute;
            left: 0;
            width: 100%;
            height: 40px;
        }

        &::before {
            top: -40px;
            background: linear-gradient(
                to top right,
                $color-bg--lighten 50%,
                transparent 51%
            );
        }

        &::after {
            bottom: -40px;
            background: linear-gradient(
                to bottom right,
                $color-bg--lighten 50%,
                transparent 51%
            );
        }
    } //.section--skew-box

    &--p-small {
        padding: $section-indent / 5 * 2 0;

        @include xs-block() {
            padding: $section-indent / 5 0;
        }
    } //.section--p-small

    &--p-top-small {
        padding: $section-indent / 5 * 2 0 $section-indent;

        @include xs-block() {
            padding: $section-indent / 5 0;
        }
    } //.section--p-small

    &--small-offset {
        @include xs-block() {
            padding: 20px 0;

            &:first-of-type {
                padding-top: 40px;
            }

            &:last-of-type {
                padding-bottom: 40px;
            }
        }
    } //.section--small-offset

    &--dark {
        color: #fff;

        .bb-title {
            color: #fff;

            &__sub {
                color: $gray--lightest;
            }
        }
    } //.section--dark

    &--img-mobile {
        @include sm-block() {
            .section {
                &__img-bg {
                    position: absolute;
                    top: 50%;
                    left: 55px;
                    display: block;
                    width: auto;
                    height: 100%;
                }

                &__img-bg-2 {
                    position: absolute;
                    top: 205px;
                    left: 225px;
                    display: block;
                    width: auto;
                    height: 70%;
                }
            }
        }

        @include xs-block() {
            padding-top: 70%;

            .section {
                &__img-bg {
                    position: absolute;
                    top: -25%;
                    left: -50px;
                    display: block;
                    width: auto;
                    height: 70%;
                }

                &__img-bg-2 {
                    position: absolute;
                    top: -25px;
                    left: 205px;
                    display: block;
                    width: auto;
                    height: 50%;
                }
            }
        }
    } //.section--img-mobile

    &--flex {
        display: flex;
    } //.section--flex

    &--img {
        @include cover;
    } //.section--img

    &--clipped {
        overflow: hidden;
    } //.section--clipped

    &--firstscreen {
        padding: 0;
    } //.section--firstscreen

    &.no-padding {
        padding: 0;
    } //.section.no-padding

    &.pt-0 {
        padding-top: 0;
    } //.section.pt-0

    &.pt-xs-0 {
        @include xs-block() {
            padding-top: 0;
        }
    }
} //.section

