            } //.list--gallery .list__item
        }
    } //.list--gallery

    &--dot {
        .list {
            &__item {
                padding-left: 0;
            }

            &__text {
                margin-left: 0;
            }
        }
    } //.list--dot

    &--vertical-dot {
        .list__item {
            padding-left: 0;

            font-size: 16px;
        }

        .list__item + .list__item {
            margin-top: 0;

            &::before {
                content: '';
                display: block;
                width: 7px;
                height: 7px;
                margin: 7px auto;
                border-radius: 50%;
                background-color: $color-accent;
            }
        }
    } //.list--vert-dot

    &--dots {
        .list__item {
            position: relative;
            margin-bottom: 15px;
            padding-left: 10px;

            font-size: 17px;

            &::before {
                content: '';
                position: absolute;
                top: 10px;
                left: 0;
                width: 5px;
                height: 5px;
                border-radius: 50%;
                background-color: $color-accent;
            }
        } //.list--bb-dot .list__item
    } //.list--bb-dot

    &--progress {
        counter-reset: list;

        @include min(md) {
            margin-bottom: 60px;
            @include row-flex;
        }

        & > .list__item {
            position: relative;
            margin-bottom: 0;
            margin-left: 30px;
            padding-left: 40px;
            list-style-type: none;

            @include min(md) {
                margin-top: 80px;
                margin-left: 0;
                @include col;
                @include size(4);
            }

            &:not(:last-child) {
                padding-bottom: 35px;
                border-left: 2px dashed $color-accent;

                @include min(md) {
                    padding-bottom: 0;
                    border: 0;
                }
            }
        }

        .list {
            &__title {
                margin-bottom: 14px;
                font-size: 18px;
                font-weight: 600;
                font-weight: 600;

                @include min(md) {
                    margin-bottom: 20px;
                    font-size: 22px;
                }

                &::before {
                    content: counter(list);
                    position: absolute;
                    top: -5px;
                    left: -18px;
                    display: block;
                    width: 35px;
                    height: 35px;
                    padding-top: 5px;
                    border-radius: 50%;
                    background-color: $color-accent;
                    color: #fff;
                    text-align: center;
                    counter-increment: list;

                    @include min(md) {
                        top: -200px;
                        opacity: 0.07;
                        padding: 0;
                        background-color: transparent;
                        color: $color-accent;
                        font-size: 300px;
                        @include centre('x');
                    }
                }
            }
        }
    } //.list--progress

    &--square {
        .list__item {
            padding-left: 20px;

            &::before {
                content: '';
                position: absolute;
                top: 7px;
                left: 0;
                display: inline-block;
                width: 10px;
                height: 10px;
                border-radius: $border-radius;
                background-color: $color-accent;
            }

            & + .list__item {
                margin-top: 20px;
            }
        }
    } //.list--square

    &--large {
        .list__item {
            font-size: 18px;
        }
    } //.list--large

    &--dashed {
        .list {
            &__item {
                @include listIcon;

                &::before {
                    top: 9px;
                }
            }
        }
    } //.list--dashed

    &--icon {
        .list {
            &__item {
                margin-bottom: $gutter / 2;
                padding-left: 0;
                @include flexCenter;

                @include max(i5) {
                    margin: 0 0 $gutter / 2;
                }

                &:last-child {
                    margin-bottom: 0;
                }

                strong {
                    margin: 0 3px;
                }
            }

            &__icon {
                position: static;
                font-size: 20px;
            }

            &__text {
                font-size: 15px;
            }
        }
    } //.list--icon

    &--devider {
        .list {
            &__item {
                &:first-child {
                    .list__icon {
                        &::after {
                            right: -46px;
                            @include max(i5) {
                                right: -38px;
                            }
                        }
                    }
                }

                &:last-child {
                    .list__icon {
                        &::after {
                            display: none;
                        }
                    }
                }
            }

            &__icon {
                &::after {
                    content: '';
                    position: absolute;
                    top: 10px;
                    right: -41px;
                    display: block;
                    width: 70px;
                    height: 1px;
                    background-color: $color-border--lighten;

                    @include max(i5) {
                        right: -20px;
                        width: 50px;
                    }
                }
            }
        }
    } //.list--devider

    &--row {
        justify-content: center;
        @include row-flex;

        .list {
            &__item {
                align-items: center;
                @include col;
                @include size(4);
                @include flexColumn;
            }

            &__icon {
                margin-right: 0;
                margin-bottom: $gutter / 2;
            }
        }
    } //.list--row

    &--inline {
        flex-wrap: wrap;
        @include flexCenter;

        .list {
            &__item {
                margin-right: 10px;
                margin-bottom: 0;
                padding-left: 0;

                &:last-child {
                    margin-right: 0;
                }
            } //.list--inline .list__item
        } //.list--inline
    } //.list--inline

    &--color {
        position: relative;

        .list {
            &__item {
                margin: 0;
                padding: 17px 20px 17px 55px;

                font-size: 16px;
                cursor: pointer;

                &:hover {
                    background-color: $color-bg--hover;
                }

                & + .list__item {
                    border-top: 1px solid $color-border--lightest;
                }
            }

            &__text {
                margin-left: 0;
                color: $gray--darkest;
            } //.list--box .list__text

            &__icon {
                right: 15px;
                left: auto;
                width: 15px;
                height: 15px;
                fill: $gray;
                @include centre('y');
            } //.list--box .list__icon
        }

        .color-line {
            left: 15px;
            width: 23px;
            height: 23px;
            min-height: auto;
            border-radius: 50%;
            @include centre('y');
        }
    } //.list--color

    &--border {
        .list {
            &__item {
                margin-bottom: 10px;
                border: 1px solid $color-border--lightest;
                border-radius: 30px;

                &.is-active {
                    border-color: $color-bg;
                    background-color: $color-bg;

                    .list__link {
                        color: #fff;
                    }
                }
            } //.list--border list__item

            &__link {
                display: inline-block;
                padding: 5px 15px;
            } //.list--border list__link
        }
    } //.list--border

    &-bg {
        @each $theme, $color in $bg-colors {
            &--#{$theme} {
                .list {
                    &__item {
                        background-color: $color;
                    }
                }
            }
        }

        .list {
            &__item {
                border-radius: 30px;
            } //.list-bg .list__item

            &__link {
                display: inline-block;
                padding: 5px 15px;
                color: $color-text--darkest;
            } //.list-bg .list__link
        }
    } //.list-bg

    &-color {
        @each $theme, $color in $button-colors {
            &--#{$theme} {
                .list {
                    &__icon {
                        color: $color;

                        &::after {
                            background-color: $color;
                        }
                    }
                }
            }
        }
    } //.list-color
} //.list

