                    color: $gray--light;
                    cursor: pointer;

                    &:hover {
                        text-decoration: underline;
                    }
                }
            }
        }
    }
} //.category-color

.info-block {
    position: relative;
    display: inline-block;
    margin: -10px;
    padding: 10px;
    cursor: help;

    @include xs-block() {
        cursor: pointer;
    }

    &::before {
        content: '\f05a';
        display: block;
        min-width: 20px;
        min-height: 20px;
        color: $color-icon;
        font-size: 19px;
        line-height: 1;
        text-align: center;
        @include fa('light');
        @include tr;
    }

    &:hover {
        &::before {
            color: $gray--dark;
        }
    }

    &__list {
        display: block;
        margin-bottom: 10px;

        &-title {
            font-size: 14px;
            font-weight: 600;
        } //.info-block__list-title

        li {
            font-size: 14px;
            @include listIcon(9px);
        }

        input[type='file'] {
            display: none;
        }

        &.no-dash {
            padding: 0;

            li {
                padding-left: 0;

                &::before {
                    display: none;
                } //.info-block__list.no-dash li::before
            } //.info-block__list.no-dash li
        } //.info-block__list.no-dash
    } //.info-block__list

    &__item {
        position: relative;
        padding-left: 15px;
        cursor: pointer;

        @include min(sm) {
            &:not(:last-child) {
                border-bottom: 1px solid #f1f1f1;
            }
        }

        @include sm-block() {
            cursor: pointer;
        }

        &::before {
            content: '';
            position: absolute;
            top: 10px;
            left: 0;
            display: block;
            width: 8px;
            height: 2px;
            background-color: $color-accent;
        }
    } //.info-block__item

    &__link {
        display: block;
        width: 100%;
        padding: 6px 15px;
        transition: initial;
        border: 0;
        outline: none;
        background-color: transparent;
        color: $color-text;
        font-size: 14px;
        font-weight: 500;
        cursor: pointer;
        @include flexCenter;

        &:hover {
            background-color: #f9f9f9;
            color: #000;
        }

        &.text-color--red {
            color: #f67c93;
        }
    } //.info-block__link

    &__icon {
        width: 15px;
        height: 15px;
        margin-right: 10px;
        font-size: 13px;

        &.fa-trash-alt {
            margin-right: 10px;
        }
    } //.info-block__icon

    &__text {
        margin-bottom: 10px;

        &:last-child {
            margin-bottom: 0;
        }
    } //.info-block__text

    &--large {
        .bb-dropdown {
            &__list {
                max-width: 480px;
            }
        } //.info-block__text
    } //.info-block--large

    &--more {
        cursor: pointer;

        &::before {
            content: '\f142';
            color: $color-icon--darken;
            font-size: 24px;
            @include fa('regular');
        }

        .info-block {
            &__list {
                margin-bottom: 0;
            }

            &__item {
                padding-left: 0;
                cursor: pointer;

                &::before {
                    display: none;
                }
            }
        } //.info-block--more .info-block

        .bb-dropdown {
            &__list {
                padding: 0;
            } //.info-block--more .bb-dropdown__list
        }
    } //.info-block--more

    &--arrow {
        cursor: pointer;

        &::before {
            content: '\f107';
            font-size: 16px;
        }

        .info-block {
            &__list {
                margin-bottom: 0;
            }

            &__item {
                padding-left: 0;
                cursor: pointer;

                &::before {
                    display: none;
                }
            }
        } //.info-block--more .info-block

        .bb-dropdown {
            &__list {
                padding: 0;
            } //.info-block--more .bb-dropdown__list
        }
    } //.info-block--more

    &--rotate {
        &::before {
            @include rotate(90);
        }
    } //.info-block--rotate

    &--clear {
        &::before {
            display: none;
        } //.info-block--clear

        .bb-dropdown {
            &__list {
                padding: 0;

                ul {
                    margin: 0;
                }
            } //.info-block--clear .bb-dropdown__list
        } //.info-block--clear .bb-dropdown
    } //.info-block--clear
} //.info-block

//===== Практически идентичные блоки нужна унификация =====

.inform-block {
    align-items: center;
    justify-content: center;
    max-width: 65ch;
    margin-top: 20px;
    margin-bottom: 20px;
    text-align: center;
    @include flexColumn;

    @include xs-block() {
        width: 100%;
        margin-top: 0;
        margin-bottom: 0;
        padding: 20px 10px;
    }

    &__block {
        margin-bottom: $gutter;
        @include flexCenter;

        .inform-block__text {
            margin-right: $gutter;
        } //.inform-block__block .inform-block__text
    } //.inform-block__block

    &__title {
        display: block;
        margin-bottom: 0;
        color: $gray--darken;
        font-size: 26px;
        font-weight: 500;
        text-align: center;
        @include tr;

        @include xs-block() {
            font-size: 22px;
        }

        &--medium {
            font-size: 20px;
        } //.inform-block__title--medium

        &--small {
            font-size: 16px;
            font-weight: 400;

            @include xs-block() {
                font-size: 16px;
            }
        } //.inform-block__title--small
    } //.inform-block__title

    &__text {
        display: block;
        max-width: 50ch;
        margin-top: 15px;
        color: $color-text;

        font-size: 17px;

        span {
            font-weight: 500;
        }
    } //.inform-block__text

    &__btn {
        margin-top: 35px;
        @include xs-block() {
            min-width: 80vw;
        }

        &--big {
            @include xs-block() {
                padding: 3px 35px;
                font-size: 19px !important;
            }
        }
    } //.inform-block__btn

    &__icon {
        width: 70px;
        height: 70px;
        margin-bottom: 20px;
        fill: $color-icon;
        color: $color-icon;
        font-size: 100px;

        .icon {
            width: 70px;
            height: 70px;
        }

        &.icon-rating--inform.inform-block {
            width: 120px;
            height: 25px;
        }

        &--medium {
            width: 90px;
            height: 90px;
        } //.inform-block__icon--medium

        &--large {
            width: 100%;
            max-width: 180px;
            height: 100%;
            max-height: 180px;
        } //.inform-block__icon--large
    } //.inform-block__icon

    &__avatar {
        position: relative;
        z-index: 1;
        display: inline-block;
        margin-bottom: $gutter;

        &--large {
            width: 160px;
            height: 160px;
        } //.inform-block__icon--large

        &--round {
            border-radius: 50%;
        } //.inform-block__icon--round
    } //.inform-block__icon

    &__img-animate {
        position: absolute;
        transform-style: inherit;
    } //.inform-block__img-animate

    &__img {
        margin-bottom: 15px;
    } //.inform-block__img

    &--inline {
        flex-direction: row;

        .inform-block__icon {
            margin-right: $gutter;
        }
    } //.inform-block--inline

    &--centre {
        @include min(sm) {
            position: absolute;
            top: 50%;
            left: 50%;
            transform: translateY(-50%) translateX(-50%);
        }
    } //.inform-block--centre

    &--align-right {
        align-items: flex-start;
        margin-left: auto;
        text-align: left;

        @include lg-block() {
            max-width: 60ch;
        }

        @include md-block() {
            max-width: 55ch;
        }

        @include sm-block() {
            max-width: 40ch;
        }

        @include xs-block() {
            align-items: center;
            max-width: 100%;
            margin: 0;
            text-align: center;
        }
    } //.inform-block--align-right

    &--align-left {
        align-items: flex-start;
        text-align: left;

        @include lg-block() {
            max-width: 60ch;
        }

        @include md-block() {
            max-width: 55ch;
        }

        @include sm-block() {
            max-width: 40ch;
        }

        @include xs-block() {
            align-items: center;
            margin: 0;
            text-align: center;
        }

        .inform-block {
            &__title {
                text-align: left;

                @include xs-block() {
                    text-align: center;
                }
            }
        }
    } //.inform-block--align-left

    &--custom {
        @include min(sm) {
            position: relative;
            margin: 0 auto;
            transform: translateX(40%);

            .inform-block {
                &__img {
                    position: absolute;
                    top: -140%;
                    left: -60%;
                    height: 250%;

                    img {
                        width: auto;
                        height: 100%;
                    }
                } //.inform-block--custom .inform-block__img

                &__title {
                    width: 28ch;
                    text-align: left;
                }

                &__text {
                    max-width: 40ch;
                }
            }
        }
    } //.inform-block--custom

    &--link {
        padding: 0 10px;

        &:hover {
            .inform-block__title {
                color: $color-text--light;
            } //.inform-block--link:hover .inform-block__text
        } //.inform-block--link:hover
    } //.inform-block--link

    &--transform {
        @include xs-block() {
            flex-direction: row;
            justify-content: flex-start;
            padding: $gutter / 2 0;
            @include border(top);
            @include iconAngle($direction: right, $right: 0);
        }

        .inform-block {
            &__icon {
                @include min(xs) {
                    border-radius: 50%;
                    background-color: #eee;
                }
                @include xs-block() {
                    width: 50px;
                    height: 50px;
                    margin-right: $gutter;
                    margin-bottom: 0;
                }
            } //.inform-block--transform .inform-block__icon
        }
    } //.inform-block--transform

    &--full-height {
        @include xs-block() {
            flex: 1;
        }
    } //.inform-block--full-height

    .button-box {
        @include flexCenter;

        .btn {
            & + .btn {
                margin-left: $gutter;
            }

            &-link {
                padding: 13px 0;
            }
        } //.inform-block .button-box .btn

        &--column {
            .btn {
                margin: 0;

                & + .btn {
                    margin-left: 0;
                }
            }
        } //.inform-block .button-box--column
    } //.inform-block .button-box

    .widget {
        &__icon {
            &:not(.icon-plus--filled) {
                margin-bottom: 5px;
            }
        }

        &__title {
            margin-bottom: 10px;
            text-align: center;
        } //.inform-block .widget__title

        &__text {
            color: $color-text;
        } //.inform-block .widget__text
    } //.inform-block .widget
} //.inform-block

.text-block {
    display: flex;
    justify-content: space-around;

    @include xs-block() {
        flex-direction: column;
        flex-wrap: wrap;
        justify-content: flex-start;
    }

    &__item {
        align-items: center;
        justify-content: center;
        margin: 0 10px 30px;
        text-align: center;
        @include flexColumn;

        @include sm-block() {
            max-width: 320px;
        }

        @include xs-block() {
            max-width: 100%;
            margin-right: 0;
            margin-left: 0;
        }

        &:first-child {
            margin-left: 0;
        }

        &:last-child {
            margin-right: 0;
        }
    } //.text-block__item

    &__icon {
        display: flex;
        width: 119px;
        height: 119px;
        margin-bottom: 20px;

        &.h50 {
            @include xs-block() {
                height: 80px;
            }
        }

        &--border {
            border: 2px solid $color-border--lightest;
            border-radius: 50%;
        } //.text-block__icon--border

        &--white {
            .text-block {
                &__icon {
                    border-color: #fff;
                }
            }

            .icon {
                fill: #fff;
            }
        } //.text-block__icon--white

        &--medium {
            width: 80px;
            height: 80px;

            .icon {
                width: 60%;
                height: 60%;
                margin: auto;
            }
        } //.text-block__icon--medium
    } //.text-block__icon

    &__desc {
        text-align: center;

        @include xs-block() {
            text-align: left;
        }
    }

    &__title {
        margin-bottom: 20px;
        font-size: 25px;
        font-weight: 500;

        @include md-block() {
            margin-bottom: 10px;
            font-size: 22px;
        }
    } //.text-block__title

    &__sub {
        font-size: 17px;
    } //.text-block__sub

    &--column {
        flex-direction: column;

        .text-block {
            &__item {
                margin-right: 0;
                margin-left: 0;
            } //.text-block--column .text-block__item
        }
    } //.text-block--column

    &--transform {
        @include xs-block() {
            .text-block {
                &__item {
                    flex-direction: row;
                    justify-content: flex-start;
                } //.text-block--transform .text-block__icon

                &__icon {
                    margin-right: 20px;
                    margin-bottom: 0;
                } //.text-block--transform .text-block__icon
            }
        }
    } //.text-block--transform
} //.text-block
