.user {
    display: block;

    &__inner {
        @include flexCenter;
    } //.user__inner

    &__avatar {
        position: relative;
        display: inline-block;
        margin-bottom: $gutter;

        img {
            border-radius: 50%;
        }

        &-add {
            position: absolute;
            right: 0;
            bottom: 5px;
            justify-content: center;
            width: 25px;
            height: 25px;
            border: 0;
            border-radius: 50%;
            outline: none;
            background-color: $color-accent;
            color: #999;
            cursor: pointer;
            @include tr;
            @include flexCenter;

            &:hover {
                color: $gray;
            }

            .icon {
                width: 11px;
                height: 11px;
                fill: #fff;
                @include tr;
            }
        } //.user__avatar-add

        &--top {
            position: absolute;
            top: -45px;
        } //.user__avatar--top
    } //.user__avatar

    &__img {
        position: relative;
        display: block;
        flex-shrink: 0;
        width: 90px;
        height: 90px;
        overflow: hidden;
        border-radius: 50%;

        img {
            object-fit: cover;
        }

        &--medium {
            width: 70px;
            height: 70px;
        } //.user__img--medium

        &--small {
            width: 50px;
            height: 50px;
        } //.user__img--small

        &--exsmall {
            width: 35px;
            height: 35px;
        } //.user__img--exsmall
    } //.user__img

    &__icon {
        margin: auto;
        color: #fff;
        font-size: 11px;
    } //.user__icon

    &__letter {
        justify-content: center;
        width: 100%;
        height: 100%;
        margin: auto;
        border-radius: 50%;
        background-color: #b1def1;
        color: #fff;

        font-size: 22px;
        text-align: center;
        text-transform: uppercase;
        @include flexCenter;
    } //.user__letter

    &__name {
        display: block;
        color: $gray--dark;
        line-height: 1;
        @include nosel;
    } //.user_name

    &__online,
    &__position {
        display: block;
        margin-bottom: 10px;
        color: $gray--light;
        font-size: 13px;
    } //.user__online

    &__phone {
        color: $gray--light;
        font-size: 12px;
    } //.user__phone

    &__info-block {
        margin-left: auto;
    } //.user__info-block

    &__status {
        color: $gray--light;

        font-size: 13px;

        &::before {
            display: none;
        }

        &_icon {
            position: relative;
            top: -1px;
            display: inline-block;
            width: 6px;
            height: 6px;
            margin: 0;
            padding: 0;
            border-radius: 50%;
        }

        &.is-success {
            .user__status_icon {
                background-color: $color-success;
            }
        } //.user__status.is-success

        &.is-error {
            .user__status_icon {
                background-color: #f00;
            }
        } //.user__status.is-error

        &.info-block {
            // margin: -10px 0 0;
            // padding-right: 0;
            // padding-left: 0;
            margin: 2px 0 0;
            padding: 0;

            &.bb-dropdown--hover {
                &:hover {
                    .bb-dropdown__list {
                        top: 25px;
                    }
                }
            }

            .bb-dropdown {
                &__list {
                    top: 35px;
                }
            }

            // @include xs-block() {
            //     width: 100%;
            //     margin: -10px 40px 0 0;
            // }
        } //.user__status.info-block
    } //.user__status

    &__rating {
        margin-top: 5px;
    } //.user__rating

    &__upload {
        position: absolute;
        right: 0;
        bottom: 0;
        justify-content: center;
        width: 27px;
        height: 27px;
        border: 2px solid #fff;
        border-radius: 50%;
        background-color: $gray--light;
        @include flexCenter;

        .icon {
            width: 13px;
            height: 13px;
            margin: auto;
            fill: #fff;
        }
    } //.user__upload

    &__info {
        text-align: left;
    } //.user__info

    &--small {
        .user {
            &__img {
                width: 40px;
                height: 40px;
                margin-right: $gutter / 2;
            } //.user--small .user__img

            &__name {
                max-width: 35ch;
                font-size: 14px;
                line-height: 1.3;
            } //.user--small .user__name

            &__status {
                font-size: 14px;
            } //.user--small .user_status

            &__position {
                margin-bottom: 0;
            } //.user--small .user__position
        } //.user--small .user
    } //.user--small

    &--icon {
        position: relative;
        padding: 10px;
        cursor: pointer;

        &:last-child {
            border-bottom: 1px solid $color-border--lightest;
        }

        & + .user--icon {
            border-top: 1px solid $color-border--lightest;
        } //.user--icon + .user--icon

        .user {
            &__text {
                margin-right: 15px;
            } //.user--icon .user__text

            &__icon {
                right: 10px;
                width: 13px;
                height: 13px;

                @include centre('y');
            } //.user--icon .user__icon
        }
    } //.user--icon

    &--column {
        .user {
            &__inner {
                justify-content: center;
                @include flexColumn;
            }

            &__text {
                text-align: center;
            }
        }
    } //.user--column

    &--inline {
        margin-bottom: $gutter;
        @include flexCenter;

        &:last-child {
            margin-bottom: 0;
        }

        .user {
            &__avatar {
                margin-right: 10px;
                margin-bottom: 0;
            } //.user--inline .user__avatar
        }
    } //.user--inline

    &--rating {
        flex-wrap: wrap;

        .user {
            &__avatar {
                flex-shrink: 0;
                width: 50px;
            }

            &__text {
                flex-shrink: 0;
                width: calc(100% - 50px);
            }

            &__rating {
                width: 100%;
                margin-top: 10px;
                margin-bottom: 5px;

                .rating__reviews {
                    margin-left: 10px;
                    text-decoration: underline;
                }
            }
        }
    }

    .icon--drag {
        padding: 3px;
    }
} //.user

.control {
    &__list {
        @include flexCenter;
    } //.control__list

    &__item {
        padding: 0 7px;
        cursor: pointer;
    } //.control__item

    &__link {
        border: 0;
        background-color: transparent;

        &:hover {
            .control {
                &__text,
                &__icon {
                    color: $gray--darkest;
                }
            }
        }
    } //.control__link

    &__text {
        color: $gray--light;
        @extend %textLight;
    } //.control__text

    &__icon {
        color: $gray--light;
        font-size: 16px;
    } //.control__icon
} //.control

.view-toggle {
    position: relative;
    overflow: hidden;
    border-radius: $border-radius;
    background-color: $color-bg--light;
    box-shadow: 0 2px 1px 0 rgba(255, 255, 255, 0.5),
        0 1px 3px 0 rgba(0, 0, 0, 0.2) inset;
    cursor: pointer;
    @include flexCenter;

    @include xs-block() {
        display: inline-flex;
    }

    &__item {
        position: relative;
        width: 36px;
        height: 36px;
        overflow: hidden;
        @include flexCenter;

        &:last-child {
            border: 1px solid $gray--lighten;
            background-color: $color-bg--lighten;
            box-shadow: 0 2px 1px 0 rgba(255, 255, 255, 0.5),
                0 1px 3px 0 rgba(0, 0, 0, 0.2);

            .icon {
                width: 20px;
                height: 20px;
                margin: auto;
                fill: $gray--light;
            }
        }

        .icon {
            width: 20px;
            height: 20px;
            margin: auto;
            fill: $color-accent;
        }
    } //.view-toggle__item

    &.is-active {
        .view-toggle__item {
            &:first-child {
                border: 1px solid $gray--lighten;
                background-color: $color-bg--lighten;
                box-shadow: 0 2px 1px 0 rgba(255, 255, 255, 0.5),
                    0 1px 3px 0 rgba(0, 0, 0, 0.2);

                .icon {
                    fill: $gray--light;
                }
            }

            &:last-child {
                border: 0;
                background-color: $color-bg--light;
                box-shadow: 0 2px 1px 0 rgba(255, 255, 255, 0.5),
                    0 1px 3px 0 rgba(0, 0, 0, 0.2) inset;

                .icon {
                    fill: $color-accent;
                }
            }
        }
    } //.view-toggle.is-active

    &--text {
        .view-toggle {
            &__item {
                width: auto;
                padding: 5px 13px;
                font-size: 14px;
                font-weight: 500;
                white-space: nowrap;
            }
        }
    } //.view-toggle--text
} //.view-toggle

