        border: 2px solid $gray--light;
        border-radius: 50%;
        background-color: #fff;
        font-size: 16px;
        font-weight: 600;
        line-height: 16px;
    } // .step-block__number
} //.step-block

.item-with-desc {
    display: flex;
    flex-direction: column;
    justify-content: center;
    width: 100%;

    &__title,
    &__subtitle {
        width: 100%;
        max-width: 40ch;
        // @include text-overflow;
    } //.item-with-desc__title, .item-with-desc__subtitle

    &__title {
        font-size: 16px;
        font-weight: 500;

        &--bold {
            font-weight: 600;
        }

        &--light {
        }
    } //.item-with-desc__title

    &__subtitle {
        color: $gray--light;

        font-size: 14px;

        &--dark {
            color: $gray;
        }
    } //.item-with-desc__subtitle

    &--left {
        align-items: flex-start;
        text-align: left;
    } //.item-with-desc--left

    &--center {
        align-items: center;
        text-align: center;
    } //.item-with-desc--center

    &--right {
        align-items: flex-end;
        text-align: right;
    } //.item-with-desc--right

    &--small {
        .item-with-desc {
            &__title {
                font-size: 14px;
            } //.item-with-desc--small .item-with-desc__title

            &__subtitle {
                font-size: 13px;
            } //.item-with-desc--small .item-with-desc__subtitle
        }
    } //.item-with-desc--small

    &--xs-small {
        .item-with-desc {
            &__title {
                @include xs-block() {
                    font-size: 14px;
                }
            } //.item-with-desc--xs-small .item-with-desc__title

            &__subtitle {
                @include xs-block() {
                    font-size: 13px;
                }
            } //.item-with-desc--xs-small .item-with-desc__subtitle
        }
    } //.item-with-desc--xs-small

    &--padding-on {
        padding: 15px;
    }
} //.item-with-desc

.prop-value {
    font-size: 14px;

    span {
        &:last-child {
            margin-left: 5px;
            font-weight: 500;
        }
    } //.prop-value span
} //.prop-value

.block-add-photo {
    @include flexCenter;

    &__img {
        width: 100px;
        height: 100px;
        margin-right: 10px;
        overflow: hidden;
        border-radius: $border-radius;

        img {
            border-radius: $border-radius;
        }
    }
} //.block-add-photo

[data-sticky] {
    will-change: min-height;
}

[data-sticky-inner] {
    transform: translate(0, 0); /* For browsers don't support translate3d. */
    transform: translate3d(0, 0, 0);
    will-change: position, transform;
}

@keyframes drop {
    0% {
        top: 5px;
        opacity: 0;
    }

    30% {
        top: 10px;
        opacity: 1;
    }

    100% {
        top: 25px;
        opacity: 0;
    }
} //.mouse-icon animate

@keyframes animateRotation {
    0% {
        transform: rotate(0deg);
    }

    100% {
        transform: rotate(220deg);
    }
} //animation loader

@keyframes animateRotationShadow {
    0% {
        transform: rotate(-140deg);
        box-shadow: inset $color-accent 0 0 0 17px;
    }

    50% {
        box-shadow: inset $color-accent 0 0 0 2px;
    }

    100% {
        transform: rotate(140deg);
        box-shadow: inset $color-accent 0 0 0 17px;
    }
} //animation loader

@keyframes pulse {
    0% {
        box-shadow: 0 0 0 0 rgba($color-accent, 0.4);
    }

    70% {
        box-shadow: 0 0 0 10px rgba($color-accent, 0);
    }

    100% {
        box-shadow: 0 0 0 0 rgba($color-accent, 0);
    }
} //animation pulse
