.profile-editor {
    &__in {
        align-items: flex-start;
        @include row-flex;
    } //.profile-editor__in

    &__progressbar {
        margin-top: $gutter;
        margin-bottom: $gutter * 2;

        @include max(xs) {
            margin: $gutter * 1.5 0;
        }
    } //.profile-editor__progressbar

    &__steps {
        @include col();
        @include size(2);
        @include size-lg(3);
        @include size-xs(12);

        @include max(xs) {
            margin-bottom: $gutter;
        }
    } //.profile-editor__steps

    &__form {
        position: relative;
        width: 100%;
        border: 1px solid $color-border--lightest;
        background-color: #fff;
        //@include box;

        @include max(xs) {
            width: calc(100% + 20px);
            margin: 0 -10px;
            box-shadow: none;
            border: {
                top: 0;
                right: 0;
                bottom: 1px solid $color-border--lightest;
                left: 0;
            }
        }

        &--small {
            .profile-editor {
                &__inner {
                    max-width: 768px;
                    margin: auto;
                }
            }
        } //.profile-editor__form--small

        .btn-link--pos {
            position: absolute;
            top: -37px;
            right: 0;
            font-weight: 300;
        } //.profile-editor__form .btn-link--pos
    } //.profile-editor__form

    &__info {
        padding: 15px 20px;
        @include box;

        @include sm-block() {
            margin-bottom: $gutter;
            padding: 10px;
        }
    } //.profile-editor__info

    &__header {
        position: relative;
        display: flex;
        align-items: center;
        height: 50px;
        padding: 0 20px;
        border-bottom: 1px solid $color-border--lightest;
        border-radius: 2px 2px 0 0;
        outline: none;
        color: $gray--darkest;
        font-size: 16px;
        font-weight: 500;

        @include xs-block() {
            padding: 0 10px;
        }

        &_icon {
            width: 21px;
            height: 21px;
            fill: $color-icon;
        } //.profile-editor__header_icon

        .btn-link {
            right: 14px;
            padding: 0;
            outline: none;
            line-height: 1;
            @include centre('y');
        } //.profile-editor__header .btn-link
    } //.profile-editor__header

    &__inner {
        padding: $gutter;

        @include max(xs) {
            padding: $gutter $gutter / 2;
        }

        &--column {
            align-items: center;
            justify-content: center;
            @include flexColumn;
        }

        .devider {
            margin: 20px 0;
        }

        &.pb-0 {
            padding-bottom: 0;
        }
    } //.profile-editor__inner

    &__footer {
        padding: 20px 0;
        border-top: 1px solid $color-border--lightest;
        text-align: center;

        @include max(xs) {
            padding: 20px 10px;
        }

        .btn {
            font-size: 16px;
            font-weight: 500;
            letter-spacing: 0.5px;

            @include max(xs) {
                width: 100%;
            }
        } //.profile-editor__footer .btn
    } //.profile-editor__footer

    &__item {
        display: flex;
        @include itemGutter(24px);

        @include max(sm) {
            flex-wrap: wrap;
            width: 100%;
            margin-bottom: $gutter;
        }

        &_info {
            flex-direction: column;
            justify-content: center;
        }

        &.mb-30 {
            margin-bottom: 35px;
        }

        &-inner {
            position: relative;
            display: flex;

            @include max(sm) {
                flex-wrap: wrap;
            }
        }

        &--column {
            flex-direction: column;

            .profile-editor {
                &__item-inner {
                    .profile-editor__label {
                        width: 195px;
                        margin-bottom: 0;
                        font-size: 13px;
                        text-align: right;

                        @include max(sm) {
                            margin-bottom: 5px;
                            font-size: 15px;
                            text-align: left;
                        }
                    }
                }

                &__label {
                    width: 100%;
                    margin-bottom: 5px;
                    text-align: left;
                }
            }
        }

        &--small {
            max-width: 320px;
        } //.profile-editor--small

        .color-success,
        .color-danger {
            margin-left: 15px;
            font-size: 14px;
            font-weight: 300;
            @include max(xs) {
                margin-left: auto;
            }
        }

        .bb-select {
            width: 100%;
        }

        .text-description {
            color: $color-text--light;
            font-size: 14px;
            font-weight: 300;
            font-weight: 300;
            line-height: 1.4;
        }
    } //.profile-editor__item

    &__label {
        width: 195px;
        margin-right: 10px;
        font-size: 13px;
        text-align: right;

        @include max(md) {
            width: 170px;
        }

        @include max(sm) {
            width: 100%;
            margin-right: 0;
            margin-bottom: 5px;
            padding-top: 0;
            text-align: left;
        }
    } //.profile-editor__label

    &__input {
        position: relative;
        flex-wrap: wrap;
        width: 320px;
        @include flexCenter;

        @include max(sm) {
            width: 100%;
        }

        &_confirm {
            position: relative;
            margin-bottom: 5px;

            &::before {
                right: 5px;
                @include fa('solid');
                @include centre('y');
            }

            input {
                padding-right: 30px;
                @include max(sm) {
                    max-width: 100%;
                }
            }

            &.is-invalid {
                margin-bottom: 50px;

                .profile-editor__attention {
                    top: 60px;
                }
            }

            &.is-confirm {
                margin-bottom: 20px;

                &::before {
                    content: '\f00c';
                    color: $color-success;
                    font-size: 16px;
                    @include fa('regular');
                }

                .profile-editor__attention {
                    &--invalid {
                        display: none;
                    }

                    &--success {
                        display: block;
                    }
                }
            }

            &.is-info {
                margin-bottom: 55px;
            }

            &.is-error {
                &::before {
                    content: '\f12a';
                    right: 10px;
                    color: $color-danger;
                    font-size: 16px;
                }

                .profile-editor__attention--success {
                    display: none;
                }
            }

            .color-success {
                font-size: 14px;
                font-weight: 300;
            }
        } //.profile-editor__input_confirm

        input {
            width: 100%;
            background-color: transparent;
            @extend %styleInput;
        } //.profile-editor__input input

        &.is-invalid,
        &.is-error {
            input,
            textarea,
            .select2-selection {
                border: 1px solid $color-danger;
            }

            .profile-editor__error {
                display: block;
            }

            .bb-input-tip {
                display: block;
                border-color: rgba(247, 73, 105, 0.75);
                background: #fbebee;
                color: $color-danger;

                &__arrow-up {
                    &::before {
                        border-color: rgba(247, 73, 105, 0.75);
                        background: #fbebee;
                    }
                }
            }

            textarea {
                margin-bottom: 1px;
            }
        } //.profile-editor__input.is-invalid

        &.is-info {
            input,
            textarea,
            .select2-selection {
                border: 1px solid $color-info;
            }

            .bb-input-tip {
                display: block;
                border-color: rgba(40, 131, 210, 0.75);
                background: #ebf5fd;
                color: $color-info;

                &__arrow-up {
                    &::before {
                        border-color: rgba(40, 131, 210, 0.75);
                        background: #ebf5fd;
                    }
                }
            }

            textarea {
                margin-bottom: 1px;
            }
        }

        &--born {
            .profile-editor__select + .profile-editor__select {
                margin-left: 20px;
            }

            .btn {
                margin-left: 20px;
            } //.profile-editor__input--born .btn
        } //.profile-editor__input--born

        .bb-date {
            width: 150px;

            &__input {
                padding-right: 30px;
            }
        } //.profile-editor__input .bb-date

        .bb-checkbox {
            // margin-top: 10px;
            margin-right: 30px;

            @include flexCenter;

            @include max(xs) {
                margin-right: 10px;
            }

            &:last-child {
                margin-right: 0;
                margin-bottom: 0;
            }

            &__title {
                font-size: 14px;
            }

            &__box {
                top: -1px;
                display: inline-block;
            } //.profile-editor__input .bb-checkbox__box
        } //.profile-editor__input .bb-checkbox

        .bb-input {
            width: 100%;
        } //.profile-editor__input .bb-input

        .btn-link-info {
            bottom: -20px;
            padding: 0;
            @include centre('x');

            @include max(xs) {
                bottom: -25px;
            }
        } //.profile-editor__input .btn-link-info
    } //.profile-editor__input

    &__title {
        display: block;
        margin-bottom: $gutter;
        color: $gray--darkest;
        font-size: 15px;
        font-weight: 500;

        &--small {
            margin-bottom: $gutter / 2;
        }
    } //.profile-editor__title

    &__confirm {
        display: block;
        max-width: 300px;
        margin: 5px 0 $gutter/2 205px;

        @include max(sm) {
            margin-left: 0;
        }

        &.ml-0 {
            margin-left: 0;
        }

        .confirm {
            &__link {
                margin-bottom: 0;
            } //.profile-editor__confirm .confirm__link
        }
    } //.profile-editor__confirm

    &__attention {
        position: absolute;
        top: 45px;
        left: 0;
        display: block;
        max-width: 300px;
        color: $gray--light;
        font-size: 12px;
        line-height: 1.2;
        @extend %textLight;

        @include max(sm) {
            margin-left: 0;
        }

        &.ml-0 {
            margin-left: 0;
        }

        &--success {
            display: none;
        }
    } //.profile-editor__attention

    &__select {
        & + & {
            margin-left: auto;

            @include max(xs) {
                margin-left: 15px;
            }

            @include max(i5) {
                margin-left: 2px;
            }
        } //.profile-editor__select+.profile-editor__select

        &--day,
        &--year,
        &--color {
            select,
            .select2-container {
                width: 80px !important;
                min-width: 80px;
            }
        } //.profile-editor__select--day, .profile-editor__select--year, .profile-editor--color

        &--materials {
            box-sizing: content-box;
            width: 100%;

            // @include max(sm) {

            //     .select2-container--default {

            //         &.select2-container--focus .select2-selection--multiple {
            //             box-shadow: none;
            //         }

            //         .select2-selection--multiple {
            //             border-radius: 0;
            //             border: none;
            //             border-bottom: 1px solid $color-border--light;

            //             .select2-selection__rendered {
            //                 padding: 0;

            //                 & li {
            //                     position: relative;
            //                     width: 48.5%;
            //                     padding-right: 15px;
            //                     @include text-overflow;
            //                 }
            //             }

            //             .select2-selection__choice {
            //                 padding: 4px 5px;
            //                 margin: 3px 4px 3px 0;
            //                 border: none;
            //             }

            //             .select2-selection__choice__remove {
            //                 position: absolute;
            //                 right: 5px;
            //             }
            //         }
            //     }

            //     .select2-container .select2-search--inline {
            //         width: 100% !important;
            //     }

            //     .select2-container--open .select2-dropdown--above {
            //         border-bottom: 1px solid $color-border--light;
            //     }
            // }
        } //.profile-editor__select--materials

        &--month {
            select,
            .select2-container {
                width: 110px !important;
                min-width: 110px;
            }
        } //.profile-editor__select

        .select,
        .select2-container--default .select2-selection--single {
            height: 40px;
            font-size: 14px;
            line-height: 40px;
        } //.profile-editor__select .select, .select2-container--default .select2-selection--single

        .select2-container--default
            .select2-selection--single
            .select2-selection__rendered {
            line-height: 39px;

            @include max(xs) {
                line-height: 41px;
            }
        } //.profile-editor__select .select2-container--default .select2-selection--single .select2-selection__rendered

        &.is-invalid {
            .select2-container--default .select2-selection--multiple,
            .select2-container--default .select2-selection--single {
                border-color: $color-danger;
            }

            .select2-container--default.select2-container--open.select2-container--below
                .select2-selection--single:hover {
                border-color: $color-danger;
            }

            .profile-editor__error {
                display: block;
            }
        } //.profile-editor__select.is-invalid
    } //.profile-editor__select

    &__error {
        position: absolute;
        bottom: -20px;
        left: 0;
        display: none;
        color: $color-danger;
        font-size: 13px;
        @include text-overflow;
    } //.profile-editor__error

    &__checkbox {
        margin-bottom: 10px;

        &:last-child {
            margin-bottom: 0;
        }
    } //.profile-editor__input

    &__add {
        position: relative;
        top: -2px;
        margin-left: 15px;
    } //.profile-editor__add

    &__map {
        position: relative;
        padding-bottom: 33.33%;
        overflow: hidden;

        @include max(xs) {
            padding-bottom: 56.25%;
        }

        #cabinet-map {
            @include coverdiv;
        }
    } //.profile-editor__maps

    &__upload {
        padding: $gutter $gutter 0;

        .bb-upload {
            &__item {
                @include size(3);
                @include size-lg(4);
            }
        }
    } //.profile-editor__upload

    &--steps {
        max-width: 768px;
        margin: auto;
        @include max(xs) {
            .bb-accordeon {
                @include max(xs) {
                    margin: -20px;
                }
            }

            .bb-accordeon__item + .bb-accordeon__item {
                margin-top: 0;
                border-top: 1px solid #eee;
            }
        }

        .profile-editor {
            &__attention {
                margin-left: 0;
            }

            &__confirm {
                margin-left: 0;
            }
        }
    } //.profile-editor--steps

    .devider {
        margin: $gutter / 2 0 $gutter;
    }

    .selectize-control {
        .selectize-input {
            font-size: 13px;

            input {
                height: 14px;
            }
        }
    }
} //.profile-editor

.profile-editor-info {
    &__title {
        display: block;
        margin-bottom: 5px;
        font-style: italic;
        font-weight: 600;
    } //.profile-editor-info__title

    &__desc {
        color: $gray--light;
        font-size: 15px;
        font-style: italic;

        & + & {
            margin-top: $gutter / 2;
        }

        .text-color--black {
            color: $gray--dark;
            font-style: italic;
            font-weight: 600;
        }
    } //.profile-editor-info__desc
} //.profile-editor-info
