.l-registration {
    min-height: 40vh;
    max-height: 85vh;
    margin: auto;
    border-radius: $border-radius;
    background-color: #fff;
    box-shadow: $bxs-box;
    @include flexColumn;

    @include xs-block() {
        position: fixed;
        top: 0;
        right: 0;
        bottom: 0;
        left: 0;
        z-index: z(fixed);
        max-height: 100%;
        margin: 0;
        border-radius: 0;
    }

    &__header {
        position: relative;
        flex-shrink: 0;
        justify-content: center;
        height: 50px;
        padding: 0 15px;
        @include border(bottom);
        @include flexCenter;
    } //.l-registration__header

    &__body {
        position: relative;
        flex: 1;
        justify-content: center;
        max-height: 80vh;
        @include flexColumn;

        @include xs-block() {
            justify-content: initial;
            height: calc(100vh - 50px);
        }
    } //.l-registration__body

    &__inner {
        padding: 30px 30px 45px;

        @include xs-block() {
            padding: 30px 15px;
        }

        &.no-padding {
            padding: 0;
        }

        &--scrolled {
            @include scroll;
        } //.l-registration__inner--scrolled
    } //.l-registration__inner

    &__form {
        padding: 30px 30px 45px;
        overflow-x: hidden;
        @include scroll;

        @include xs-block() {
            padding: 30px 15px;
        }

        & > .bb-input {
            &:not(.bb-input--confirm) {
                width: 100%;
            }
        } //.l-registration__form .bb-input

        &.is-confirm {
            .form-confirm {
                @include tY(0);
            }
        }
    } //.l-registration__form

    &__title {
        flex: 1;
        width: 80%;
        font-size: 16px;
        font-weight: 500;
        text-align: center;
        @include centre();
        @include nosel;
        @include text-overflow;
    } //.l-registration__title

    &__save {
        height: 50px;
        margin-left: auto;
        color: $color-accent;
        font-size: 12px;
        font-weight: 600;
        line-height: 50px;
        text-transform: uppercase;
        @include xs-block() {
            padding: 0;
            font-size: 12px;
        }

        &:hover,
        &.is-active {
            transform: none;
            color: $color-accent;
            text-decoration: none;
        }
    } //.l-registration__save

    &__back {
        height: 50px;
        margin-right: auto;
        line-height: 50px;

        @include xs-block() {
            padding: 0;
        }

        .icon {
            fill: $gray;
        }
    } //.l-registration__back

    &__user {
        align-items: center;
        justify-content: center;
        margin-bottom: 30px;
        @include flexColumn;

        .user {
            &__name {
                color: $gray--light;
                font-weight: 500;
                text-align: center;
            }
        } //.l-registration__user .user
    } //.l-registration__user

    &__left {
        flex: 1;
        padding: 40px 100px;
        background-color: #fff;
    } //.l-registration__left

    &__right {
        flex-shrink: 0;
        justify-content: center;
        // flex-basis: 300px;
        max-width: 300px;
        @include flexColumn;
    } //.l-registration__right

    &__bottom {
        justify-content: center;
        @include flexCenter;
    } //.l-registration__bottom

    &__icon {
        width: 50px;
        height: 50px;
        margin: 0 auto 30px;
    } //.l-registration__icon

    &__checkbox {
        padding: 15px;
        @include border(bottom);

        &:last-child {
            border-bottom: 0;
        }

        .bb-checkbox {
            &__box {
                top: 16px;
                right: 15px;
                left: auto;
                margin-right: 0;
            }
        }
    } //.l-registration__checkbox

    &__map {
        position: relative;
        padding-top: 56.25%;

        & > div {
            @include coverdiv;
        }
    } //.l-registration__map

    .bb-input {
        &:not(:last-child) {
            margin-bottom: $gutter;
        }

        &__row {
            .bb-input {
                margin-bottom: 0;

                @include xs-block() {
                    & + .bb-input {
                        margin-left: 20px;
                    }
                }
            }
        }

        &--born {
            max-width: 100%;
        } //.l-registration .bb-input--born
    } //.l-registration .bb-input

    .bb-checkbox--icon {
        & + .bb-checkbox--icon {
            margin-top: 20px;
        } //.l-registration .bb-checkbox--icon + .bb-checkbox--icon
    } //.l-registration .bb-checkbox--icon

    .form-confirm {
        &__inner {
            max-width: 318px;
        } //.l-registration .form-confirm __inner
    } //.l-registration .form-confirm
} //.l-registration
