$color-event-new: $color-info;
$color-event-working: $color-warning;
$color-event-done: $color-success;
$color-event-canceled: $color-danger;

$colorEvent: (
    new: $color-event-new,
    working: $color-event-working,
    done: $color-event-done,
    canceled: $color-event-canceled,
);

@mixin eventColor($color) {
    border-color: $color;

    .dhx_header,
    .dhx_title,
    .dhx_body,
    .dhx_footer {
        background-color: lighten($color, 33%);
        color: $gray--darken;
    }
}

@mixin eventTitleIcon($icon, $color) {
    .dhx_title {
        &::before {
            content: $icon;
            color: $color;
            @include fa('light');

            @include i5-block() {
                display: none;
            }
        }
    }
}

.dhtmlXTooltip {
    &.tooltip {
        z-index: 11;
        padding: 0;
        border: 0;
        border-radius: $border-radius;
        box-shadow: 0 5px 30px rgba(#000, 0.4);
        color: $color-text;

        @include xs-block() {
            display: none !important;
        }
    }
}

.calendar {
    padding: 0;
    // background-color: #fff;

    @include md-block() {
        padding-bottom: 0;
    }

    @include xs-block() {
        padding: 0;
    }

    &__top-line {
        padding: 30px;

        @include md-block() {
            margin-bottom: 15px;
            padding: 0;
        }

        @include xs-block() {
            margin: 20px 0 10px;
            padding: 0 10px;
        }

        .bb-checkbox-row {
            .bb-checkbox {
                min-width: 128px;
            } //.calendar__box-row .bb-checkbox-row .bb-checkbox
        } //.calendar__box-row .bb-checkbox-row

        .bb-checkbox {
            &__title {
                height: $base-btn-height;
                font-size: 15px;
                line-height: 33px;
            } //.calendar__top-line .bb-checkbox__title
        } //.calendar__top-line .bb-checkbox
    } //.calendar__top-line

    &__sorting {
        $o: 10px;
        align-items: flex-start;
        @include row-flex;

        @include sm-block() {
            @include u-row-flex($o);

            .calendar {
                &__box-row {
                    @include u-col($o);
                    @include u-size(2, 12, $o);
                }
            }

            .top-line__select {
                & + & {
                    margin-left: 10px;
                }
            }
        }

        @include xs-block() {
            @include u-row-flex($o);

            .calendar {
                &__box-row {
                    @include u-col($o);
                    @include u-size(2, 12, $o);
                }

                &__date {
                    @include u-col($o);
                    @include u-size(6, 12, $o);
                }

                &__view {
                    @include u-col($o);
                    @include u-size(3, 12, $o);
                }
            }
        }

        &--small {
            display: flex;
            justify-content: flex-start;
            @include col;
            @include size(5);
            @include size-md(3);

            .calendar {
                &__box-row {
                    @include size(3);
                }

                &__date {
                    @include size(6);
                }
            }

            .top-line {
                &__select {
                    flex-basis: 100%;
                    width: 100%;

                    @include md-block() {
                        flex-basis: 100%;
                        width: 100%;
                        min-width: 100%;
                    }
                }
            }
        } //.calendar__sorting--small
    } //.calendar__sorting

    &__box-row {
        display: flex;
        flex-shrink: 0;
        justify-content: flex-start;
        @include col;
        @include size-xs(2);
        @include size-sm(3);

        @include md-block() {
            .top-line__select {
                // flex-basis: 50%;

                // &:first-child {

                //     &:hover,
                //     &:focus {

                //         & + .top-line__select {

                //             select {
                //                 border-left-color: $input-color-border--hover;
                //             }
                //         }
                //     }

                //     select {
                //         border-right: 0;
                //         border-radius: $border-radius 0 0 $border-radius;
                //     }
                // }

                // &:last-child {

                //     select {
                //         border-radius: 0 $border-radius $border-radius 0;
                //     }
                // }

                // &:only-child {

                //     select {
                //         border-right: 1px solid $input-color-border;
                //         border-radius: $border-radius;
                //     }
                // }

                & + .top-line__select {
                    margin-top: 0;
                    margin-left: 10px;
                }

                .bb-select {
                    width: 100%;
                    max-width: 100%;
                    margin-right: 0;
                }

                select {
                    width: 100%;
                    max-width: 100%;
                    border-radius: $border-radius;
                }
            }
        }

        @include i5-block() {
            margin-right: 0;
        }

        &--for-icon {
            @include xs-block() {
                width: calc(25% - 20px) !important;
            }
        } //.calendar__box-row--for-icon
    } //.calendar__box-row

    &__date {
        flex: 1;
        justify-content: center;
        @include col;
        @include size(4);
        @include size-md(6);

        @include i5-block() {
            width: 50%;

            .bb-date__field {
                font-size: 14px;
                @include text-overflow;
            }
        }

        .bb-date {
            &__btn {
                &--today {
                    @include sm-block() {
                        display: block;
                    }

                    @include xs-block() {
                        display: none;
                    }
                } //.calendar__date .bb-date__btn--today

                &--prev {
                    @include xs-block() {
                        border-right: 0;
                    }
                } //.calendar__date .bb-date__btn--prev
            } //.calendar__date .bb-date__btn
        }
    } //.calendar__date

    .top-line {
        &__sorting {
            flex-direction: row;
            justify-content: space-between;
            width: auto;
            @include xs-block() {
                flex-wrap: nowrap;
            }
        } //.calendar .top-line__sorting

        &__select {
            width: 235px;
            margin-top: 0;
            // min-width: 240px;
            margin-bottom: 0;

            @include md-block() {
                width: auto;
                min-width: auto;
            }

            &:only-child {
                width: 320px;
            }

            // @include xs-block() {
            //     margin-top: 10px;
            // }
        } //.calendar .top-line__select
    } //.calendar .top-line
} //.calendar

.c-calendar {
    position: relative;
    height: 100%;
    min-height: 300px;

    @include xs-block() {
        margin: 0 -10px;
    }

    &__btn {
        position: absolute;
        top: 100px;
        z-index: 3;
        border: 0;
        outline: none;
        background-color: #fff;
        box-shadow: $bxs-box;
        cursor: pointer;
        @include border(all);

        &:hover {
            box-shadow: $bxs-hover;

            i {
                color: $gray;
            }
        }

        &:active {
            box-shadow: $bxs-box-two;
        }

        &--prev {
            left: 55px;
        } //.c-calendar__btn--prev

        &--next {
            right: 23px;

            @include md-block() {
                right: 0;
            }

            @include xs-block() {
                right: 13px;
            }
        } //.c-calendar__btn--next

        i {
            color: $gray--lighten;
            font-size: 28px;
            cursor: pointer;
        }
    } //.c-calendar__btn

    &__timespan {
        z-index: 9;
        display: none;
        padding-left: 5px;
        background-color: #c5ccdf;

        font-size: 14px;
    } //.c-calendar__timespan

    &__empty {
        padding-top: 50px;
    } //.c-calendar__empty

    .user {
        display: flex;
        align-items: center;
        justify-content: center;
        padding: 10px;

        @include xs-block() {
            flex-direction: column;
        }

        &__img {
            flex-shrink: 0;
            width: 40px;
            height: 40px;
            @include xs-block() {
                margin: 5px auto;
            }
        } //.c-calendar .user __img

        &__name,
        &__surname {
            margin-left: 10px;
            color: $color-text;
            font-size: 14px;
            font-weight: 500;
            text-align: left;
            @include xs-block() {
                margin-left: 0;
                font-size: 12px;
                text-align: center;
            }
        } //.c-calendar .user __name, .c-calendar .user __surname
    } //.c-calendar .user

    .dhx_cal_container {
        position: absolute;
        top: 0;
        right: 0;
        bottom: 0;
        left: 0;
        overflow: inherit;
        background-color: transparent;
    } //.c-calendar .dhx_cal_container

    .dhx_scale_holder {
        overflow: hidden;

        &:hover {
            & > .c-calendar__timespan {
                display: block;
            }
        }

        .dhx_scale_hour {
            @include xs-block() {
                padding-left: 8px;
            }
        }
    }

    .dhx_scale_holder_now {
        overflow: hidden;

        &:hover {
            & > .c-calendar__timespan {
                display: block;
            }
        }
    }

    .dhx_cal_header,
    .dhx_scale_bar {
        display: flex;
        justify-content: center;
        border: 0;
        background: transparent;
        color: $color-text;
        font-size: 14px;
        font-weight: 500;
    } //.c-calendar .dhx_cal_header, .c-calendar .dhx_scale_bar

    .dhx_cal_tab {
        &.week_tab {
            border-radius: 0 $border-radius $border-radius 0;
        }
    } //.c-calendar .dhx_cal_tab

    .dhx_cal_next_button,
    .dhx_cal_tab_last {
        border-radius: 0 $border-radius $border-radius 0;
    } //.c-calendar .dhx_cal_next_button, .c-calendar .dhx_cal_tab_last

    .dhx_cal_next_button,
    .dhx_cal_prev_button {
        top: 75px;
        border: 0;
        background: none;

        &::before {
            content: '\f105';
            color: $gray--lighten;
            font-size: 28px;
            @include fa('light');
            @include tr;
            @include centre;
        }

        &:hover {
            &::before {
                color: $gray;
            }
        }
    } //.c-calendar .dhx_cal_next_button, .c-calendar .dhx_cal_prev_button

    .dhx_cal_prev_button {
        left: 35px;

        &::before {
            content: '\f104';
        }
    } //.c-calendar .dhx_cal_prev_button

    .dhx_cal_prev_button,
    .dhx_cal_tab_first {
        border-radius: $border-radius 0 0 $border-radius;
    } //.c-calendar .dhx_cal_prev_button

    .dhx_cal_today_button {
        border-radius: $border-radius;
    } //.c-calendar .dhx_cal_today_button

    .dhx_cal_data {
        border-bottom: 0;
        background-color: $color-bg--lightest;
        background-image: linear-gradient(
            45deg,
            transparent 46%,
            rgba(36, 51, 74, 0.2) 49%,
            rgba(36, 51, 74, 0.2) 51%,
            transparent 55%
        );
        // background-image:  url('../libs/calendar/imgs_dhx_terrace/bb_databg_now.png');
        background-size: 8px 8px;

        .dhx_scale_hour {
            & > div {
                &:first-child {
                    color: $gray--dark;
                    font-size: 14px;
                    font-weight: 500;
                }
            }
        }
    } //.c-calendar .dhx_cal_data

    .dhx_scale_holder,
    .dhx_scale_holder_now {
        background-image: url('../libs/calendar/imgs_dhx_terrace/td_bg.png');

        &:hover {
            cursor: cell;
        }
    } //.c-calendar .dhx_scale_holder_now

    .dhx_time_reserved {
        // background-image: url('../libs/calendar/imgs_dhx_terrace/bb_databg_now.png');
        background-image: url('../libs/calendar/imgs_dhx_terrace/databg.png');
    } //.c-calendar .dhx_time_reserved

    .dhx_cal_event {
        z-index: 10;
        min-height: 52px;
        margin-left: -1px;
        border-radius: $border-radius;

        &:hover {
            cursor: pointer;
        }

        &:not(.dhx_cal_select_menu) {
            border-left: 4px solid transparent;

            .dhx_title {
                position: relative;

                &::before {
                    position: absolute;
                    right: 5px;
                }
            }

            &.event_new {
                @include eventColor($color-event-new);
                @include eventTitleIcon('\f118', $color-event-new);
            } //.dhx_cal_event.event_new

            &.event_working {
                @include eventColor($color-event-working);
                @include eventTitleIcon('\f017', $color-event-working);
            } //.dhx_cal_event.event_working

            &.event_done {
                @include eventColor($color-event-done);
                @include eventTitleIcon('\f00c', $color-event-done);
            } //.dhx_cal_event.event_done

            &.event_canceled {
                @include eventColor($color-event-canceled);
                @include eventTitleIcon('\f119', $color-event-canceled);
            } //.dhx_cal_event.event_canceled
        }

        .dhx_header,
        .dhx_title,
        .dhx_body,
        .dhx_footer {
            border: 0;
            background-color: #fff;
            color: $color-text;
            @include nosel;
        }

        .dhx_title {
            width: calc(100% - 10px);
            padding: 5px;
            border-top-left-radius: 0;
            border-top-right-radius: $border-radius;
            font-weight: 500;
            text-align: left;
        } //.c-calendar .dhx_cal_event .dhx_title

        .dhx_body {
            min-height: 23px;
            padding-top: 0;
            padding-bottom: 8px;
            border-radius: 0 0 $border-radius $border-radius;
        } //.c-calendar .dhx_cal_event .dhx_body

        .dhx_footer {
            height: 13px;
            margin-top: -13px;
        } //.c-calendar .dhx_cal_event .dhx_footer
    } //.c-calendar .dhx_cal_event //Стили для карточки события

    .dhx_menu_head,
    .dhx_menu_icon {
        background-image: url('../libs/calendar/imgs_dhx_terrace/controls--black.png');
    } //Меняем цвет контролов

    .dhx_cal_select_menu,
    .dhx_cal_editor {
        display: none;
    }
} //.c-calendar

.c-calendar-tooltip {
    @include flexColumn;

    &__header {
        padding: 5px 10px 4px;
        border-radius: $border-radius $border-radius 0 0;
        color: #fff;
        font-size: 14px;
        text-align: center;
        @include border(bottom);

        @each $theme, $color in $colorEvent {
            &.event_#{$theme} {
                background-color: $color;
            }
        }

        // &.event_new {
        //     background-color: $color-event-new;
        // }

        // &.event_working {
        //     background-color: $color-event-working;
        // }

        // &.event_done {
        //     background-color: $color-event-done;
        // }

        // &.event_canceled {
        //     background-color: $color-event-canceled;
        // }
    }

    &__body {
        padding: 5px 10px;
        @include flexColumn;

        @each $theme, $color in $colorEvent {
            &.event_#{$theme} {
                .c-calendar-tooltip__status {
                    border-color: $color;
                    color: $color;
                }
            }
        }

        & > span {
            &:not(:first-child) {
                padding-top: 5px;
                @include border(top);
            }

            & + span {
                margin-top: 5px;
            }
        }
    }

    &__user {
        font-size: 14px;
        font-weight: 500;
    }

    &__status {
        margin-left: 15px;
        padding: 0 7px;
        border: 1px solid $color-border--lightest;
        border-radius: 15px;
        font-size: 11px;
    }

    &__phone,
    &__time {
    }
} //.c-calendar-tooltip
