.l-slide-block {
    position: fixed;
    top: 0;
    right: 0;
    bottom: 0;
    z-index: z(popup);
    opacity: 1;
    transition: $trs-transform;
    background-color: #fff;
    @include tX(0);
    @include nosel;
    @include flexColumn;

    @include xs-block() {
        border-top: 0;
    }

    &:not(.is-open) {
        border: 0;
        border-radius: 0;
        box-shadow: 0 0 0 rgba(0, 0, 0, 0.2);
        @include tX(100vw);
    } //.move-block:not(.is-open)

    &__header {
        position: relative;
        flex-shrink: 0;
        height: 50px;
        padding: 0 15px;
        color: $gray--darkest;
        font-size: 18px;
        @include border(bottom);
        @include flexCenter;

        @include xs-block() {
            height: 50px;
            font-size: 16px;
            font-weight: 500;
        }

        span {
            flex: 1;
            @extend %styleBlockHeader;

            @include xs-block() {
                text-align: center;
            }
        } //.l-slide-block__header span

        .btn-icon--close {
            right: 10px;
            @include centre('y');
        } //.l-slide-block__header .btn-icon--close
    } //.l-slide-block__header

    &__body {
        flex: 1;
        overflow-x: hidden;

        &:not(.no-scroll) {
            @include scroll;
        }

        &.has-add-btn {
            margin-top: 55px;

            .btn-select + .top-line {
                @include xs-block() {
                    margin: 20px 0;
                }
            }
        } //.l-slide-block__body.has-add-btn

        & > .bb-input,
        & > .bb-select {
            margin-bottom: 20px;
        }

        .top-line {
            padding-right: 10px;
            padding-left: 10px;
        } //.l-slide-block__body .top-line--side-offset

        .worker__items {
            @include xs-block() {
                margin: 0;
            }
        } //.l-slide-block__body .worker__items

        // .btn-select {
        //     & ~ .worker {
        //         margin-top: 10px;
        //     }//.l-slide-block__body .btn-select ~ .worker
        // } //.l-slide-block__body .btn-select

        .l-slide-block__btn {
            top: 0;
            right: 0;
            left: auto;
            transform: translateY(0);
        } //.l-slide-block__body .l-slide-block__btn

        .o-list-title {
            & + .market-product-item--horizon {
                border-top: 0;
            }
        } //.l-slide-block__body .o-list-title

        &--offset {
            padding-right: 10px;
            padding-left: 10px;

            .top-line {
                padding-right: 0;
                padding-left: 0;
            } //.l-slide-block__body--offset .top-line
        } //.l-slide-block__body--offset

        &.no-offset {
            padding: 0 !important;
        } //.l-slide-block__body.no-offset
    } //.l-slide-block__body

    &__inner {
        padding: 20px;

        @include xs-block() {
            height: 100%;
            padding: 15px;
            @include scroll;
        }

        &--no-offset {
            padding-top: 0;
            padding-bottom: 0;
        } //.l-slide-block__inner--no-offset

        &.pt-0 {
            padding-top: 0;
        } //.l-slide-block__inner.pt-0

        &.pb-0 {
            padding-bottom: 0;
        } //.l-slide-block__inner.pb-0

        &.no-offset {
            padding: 0 !important;
        } //.l-slide-block__inner.no-offset

        & > .bb-input {
            width: 100%;
            margin-bottom: 25px;
        } //.l-slide-block__inner > .bb-input
    } //.l-slide-block__inner

    &__footer {
        @include footerBtn;
    } //.l-slide-block__footer

    &__btn {
        &--close {
            left: 0;
            z-index: z(default);
            width: 50px;
            height: 50px;
            @include centre('y');
        } //.l-slide-block__btn-close

        &--done {
            right: 15px;
            height: 50px;
            font-size: 12px;
            font-weight: 600;
            text-transform: uppercase;
            @include centre('y');

            @include xs-block() {
                padding: 0;
                font-size: 12px;
            }

            &:hover {
                transform: translate(0, -50%);
                text-decoration: none;
            }
        } //.l-slide-block__btn--done
    } //.l-slide-block__btn

    &__item {
        padding: 15px;

        @include border(bottom);
        @include flexColumn;

        @include xs-block() {
            margin: 0 -15px;
        }

        &:last-child {
            border-bottom: 0;
        }

        .bb-input {
            width: 100%;
        } //.l-slide-block__item .bb-input
    } //.l-slide-block__item

    &__box {
        margin-bottom: 25px;

        &:last-child {
            margin-bottom: 0;
        }
    } //.l-slide-block__box

    &__view-toggle {
        width: min-content;

        .view-toggle {
            &__item {
                justify-content: center;
                min-width: 118px;
                text-align: center;

                &:hover {
                    border: 1px solid $color-border--light !important;
                }
            }
        }
    } //.l-slide-block__view-toggle

    &__btn-select {
        position: fixed;
        top: 50px;
        right: 0;
        left: 0;
        z-index: z(fixed);
    } //.l-slide-block__btn-select

    &__interval {
        position: relative;
        display: none;

        &.is-visible {
            display: block;
        }

        .icon-close {
            position: absolute;
            top: -1px;
            right: 0;
            width: 11px;
            height: 11px;
            fill: $color-accent;
            cursor: pointer;
            @include tr;

            &:hover {
                @include rotate(90);
            }
        }
    } //.l-slide-block__interval

    &__add {
        margin: 0 auto 10px;

        &:hover {
            border-color: $color-border;
            color: $gray--darkest;
        }
    } //.l-slide-block__add

    &__services {
        padding-top: 50px;
    } //.l-slide-block__services

    &__preloader {
        top: 51px;
        z-index: z(modal);
    } //.l-slide-block__preloader

    &.is-open {
        box-shadow: -5px 10px 20px rgba(0, 0, 0, 0.2);
        // @include tX(0);
    } //.l-slide-block.is-open

    &.is-loading {
        .l-slide-block {
            &__preloader {
                display: block;
            }
        }
    } //.l-slide-block.is-loading

    &--xs {
        width: 100%;
        max-width: 375px;

        @include xs-block() {
            max-width: 100%;
            box-shadow: none;
        }

        &.is-open {
            &:not(.l-slide-block--shadow) {
                box-shadow: none;
            }
        } //.l-slide-block.is-open

        .l-slide-block {
            &__inner {
                &.pt-0 {
                    @include xs-block() {
                        padding-top: 0;
                    }
                }
            } //.l-slide-block--xs .l-slide-block__inner

            &__header {
                span {
                    font-size: 18px;
                    font-weight: 400;
                }
            } //.l-slide-block--xs .l-slide-block__header

            &__body {
                &:not(.has-add-btn) {
                    @include xs-block() {
                        padding-top: 10px;
                    }
                }

                & > .o-list-title {
                    @include xs-block() {
                        &:first-child {
                            padding: 10px 15px;
                        }
                    }
                }
            } //.l-slide-block--xs .l-slide-block__body
        } //.l-slide-block--xs .l-slide-block
    } //.l-slide-block--xs

    &--menu {
        z-index: z(dropdown) - 1;
        margin-bottom: $base-header-size--small;
    } //.l-slide-block--menu

    &--shadow {
        &.is-open {
            box-shadow: 2px -10px 10px rgba(0, 0, 0, 0.2);
        }
    } //.l-slide-block--shadow

    .bb-input {
        &__row {
            .bb-select {
                @include xs-block() {
                    width: 100%;
                }
            }
        } //.l-slide-block .bb-input__row

        &__title {
            position: relative;
            top: -2px;
            display: block;
            flex-shrink: 0;
            margin-bottom: 7px;
            color: $gray--light;
            font-size: 14px;
            line-height: 1;
        } //.l-slide-block .bb-input__title

        &__input {
            width: 100%;
        } //.l-slide-block__input

        &.select-color {
            .bb-input__title {
                white-space: nowrap;
            }
        } //.l-slide-block.select-color
    } //.l-slide-block .bb-input

    .services {
        @include xs-block() {
            margin: 0 -15px;
        }

        &__item {
            &.bb-accordeon__item {
                &:first-child {
                    border-top: 0;
                }
            }
        } //.l-slide-block .services__item
    } //.l-slide-block .services

    .album-item {
        &__img {
            border-radius: $border-radius;
        } //.l-slide-block .album-item__img

        &__desc {
            padding-right: 2px;
            padding-left: 2px;
        } //.l-slide-block .album-item__desc
    } //.l-slide-block .album-item

    .item-services {
        &__header {
            padding-left: 7px;
            // background-color: #fff;
        } //.l-slide-block .item-services__header
    } //.l-slide-block .item-services

    .item-category-services {
        padding-left: 3px;
    } //.l-slide-block .item-category-services

    .select-color__item {
        p {
            max-width: 280px;
        } //.l-slide-block .select-color__item p
    } //.l-slide-block .select-color__item

    .custom-select-dropdown {
        &__option {
            padding: 10px;
        } //.l-slide-block .custom-select-dropdown__option
    } //.l-slide-block .custom-select-dropdown

    .datepicker-inline {
        position: static;
        opacity: 1;
        display: block;
        transform: translate(0);
    } //.l-slide-block .datepicker-inline

    .datepicker {
        &--cell-day {
            &.-other-month- {
                &.-selected- {
                    background-color: rgba(#1279e6, 0.8);

                    &.-focus- {
                        background-color: rgba(#1279e6, 0.5);
                    }
                }
            } //.l-slide-block .datepicker--cell-day -other-month-

            &.-focus- {
                background-color: rgba(#1279e6, 0.05);
            } //.l-slide-block .datepicker--cell-day -focus-

            &.-selected- {
                background-color: rgba(#1279e6, 0.8);

                &.-focus- {
                    background-color: rgba(#1279e6, 0.5);
                }
            } //.l-slide-block .datepicker--cell-day.-selected-

            &.is-checked {
                background-color: rgba(#1279e6, 0.2);

                &.-focus- {
                    background-color: rgba(#1279e6, 0.5);
                }

                &.-selected- {
                    background-color: rgba(#1279e6, 0.8);

                    &.-focus- {
                        background-color: rgba(#1279e6, 0.5);
                    }
                } //.l-slide-block .datepicker--cell-day.-selected-
            } //.l-slide-block .datepicker--cell-day
        } //.l-slide-block .datepicker--cell-day
    } //.l-slide-block .datepicker

    .bb-dropdown__list {
        min-width: 375px;
    } //.l-slide-block .bb-dropdown__list

    .widget {
        border-right: 0;
        border-left: 0;

        &:not(.widget--header-transform) {
            &__header {
                @include xs-block() {
                    padding: 15px;

                    & + .widget__body {
                        padding-top: 0;
                    }
                }
            } //.l-slide-block .widget__header
        }

        &__body {
            &:not(.no-offset) {
                @include xs-block() {
                    padding: 15px;
                }
            }
        } //.l-slide-block .widget__body
    } //.l-slide-block .widget

    &[data-slide-block='select-master'] {
        .master-picker__item {
            margin-bottom: 20px;
            border: 1px solid $color-border--lightest;

            &:last-child {
                margin-bottom: 0;
            }
        }
    } //.l-slide-block[data-slide-block='select-master']

    &[data-slide-block='filter'],
    &[data-slide-block|='setting'] {
        z-index: z(popup) + 2;

        @include xs-block() {
            max-width: 320px;
        }

        @include i5-block() {
            max-width: 100%;
        }

        &.is-open {
            & ~ .l-slide-block-overlay {
                z-index: z(popup);
            }
        }
    } //.l-slide-block[data-slide-block="filter"]
} //.l-slide-block

.l-slide-block-box {
    &__title {
        position: relative;
        background-color: transparent;
        @include border(bottom);
        @include flexCenter;

        .info-block {
            position: absolute;
            top: 20px;
            right: 10px;
        }

        .bb-dropdown__list {
            min-width: 328px;
        }
    } //.l-slide-block-box__title

    .o-list-title {
        width: 100%;
    } //.l-slide-block-box .o-list-title
} //.l-slide-block-box

.l-slide-block-overlay {
    position: fixed;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    z-index: z(overlay) - 1;
    display: none;
} //.l-slide-block-overlay
