$checkbox-transform-window-width: 768px !default;
$checkbox-color-main: $gray--dark !default;

.bb-checkbox {
    position: relative;
    display: flex;
    flex-direction: column;
    flex-shrink: 0;
    flex-wrap: wrap;
    justify-content: center;
    padding-left: 27px;
    font-size: 14px;
    cursor: pointer;
    @include nosel;

    &:hover {
        @include min(xs) {
            .bb-checkbox__box {
                border: 2px solid $checkbox-color-main;
            }
        }
    } //.bb-checkbox:hover

    @each $theme, $color in $button-colors {
        &--#{$theme} {
            &:hover {
                .bb-checkbox__box {
                    border-color: $color;
                }
            }

            input:checked + .bb-checkbox__box {
                border-color: $color;
                background-color: $color;
            }

            &.bb-checkbox--radio {
                &:hover {
                    .bb-checkbox__box::before {
                        background-color: $color;
                    } //.box-cart__inner .bb-checkbox--radio:hover .bb-checkbox__box::before

                    input:checked + .bb-checkbox__box {
                        border-color: $color;
                        background-color: transparent;

                        &::before {
                            background-color: $color;
                        } //.box-cart__inner .bb-checkbox--radio:hover input:checked + .bb-checkbox__box::before
                    } //.box-cart__inner .bb-checkbox--radio:hover input:checked + .bb-checkbox__box
                }

                .bb-checkbox__box::before {
                    background-color: $color;
                } //.box-cart__inner .bb-checkbox--radio .bb-checkbox__box::before

                input:checked + .bb-checkbox__box {
                    border-color: $color;
                    background-color: transparent;
                }
            }
        }
    }

    &__wrap {
        flex-wrap: wrap;
        align-items: flex-start;
        width: 100%;
        text-align: center;
        @include flexCenter;

        &--column {
            flex-direction: column;
            align-items: flex-start;
        } //.bb-checkbox__wrap--column

        .bb-checkbox {
            margin-right: 15px;

            &:last-child {
                margin-right: 0;
            }
        }
    } //.bb-checkbox__wrap

    &__row {
        display: flex;
        flex-wrap: wrap;

        & > .bb-checkbox {
            margin-right: 15px;
            margin-bottom: 15px;

            &:last-child {
                margin-right: 0;
            }
        } //.bb-checkbox__row > .bb-checkbox
    } //.bb-checkbox__row

    &__list {
        &.column-2 {
            @include row-flex;

            @include xs-block() {
                margin: 0;
            }

            .bb-checkbox {
                @include col;
                @include size(6);
                @include size-sm(12);

                @include xs-block() {
                    width: 100%;
                    margin: 0;
                    // padding: 15px 30px 15px 0;
                    // border-bottom: 1px solid $color-border--lightest;
                }

                // &__box {
                //     @include xs-block() {
                //         width: 22px;
                //         height: 22px;
                //         margin: 0;
                //         left: auto;
                //         right: 0;
                //         top: 50%;
                //         @include tY(-50%);

                //         &::before {
                //             font-size: 15px;
                //             left: 2px;
                //         }
                //     }
                // } //.bb-checkbox__list.column-2 .bb-checkbox__box

                // &__title {
                //     @include text-overflow;

                //     @include xs-block() {
                //         font-size: 16px;
                //     }
                // } //.bb-checkbox__list.column-2 .bb-checkbox__title

                // &__list {
                //     @include row-flex;

                //     @include max(xs) {
                //         display: block;
                //         margin: 15px 0 0;
                //     }
                // } //.bb-checkbox__list.column-2 .bb-checkbox__list
            } //.bb-checkbox__list.column-2 .bb-checkbox
        } //.bb-checkbox__list.column-2
    } //.bb-checkbox__list

    &__title {
        span {
            color: $gray--light;
            font-style: italic;
            font-weight: 400;

            @include max(md) {
                display: block;
            }
        }
    } //.bb-checkbox__title

    &__subtitle {
        color: $color-text--light;
        font-size: 13px;
    } //.bb-checkbox__subtitle

    &__dell {
        z-index: z(default);
        display: none;
        background-color: rgba(#fff, 0.8);
        @include coverdiv;

        .icon {
            width: 32px;
            height: 32px;
            fill: $checkbox-color-main;
            @include centre;
        }
    } //.bb-checkbox__dell

    &__icon {
        position: absolute;
        right: 0;
        bottom: 0;
        font-size: 22px;

        &.fa-whatsapp {
            font-size: 24px;
        }
    } //.bb-checkbox__icon

    &__box {
        position: absolute;
        top: 1px;
        left: 0;
        display: inline-block;
        width: 18px;
        height: 18px;
        margin-right: 5px;
        border: 1px solid $color-border--light;
        border-radius: 2px;

        &::before {
            display: none;
            @include checkIcon;
        } //.bb-checkbox__box::before
    } //.bb-checkbox__box

    input {
        position: absolute;
        z-index: -1;
        visibility: hidden;
        opacity: 0;

        &:checked + .bb-checkbox__box {
            border: 2px solid $checkbox-color-main;
            background-color: $checkbox-color-main;

            &::before {
                display: block;
            }
        }
    } //.bb-checkbox input

    &--pseudo {
        &.is-checked {
            .bb-checkbox__box {
                border: 2px solid $gray--darken;
                background-color: $gray--darken;

                &::before {
                    display: block;
                }
            }
        }
    } //.bb-checkbox--pseudo

    &--big {
        &:hover {
            .bb-checkbox__box {
                border-color: $color-border;
            }
        }

        input {
            &:checked + .bb-checkbox__box {
                border-color: transparent;
                background-color: $checkbox-color-main;

                &::after {
                    right: 0;
                    left: auto;
                    border-color: transparent;
                }
            }
        }

        .bb-checkbox__box {
            display: block;
            width: 40px;
            background-color: $color-border;
            @include br(10px);

            &::before {
                left: 4px;
                color: #fff;
            }

            &::after {
                content: '';
                position: absolute;
                top: 2%;
                left: 0;
                display: block;
                width: 16px;
                height: 96%;
                background-color: #fff;
                @include br(50%);
                @include tr;
            }
        }
    } //.bb-checkbox--big

    &--radio {
        background-color: transparent;

        &:hover {
            input {
                &:checked + .bb-checkbox__box {
                    border-color: lighten($checkbox-color-main, 7%);

                    &::before {
                        background-color: lighten($checkbox-color-main, 7%);
                    }
                }
            }
        }

        &_inline {
            width: 50%;
            padding: 7px 0 9px;
            border: 1px solid $color-border--lighten;
            border-right-color: transparent;
            background-color: $gray--lightest;
            color: $color-text;

            &:hover {
                border-color: $color-border--dark;
            }

            &:first-child {
                border-radius: $border-radius 0 0 $border-radius;
                border-color: $color-border--lighten;
                border-right-color: transparent;
            }

            &:last-child {
                border-radius: 0 $border-radius $border-radius 0;
                border-color: $color-border--lighten;
            }

            &.is-checked {
                background-color: #fff;
                color: $color-text;

                &::before {
                    color: $gray--darken;
                }
            }

            .bb-checkbox__box {
                display: none;
            }
        } //.bb-checkbox--radio_inline

        input {
            &:checked + .bb-checkbox__box {
                border: 2px solid $checkbox-color-main;
                background-color: transparent;

                &::before {
                    display: block;
                    //. background-color: $checkbox-color-main;
                }
            }
        }

        .bb-checkbox__box {
            display: inline-block;
            border-radius: 50%;
            background-color: transparent;

            &::before {
                content: '';
                display: none;
                width: 8px;
                height: 8px;
                border: 0;
                border-radius: 50%;
                background-color: $checkbox-color-main;
                @include centre;
            }
        }
    } //.bb-checkbox--radio

    &--block {
        display: flex;
        width: 100%;
        margin: 0;
        padding: 19px 0 19px 25px;

        @include xs-block() {
            padding-left: 0;
        }

        &:hover {
            border-color: $color-bg--hover;
            background-color: $color-bg--hover;

            & + .bb-checkbox--block {
                border-color: $color-bg--hover;
            }
        }

        & + .bb-checkbox--block {
            border-top: 1px solid $color-bg--lighten;

            &:hover {
                border-color: $color-bg--hover;
            }
        }

        // &:hover {
        //     // background-color: $color-gray--hover;
        // }

        .bb-checkbox {
            &__box {
                top: 50%;
                left: 0;
                @include tY(-50%);

                @include xs-block() {
                    top: 50%;
                    right: 0;
                    left: auto;
                    width: 22px;
                    height: 22px;
                    margin: 0;
                    @include tY(-50%);

                    &::before {
                        top: 2px;
                        left: 6px;
                        font-size: 15px;
                    }
                }
            }

            &__title {
                color: $gray;
                font-size: 15px;
                font-weight: 500;

                @include xs-block() {
                    font-size: 17px;
                }
            }
        }
    } //.bb-checkbox--block

    &--inline {
        position: relative;
        z-index: z(default);
        border: 1px solid $color-border;
        border-right-color: transparent;

        &:hover {
            border-color: $color-border--dark;
            border-right-color: transparent;
            color: #000;

            &:last-child {
                border-right: 1px solid $color-border--dark;
            }

            & + .bb-checkbox--inline {
                border-left-color: $color-border--dark;
            }
        }

        &:first-child {
            border-radius: 4px 0 0 4px;
        }

        &:last-child {
            border-right: 1px solid $color-border;
            border-radius: 0 4px 4px 0;
        }

        &.is-checked {
            background-color: $checkbox-color-main;
            color: #fff;
            //. border-color: $checkbox-color-main;

            &:hover {
                //. border: 1px solid $checkbox-color-main;
                color: #fff;
            }

            & + .bb-checkbox--inline,
            & + .bb-checkbox--inline.is-checked {
                border-left-color: #fff;
            }
        }

        input {
            &:checked + .bb-checkbox__box {
                background-color: #c4c4c4;
            }
        }

        .bb-checkbox__box {
            display: none;
        }
    } //.bb-checkbox--inline

    &--transform {
        @include sm-block() {
            padding-left: 0;
        }

        .bb-checkbox {
            &__title {
                @include sm-block() {
                    padding: 6px 15px;
                    border: 1px solid $color-border--lighten;
                    border-radius: 50px;
                    font-size: 15px;
                    font-weight: 300;
                }
            }

            &__box {
                @include max-screen($checkbox-transform-window-width) {
                    display: none;
                }
            }
        }

        input {
            @include max-screen($checkbox-transform-window-width) {
                &:checked ~ .bb-checkbox__title {
                    border: 0;
                    background-color: $checkbox-color-main;
                    color: #fff;
                    font-weight: 300;
                }
            }
        }
    } //.bb-checkbox--transform

    &--btn {
        padding-left: 0;

        .bb-checkbox {
            &__title {
                padding: 4px 15px;
                border-radius: $border-radius;
                font-size: 15px;
                @include border(all, $input-color-border);
            } //.bb-checkbox--btn .bb-checkbox__title

            &__box {
                display: none;
            } //.bb-checkbox--btn .bb-checkbox__box
        } //.bb-checkbox--btn .bb-checkbox

        input {
            &:checked ~ .bb-checkbox__title {
                border-color: $checkbox-color-main;
                background-color: $checkbox-color-main;
                color: #fff;
            }
        }
    } //.bb-checkbox--btn

    &--button {
        position: relative;
        align-items: flex-start;
        width: 100%;
        padding: 20px;
        border: 1px solid $color-border-very-light;
        border-radius: $border-radius * 3;
        background-color: #fff;
        text-align: left;
        cursor: pointer;
        @include flexColumn;

        @include xs-block() {
            display: flex;
            flex-wrap: wrap;
            padding: 20px 15px;
        }

        &:hover {
            box-shadow: 0 0 10px $color-border-very-light;
            @include border(all);
        }

        &.is-checked {
            border: 1px solid $color-success;
            box-shadow: inset 0 0 0 1px $color-success;
        }

        & + .bb-checkbox--button {
            margin-top: 15px;
        }

        .bb-checkbox {
            &__box {
                left: 15px;
                margin-right: 0;
                border-radius: 50%;
                @include centre('y');

                & ~ .bb-checkbox__title {
                    margin-left: 26px;

                    @include xs-block() {
                        margin-left: 30px;
                    }
                }

                & ~ .bb-checkbox__subtitle {
                    margin-left: 26px;

                    @include xs-block() {
                        margin-left: 30px;
                    }
                }
            } //.bb-checkbox--btn .bb-checkbox__box

            &__title {
                border: 0;
                color: $gray--darken;
                font-weight: 500;

                @include xs-block() {
                    font-size: 16px;
                }
            } //.bb-checkbox--btn .bb-checkbox__title

            // &__subtitle {
            //     margin-left: 26px;
            // } //.bb-checkbox--btn .bb-checkbox__subtitle

            &__img {
                width: 150px;
                height: 100%;
                margin-left: 26px;
            } //.bb-checkbox--btn .bb-checkbox__img
        } //.bb-checkbox--btn
    } //.bb-checkbox--button

    &--small {
        padding: 2px 8px;
        border: 1px solid $color-border--light;

        &:hover {
            border-color: $color-border;
        }

        & + .bb-checkbox--small {
            border-left-color: transparent;
        }

        &.is-checked {
            background-color: #ffeba0;
        }

        .bb-checkbox {
            &__title {
                font-size: 13px;
                line-height: 24px;
            }
        }
    } //.bb-checkbox--small

    &--slide {
        // position: relative;
        // left: 3px; //Не задавать напрямую
        display: flex;
        flex-direction: row;
        flex-wrap: nowrap;
        align-items: flex-start;
        // margin-bottom: 20px; //Не задавать напрямую
        padding-left: 0;

        &:hover {
            .bb-checkbox__box {
                border: 0 !important;
            }
        }

        & + .bb-checkbox--small {
            border-left-color: transparent;
        }

        &.is-checked {
            .bb-checkbox {
                &__box {
                    // border-color: transparent;
                    border: 0;
                    // background-color: lighten($checkbox-color-main, 17%);
                    background-color: $color-accent;

                    &::before {
                        display: none;

                        // display: block;
                        // content: '';
                        // right: auto;
                        // left: 7px;
                    } //.bb-checkbox--slide.is-checked .bb-checkbox__box::before

                    &::after {
                        left: 29px;
                        background-color: #fff;
                    } //.bb-checkbox--slide.is-checked .bb-checkbox__box::after
                } //.bb-checkbox--slide.is-checked .bb-checkbox__box

                &__title {
                    span {
                        &:first-child {
                            opacity: 1;
                            transform: translateY(55%);
                        }

                        &:last-child {
                            opacity: 0;
                            transform: translateY(100%);
                        }
                    }

                    &--true {
                        color: $color-success;
                    }

                    &--false {
                        color: $gray--light;
                    }
                } //.bb-checkbox--slide.is-checked .bb-checkbox__title
            } //.bb-checkbox--slide.is-checked .bb-checkbox
        } //.bb-checkbox--slide.is-checked

        input {
            &:checked + .bb-checkbox__box {
                // border-color: transparent;
                border: 0;
                // background-color: $checkbox-color-main;
                background-color: $color-accent;

                &::before {
                    display: none;
                    // display: block;
                    // content: '';
                    // right: auto;
                    // left: 7px;
                }

                &::after {
                    right: 3px;
                    left: auto;
                    background-color: #fff;
                }
            }
        } //.bb-checkbox--slide input

        .bb-checkbox {
            &__title {
                position: relative;
                margin: auto 10px auto 0;
                overflow: hidden;
                font-size: 14px;
                // font-weight: 500;
                line-height: 1;
                @include flexColumn;

                span {
                    transition: $trsMenu;
                    color: $color-text;
                    font-weight: 500;
                    text-align: right;

                    &:first-child {
                        opacity: 0;
                        transform: translateY(-100%);
                    }

                    &:last-child {
                        opacity: 1;
                        transform: translateY(-45%);
                    }
                }

                &--left {
                    margin-right: 10px;
                } //.bb-checkbox--slide .bb-checkbox__title--left

                &--right {
                    margin-left: 10px;
                } //.bb-checkbox--slide .bb-checkbox__title--right

                &--false {
                    color: $color-success;
                } //.bb-checkbox--slide .bb-checkbox__title--false

                &--true {
                    color: $gray--light;
                } //.bb-checkbox--slide .bb-checkbox__title--true

                &--active {
                } //.bb-checkbox--slide .bb-checkbox__title--active
            } //.bb-checkbox--slide .bb-checkbox__title

            &__box {
                position: relative;
                top: auto;
                flex-shrink: 0;
                width: 53px;
                height: 27px;
                margin-right: 0;
                margin-left: -3px;
                border: 0;
                border-radius: 50px;
                background-color: $gray--lightest;

                &::before {
                    display: none;
                    // content: '';
                    // font-weight: 600;
                    // line-height: 1;
                    // // text-transform: uppercase;
                    // font-size: 12px;
                    // display: block;
                    // position: absolute;
                    // top: 14px;
                    // right: 5px;
                    // left: auto;
                }

                &::after {
                    content: '';
                    position: absolute;
                    top: 3px;
                    left: 3px;
                    display: block;
                    width: 21px;
                    height: 21px;
                    transition: $trsMenu;
                    border-radius: 50%;
                    background-color: #fff;
                    box-shadow: $bxs-box;
                }
            } //.bb-checkbox--slide .bb-checkbox__box
        } //.bb-checkbox--slide .bb-checkbox
    } //.bb-checkbox--slide

    &--control {
        display: flex;
        flex-wrap: nowrap;
        align-items: center;

        .bb-checkbox {
            &__box {
                margin-left: auto;
            } //.bb-checkbox--slide .bb-checkbox__box
        } //.bb-checkbox--slide .bb-checkbox
    } //.bb-checkbox--control

    &--toggle {
        align-items: flex-start;
        justify-content: flex-start;
        @include flexColumn;

        .bb-checkbox {
            &__box {
                top: 50%;
                @include tY(-50%);
            }
        }
    } //.bb-checkbox--toggle

    &--icon {
        position: relative;
        align-items: flex-start;
        margin: 0;
        padding: 15px 65px 15px 25px;
        @include flexColumn;

        .bb-checkbox {
            &__title {
                font-size: 17px;
                font-weight: 500;
            } //.bb-checkbox--icon .bb-checkbox__title

            &__subtitle {
                font-size: 14px;
                font-style: italic;
            } //.bb-checkbox--icon .bb-checkbox__subtitle

            &__icon {
                top: 60%;
                right: 17px;
                width: 32px;
                height: 32px;
                @include centre('y');
            } //.bb-checkbox--icon .bb-checkbox__icon

            &__box {
                top: 17px;
            }
        }

        &.is-checked {
            .bb-checkbox__icon {
                fill: $color-accent;
            }
        }
    } //.bb-checkbox--icon

    &--toggleIcon {
        .bb-checkbox {
            &__icon {
                top: 50%;
                right: 15px;
                bottom: auto;
                width: 26px;
                height: 26px;
                @include tY(-50%);

                &--remove {
                    right: 16px;
                    display: none;
                    width: 24px;
                    height: 24px;
                    fill: $color-danger;
                }

                &--check {
                    right: 16px;
                    display: none;
                    width: 24px;
                    height: 24px;
                    fill: $color-success;
                }
            }
        }

        &.is-checked {
            .bb-checkbox {
                &__icon {
                    display: none;

                    &--remove,
                    &--check {
                        display: block;
                    }
                }
            }
        }
    } //.bb-checkbox--toggleIcon

    &--border {
        border: 1px solid $color-border--lighten;
        border-radius: $border-radius;
        @include flex;

        &.is-checked {
            border-color: $color-accent;
        }
    } //.bb-checkbox--border

    &--image {
        padding-left: 0;

        &:focus,
        &:hover {
            &::before {
                content: '';
                display: block;
                background-color: rgba(#fff, 0.5);
                @include coverdiv;
            }
        }

        &.is-checked {
            .bb-checkbox {
                &__box {
                    display: flex;
                }
            }
        }

        .bb-checkbox {
            &__box {
                position: absolute;
                top: 3px;
                right: 3px;
                left: auto;
                display: none;
                align-items: center;
                justify-content: center;
                margin-right: 0;
                border-width: 2px;
                border-radius: 50%;
                border-color: $color-accent;
                background-color: $color-accent;
                color: #fff;
                font-size: 12px;
                text-align: center;

                &::before {
                    display: none;
                }
            } //.bb-checkbox--image .bb-checkbox__box
        }

        input {
            &:checked + .bb-checkbox__box {
                border-color: $color-accent;
                background-color: $color-accent;

                &::before {
                    display: none;
                }
            }
        }
    } //.bb-checkbox--image

    &--master {
        padding: 10px 27px 10px 15px;
        @include flexCenter;

        & + .bb-checkbox--master {
            @include border(top);
        }

        .bb-checkbox {
            &__box {
                right: 11px;
                left: auto;
                margin-right: 0;
                @include centre('y');
            } //.bb-checkbox--master .bb-checkbox__box
        }

        .user {
            width: 100%;

            &__avatar {
                margin-bottom: 0;
            } //.bb-checkbox--master .user__avatar
        } //.bb-checkbox--master .user
    } //.bb-checkbox--master
} //.bb-checkbox

.bb-checkbox-row {
    @include flexCenter;

    .bb-checkbox {
        flex: 1 1 0;
        margin: 0;
        padding: 0;

        &:first-child {
            .bb-checkbox {
                &__title {
                    border-radius: $border-radius 0 0 $border-radius;
                }
            }
        }

        &:last-child {
            .bb-checkbox {
                &__title {
                    border-radius: 0 $border-radius $border-radius 0;
                }
            }
        }

        &:not(:last-child) {
            .bb-checkbox__title {
                border-right: 0;
            }
        }

        &__title {
            display: inline-block;
            width: 100%;
            padding: 3px 10px;
            border: 1px solid $input-color-border;
            background-color: transparent;
            text-align: center;

            &:hover {
                border-color: $input-color-border--hover;
            }
        } //.bb-checkbox-row .bb-checkbox__title

        &__box {
            display: none;

            &::before {
                display: none;
            }
        } //.bb-checkbox-row .bb-checkbox__box

        input {
            &:checked ~ .bb-checkbox__title {
                background-color: #a0a0a0;
                color: #fff;
            }
        } //.bb-checkbox-row .bb-checkbox input

        &.is-checked {
            & + .bb-checkbox {
                .bb-checkbox__title {
                    border-left-color: $input-color-border--hover;
                }
            }

            .bb-checkbox__title {
                border-color: $input-color-border--hover;
                background-color: $color-gray--hover;
                // color: #fff;
                color: darken($color-text, 15%);
            }
        }
    } //.bb-checkbox-row .bb-checkbox
} //.bb-checkbox-row

.bb-checkbox-slide {
    @include flexCenter;

    .bb-checkbox {
        &__box {
            margin: 4px 17px 0 15px;
        }
    }
} //.bb-checkbox-slide

.radio-group {
    display: inline-flex;
    border-radius: $border-radius;
    @include border(all);

    &__item {
        position: relative;
        padding-left: 0;

        &:first-child {
            border-radius: $border-radius 0 0 $border-radius;
        }

        &:last-child {
            border-radius: 0 $border-radius $border-radius 0;
        }

        .bb-checkbox {
            &__title {
                padding: 5px 10px;
                background-color: #fff;
            }

            &__box {
                position: absolute;
                width: 1px;
                height: 1px;
                margin: -1px;
                padding: 0;
                overflow: hidden;
                clip: rect(0 0 0 0);
                border: 0;
                white-space: nowrap;
                clip-path: inset(50%);
            }
        }

        input {
            position: absolute;
            width: 1px;
            height: 1px;
            margin: -1px;
            padding: 0;
            overflow: hidden;
            clip: rect(0 0 0 0);
            border: 0;
            white-space: nowrap;
            clip-path: inset(50%);

            &:checked {
                & ~ .bb-checkbox__title {
                    background-color: $gray--lightest;
                }
            }
        }

        & + .radio-group__item {
            @include border(left);
        }
    }
}
