.page-instagram {
    .main {
        background-color: #fff;
    } //.page-instagram .main
} //.page-instagram

.beautylink {
    position: relative;
    flex-grow: 1;
    background-color: #fff;
    @include flexColumn;

    &:not(.beautylink--edit-mode) {
        .beautylink {
            &__change-bg {
                display: none;
            }

            &__edit {
                display: none;
            } //.beautylink:not(.beautylink--edit-mode) .beautylink__edit
        } //.beautylink:not(.beautylink--edit-mode) .beautylink

        .edit-block {
            border: 0;

            &__icon {
                display: none;
            } //.beautylink:not(.beautylink--edit-mode) .edit-block__icon
        } //.beautylink:not(.beautylink--edit-mode) .edit-block

        .field-edit {
            &__btn {
                display: none;
            } //.beautylink:not(.beautylink--edit-mode) .field-edit__btn
        } //.beautylink:not(.beautylink--edit-mode) .field-edit
    } //.beautylink:not(.beautylink--edit-mode)

    &__inner {
        width: 100%;
        max-width: 650px;
        margin: auto;

        @include sm-block() {
            padding: 0 10px;
        }

        &.no-offset {
            padding: 0;
        } //.beautylink__inner.no-offset

        .link-box {
            padding: 15px;
            border: 1px solid $color-border--lightest;

            // &__btn {
            //     &:first-of-type {
            //         margin-left: auto;
            //     }
            // }//.beautylink__inner .link-box__btn

            // .bb-input--title {
            //     width: 82%;

            //     @include xs-block() {
            //         width: 72%;
            //     }

            //     @include i5-block() {
            //         width: 65%;
            //     }
            // }//.beautylink__inner .link-box .bb-input--title
        } //.beautylink__inner .link-box
    } //.beautylink__inner

    &__header {
        position: relative;
        flex-shrink: 0;
        justify-content: center;
        padding: 40px 0 50px;
        // padding-top: 10px;
        text-align: center;
        // min-height: 200px;
        @include flexColumn;

        &--toner {
            position: absolute;
            top: 0;
            width: 100%;
            height: 100%;
            background: rgba(0, 0, 0, 0.5);
        } //.beautylink__header--toner
    } //.beautylink__header

    &__content {
        flex: 1;
        // margin-bottom: 20px;

        & > .top-line {
            max-width: 650px;
            margin: 20px auto;

            @include xs-block() {
                margin-right: 10px;
                margin-left: 10px;
            }

            .top-line__sorting {
                @include min(md) {
                    padding: 10px 0;
                }
            }

            & ~ .beautylink__inner {
                .beautylink__entry {
                    &:first-child {
                        margin-top: 0;
                    }
                }
            }
        } //.beautylink__content>.top-line
    } //.beautylink__content

    &__footer {
        display: flex;
        flex-shrink: 0;
        align-items: center;
        justify-content: center;
        width: 100%;
        margin-top: 20px;
        padding: 10px 0;
        background: #fff;
        @include border(top);

        @include xs-block() {
            padding: 10px 0;
        }

        span {
            font-size: 14px;
        } //.beautylink__footer span
    } //.beautylink__footer

    &__change-bg {
        align-items: center;
        min-height: 200px;
        margin: 0;
        padding: 40px 0;
        border: 1px dashed $color-border--light;
        border-radius: $border-radius;
        cursor: pointer;

        .empty {
            &__icon {
                width: 25px;
                height: 25px;
                margin-bottom: 10px;
                fill: $gray;

                &.icon-plus--filled {
                    display: none;
                } //.beautylink__change-bg .empty__icon.icon-plus--filled

                &.icon-photo-camera {
                    margin-bottom: 0;
                } //.beautylink__change-bg .empty__icon.icon-photo-camera
            } //.beautylink__change-bg .empty__icon

            &__text {
                margin-bottom: 0;
            } //.beautylink__change-bg .empty__text
        } //.beautylink__change-bg .empty

        &[data-dropdown-position^='right'] {
            &.is-active,
            &:hover {
                .bb-dropdown {
                    &__list {
                        top: 92%;

                        @include xs-block() {
                            top: 51%;
                        }
                    }
                }
            }

            .bb-dropdown {
                &__list {
                    right: 2px;

                    @include xs-block() {
                        top: 54%;
                        right: 46.9%;
                    }
                } ///.beautylink__change-bg[data-dropdown-position^=right] .bb-dropdown__list
            }
        } //.beautylink__change-bg[data-dropdown-position^=right]
    } //.beautylink__change-bg

    &__bg-edit {
        position: absolute;
        right: 5px;
        bottom: 8px;
        padding: 0 5px;

        .icon {
            width: 20px;
            height: 20px;
            fill: #fff;
        }
    } //.beautylink__bg-edit

    &__box {
        &-title {
            display: block;
            margin-bottom: 20px; //Не затирать
            padding: 15px 0;
            background-color: $color-bg--lightest;
            font-size: 14px;
            // color: #c7c7c7;
            font-weight: 500;
            text-align: center;
            text-transform: uppercase;

            @include min(sm) {
                margin-bottom: 20px;
            }

            &.no-offset {
                @include min(sm) {
                    margin-bottom: 0;
                }

                @include xs-block() {
                    margin-bottom: 0;
                }

                & + .master-picker-item {
                    margin-top: 0;
                    @include xs-block() {
                        margin-top: 0;
                    }
                }

                & + .time-picker__time-box {
                    @include border(top);
                    @include border(bottom);
                }
            } //.beautylink__box-title.no-offset
        } //.beautylink__box-title

        .beautylink__services {
            margin: 0;

            @include xs-block() {
                margin: 0;
            }
        } //.beautylink__box .beautylink__services

        & > .button-box {
            @include xs-block() {
                padding: 0 10px;
            }
        } //.beautylink__box > .button-box

        &.is-open {
            margin-bottom: $gutter;
        } //.beautylink__box.is-open

        &.no-offset {
            margin-bottom: 0;
        } //.beautylink__box.no-offset
    } //.beautylink__box

    &__profile {
        margin: -45px 0 45px;

        .user {
            &__avatar {
                margin-bottom: 10px;
            } //.beautylink__profile .user__avatar

            &__name {
                font-size: 18px;
                font-weight: 500;
            } //.beautylink__profile .user__name

            &__status {
                display: block;
                margin-top: 5px;
                font-size: 15px;
            } //.beautylink__profile .user__status

            &__img {
                border: 4px solid #fff;
            }
        } //.beautylink__profile .user
    } //.beautylink__profile

    &__search {
        width: 100%;
        max-width: 100%;
    } //.beautylink__search

    &__services {
        margin: 20px 0 45px;

        @include xs-block() {
            margin: 0 0 45px;
        }

        .bb-accordeon {
            &__item {
                @include xs-block() {
                    border-right: 0;
                    border-bottom: 0;
                    border-left: 0;
                    @include border(top);
                }

                & + .bb-accordeon__item {
                    @include min(sm) {
                        margin-top: 10px;
                    }
                }
            } //.beautylink__services .bb-accordeon__item

            &__title {
                border-bottom: 1px solid $color-border--lightest;
                border-radius: 0;

                @include xs-block() {
                    margin-top: 0;
                    padding: 15px 10px;
                }

                &::before {
                    color: $color-icon--light;
                    @include fa('light');
                }
            } //.beautylink__services .bb-accordeon__title
        } //.beautylink__services .bb-accordeon

        .card-services {
            @include sm-block() {
                margin-bottom: 0;
            }
        } //.beautylink__services .card-services

        &--inmodal {
            margin: 0 0 45px;

            .bb-accordeon {
                &__item {
                    border-right: 0;

                    border-bottom: 0;
                    border-left: 0;
                    border-radius: 0;
                    box-shadow: none;

                    &.is-open {
                        @include border(bottom);
                    }
                } //.beautylink__services--inmodal .bb-accordeon__item

                &__title {
                    border-radius: 0;

                    @include xs-block() {
                        padding: 15px 10px;
                    }

                    &::before {
                        color: $color-icon--light;
                        @include fa('light');
                    }
                } //.beautylink__services--inmodal .bb-accordeon__title
            }

            .card-services {
                &-item {
                    &:last-child {
                        border-bottom: 0;
                    }
                } //.beautylink__services--inmodal .card-services-item
            }
        } //.beautylink__services--inmodal
    } //.beautylink__services

    &__get-link {
        position: fixed;
        right: 0;
        bottom: 0;
        left: 0;
        width: 100%;
        height: 50px;
        border: 0;
        background-color: $color-accent;
        color: #fff;
    } //.beautylink__get-link

    &__list {
        @include xs-block() {
            padding: 0 10px;
        }
    } //.beautylink__list

    &__btn-back {
        position: fixed;
        bottom: 10px;
        left: 10px;
        z-index: z(fixed);
        width: 45px;
        height: 45px;
        padding: 0;
        border: 0;
        border-radius: 50%;
        background-color: #fff;
        box-shadow: $bxs-box;
        @include flexCenter;
        @include tr;

        .icon {
            margin: auto;
            fill: $gray;
        }
    } //.beautylink__btn-back

    &__btn-order {
        position: fixed;
        display: flex;
        justify-content: center;
        padding: 10px;
        background-color: #fff;
        @include border(top);
        @include tr;

        @include min(md) {
            bottom: 0;
            left: 50%;
            @include tX(-50%);

            button {
                width: 100%;
                max-width: 650px;
            }
        }

        @include xs-block() {
            background-color: #fff;
            @include border(top);
        }

        .btn {
            @include xs-block() {
                width: 100%;
            }
        }
    } //.beautylink__btn-order

    &__admin-line,
    &__client-line {
        // position: sticky;
        position: fixed;
        top: 0;
        right: 0;
        left: 0;
        z-index: z(fixed);
        height: $base-header-size;
        padding: 0 0 0 10px;
        background-color: #fff;
        @include border(bottom);
        @include flexCenter;

        @include xs-block() {
            height: $base-header-size--small;
        }

        // & ~ .beautylink__header {
        //     margin-top: 50px;
        //     padding-top: 0;
        // }
    } //.beautylink__admin-line

    &__admin-line {
        .user {
            &__avatar {
                margin-bottom: 0;
            } //.beautylink__admin .user__avatar

            &__img {
                width: 35px;
                height: 35px;
            } //.beautylink__admin .user__img
        } //.beautylink__admin .user

        .header-user {
            margin-left: auto;

            @include xs-block() {
                display: flex;
                padding: 0;

                &::after {
                    right: 5px;
                    display: block;
                }
            }

            &::after {
                top: 52%;
            } //.beautylink__admin-line .header-user::after

            // &__img {
            //     margin-right: 15px;
            //     margin-left: 0;
            // } //.beautylink__admin-line .header-user__img

            &__name {
                font-size: 13px;
            } //.beautylink__admin-line .header-user__name

            &__dropdown {
                top: 60px;
                left: 9px;
            } //.beautylink__admin-line .header-user__dropdown

            &.is-active {
                .header-user {
                    &__dropdown {
                        top: 50px;
                    }
                }
            } //.beautylink__admin-line .header-user:hover

            .info-block {
                &__item {
                    padding: 0;

                    @include min(xs) {
                        border: 0;
                    }

                    &::before {
                        display: none;
                    }
                } //.beautylink__admin-line .header-user .info-block__item

                &__link {
                    @include min(xs) {
                    }
                } //.beautylink__admin-line .header-user .info-block__link
            } //.beautylink__admin-line .header-user .info-block
        } //.beautylink__admin-line .header-user

        .bb-checkbox {
            margin-left: auto;
            @include flexCenter;

            &__title {
                order: -1;
                margin-right: 15px;

                font-size: 13px;
            } //.beautylink__admin .bb-checkbox__title

            &--slide {
                margin-bottom: 0;
            } //.beautylink__admin .bb-checkbox--slide
        } //.beautylink__admin .bb-checkbox

        &.bg--dark {
            background-color: $color-bg--darken;

            .header-user {
                color: $gray--light;

                &:hover {
                    background-color: $color-bg--dark;
                } //.beautylink__admin-line.bg--dark .header-user:hover

                &.is-active {
                    background-color: $color-bg--dark;
                } //.beautylink__admin-line.bg--dark .header-user.is-active
            } //.beautylink__admin-line.bg--dark .header-user

            .bb-checkbox {
                &__title {
                    color: $gray--light;
                } //.beautylink__admin-line.bg--dark .bb-checkbox__title
            } //.beautylink__admin-line.bg--dark .bb-checkbox
        } //.beautylink__admin-line.bg--dark
    } //.beautylink__admin-line

    &__client-line {
        background-color: #fff;

        .btn-icon {
            position: absolute;
            top: 0;
            right: 0;
        }
    } //.beautylink__client-line

    &__title {
        &--left {
            .beautylink {
                &__maintitle {
                    text-align: left;
                } //.beautylink__title--left .beautylink__maintitle

                &__subtitle {
                    text-align: left;
                } //.beautylink__title--left .beautylink__subtitle
            } //.beautylink__title--left
        } //.beautylink__title--left
    } //.beautylink__title

    &__maintitle {
        display: block;
        width: 100%;
        color: #fff;
        font-size: 30px;
        font-weight: 500;
        line-height: 1.2;
    } //.beautylink__maintitle

    &__subtitle {
        margin-top: 5px;
        color: #fff;

        font-size: 14px;
    } //.beautylink__subtitle

    &__entry {
        margin-top: 20px;
        border: 1px solid $color-border--lightest;
        border-radius: $border-radius;
        background-color: #fff;
    } //.beautylink__entry

    &__entry-go {
        position: fixed;
        right: 0;
        bottom: 0;
        left: 0;
        z-index: z(dropdown);
        display: flex;
        align-items: center;
        justify-content: center;
        padding: 15px;
        background-color: #fff;
        @include border(top);

        .btn {
            width: 100%;
        }
    } //.beautylink__entry-go

    &.beautylink--no-bg {
        .edit-block {
            display: none;
        } //.beautylink.beautylink--no-bg .edit-block

        .beautylink__header {
            &--toner {
                display: none;
            }
        }
    } //.beautylink.beautylink--no-bg

    &.is-booked {
        .beautylink {
            &__content {
                margin-bottom: 30px;
            } //.beautylink.is-booked .beautylink__content

            &__btn-back {
                bottom: 70px;
            } //.beautylink.is-booked .beautylink__btn-back

            &__btn-order {
                display: flex;
                @include tY(0);

                @include min(sm) {
                    transform: translate(-50%, 0);
                }
            } //.beautylink.is-booked .beautylink__btn-order
        }
    } //.beautylink.is-booked

    &.has-top-line {
        margin-top: $base-header-size;

        @include xs-block() {
            margin-top: $base-header-size--small;
        }
    } //.beautylink.has-top-line

    &--scroll {
        @include scroll;
    } //.beautylink--scroll

    &--fixlink {
        margin-bottom: 50px;
    } //.beautylink--fixlink

    &--head-line {
        padding-top: 50px;

        .beautylink {
            &__admin-line {
                display: flex;
            }
        }
    } //.beautylink--admin-mode

    &--edit-mode {
        &:not(.beautylink--no-bg) {
            .beautylink {
                &__change-bg {
                    position: absolute;
                    right: 0;
                    bottom: 0;
                    z-index: z(fixed);
                    width: auto;
                    min-height: auto;
                    padding: 10px;
                    border: 0;

                    .icon {
                        fill: #fff;
                    } //.beautylink--edit-mode:not(.beautylink--no-bg) .beautylink__change-bg .icon

                    .empty {
                        &__text {
                            display: none;
                        } //.beautylink--edit-mode:not(.beautylink--no-bg) .beautylink__change-bg .empty__text

                        &__icon {
                            &.icon-photo-camera {
                                display: block;
                            } //.beautylink--edit-mode:not(.beautylink--no-bg) .beautylink__change-bg.empty__icon.icon-photo-camera

                            &.icon-plus--filled {
                                display: none;
                            }
                        } //.beautylink--edit-mode:not(.beautylink--no-bg) .beautylink__change-bg.empty__icon
                    } //.beautylink--edit-mode:not(.beautylink--no-bg) .beautylink__change-bg.empty
                } //.beautylink--edit-mode:not(.beautylink--no-bg) .beautylink__change-bg
            }
        } //.beautylink--edit-mode:not(.beautylink--no-bg)

        .beautylink {
            &__title {
                display: block;
            } //.beautylink--edit-mode .beautylink__title

            &__bg-edit {
                display: block;
            } //.beautylink--edit-mode .beautylink__bg-edit
        } //.beautylink--edit-mode .beautylink
    } //.beautylink--edit-mode

    &--no-bg {
        background: transparent;

        .beautylink {
            // &__title {
            //     display: none;
            // } //.beautylink--no-bg .beautylink__title

            &__change-bg {
                display: flex;

                .empty {
                    &__icon {
                        &.icon-photo-camera {
                            display: none;
                        } //.beautylink--edit-mode .beautylink__change-bg .empty__icon.photo-camera

                        &.icon-plus--filled {
                            display: block;
                        } //.beautylink--edit-mode .beautylink__change-bg .empty__icon.plus--filled
                    } //.beautylink--edit-mode .beautylink__change-bg .empty__icon
                } //.beautylink--edit-mode .beautylink__change-bg .empty

                &[data-dropdown-position^='right'] {
                    &.is-active,
                    &:hover {
                        .bb-dropdown {
                            &__list {
                                top: 51%;
                                right: 46.8%;

                                @include xs-block() {
                                    top: 51%;
                                }
                            }
                        }
                    }

                    .bb-dropdown {
                        &__list {
                            top: 54%;
                            // right: 2px;
                            right: 46.9%;

                            @include xs-block() {
                                top: 54%;
                                right: 46.9%;
                            }
                        } ///.beautylink__change-bg[data-dropdown-position^=right] .bb-dropdown__list
                    }
                } //.beautylink__change-bg[data-dropdown-position^=right]
            } //.beautylink--no-bg .beautylink__change-bg

            &__header {
                background-image: none !important;
            } //.beautylink--no-bg .beautylink__header

            &__bg-edit {
                display: none;
            } //.beautylink--no-bg .beautylink__bg-edit
        } //.beautylink--no-bg .beautylink

        .user {
            &__img {
                border: 5px solid #fff;
            }
        }
    } //.beautylink--no-bg

    &--entry {
        background-color: $color-bg--lightest;

        .beautylink__content {
            background-color: $color-bg--lightest;
        } //.beautylink--entry .beautylink__content

        .powered-by {
            margin-top: 50px;
        } //.beautylink--entry .powered-by
    } //.beautylink--entry

    .btn--big {
        display: flex;
        align-items: center;
        justify-content: center;
        padding: 5px 0;
    } //.beautylink .btn

    .button-box {
        &--column {
            .btn--big {
                width: 100%;
                max-width: 320px;
                margin: 0;
                padding: 5px 10px;

                @include xs-block() {
                    max-width: 85%;
                }

                & + .btn--big {
                    margin-top: 20px;
                } //.beautylink .button-box--column .btn + .btn
            } //.beautylink .button-box--column .btn
        } //.beautylink .button-box--column
    } //.beautylink .button-box

    .order-block {
        &__btn {
            flex-shrink: 0;
            margin-right: 0;
            margin-left: auto;
        } //.beautylink .order-block__btn
    } //.beautylink .order-block

    .edit-block {
        margin: 0 28px;

        @include xs-block() {
            margin: 0 15px;
        }

        &__title {
            line-height: 1.2;
        }

        .field-edit {
            &__input {
                width: 100%;
                font-size: 18px;
            } //.beautylink .edit-block .field-edit__input

            &__text {
                color: #fff;
            } //.beautylink .edit-block .field-edit__text

            & + .field-edit {
                margin-top: 0;
            } //.beautylink .edit-block .field-edit + .field-edit
        } //.beautylink .edit-block
    } //.beautylink .edit-block

    .services {
        margin: 0;
        padding: 0;

        &__list {
            margin-right: 0;
            margin-bottom: 30px;
            margin-left: 0;
            border-bottom: 1px solid $color-border--lightest;
        } //.beautylink .services__list
    } //.beautylink .services

    .item-services {
        &__header {
            padding-top: 5px;
            padding-left: 18px;
            background-color: #fff;
        } //.beautylink .item-services__header

        &__title {
            font-size: 17px;
        } //.beautylink .item-services__title
    } //.beautylink .item-services

    .item-category-services {
        @include sm-block() {
            border-right: 1px solid $color-border--lightest;
        }

        &__inner {
            @include xs-block() {
                padding: 5px 0 6px 4px;
            }
        } //.beautylink .item-category-services__inner

        &__name {
            margin-right: 30px;
            margin-left: 15px;
            padding-left: 0;
        } //.beautylink .item-category-services__name

        &__title {
        } //.beautylink .item-category-services__title

        &__info {
            margin-left: 15px;
        } //.beautylink .item-category-services__info

        &__time {
            left: 20px;

            @include min(sm) {
                margin-right: 0;
                text-align: right;
            }
