.entry {
    position: relative;
    border-radius: $border-radius;
    @include border(all);

    &__item {
        position: relative;
        display: flex;
        flex-direction: column;
        align-items: flex-start;
        justify-content: center;
        min-height: 65px;
        padding: 15px 30px 15px 15px;
        cursor: pointer;
        @include border(top);

        .btn-link--invite {
            display: none;
        }

        &.is-check {
            .entry {
                &__arrow {
                    display: none;
                }

                &__subtext {
                    display: inline-block;
                }
            }

            .btn-reset {
                display: flex;
            }

            .btn-link--invite {
                display: flex;
                @include min(sm) {
                    &:hover {
                        background-color: $color-bg--lighten;
                    }
                }
            }
        }
    } // .entry__item

    &__header {
        position: relative;
        display: flex;
        flex-direction: column;
        align-items: flex-start;
        justify-content: center;
        min-height: 65px;
        padding: 15px;
        cursor: pointer;

        .entry__arrow {
            transform: translateY(-50%) rotateZ(90deg);
            transform-origin: center;
        }

        .btn-link--invite {
            @include min(sm) {
                &:hover {
                    background-color: $color-bg--lighten;
                }
            }
        }
    } // .entry__header

    &__text {
        color: $gray--dark;
        font-size: 16px;
        font-weight: 500;
    } // .entry__text

    &__subtext {
        display: none;
        margin-top: 4px;
        color: $gray--light;
        font-size: 14px;
    } // .entry__subtext

    &__arrow {
        right: 9px;
        width: 10px;
        height: 10px;
        @include centre('y');
    } // .entry__arrow

    &__add {
        position: absolute;
        bottom: 0;
        left: 50%;
        display: flex;
        align-items: center;
        justify-content: center;
        width: 50px;
        height: 50px;
        transform: translate(-50%, 50%);
        border: 1px solid $color-border--lightest;
        border-radius: 50%;
        background-color: #fff;

        .icon {
            width: 20px;
            height: 20px;
            fill: $gray--dark;
        }
    } // .entry__add

    &__dell {
        right: 0;
        @include centre('y');
    } // .entry__dell

    &__record {
        width: 100%;
        margin-top: 10px;
        padding-top: 10px;
        font-size: 14px;
        @include border(top);

        & + & {
            margin-top: 0;
            padding-top: 0;
            border-top: 0;
        }

        span {
            font-weight: 500;
        }
    } // .entry__record

    .btn-link--invite {
        margin-top: 10px;
    } // .entry__item .btn-link--invite

    .btn-reset {
        display: none;
    } // .entry__item .btn-reset

    &.is-open {
        .entry__header {
            .entry__arrow {
                transform: translateY(-50%) rotateZ(-90deg);
            }
        }
    } // .entry__item.is-open

    &--clean {
        .entry {
            &__header {
                display: none;
            } // .entry__item--clean .entry__header

            &__item {
                &:first-child {
                    border: 0;
                }
            } // .entry__item--clean .entry__item
        }
    } // .entry__item--clean

    &--static,
    &--move {
        .entry {
            &__item {
                &:first-child {
                    border-top: 0;
                }
            }

            &__subtext {
                display: block;
                margin-top: 0;
            }

            // &__arrow {
            //     top: 25px;
            // }
        }
    }

    &--move {
        position: fixed;
        right: 0;
        bottom: 0;
        left: 0;
        transform: translateY(100%);
        @include tr;

        &.is-active {
            transform: translateY(0);
            box-shadow: 0px -1px 10px 5px rgba(0, 0, 0, 0.25);
        }
    } //.entry--move
} //.entry

.link-box {
    padding: 10px;

    &__item {
        display: flex;
        align-items: flex-end;

        &:not(:first-child) {
            margin-top: 15px;
        }

        .bb-input {
            flex-grow: 1;
            margin-right: 10px;

            @include xs-block() {
                width: 50%;
            }

            &__input {
                padding-right: 10px;
            }
        } //.link-box__item .bb-input
    } //.link-box__item

    &__btn {
        flex-shrink: 0;
        width: 40px;
        height: 40px;
        padding: 7px;
        // border-radius: $border-radius;
        background-color: $gray--lighten;
        line-height: 1;

        & + .link-box__btn {
            margin-left: 10px;
        }
    } //.link-box__btn
} //.link-box

.pick-out {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 100%;
    height: 50px;
    background-color: #fff;

    &__btn {
        position: relative;
        height: 100%;
        margin: 0 10px;
        padding: 0;
        border: 0;
        background: transparent;
        color: $color-text--light;

        &::after {
            content: '';
            position: absolute;
            right: 0;
            bottom: 0;
            left: 0;
            display: none;
            height: 2px;
            background-color: $gray;
        }

        &.is-active {
            color: $color-text;

            &::after {
                display: block;
            }
        }
    } // .pick-out__btn
} // .pick-out

.modal-worker-review {
    .modal-body {
        padding-bottom: 10px;
        background-color: #fff;
    } // .modal-worker-review .modal-body

    .beautylink__header {
        min-height: 150px;
    } // .modal-worker-review .beautylink__header

    .beautylink__inner {
        padding: 10px;
    } // .modal-worker-review .beautylink__inner

    .beautylink__profile {
        margin: -45px 0 0;
        background-color: #fff;
    } // .modal-worker-review .beautylink__profile

    .item-reviews {
        margin: 10px 10px 0;
    } // .modal-worker-review .item-reviews
} // .modal-worker-review

.modal-body {
    .top-line {
        flex-shrink: 0;
    } //.modal-body .top-line
} //.modal-body
