.worker {
    &__items {
        background-color: #fff;
        @include bS;

        @include xs-block() {
            @include resetBox;
            @include border(top);
            @include border(bottom);
        }

        .phone {
            position: relative;
            z-index: 1;
            color: $gray;

            font-size: 15px;

            @include xs-block() {
                width: 100%;
                margin-top: 5px;
                font-size: 14px;
            }
        } //.worker__items .phone

        .icon-status {
            font-size: 20px;

            & + .icon-status {
                margin-left: $gutter / 2;
            }
        } //.worker__items .icon-status
    } //.worker__items

    &__item {
        position: relative;
        min-height: 60px;
        padding: 15px;
        background-color: #fff;
        cursor: pointer;

        @include xs-block() {
            margin-left: 75px;
            padding: 0;

            &:last-child {
                .row {
                    border-bottom: 0;
                }
            }

            &.item-category-services {
                margin-left: 0;
                @include border(bottom);

                &:last-of-type {
                    border-bottom: 0;
                }
            }
        }

        &:hover {
            &:not(.worker__item--header) {
                @include min(xs) {
                    background-color: $color-gray--hover;

                    .worker {
                        &__controls {
                            display: block;
                        }
                    }
                }
            }
        }

        & + .worker__item:not(.worker__item--header) {
            @include min(xs) {
                border-top: 1px solid $color-border--lightest;
            }
        }

        .row {
            @include xs-block() {
                margin: 0;
                padding: 15px 18px 15px 0;
                border-bottom: 1px solid $color-border--lightest;
            }
        } //.worker__item .row

        .col {
            @include xs-block() {
                margin: 0;
            }
        } //.worker__item .col

        &--header {
            min-height: auto;
            background-color: #fff;
            color: $gray--darkest;
            font-size: 15px;
            font-weight: 500;

            @include xs-block() {
                display: none;
            }
        } //.worker__item--header

        .btn-link--invite {
            display: block;
            margin-top: 5px;
        } //.worker__item .btn-link--invite

        &.item-category-services {
            display: flex;
            padding: 0;
            border-bottom: 0;
        }
    } //.worker__item

    &__pagination {
        margin-top: $gutter * 2;
    } //.worker__pagination

    &__empty {
        margin-bottom: $gutter;
        padding: $gutter;
    } //.worker__empty

    &__user {
        .info-block {
            margin: 0;
            padding: 0;
        } //.worker__user .info-block

        .bb-dropdown {
            &:hover {
                .bb-dropdown {
                    &__list {
                        top: 22px;
                    }
                }
            }

            &__list {
                top: 32px;
            }
        } //.worker__user .bb-dropdown
    } //.worker__user

    &__controls {
        position: absolute;
        top: 50%;
        right: 5px;
        display: none;
        margin-top: -12px;
        margin-left: auto;

        @include max(sm) {
            display: block;
            margin-left: 0;

            .control {
                &__item {
                    margin: 0;
                }
            } //.worker__controls .control

            .info-block {
                position: absolute;
                top: 50%;
                right: -5px;
                z-index: z(fixed) - 1;
                margin: 0;
                margin-top: -10px;
                padding: 10px;

                &::before {
                    transform: rotate(0);
                }

                &.is-active {
                    z-index: z(fixed);
                }
            } //.worker__controls .info-block
        }
    } //.worker__controls

    &__link {
        position: absolute;
        top: 0;
        right: 100%;
        bottom: 0;
        left: 0;
        width: 88%;
        border: 0;
        outline: none;
        background-color: transparent;

        @include min(sm) {
            display: none;
        }
    } //.worker__link

    .category {
        font-size: 13px;

        &__list {
            flex-wrap: wrap;
            margin-right: 70px;
            @include flexCenter;
        } //.category__list

        &__item {
            margin-bottom: 5px;

            &:not(.category__item--more) {
                cursor: default;
            }
        } //.category__item
    } //.worker .category

    .user {
        @include xs-block() {
            width: 100%;
        } //.worker .user__position

        &__img {
            align-self: flex-start;
            margin-right: 5px;

            @include xs-block() {
                position: absolute;
                top: 15px;
                left: -57px;
            }
        } //.worker .user__img

        &__position {
            @include xs-block() {
                display: none;
            }
        } //.worker .user__position

        &__text {
            @include xs-block() {
                width: 100%;
            }
        } //.worker .user__text

        &__confirm {
            width: 100%;
        } //.worker .user__confirm
    } //.worker .user

    .top-line {
        &__bnt {
            &_controls {
                width: 40px;
                height: 40px;
            } //.worker .top-line__bnt_controls
        } //.worker .top-line__bnt

        &__reset {
            @include xs-block() {
                margin: 10px auto 0;
            }
        } //.worker .top-line__reset
    } //.worker .top-line

    .row {
        position: relative;
        align-items: center;
    } //.worker .row

    .col {
        @include flexCenter;

        @include xs-block() {
            flex-wrap: wrap;
        }
    } //.worker .col

    &--small {
        .item-category-services__name {
            padding-left: 10px;
        } //.worker--small .item-category-services__name
    } //.worker--small
} //.worker

.worker-add {
    position: fixed;
    top: 50px;
    right: 0;
    bottom: 51px;
    left: 0;
    z-index: z(modal);
    overflow: hidden;
    transition: $trs;
    background-color: #fff;
    will-change: transform;
    @include tX(100vw);

    @include min(xs) {
        top: 0;
        right: 0;
        bottom: 0;
        left: auto;
        width: 480px;
    }

    &__inner {
        position: absolute;
        top: 0;
        right: 0;
        bottom: 0;
        left: 0;
        padding: $gutter;
        overflow-x: hidden;
        @include scroll;
    } //.worker-add__inner

    &__user {
        display: flex;
        justify-content: center;
    } //.worker-add__uset

    &__icon {
        &--close {
            position: absolute;
            top: 0;
            right: 4px;
            z-index: z(fixed);
            width: 40px;
            height: 40px;
            padding: 10px;
        } //.worker-add__icon--close
    } //.worker-add__icon

    &.is-visible {
        box-shadow: $bxs-with-overlay;
        @include tX(0);
    }

    .user {
        &__avatar {
            margin-right: 0;
        }
    } //.worker-add .user

    .profile-editor {
        &__item {
            &:last-child {
                margin-bottom: 0;
            }
        }

        &__label {
            width: 100%;
            margin-right: 0;
            margin-bottom: 7px;
        }
    } //.worker-add .profile-editor

    .info-block {
        top: 2px;
        display: none;
    } //.worker-add .info-block

    .add-avatar {
        height: 100%;

        &__inner {
            position: relative;
            height: 100%;
            padding: $gutter * 2 $gutter $gutter;
            background-color: #fff;
            @include scroll;
        }

        &__footer {
            bottom: 0;
            padding: $gutter;
        }
    } //.add-avatar
} //.worker-add

.item-worker {
    &__status {
        @include xs-block() {
            margin-top: 3px;
            font-size: 14px;
        }
    } //.item-worker__status

    .user {
        &__name {
            @include xs-block() {
                font-weight: 500;
            }
        }
    }
} //.item-worker
