$analitic-offset: 0px;
$o: 15px;
$om: 2px;

.page-cabinet {
    @include xs-block() {
        background-color: $color-bg--lightest;
    }

    .container {
        @include max(lg) {
            padding: 0;
        }

        &.mob-padding {
            @include sm-block() {
                padding: 10px;
            }
        }
    } //.page-cabinet .container

    .header {
        position: fixed;
        top: 0;
        right: 0;
        left: $base-header-size;
        z-index: z(dropdown);
        transition: margin ease-in-out 200ms;
        background-color: #fff;
        @include border(bottom);

        @include xs-block() {
            left: 0;
            width: 100%;
        }

        &__top,
        &__inner {
            height: $base-header-size;
            @include xs-block() {
                height: $base-header-size--small;
            }
        }

        &__title {
            transition: transform ease-out 0.3s;
        } //.page-cabinet .header__title

        &__inner {
            width: 100%;
            border-bottom: 0;
            @include xs-block() {
                display: flex;
                align-items: center;
            }
        } //.page-cabinet .header__inner

        &__logo {
            @include min-screen(426) {
                display: none;
            }
        } //.page-cabinet .header__logo

        &__sub-nav {
            margin-left: 5px;

            @include md-block() {
                margin-left: 0;
            }

            @include xs-block() {
                margin-left: 0;
            }
        } //.page-cabinet .header__sub-nav

        &.is-moving {
            margin-left: 160px;
        } //.page-cabinet .header.is-moving

        &.bg--dark {
            .header-action__request,
            .header-user {
                border-right: 0;
            }

            .header-action__request-info {
                border: 0;
            }

            .header-user {
                &__img {
                    border: 2px solid $color-border--darken;
                }
            }

            .request-info__icon {
                @include xs-block() {
                    color: #fff;
                }
            }
        } //.page-cabinet .header.bg--dark
    } //.page-cabinet .header

    .header-user {
        &[data-dropdown-position^='right'] {
            .bb-dropdown {
                &__list {
                    right: 10px;

                    &::before,
                    &::after {
                        right: 27px;

                        @include sm-block() {
                            right: 12px;
                        }

                        @include landscape {
                            right: 12px;
                        }
                    }
                }
            }
        }
    } //.page-cabinet .header-user

    .header-action {
        @include xs-block() {
            border-left: 0;
        }

        &__fav,
        &__request {
            width: $base-header-size;
            height: $base-header-size;
            @include baseSize;

            &:hover {
                background-color: rgba($color-bg--lighten, 0.45);

                &::before {
                    color: $gray--darkest;
                }
            }
        }

        &__request {
            margin-left: auto;

            &-info {
                @include xs-block() {
                    border-right: 0;
                    border-left: 0;
                }
            }
        }
    } //.page-cabinet .header-action

    .request {
        &__items {
            @include xs-block() {
                height: calc(100vh - 127px - 50px);
            }
        }

        .bb-dropdown {
            &__list {
                @include dropdownResponseWidth;

                @include xs-block() {
                    right: -50px;
                    width: 425px;
                    border-right: 0;
                    border-bottom: 0;
                    border-left: 0;
                    border-top-color: $color-border--lightest;

                    &::after,
                    &::before {
                        right: 69px;
                    }
                }
            }
        }
    } //.page-cabinet .request

    .request-info {
        &[data-dropdown-position^='right'] {
            & > .bb-dropdown__list {
                bottom: 50px;
            }
        } //.page-cabinet .request-info[data-dropdown-position^=right]>.bb-dropdown__list
    } //.page-cabinet .request-info

    .toggle-menu {
        margin-left: 0;

        @include min(xs) {
            display: none;
        }
    } //.page-cabinet .toggle-mnu

    .info-block {
        &[data-dropdown-position^='right'] {
            @include screen(480, 768) {
                .bb-dropdown {
                    &__list {
                        left: 70px;
                    }
                }
            }
        }
    } //.page-cabinet .info-block

    .btn-fixed {
        &[data-position='bottom right'] {
            right: 20px;
            bottom: 20px;

            @include landscape {
                right: 15px;
                bottom: 15px;
            }

            @include xs-block() {
                right: 13px;
                bottom: 60px;
            }
        } //.page-cabinet .btn-fixed[data-position='bottom right']
    } //.page-cabinet .btn-fixed

    /*
    * Фиксы для beautylink
    * на страницах Cabinet
    */

    .beautylink {
        height: auto;

        &__box {
            &:last-child {
                margin-bottom: 0;
            } //.page-cabinet .beautylink__box:last-child
        } //.page-cabinet .beautylink__box
    } //.page-cabinet .beautylink

    &.menu-open {
        .cabinet {
            width: calc(100% - 220px);
            margin-left: 220px;
        } //.page-cabinet.menu-open .cabinet

        & ~ .preloader--cabinet {
            left: 248px;
        }
    } //.page-cabinet.menu-open

    &.mobile-nav--open {
        .cabinet {
            pointer-events: none;
        }
    } //.page-cabinet.mobile-nav--open

    &.menu-hidden {
        .menu {
            display: none;
        } //.page-cabinet.menu-hidden .menu

        .header {
            left: 0;
        } //.page-cabinet.menu-hidden .header

        .cabinet {
            margin-left: 0;
        } //.page-cabinet.menu-hidden .cabinet
    } //.page-cabinet.menu-hidden

    &.header--hidden {
        .header {
            &__top {
                @include xs-block() {
                    display: none;
                }
            } //.page-cabinet.header--hidden .header__top
        } //.page-cabinet.header--hidden .header
    } //.page-cabinet.header--hidden

    &.has-subnav {
        .cabinet {
            margin-top: $base-header-size + 50;

            @include xs-block() {
                margin-top: 100px;
                padding-top: 10px;
            }

            &.has-add-btn {
                @include xs-block() {
                    margin-top: 100px;
                    padding-top: 65px;
                }

                .top-line {
                    @include xs-block() {
                        margin-top: 10px;
                    }
                }
            } //.page-cabinet.has-subnav .cabinet.has-add-btn
        } //.page-cabinet.has-subnav .cabinet

        .header-user {
            &:hover,
            &.is-active {
                background-color: transparent;
            }

            .bb-dropdown {
                &__list {
                    box-shadow: 0 2px 4px 0 rgba(0, 0, 0, 0.1),
                        0 0 4px 0 rgba(0, 0, 0, 0.1);
                }
            }
        } //.page-cabinet.has-subnav .header-user

        .header-action {
            &__request-info {
                border: 0;
            }
        } //.page-cabinet.has-subnav .header-action

        .request-info {
            &:hover {
                background-color: transparent;
            }
        } //.page-cabinet.has-subnav .request-info

        .btn-select {
            top: 101px;

            &--no-header {
                top: 50px;
            } //.page-cabinet.has-subnav .btn-select--no-header
        } //.page-cabinet.has-subnav .btn-select

        .menu-dropdown {
            top: 50px;
        } //.page-cabinet.has-subnav .menu-dropdown

        &.header--hidden {
            @include xs-block() {
                .cabinet {
                    margin-top: 50px;
                    // padding: 0;
                }
            }
        } //.page-cabinet.has-subnav.header--hidden
    } //.page-cabinet.has-subnav
} //.page-cabinet

.page-instagram {
    .beautylink {
        background-color: #fff;

        @include xs-block() {
            margin: 0 -10px;
            padding-bottom: 30px;
        }

        &__header {
            padding-top: 0;
        } //.page-instagram .beautylink__header
    } //.page-instagram .beautylink
} //.page-instagram

.page-menu {
    .cabinet {
        margin-top: 0;
        padding: 0;
    } //.page-menu .cabinet

    .header {
        display: none;
    } //.page-menu .header
} //.page-menu

.cabinet {
    position: relative;
    display: flex;
    flex: 1 1;
    flex-direction: column;
    height: calc(100% - #{$base-header-size});
    margin-top: $base-header-size;
    margin-left: $base-header-size;
    padding: 30px;
    overflow: hidden;
    transition: margin-left ease-out 0.3s, width ease-out 0.3s;
    background-color: $color-bg--lightest;

    @include md-block() {
        padding: 20px 10px;
    }

    @include xs-block() {
        margin: $base-header-size--small 0;
        padding-top: 0;
        padding-bottom: 10px;
    }

    &__title {
        margin-bottom: 17px;
        font-size: 18px;
        font-weight: 500;
        @include flexCenter;

        @include min-screen(426) {
            display: none;
        }

        @include xs-block() {
            font-size: 22px;
        }

        .btn {
            margin-left: auto;
            padding: 7px 15px;
        }
    } //.cabinet__title

    &__logo {
        display: none;
        align-items: center;
        justify-content: center;
        width: 165px;
        height: 100%;
        margin-right: auto;

        @include xs-block() {
            flex: 1;
            margin-top: 3px;
            @include flex;
        }

        &__link {
            color: #fff;
            @include xs-block() {
                color: $gray--darkest;
            }
        }
    } //.cabinet__logo

    &__tab {
        @include xs-block() {
            margin: 0 -10px;
            padding: 20px 10px 0;
            background-color: #fff;
        }

        .tab {
            &__titles {
                margin-bottom: $gutter;
            }
        } //.cabinet__tab .tab

        .button-box {
            margin-bottom: $gutter;
            @include flexCenter;

            // @include xs-block() {
            //     margin: 0 10px;
            // }
        } //.cabinet__tab .button-box

        .profile-editor {
            &__inner {
                @include xs-block() {
                    padding-top: 0;
                }
            }
        }
    } //.cabinet__tab

    &__toggle {
        .widget {
            margin-bottom: 7px;

            &__inner {
                @include xs-block() {
                    padding: 0;
                }
            } //.cabinet__toggle .widget__inner

            &__body {
                @include xs-block() {
                    min-height: auto;
                    padding: 10px;
                }
            } //.cabinet__toggle //.widget__body

            &__left {
                @include xs-block() {
                    padding: 0;
                }
            } //.cabinet__toggle .widget__left

            &__right {
                @include xs-block() {
                    margin-left: 20px;
                    padding: 0;
                }
            } //.cabinet__toggle .widget__right
        } //.cabinet__toggle .widget

        .list {
            &__item {
                @include xs-block() {
                    align-items: flex-start;
                }
            }

            &__icon {
                @include xs-block() {
                    font-size: 17px;
                }
            } //.cabinet__toggle .list__icon

            &__text {
                @include xs-block() {
                    font-size: 13px;
                    line-height: 1.2;
                }
            }
        } //.cabinet__toggle .list

        .bb-checkbox--toggle {
            .bb-checkbox {
                &__title {
                    font-weight: 500;
                } //.bb-checkbox--title
            } //.bb-checkbox
        } //.bb-checkbox--toggle
    } //.cabinet__toggle

    .top-line {
        @include xs-block() {
            margin-top: 10px;
        }
    } //.cabinet .top-line

    &.has-add-btn {
        @include xs-block() {
            margin-top: 105px;
            padding-top: 10px;
        }
    } //.cabinet.has-btn-select

    &.bg--light {
        background-color: $color-bg--lightest;
    } //.cabinet.bg--light

    &.bg--white {
        background-color: #fff;
    } //.cabinet.bg--white

    // Убрать после переноса страницы beautyicons в табы

    &.full-screen {
        .album {
            &__inner {
                @include xs-block() {
                    @include u-row-flex($o);
                }

                .album-item {
                    @include size(2);
                    @include size-lg(3);
                    @include size-sm(4);

                    @include xs-block() {
                        margin-bottom: $o;
                        @include u-col($o);
                        @include u-size(6, 12, $o);
                    }

                    @include i7-block() {
                        &__img {
                            border-radius: 0;
                        }
                    }

                    &__info {
                        @include i5-block() {
                            display: none;
                        }
                    }

                    &--icon {
                        @include i7-block() {
                            border-top: 1px solid $color-border--lightest;
                        }
                    }
                } //.cabinet.full-screen .album__inner .album-item
            } //.cabinet.full-screen .album__inner
        }
        $o: 10px;
    } //.cabinet.full-screen

    &.one-screen {
        position: fixed;
        top: 0;
        right: 0;
        bottom: 0;
        left: 0;
        flex: 1 1 auto;
        padding: 0;
        overflow: hidden;
        @include flexColumn;
    } //.cabinet.one-screen

    &.no-padding {
        padding-right: 0;
        padding-bottom: 0;
        padding-left: 0;

        .top-line {
            padding: 0 30px;
        }
    } //.cabinet.no-padding

    &.no-offset {
        @include xs-block() {
            padding-right: 0;
            padding-left: 0;
        }
    } //.cabinet.no-offset
} //.cabinet

.c-swiper-container {
    @include xs-block() {
        position: fixed;
        top: 50px;
        right: 0;
        bottom: 50px;
        left: 0;
        margin: 0;
    }

    &__inner {
        @include xs-block() {
            overflow: hidden;
            @include coverdiv;
        }
    } //.c-swiper-container__inner

    &__item {
        @include xs-block() {
            height: 100%;
        }

        & > div {
            @include xs-block() {
                height: 100%;
                padding: 10px;
                overflow-x: hidden;
                overflow-y: auto;
            }
        }

        &.is-loading {
            & > div {
                overflow: hidden;
            }

            .preloader--cabinet {
                display: block;
            } //.c-swiper-container__item.is-loading .preloader--cabinet
        } //.c-swiper-container__item.is-loading
    } //.c-swiper-container__item

    .widget {
        @include xs-block() {
            margin: 0;

            &:not(.widget--transparent) {
                border-right: 1px solid $color-border--lightest;
                border-left: 1px solid $color-border--lightest;
            }

            &.widget--counter {
                border: 0;
            }
        }

        &--counter {
            @include xs-block() {
                margin-bottom: 0;
            }
        } //.c-swiper-container .widget--counter
    } //.c-swiper-container .widget

    .request-info {
        &__item {
            @include xs-block() {
                border-right: 1px solid $color-border--lightest;
                border-left: 1px solid $color-border--lightest;

                &:first-child {
                    border-top: 1px solid $color-border--lightest;
                }

                &:last-child {
                    border-bottom: 1px solid $color-border--lightest;
                }
            }
        }

        &__inner {
            @include xs-block() {
                padding: 0;
                overflow: hidden;
            }
        }
    } //.c-swiper-container .request-info

    .preloader {
        &__icon {
            top: 10%;
            width: 25px;
            height: 25px;
        }

        &--cabinet {
            background-color: $color-bg--lightest;
        }
    } //.c-swiper-container .preloader--cabinet
} //.c-swiper-container

.swiper-container {
    .swiper-slide {
        &:first-child {
            .widget {
                margin-left: 0;

                @include xs-block() {
                    margin-left: 10px;
                }
            } //.swiper-container .swiper-slide:first-child .widget
        } //.swiper-container .swiper-slide:first-child
    } //.swiper-container .swiper-slide
} //.swiper-container

.card-item {
    @include box;

    @include sm-block() {
        margin-bottom: 40px;
    }

    @include xs-block() {
        @include resetBox;
    }

    &__wrap {
        @include flex;
        @include sm-block() {
            flex-wrap: wrap;
        }
    } //.card-item__wrap

    &__header {
        padding: 10px 15px;
        border-bottom: 1px solid $color-border--lightest;
        border-radius: $border-radius $border-radius 0 0;
        color: $gray--darkest;
        font-size: 14px;
        font-weight: 500;
        @include flexCenter;

        @include xs-block() {
            padding: 10px;
            border-radius: 0;
        }

        @include i7-block() {
            font-size: 15px;
        }

        i {
            margin-right: 10px;
        }
    } //.card-item__header

    &__body {
        @include flexColumn;

        @include xs-block() {
            padding: $gutter / 2;
        }

        p {
            display: block;
            margin-bottom: 15px;
            font-size: 14px;
            @extend %textLight;
        }
    } //.card-item__body

    &__box {
        padding: 20px 15px;
        border-top: 1px solid $color-border--lightest;
        @include flexColumn;

        @include xs-block() {
            padding: 0;
            border: 0;

            &.mb-17 {
                margin-bottom: 10px;
            }
        }

        &:first-child {
            border-top: 0;
        }

        .card-item {
            &__subtitle {
                color: $gray;
                font-size: 14px;
            } //.card-item__box .card-item__subtitle
        } //.card-item__box .card-item

        .bb-input {
            flex: 1;
            margin-right: $gutter;

            @include md-block() {
                &:not(.bb-input--phone) {
                    width: 100%;
                    margin-bottom: $gutter / 2;
                }
            }

            @include sm-block() {
                margin-right: 0;
            }

            @include max(i5) {
                &::before {
                    right: 10px;
                }
            }

            &__wrap {
                align-items: flex-start;
                @include flex;

                @include sm-block() {
                    flex-wrap: wrap;
                    justify-content: flex-start;
                }

                & + .bb-input__wrap {
                    margin-top: $gutter;
                }
            } //.card-item__box .bb-input__wrap

            &__title {
                position: static;
                min-width: 160px;
                margin-top: 9px;
                color: #000;
                font-size: 16px;
                font-weight: 500;

                @include xs-block() {
                    margin-top: 9px;
                }

                @include max(i5) {
                    min-width: max-content;
                    margin-right: $gutter / 2;
                }
            } //.card-item__box .bb-input__title

            &__input {
                width: 100%;

                @include max(i5) {
                    padding: 9px 20px 9px 10px;
                }
            } //.card-item__box .bb-input__input

            &__message {
                top: 44%;
                right: 0;
                left: 0;

                @include md-block() {
                    top: 100%;
                }
            } //.card-item__box .bb-input__message

            &--phone {
                max-width: 425px;
            } //.card-item__box .bb-input--phone
        } //.card-item__box .bb-input

        &.no-border {
            border-bottom: 0;
        } //.card-item__box--clear

        &--first {
            padding-top: 0;
        } //.card-item__box--first

        &--vk {
            .bb-input {
                max-width: 350px;
                margin-right: auto;

                &__title {
                    min-width: 75px;
                    margin-top: 9px;
                    font-weight: 500;
                }
            }
        } //.card-item__box--vk
    } //.card-item__box

    &__title {
        margin-bottom: 10px;
        font-size: 14px;
    } //.card-item__title

    &__caption {
        margin-bottom: 10px;
        color: #000;

        font-size: 16px;
        @include flexCenter;

        span {
            line-height: 1;
        }

        .ico {
            margin-right: 10px;
        }
    } //.card-item__title

    &__social {
        margin-right: 185px;
        @include flexCenter;

        @include max(lg) {
            margin-right: 195px;
        }

        @include md-block() {
            margin-right: 0;
        }

        @include xs-block() {
            .btn-social {
                min-width: 45px;
                max-width: 45px;
                padding: 10px 0;
            }
        }

        .bb-input {
            margin-right: 0;

            &__wrap {
                flex: 1;
            }
        }
    } //.card-item__social

    &__btn {
        min-width: 175px;

        @include xs-block() {
            width: 100%;
        }
    } //.card-item__btn

    .social {
        margin-right: $gutter;

        &__inner {
            max-width: 170px;
            @include flexCenter;

            @include max(i7) {
                max-width: 100%;
            }
        }
    } //.card-item .social

    .btn-social {
        width: max-content;
        text-transform: initial;

        @include max(i7) {
            width: 100%;
        }

        & + .btn-social {
            margin-left: 7px;
        }
    } //.card-item .btn-social

    .button-box {
        margin-bottom: 0;
        @include flexColumn;

        @include sm-block() {
            width: 100%;
        }

        .btn {
            max-width: 175px;
            margin: auto;

            @include xs-block() {
                max-width: 100%;
            }

            & + .btn {
                margin-top: $gutter / 2;
            }
        }
    } //.card-item .button-box

    .bb-accordeon {
        @include xs-block() {
            // @include border(top);
        }

        &__inner {
            padding-bottom: $gutter;
            @include border(bottom);
        } //.card-item .bb-accordeon__inner

        &__item {
            border-right: 0;
            border-bottom: 0;
            border-left: 0;
            border-radius: 0;

            &:last-child {
                border-bottom: 0;
            }

            &.is-open {
                border-bottom-color: $color-border--lightest;
            }

            & + .bb-accordeon__item {
                margin-top: 0;
            }
        } //.card-item .bb-accordeon__item

        &__title {
            padding: 18px 15px;
            background-color: $color-bg--lightest;
            color: $gray;
            font-size: 15px;

            @include xs-block() {
                padding: 10px;
            }

            &::before {
                color: $gray;
                @include fa('light');

                @include xs-block() {
                    right: 11px;
                }
            }
        } //.card-item .bb-accordeon__title

        &__content {
            padding: 15px 15px 20px;
            border-top: 1px solid $color-border--lightest;

            @include xs-block() {
                padding: 10px;
            }
        } //.card-item .bb-accordeon__content

        .bb-input--title,
        .bb-text--title {
            margin-right: 0;
        } //.card-item .bb-accordeon

        .card-item__box {
