.box-inline {
    @include box;
    @include flexColumn;

    &__header {
        display: flex;
        align-items: center;
        height: 50px;
        padding: 0 20px;
        border-bottom: 1px solid $color-border--lightest;
        border-radius: 2px 2px 0 0;
        outline: none;
        color: $gray--darkest;
        font-size: 16px;
        font-weight: 500;

        @include xs-block() {
            padding: 0 10px;
        }
    } //.box-inline__header

    &__in {
        padding: $gutter;
        @include flex;

        @include xs-block() {
            padding: $gutter $gutter / 2;
        }
    } //.box-inline__body

    &__text {
        line-height: 1.2;

        &--right {
            margin-left: auto;
        } //.box-inline__text--right

        i {
            font-size: 11px;
        }
    } //.box-inline__text

    &__title {
        margin-right: 15px;
        color: $gray--darkest;
        font-size: 15px;
        font-weight: 500;
        line-height: 1.2;
    } //.box-inline__title

    &__subtitle {
        position: relative;
        padding-left: 15px;
        color: $gray--light;
        font-size: 14px;
        white-space: nowrap;

        &::before {
            content: '';
            left: 0;
            display: block;
            width: 1px;
            height: 100%;
            background-color: $gray--light;
            @include centre('y');
        }
    } //.box-inline__subtitle

    &__controls {
        margin-left: auto;
    } //.box-inline__control

    &__list {
        padding-left: 10px;
        border-left: 3px solid $color-border--light;
        @include flexColumn;

        & + & {
            margin-top: $gutter;
        }

        &.is-success {
            border-color: $color-success;
        }

        &.is-abort {
            border-color: $color-danger;
        }
    } //.box-inline__list

    &__item {
        @include flexColumn;

        & + & {
            margin-top: 5px;
        }
    } //.box-inline__item

    &__line {
        font-size: 15px;
        line-height: 1.3;
        @include flexCenter;

        &--bottom {
            color: $gray--light;
            font-size: 14px;
            font-style: italic;
            font-weight: 400;
        }

        .box-inline__text {
            line-height: 1.5;
        }
    } //.box-inline__line
} //.box-inline

