.control-box {
    flex: 1;

    &--flex {
        display: flex;
        @include itemOffset;

        @include xs-block() {
            flex-wrap: wrap;
        }

        & > :only-child {
            flex-basis: 100%;
        }

        & > :nth-last-child(2):first-child {
            flex-basis: 50%;

            @include xs-block() {
                flex-basis: 100%;
            }

            & ~ .bb-input {
                flex-basis: 50%;

                @include xs-block() {
                    flex-basis: 100%;
                }
            }
        }

        & > :nth-last-child(3):first-child {
            flex-basis: 33.33%;

            @include xs-block() {
                flex-basis: 100%;
            }

            & ~ .bb-input {
                flex-basis: 33.33%;
                @include xs-block() {
                    flex-basis: 100%;
                }
            }
        }

        // & + .bb-input {
        //     margin-left: 20px;

        //     @include xs-block() {
        //         margin-left: 0;
        //     }
        // } //.controls-box .bb-input + .bb-input
    } //.control-box--flex
} //.controls-box

