.item-reviews {
    padding: 15px;
    border: 1px solid $color-border--lightest;
    border-radius: $border-radius;
    background-color: #fff;

    @include xs-block() {
        padding: 10px;
    }

    &__wrap {
        margin-bottom: 30px;

        .user--small {
            .user__name {
                font-size: 17px;
            }
        }
    }

    &__header {
        margin-bottom: 10px;
    }

    &__body {
        margin-bottom: 10px;
    }

    &__footer {
        justify-content: space-between;
        @include flexCenter;
    }

    &__title {
        margin-bottom: 10px;
    } //.item-reviews__title

    &__name {
        margin-left: 10px;
        color: #000;
        font-size: 14px;
    } //.item-reviews__name

    &__time {
        color: $color-text--light;
        font-size: 13px;
        font-style: italic;
        font-weight: 300;
    } //.item-reviews__time

    &__text {
        color: $gray--darkest;
        font-size: 13px;
        font-weight: 300;
    } //.item-reviews__text

    &__reply {
        margin-left: 5px;
        border: none;
        background: transparent;
        color: $color-text;
        font-size: 13px;

        &:hover,
        &:active {
            text-decoration: underline;
        }
    }

    &__took-away {
        display: none;

        .item-reviews {
            padding-right: 0;
            padding-left: 25px;
        }
    }

    &__more-btn {
        margin-top: 10px;
        border: none;
        background: transparent;
        color: $gray;
        font-size: 13px;

        &:hover,
        &:active {
            text-decoration: underline;
        }
    }
} //.item-reviews
