.bb-status-bar {
    position: relative;
    width: 110px;
    height: 110px;
    overflow: hidden;
    border-radius: 50%;
    background-color: $gray--lighten;

    &__rotate {
        position: absolute;
        z-index: 6;
        width: 50%;
        height: 100%;
        transform: rotate(0deg);
        transform-origin: center right;
        transition: rotate 1s ease-out 0.5s;
        background-color: $gray--lighten;
    }

    &__content {
        position: absolute;
        top: 50%;
        left: 50%;
        z-index: 50;
        display: flex;
        align-items: center;
        justify-content: center;
        width: 90%;
        height: 90%;
        transform: translate(-50%, -50%);
        border-radius: 50%;
        background-color: #fff;
    }

    &__load {
        position: absolute;
        top: 0;
        z-index: 3;
        width: 50%;
        height: 100%;
        background-color: $gray--lighten;

        &--left {
            left: 0;
            background-color: $color-accent;
        }

        &--right {
            left: 50%;
        }
    }
}
