.is-hidden {
    display: none !important;
}

.hidden {
    display: none;
}

.is-notvisible {
    position: absolute;
    z-index: -1;
    opacity: 0;
}

.transition {
    @include tr;
}

.has-scroll {
    @include xs-block() {
        overflow: hidden !important;

        & > ul,
        & > div {
            flex-wrap: nowrap;
            margin-bottom: -30px !important;
            padding-bottom: 30px !important;
            overflow-x: scroll;
            scroll-behavior: smooth;
        }
    }
} //.has-scroll

[onclick],
.js-mobile-block--show,
.js-move-block--show {
    cursor: pointer;
}

.ui-sortable-placeholder {
    display: inline-block;
    flex-shrink: 0;
    width: 100%;
    border: 1px dashed $color-border--light;
    border-radius: $border-radius;
}

.not-action {
    pointer-events: none !important;
    touch-action: none !important;

    .icon,
    svg {
        fill: $color-icon--light !important;
    } //.not-action .icon
} //.not-action

[class*='--column'] {
    flex-direction: column;
}

._loading {
    position: relative;
    z-index: 10 !important;
    opacity: 1 !important;
    min-width: 30px;
    overflow: hidden !important;
    animation: none !important;
    text-overflow: ellipsis;

    animation: loadingAnimation 2s ease infinite;
    border: 0 !important;
    border-radius: $border-radius !important;
    background: linear-gradient(270deg, #ddd, #eee, #ddd) !important;
    background-size: 400% 400% !important;
    box-shadow: none !important;
    text-overflow: ellipsis !important;
    white-space: nowrap !important;
    pointer-events: none !important;
    min-height: 24px;

    &::after,
    &::before {
        content: '' !important;
        position: absolute !important;
        top: 0 !important;
        right: 0 !important;
        bottom: 0 !important;
        left: 0 !important;
        // z-index: 10 !important;
        opacity: 1 !important;
        display: block !important;
        width: 103% !important;
        height: 103% !important;
        transform: none !important;
        animation: loadingAnimation 2s ease infinite !important;
        border: 0 !important;
        border-radius: 0 !important;
        background: linear-gradient(270deg, #ddd, #eee, #ddd) !important;
        background-size: 400% 400% !important;
    }

    // &:not([class*='img']):not([class*='avatar']):not([class*='slide']):not([class*='preview']) {
    //     max-height: 40px !important;
    // }

    &[class*='slide'] {
        max-height: 300px !important;
    }

    &[class*='icon'],
    &[class*='avatar'],
    &[class*='header-action__link'],
    &[class*='user__img'] {
        &::after {
            display: none !important;
        }

        &:not([class^='text']):not([class*='bb-upload__item']):not([class*='bb-checkbox']):not([class*='btn']):not([class*='phone']):not([class*='inform-block__icon']) {
            width: 35px !important;
            height: 35px !important;
            border-radius: 50% !important;
            fill: none !important;
        }

        path {
            fill: none !important;
        }

        use {
            display: none !important;
        }
    }

    & > * {
        visibility: hidden !important;
        opacity: 0 !important;
    }
}

@keyframes loadingAnimation {
    0% {
        background-position: 0% 50%;
    }

    100% {
        background-position: -200% 50%;
    }
}

/*===== HACKS  =====*/
._nooffset {
    margin: 0 !important;
    padding: 0 !important;
}

._nopadding {
    padding: 0 !important;
}

._nopadding--xs {
    @include xs-block() {
        padding: 0 !important;
    }
}

._nomargin {
    margin: 0 !important;
}

._visually-hidden {
    border: 0;
    clip-path: inset(50%);
    display: inline-block;
    height: 1px;
    margin: -1px;
    overflow: hidden;
    padding: 0;
    width: 1px;
    white-space: nowrap;
}
