.modal {
    &__slider {
        .bb-slider__slides {
            visibility: hidden;
            opacity: 0;
            @include tr;

            &:not(.is-visible) {
                max-height: 375px;
                overflow: hidden;
            }

            &.is-visible {
                visibility: visible;
                opacity: 1;
                transition: initial;
            }
        } //.modal__slider .bb-slider__slides
    } //.modal__slider

    &__form {
        padding: 44px;

        @include xs-block() {
            margin: auto;
            padding: 30px;
        }
    } //.modal__form

    &__top-line {
        .bb-input {
            width: 100%;

            .search__input {
                border: 1px solid $input-color-border;
                border-radius: $border-radius;
            }
        } //.modal__top-line .bb-input
    } //.modal__top-line

    &.fade {
        .modal-dialog {
            @include xs-block() {
                transform: translate(0, 25%);
            }
        }
    } //.modal.fade

    &.zoom {
        opacity: 0;
        transition: opacity 0.15s linear;

        .modal-dialog {
            transform: scale(0.8);

            @include xs-block() {
                transform: scale(0.8) translate(0);
            }
        }

        &.show {
            opacity: 1;
        }
    } //.modal.zoom

    &.show {
        .modal-dialog {
            @include xs-block() {
                transform: translate(0, 0);
            }
        }

        &.zoom {
            .modal-dialog {
                transform: scale(1);

                @include xs-block() {
                    transform: scale(1) translate(0);
                }
            }
        }
    } //.modal.show

    &-header {
        position: relative;
        flex-shrink: 0;
        width: 100%;
        padding: 0 60px 0 15px;
        padding-right: 60px;
        border-bottom: 0;
        background-color: #fff;
        color: $color-text;
        color: #fff;
        line-height: 60px;
        @include border(bottom);

        @include xs-block() {
            padding: 0 50px 0 15px;
        }

        &--light {
            border-bottom: 1px solid $modal-border-color;
            background-color: $color-bg--lighten;
            color: $gray--darkest;

            .modal-title {
                font-weight: 500;
                font-weight: 500;
            }
        } //.modal-header--light

        &--center {
            align-items: center;
            justify-content: center;
            text-align: center;

            .modal-title {
                max-width: 77%;
                @include text-overflow;
                @include centre;
            }

            .btn-link {
                position: absolute;
                right: 10px;
            }
        } //.modal-header--center

        .modal__btn--close {
            top: 8px;
            right: 5px;
        } //.modal-header .modal__btn--close
    } //.modal-header

    &-content {
        overflow: hidden;
        border-radius: $border-radius;

        @include xs-block() {
            @include flexCenter;
        }

        &.is-success {
            .modal-success {
                @include activeSuccess;
            } //.modal-content.is-success .form-success
        } //.modal-content.is-success
    } //.modal-content

    &-body {
        width: 100%;
        padding: 15px;
        overflow-x: hidden;
        background-color: #fff;

        @include xs-block() {
            flex: inherit;
            margin: auto;
        }

        &--center {
            text-align: center;
        } //.modal-body--center

        &--flex {
            @include flexColumn;
        } //.modal-body--flex

        .modal-close {
            top: 0;
            right: 0;
            width: 50px;
            height: 50px;
            padding: 0;

            @include xs-block() {
                position: fixed;
            }
        } //.modal-body .modal-close

        &.no-padding {
            padding: 0;
        } //.modal-body.no-padding

        &.has-scroll {
            overflow-x: hidden;
            @include scroll;
        } //.modal-body.scroll

        &.bg--lighten {
            background-color: $color-bg--lighten;
        } //.modal-body.bg--lighten

        .master-picker-item {
            border-right: 0;
            border-left: 0;
        } //.modal-body .master-picker-item

        .product-img {
            width: 100px;
            height: auto;
            margin: 0 auto 15px;
        } //.modal-body .product-img

        // .form {
        //     max-width: auto;

        //     // @include xs-block() {
        //     //     padding: 0;
        //     // }

        //     // &__inner {
        //     //     padding: 0 15px;
        //     // } //.modal-body .form__inner

        //     // .form-success {
        //     //     // display: flex;
        //     //     // flex-direction: column;
        //     //     // padding: 0;

        //     //     // &__inner {
        //     //     //     flex-grow: 1;
        //     //     //     // margin: 0;
        //     //     // } //.modal-body .form .form-success__inner

        //     //     &__title {
        //     //         margin-bottom: 0;
        //     //     } //.modal-body .form .form-success__title
        //     // } //.modal-body .form .form-success

        //     // &.is-success {
        //     //     height: 100%;
        //     // } //.modal-body .form.is-success
        // } //.modal-body .form
    } //.modal-body

    &-dialog {
        width: 100%;
        margin: 60px auto 0;

        @include xs-block() {
            max-width: calc(375px - 20px);
        }

        // &--lg, &--md, &--sm {

        //     .modal-content {
        //         max-width: 80vh;
        //         min-height: 320px;
        //     }

        //     .modal-header {
        //         height: 50px;
        //         line-height: 50px;
        //     }

        //     .modal-title {
        //
        //         font-weight: 400;
        //         font-size: 18px;
        //     }
        // }

        // &--lg {
        //     max-width: 960px;
        // } //.modal-dialog--lg

        // &--md {
        //     max-width: 768px;
        // } //.modal-dialog--md

        // &--sm {
        //     max-width: 425px;

        //     .modal-title {
        //
        //         font-size: 18px;
        //         font-weight: 400;
        //     } //.modal--sm .modal-title
        // } //.modal-dialog--sm

        // &--triumph {
        //     max-width: 564px;
        //     @extend .modal-dialog--sm;
        // }
    } //.modal-dialog

    &-title {
        color: $color-text;
        font-size: 28px;
        font-weight: 300;
        @include text-overflow;
    } //.modal-title

    &-footer {
        flex-shrink: 0;
        width: 100%;
        padding: 15px;
        border-top: 1px solid $modal-border-color;
        border-bottom-left-radius: $modal-border-radius;
        border-bottom-right-radius: $modal-border-radius;
        background-color: #fff;
        @include flexCenter;

        button,
        a {
            // padding: 4px 20px;
            padding: 0;
            font-size: 16px;
            font-weight: 500;
            // text-transform: uppercase;

            &:only-child {
                flex-basis: 100%;
            }

            &:nth-last-child(2):first-child {
                flex-basis: 50%;

                & ~ button {
                    flex-basis: 50%;
                }
            }

            &:nth-last-child(3):first-child {
                flex-basis: 33.33%;

                & ~ button {
                    flex-basis: 33.33%;
                }
            }

            &:not([class*='outline']) {
            }

            &.btn-link {
                padding: 8px 20px 7px;
                transition: initial;

                &:hover {
                    background-color: rgba($color-accent, 0.2);
                    text-decoration: none;
                }
            }

            &.btn--big {
                margin: auto;
                padding: 6px 30px;
                font-size: 15px;
            }
        } //.modal-footer button

        &--custom {
            padding: 0;
            border: 0;
            background-color: transparent;
            @include flexCenter;

            & > :not(:last-child) {
                margin-right: 0;
            }

            & > :not(:first-child) {
                margin-left: 0;
            }

            button {
                width: 50%;
                height: 50px;
                margin: 0;
                padding: 0;
                border: 0;
                border-radius: 0;
                box-shadow: none;
                font-size: 16px;

                &:first-child {
                    border-top: 1px solid $color-border--lightest;

                    &:hover {
                        border-right: 0;
                        border-bottom: 0;
                        border-left: 0;
                        background-color: transparent;
                        color: darken($color-accent, 15%);
                    }
                }
            }
        } //.modal-footer--custom

        &.order-block {
            justify-content: space-between;

            button {
                padding: 0 35px;
            }
        }
    } //.modal-footer

    &-close {
        position: absolute;
        top: 0;
        right: 0;
        left: auto;
        z-index: z(default);
        width: 50px;
        height: 50px;
        padding: 0;
        border: 0;
        outline: none;
        background-color: transparent;
        box-shadow: none;
        color: #fff;
        font-size: 21px;
        font-weight: 600;
        line-height: 1;
        text-shadow: none;
        cursor: pointer;

        @include xs-block() {
            right: auto;
            left: 0;
        }

        &:hover {
            opacity: 1;
            text-decoration: none;

            .icon {
                fill: $color-icon--hover;
            }
        }

        &:not(:disabled):not(.disabled) {
            cursor: pointer;
        }

        &--left {
            left: 0;

            @include xs-block() {
                right: auto;
                left: 0;
            }
        } //.modal-close--left

        &--right {
            right: 0;

            @include xs-block() {
                right: 0;
                left: auto;
            }
        } //.modal-close--right

        &--dark {
            color: $gray;
        } //.modal-close--dark
    } //.modal-close

    &--fluid {
        @include xs-block() {
            // height: calc(100vh - 71px);
            height: 100%;
            max-height: 100vh;
            padding: 0 !important;
            overflow: hidden !important;

            .modal {
                &-dialog {
                    height: 100%;

                    @include xs-block() {
                        margin: 0;
                    }
                } //.modal--fluid .modal-dialog

                &-content {
                    @include xs-block() {
                        height: 100%;
                        max-height: 100vh;
                        border-radius: 0;
                        @include flexColumn;
                    }
                } //.modal--fluid .modal-content

                &-header {
                    flex-shrink: 0;
                    width: 100%;
                } //.modal--fluid .modal-header

                &-body {
                    flex: 1;
                    // flex-shrink: 1;
                    width: 100%;
                    max-height: 100%;
                    padding-top: 0;
                    overflow-x: hidden;
                    border: 0;
                    @include scroll;
                } //.modal--fluid .modal-body

                &-title {
                    height: 50px;
                    line-height: 50px;
                } //.modal--fluid .modal-title
            } //.modal--fluid .modal
        }

        .form {
            @include xs-block() {
                margin: auto;
            }
        } //.modal-dialog--fluid .form

        // .form-success {
        //     @include xs-block() {
        //         position: fixed;
        //     }
        // } //.modal--fluid .form-success
    } //.modal-dialog--fluid

    &--lg {
        .modal-dialog {
            max-width: 960px;
        }
    } //.modal-dialog--lg

    &--md {
        .modal-dialog {
            max-width: 768px;
        }
    } //.modal-dialog--md

    &--sm {
        .modal-dialog {
            max-width: 480px;
        } //.modal--sm .modal-dialog

        .modal-content {
            min-height: 320px;
            max-height: 80vh;
        } //.modal--sm .modal-content

        .modal-header {
            height: 50px;
            line-height: 50px;
        } //.modal--sm .modal-header

        .modal-title {
            margin: auto 0;

            font-size: 18px;
            font-weight: 400;
        } //.modal--sm .modal-title
    } //.modal--sm

    &--xs {
        .modal-dialog {
            max-width: 425px;
            @include landscape {
                position: fixed;
                top: 0;
                right: 0;
                bottom: 0;
                max-width: 100%;
                max-height: 100%;
                margin: 0;
                border-radius: 0;

                .modal-content {
                    min-height: 100vh;
                }
            }
        }

        .modal-body {
            @include scroll;
            @include min(sm) {
                max-height: 75vh;
            }
        }

        .modal-header {
            height: 50px;
            line-height: 50px;
        }

        .modal-title {
            font-size: 18px;
            font-weight: 400;
        }

        &.modal-order {
            .modal-body__scroll-wrap {
                max-height: 100%;
            }
        }
    } //.modal--xs

    &--mobile {
        @include xs-block() {
            right: 10px;
            left: 10px;
        }

        .modal-title {
            margin-bottom: 10px;
            overflow: initial;
            color: $color-text--dark;
            font-size: 20px;
            font-weight: 500;
            font-weight: 500;
            line-height: 1.2;
            text-align: center;
            white-space: normal;
        } //.modal--mobile .modal-title

        .modal-text {
            display: block;
            width: 100%;
            margin-bottom: 0;
            text-align: center;

            & + .modal-text {
                margin-top: 15px;
            } //.modal--mobile .modal-text + .modal-text

            &--small {
                font-size: 13px;
            } //.modal--mobile .modal-text--small
        } //.modal--mobile .modal-text

        .modal-body {
            padding: 40px 30px;
        } //.modal--mobile .modal-body

        .modal-content {
            min-height: auto;
            overflow: hidden;
            border-radius: $border-radius * 2;
        } //.modal--mobile .modal-content

        .modal-dialog {
            @include xs-block() {
                margin: 0;
            }
        } //.modal-dialog .modal-footer

        .modal-footer {
            height: 50px;
            padding: 0;
            overflow: hidden;
            line-height: 50px;
            @include flexCenter;

            button,
            a {
                flex-basis: 50%;
                height: inherit;
                padding: 0;
                border: 0;
                background-color: transparent;
                font-size: 16px;
                line-height: inherit;

                @include xs-block() {
                    font-size: 18px;
                }

                &:hover {
                    @include min(xs) {
                        background-color: $color-bg--lightest;
                    }
                }

                &:only-child {
                    flex-basis: 100%;
                }

                &:not(:first-child) {
                    @include border(left);
                }

                &:not(.text-light) {
                }

                &.text-light {
                    color: $color-text;
                }

                &.text-color--success {
                    color: $color-success;
                }
            }

            & > :not(:first-child) {
                margin: 0;
            }

            & > :not(:last-child) {
                margin: 0;
            }
        } //.modal--mobile .modal-footer
    } //.modal--mobile

    &--slide {
        &.fade {
            .modal-dialog {
                transform: translate(25%, 0);
            }
        } //.modal.fade

        &.show {
            .modal-dialog {
                transform: translate(0, 0);
            }
        } //.modal.show
    } //.modal--slide

    &--clipped {
        .modal-body {
            overflow: hidden;
        } //.modal--clipped .modal-body
    } //.modal--clipped

    .services {
        margin: 0;
    } //.modal .services

    .item-services {
        &__header {
            background-color: #fff;
        } //.move-block .item-services__header
    } //.modal .item-services

    .master-picker {
        margin-bottom: 0;
        padding: 0 15px;
        border-top: 0;

        &__icon {
            width: 15px;
            height: 15px;
        } //.modal .master-picker__icon
    } //.modal .master-picker

    .time-picker {
        &__item {
            justify-content: center;
        }
    }

    .day-picker {
        &__list {
            // margin-left: -15px;
            padding-right: 15px;
            padding-left: 15px;

            @include xs-block() {
                margin-left: 0;
            }
        } //.modal .day-picker__list

        &__item {
            width: 50px;
            margin: 0 20px;

            @include xs-block() {
                width: 40px;
                margin: 0 5px;
            }

            &:first-child {
                margin-left: 0;
            }

            &:last-child {
                margin-right: 0;
            }
        } //.modal .day-picker__item

        &__arrow {
            top: -20px;

            @include xs-block() {
                top: -52px;
            }

            &--left {
                left: 15px;

                @include xs-block() {
                    left: 1px;
                }
            } //.modal .day-picker__arrow-long--left

            &--right {
                right: 15px;

                @include xs-block() {
                    right: 1px;
                }
            } //.modal .day-picker__arrow--right
        } //.modal .day-picker__arrow
    } //.modal .day-picker
} //.modal

.modal-box {
    position: relative;
    padding: 15px;
    border-bottom: 1px solid $color-border--lightest;

    &:last-child {
        border-bottom: 0;
    }

    &__inner {
        @include flexColumn;
    } //.modal-box__inner

    &__title {
        position: relative;
        flex-shrink: 0;
        margin-bottom: 7px;
        // display: inline-block;
        color: $gray--light;
        font-size: 14px;
    } //.modal-box__title

    &__validate {
        position: fixed;
        right: 15px;
        // margin: 0 15px 15px;
        bottom: 90px;
        left: 15px;
        visibility: hidden;
        opacity: 0;
        // @include centre('x');
        margin: 0;
        text-align: center;
        @include tY(150%);
        @include tr;
    } //.modal-box__validate

    &.is-invalid {
        .modal-box {
            &__inner {
                padding-bottom: 50px;
            } //.modal-box.is-invalid .modal-box__inner

            &__validate {
                z-index: z(fixed);
                visibility: visible;
                opacity: 1;
                @include tY(0);
            } //.modal-box.is-invalid .modal-box__validate
        }
    } //.modal-box.is-invalid

    &--title {
        padding-top: 15px;
    } //.modal-box--title

    &--choice {
        // padding: 10px 0 15px;
        padding: 0;
        // min-height: 350px;
        // height: 100%;
        overflow-y: auto;

        .modal-box {
            &__inner {
                max-height: 350px;
                // padding: 5px 0;
                // @include coverdiv;

                @include min(xs) {
                    @include bb-scroll;
                }
            } //.modal-box--choice .modal-box__inner

            &__title {
                flex-wrap: wrap;
                margin-left: 0;
                padding: 0 15px;
                @include flexCenter;

                .btn-link {
                    margin-left: auto;
                }

                .control-box {
                    width: 100%;
                }
            } //.modal-box--choice .modal-box__title
        }
    } //.modal-box--choice

    .select-color {
        width: 100%;
    }

    .datepicker--nav {
        position: absolute;
        top: -39px;
        right: -15px;
    } //.modal-box .datepicker--nav
} //.modal-box

.triumph {
    margin: 10px auto 50px;
    padding: 30px;
    @include xs-block() {
        width: 100%;
        padding: 10px;

        &.modal--fluid {
            margin: 0;
        }
    }

    &__inner {
        // min-height: 410px;
        text-align: center;
    } //.triumph__inner

    &__title {
        display: block;
        margin-bottom: 32px;
        color: $gray--darkest;
        font-size: 25px;
        font-weight: 600;
        line-height: 1;
        text-align: center;

        @include sm-block() {
            margin-bottom: 20px;
            font-size: 24px;
        }
    } //.triumph__title

    &__img {
        max-width: 200px;
        // background-color: #fbfbfb;
        margin: 0 auto 25px;

        @include sm-block() {
            max-width: 70%;
        }
    } //.triumph__img

    &__subtitle {
        display: block;
        margin-bottom: 10px;
        font-size: 18px;
        font-weight: 500;
    } //.triumph__subtitle

    &__desc {
        margin-bottom: 20px;
        font-size: 14px;

        @include sm-block() {
            width: auto;
            font-size: 16px;
        }
    } //.triumph__desc

    &__footnote {
        margin-top: 30px;
        color: $color-text--light;
        font-size: 15px;
    } //.triumph__footnote

    &__btn {
        width: 92%;
        max-width: 300px;
        margin-top: auto;
        padding: 9px 15px;
        @extend %button;

        @include sm-block() {
            margin-top: 20px;
        }
    } //.triumph__btn

    &__controls {
        position: relative;
        width: 100%;
    } //.triumph__controls

    &__btn-next {
        // font-weight: 600;
        // text-transform: uppercase;
        // font-size: 12px;
        position: absolute;
        top: 0;
        right: 0;
        height: 50px;
        padding: 0 15px;
        line-height: 50px;

        @include xs-block() {
            padding: 0 15px;
            font-size: 12px;
        }
    } //.triumph__btn-next

    &__btn-close {
        right: 0;
        left: 0;
        margin: 0;
    } //.triumph__btn-close

    .modal-body {
        padding: $gutter * 1.5;
        @include flexColumnCentered();
    } //.triumph .modal-body

    .modal-content {
        background-color: #fbfbfb;

        //@include xs-block() {
        //    padding-bottom: 20px;
        //}
    } //.triumph .modal-content

    .modal-close {
        color: $color-text;
    } //.triumph .modal-close

    .slick-dots {
        bottom: 0;
    } //.triumph .slick-dots

    .slick-slide {
        padding-bottom: 15px;
    }
} //.triumph

.m-album {
    min-width: 480px;

    @include xs-block() {
        min-width: 320px;
    }

    &__item {
        margin-bottom: 30px;
        @include flexColumn;

        @include itemGutter(#{$gutter});
    } //.m-album__item

    &__inner {
        justify-content: space-between;
        @include flexCenter;
        @include xs-block() {
            flex-direction: column;
            align-items: flex-start;
            justify-content: flex-start;
        }

        .bb-input {
            &:last-child {
                margin-left: auto;
            }
        }
    } //.m-album__inner

    &__select {
        &:last-child {
            @include xs-block() {
                margin-left: 0;
            }
        }
    } //.m-album__select

    &__title {
        margin-bottom: 5px;
        font-size: 14px;
        font-weight: 500;
    } //.m-album__title

    &__text {
        min-height: 110px;
        border-color: $color-border--light;
    } //.m-album__text

    .bb-input {
        @include xs-block() {
            width: 100%;
            margin-bottom: 25px;

            &:last-child {
                margin-bottom: 0;
            }
        }

        &__input {
            width: 100%;
            height: 30px;
            line-height: 30px;
        }
    }

    .select2-container,
    .bb-select {
        min-width: 190px;
        max-width: 190px;
        @include xs-block() {
            width: 100%;
            max-width: 100%;
            margin-bottom: 15px;

            &:last-child {
                margin-bottom: 0;
            }
        }

        &:last-child {
            margin-left: auto;
        }

        select {
            width: 100%;
            @include xs-block() {
                width: calc(100% - 10px);
            }
        }
    }

    .bb-select {
        &::before {
            right: 7px;
        }
    }
