.add-avatar {
    text-align: center;

    &__inner {
        display: none;
        padding: 20px 20px 0;
    } //.add-avatar__inner

    &__choice {
        padding: 15px 0;
    } //.add-avatar__choice

    &__title {
        margin-bottom: $gutter;
        font-size: 20px;
        font-weight: 500;
    } //.add-avatar__title

    &__image {
        margin-top: $gutter * 2;
    } //.add__image

    &__text {
        margin-bottom: $gutter;
        font-size: 14px;
    } //.add__text

    &__img {
        // width: calc(100% - 120px);
        margin: 0 auto $gutter;
        overflow: hidden;
    } //.add__img

    &__img-rotate {
        display: flex;
        align-items: center;
        justify-content: center;
        width: 100%;
    } //.add-avatar__img-rotate

    &__btn-rotate {
        display: block;
        width: 4rem;
        height: 4rem;
        border: 0;
        background: transparent;
    } //.add-avatar__btn-rotate

    &__icon-rotate {
        &:hover {
            fill: $gray--darken;
        }
    } //.add-avatar__icon-rotate

    &__preview {
        width: 100px;
        height: 100px;
        margin: auto;
        overflow: hidden;

        & > div {
            width: 100% !important;
            height: 100% !important;
            border-radius: 50%;
        }
    } //.add__preview

    &__footer {
        display: none;
    } //.add-avatar__footer

    &__btn {
        display: flex;
        justify-content: center;
        text-align: center;

        &:hover {
            .icon {
                fill: $color-icon;
            }
        } //.add-avatar__btn:hover

        .icon {
            @include tr;
        } //.add-avatar__btn .icon
    } //.add-avatar__btn

    &.has-photo {
        .add-avatar {
            &__inner {
                display: block;
            }

            &__footer {
                display: flex;
            }

            &__choice {
                display: none;
            }
        } //.add-avatar.has-photo add-avatar

        .empty {
            display: none;
        } //.add-avatar.has-photo .empty
    } //.add-avatar.has-photo

    .modal-body {
        text-align: center;
    } //.add-avatar .modal-body

    .empty {
        margin: auto;
        padding-right: 0;
        padding-left: 0;
    } //.add-avatar .empty
} //.add-avatar

.modal-choice {
    &__checkbox {
        .bb-checkbox {
            &__title {
                font-size: 16px;
                font-weight: 500;
            }
        }
    }

    .bb-checkbox {
        &__wrap {
            justify-content: center;
            margin: 15px 0;
            @include flexCenter;

            .bb-checkbox {
                & + .bb-checkbox {
                    margin-left: $gutter * 2;
                }
            }
        }

        .button-box {
            display: flex;
            justify-content: center;
            margin: 15px 0;

            .btn {
                min-width: 150px;

                @include max(i5) {
                    max-width: 125px;
                }

                & + .btn {
                    margin-left: $gutter;
                }
            }
        }
    } //.modal-choice
} //.modal-choice__checkbox

.shedule-picker {
    .modal-body {
        padding: 0;
    } //.shedule-picker .modal-body

    .datepicker-inline {
        position: static;
        opacity: 1;
        display: block;
        transform: translate(0);
    } //.shedule-picker .datepicker-inline

    .datepicker {
        &--cell-day {
            &.-other-month- {
                &.-selected- {
                    background-color: rgba(#1279e6, 0.8);

                    &.-focus- {
                        background-color: rgba(#1279e6, 0.5);
                    }
                }
            } //.shedule-picker .datepicker--cell-day -other-month-

            &.-focus- {
                background-color: rgba(#1279e6, 0.05);
            } //.shedule-picker .datepicker--cell-day -focus-

            &.-selected- {
                background-color: rgba(#1279e6, 0.8);

                &.-focus- {
                    background-color: rgba(#1279e6, 0.5);
                }
            } //.shedule-picker .datepicker--cell-day.-selected-

            &.is-checked {
                background-color: rgba(#1279e6, 0.2);

                &.-focus- {
                    background-color: rgba(#1279e6, 0.5);
                }

                &.-selected- {
                    background-color: rgba(#1279e6, 0.8);

                    &.-focus- {
                        background-color: rgba(#1279e6, 0.5);
                    }
                } //.shedule-picker .datepicker--cell-day.-selected-
            } //.shedule-picker .datepicker--cell-day
        } //.shedule-picker .datepicker--cell-day
    } //.shedule-picker .datepicker

    .bb-checkbox {
        padding: 10px 10px 10px 40px;

        &:hover {
            background-color: $color-bg--hover;
        }

        &__box {
            top: 10px;
            left: 15px;
        }
    } //.shedule-picker .bb-checkbox

    .user {
        &__name {
            color: $gray--darkest;
            // font-weight: 500;
        } //.shedule-picker .user__name
    } //.shedule-picker .user

    &--master {
        .user {
            &__name {
                font-weight: 500;
            } //.shedule-picker .user__name
        } //.shedule-picker .user

        .bb-input {
            width: 100%;
            margin-top: 10px;

            input {
                border: 1px solid $gray--lighten;
                border-radius: $border-radius;
                background-color: $gray--lighten;

                &:focus {
                    border-color: $input-color-border;
                    background-color: #fff;
                }
            } //.shedule-picker--master .bb-input input
        } //.shedule-picker--master .bb-input
    } //.shedule-picker--master
} //.shedule-picker

.password-recovery {
