        margin: 50px auto 0;
    } //.password-recovery__btn

    .bb-input {
        width: 100%;
        margin-bottom: 25px;

        &__title {
            width: 32ch;
            margin: 0 auto 20px;
            font-size: 15px;
            text-align: center;
