.review-add {
    position: relative;

    @include xs-block() {
        margin-top: 20px;
    }

    &__title {
        display: block;
        margin-bottom: 10px;
        font-size: 15px;
        font-weight: 500;
    } //.review-add__title

    &__text {
        width: 100%;
        min-height: 100px;
        padding: 10px 15px;
        border-radius: $border-radius;
        border-color: $color-border--lighten;
        color: $gray--darkest;
        font-size: 15px;
        font-weight: 300;
        resize: none;
        @include bb-scroll;
    } //.review-add__text

    &__success {
        z-index: z(success);
        padding: 20px;
    } //.review-add__success

    &.is-success {
        .review-add__success {
            @include tY(0);
        }
    }

    .star-rating {
        margin-bottom: 20px;
    }
} //.review-add

.popup-select-block {
    z-index: z(modal) + 2;
    display: none;
    padding: 10px 20px;
    border-left: 4px solid $color-accent;
    border-radius: 0;
    background-color: #fff;

    @include xs-block() {
        padding: 5px;
    }

    &__top {
        margin-bottom: 10px;
        padding-bottom: 7px;
        @include border(bottom);
        @include flexCenter;

        .bb-input {
            width: 100%;

            &__input {
                margin-right: 10px;
                // padding-right: 36px;
                padding-right: 0;
                padding-left: 0;
                font-size: 15px;
                font-weight: 500;
            } //.popup-select-block__top .bb-input__input
        }

        .button-box {
            margin-left: auto;
            @include flexCenter;

            .btn {
                min-width: auto;
                padding: 0 !important;
            }
        }

        .btn-link {
            & + .btn-link {
                margin-left: 10px;
            }
        }
    } //.popup-select-block__top

    &__bottom {
        .bb-input {
            &__input {
                text-align: right;
            } //.popup-select-block__bottom .bb-input__input
        } //.popup-select-block__bottom .bb-input
    } //.popup-select-block__bottom

    .bb-input {
        &__row {
            @include xs-block() {
                flex-wrap: nowrap;
            }

            .bb-input {
                @include xs-block() {
                    width: calc(50% - 5px);

                    & + .bb-input {
                        margin-left: 10px;
                    } //.popup-select-block .bb-input__row .bb-input + .bb-input
                }

                &:not(:first-child) {
                    &::before {
                        content: '';
                        left: -12px;
                        display: block;
                        width: 1px;
                        height: 90%;
                        background-color: $color-border--lighten;
                        @include centre('y');
                    } //.popup-select-block .bb-input__row .bb-input:not(:first-child)::before
                } //.popup-select-block .bb-input__row .bb-input:not(:first-child)
            } //.popup-select-block .bb-input__row .bb-input
        } //.popup-select-block .bb-input__row

        &__title {
            margin: 0;
            color: $gray--dark;
            font-size: 12px;
            font-weight: 600;
            text-transform: uppercase;
        } //.popup-select-block .bb-input__title

        &__input {
            padding-bottom: 11px;
            @include text-overflow;
        } //.popup-select-block .bb-input__input

        &--select {
            &.is-focus {
                .bb-select {
                    padding-right: 20px;
                } //.popup-select-block .bb-input--select.is-focus .bb-select
            } //.popup-select-block .bb-input--select.is-focus
        } //.popup-select-block .bb-input--select
    } //.popup-select-block .bb-input

    .bb-select {
        // padding-right: 20px;
        position: relative;
        top: -2px;
        margin-left: 5px;
        text-align: right;

        &::before {
            display: none;
        }

        &__reset {
            right: -5px;
        } //.popup-select-block__reset

        select {
            padding-right: 5px;
            text-align-last: right;

            @include i5-block() {
                padding-left: 0;
            }
        } //.popup-select-block select

        option {
            font-size: 14px;
        } //.popup-select-block option
    } //.popup-select-block

    .select2-container--open .select2-dropdown--below {
        z-index: z(modal) + 2;
    } //.popup-select-block .select2-container--open .select2-dropdown--below

    .select2-container .select2-selection--single .select2-selection__rendered {
        padding-right: 15px;
    } //.popup-select-block .select2-container .select2-selection--single .select2-selection__rendered

    .select2-container--default
        .select2-selection--single
        .select2-selection__arrow
        b {
        right: 0;
    } //.popup-select-block .select2-container--default .select2-selection--single .select2-selection__arrow b
} //.popup-select-block

.modal-datepicker {
    .flatpickr-calendar {
        &::after,
        &::before {
            display: none;
        }
    } //.modal-datepicker .flatpickr-calendar

    .flatpickr-months {
        flex-shrink: 0;
        margin-bottom: 10px;
        background-color: #fff;
        @include border(top);
        @include border(bottom);
        @include flexCenter;
    } //.modal-datepicker .flatpickr-months

    .flatpickr-month {
        height: 80px;
        fill: $color-text;
        color: $color-text;
        @include flexCenter;
    } //.modal-datepicker .flatpickr-month

    .flatpickr-prev-month,
    .flatpickr-next-month {
        width: 50px;
        height: 80px;
        padding: 12px;
    } //.modal-datepicker .flatpickr-prev-month, .modal-datepicker .flatpickr-next-month

    .flatpickr-current-month {
        align-items: center;
        justify-content: center;
        height: 45px;
        padding: 0;
        text-align: center;
        text-align-last: center;
        @include flexColumn;

        .numInputWrapper {
            margin-top: 5px;
            color: $color-text--light;

            font-size: 14px;
            text-align: center;
            pointer-events: none;
        }
    } //.modal-datepicker .flatpickr-current-month

    .flatpickr-innerContainer {
        padding-top: 15px;
        background-color: #fff;
        @include border(top);
    } //.modal-datepicker .flatpickr-innerContainer

    .flatpickr-monthDropdown-months,
    .flatpickr-custom-month {
        display: block;
        height: 100%;
        padding-left: 0;
        font-size: 26px;
        font-weight: 500;
        text-align: -webkit-center;
        text-align: center;
        text-align-last: -webkit-center;
        text-align-last: center;
        appearance: none;
    } //.modal-datepicker .flatpickr-monthDropdown-months

    .flatpickr-rContainer {
        margin: auto;
    } //.modal-datepicker .flatpickr-rContainer

    .datepicker-inline {
        position: static;
        opacity: 1;
        display: block;
        background-color: transparent;
        @include tY(0);
    } //.modal-datepicker .datepicker-inline

    .datepicker {
        width: 100%;
        max-width: 100%;
        background-color: transparent;

        &--pointer {
            background-color: transparent;
        }

        &--nav {
            margin-bottom: 10px;
            padding: 10px 25px;
            background-color: #fff;
            @include border(top);
            @include border(bottom);
        } //.modal-datepicker .datepicker--nav

        &--nav-title {
            align-items: center;
            justify-content: center;
            font-size: 26px;
            font-weight: 500;
            @include flexColumn;

            i {
                font-size: 14px;
            }
        }

        &--nav-action {
            svg {
                width: 40px;
                height: 40px;
            }
        }

        &--content {
            padding: 15px;
            background-color: #fff;
            @include border(top);
        } //.modal-datepicker .datepicker--content
    } //.modal-datepicker .datepicker

    .list {
        padding: 10px 15px;
        background-color: #fff;
        @include border(bottom);
    } //.modal-datepicker .list

    & > input {
        display: none;
        width: 100%;
    } //.modal-datepicker > input
} //.modal-datepicker

