.aplication {
    height: 100%;
    @include flexColumn;

    &:not(.aplication--new) {
        &.is-focus {
            .aplication {
                &__right {
                    box-shadow: $bxs-with-overlay;
                }
            }

            .overlay {
                display: block;
            }
        }
    }

    &__left,
    &__right {
        position: relative;
        overflow-x: hidden;
        @include flexColumn;

        &.w50 {
            flex: 1 1 50%;
            min-width: auto;
            max-width: 100%;

            @include xs-block() {
                flex: auto;
            }
        }
    }

    &__left {
        flex: 1 1 66.7%;

        &.bg-light {
            background-color: #fbfbfb;
        } //.aplication__left.bg-light
    } //.aplication__left

    &__right {
        position: relative;
        flex: 0 0 33.3%;
        min-width: 425px;
        max-width: 425px;
        @include border(left);

        @include md-block() {
            min-width: 400px;
        }

        @include sm-block() {
            min-width: 375px;
        }

        @include xs-block() {
            border: 0;
        }
    } //.aplication__right

    &__in {
        flex-grow: 1;
        @include bb-scroll--thin;
    } //.aplication__in

    &__inner {
        width: 100%;
        max-width: 900px;
        margin: 0 auto;
        padding: 20px 20px 20px 27px;

        @include sm-block() {
            padding: 23px 15px;
        }

        @include xs-block() {
            display: block;
            padding: 20px;
            overflow-x: hidden;
        }

        &--small {
            max-width: 580px;
        } //.aplication__inner--small

        .bb-input__row {
            flex-shrink: 0;

            @include xs-block() {
                flex-direction: column;
                margin: 0;
            }

            .bb-input {
                @include xs-block() {
                    width: 100%;
                    margin: 0 0 25px;
                }
            } //.aplication__inner .bb-input__row .bb-input

            .bb-select {
                @include i5-block() {
                    width: 100%;
                }
            } //.aplication__inner .bb-input__row .bb-select

            .user {
                @include xs-block() {
                    margin-top: 0;
                    margin-bottom: 30px;
                }
            } //.aplication__inner .bb-input__row .user
        } //.aplication__inner .bb-input__row

        .row,
        [class^='col'] {
            flex-shrink: 0;
        }
    } //.aplication__inner

    &__info-line {
        margin-bottom: 25px;
        @include flexCenter;

        @include md-block() {
            flex-direction: column;
            align-items: flex-start;
        }

        .select-color {
            flex-shrink: 0;
            min-width: 250px;
            margin-left: auto;

            @include md-block() {
                margin-top: 20px;
                margin-left: 30px;
            }

            @include sm-block() {
                min-width: 100%;
                margin-left: 0;
            }
        } //.aplication__info-line .select-color

        .select2-container
            .select2-selection--single
            .select2-selection__rendered {
            padding-right: 0;
        }

        .select2-container--default.select2-container--open {
            background-color: #fff;
        }

        .select2-container--default .select2-selection--single {
            background-color: transparent;
            box-shadow: none;
        }
    } //.aplication__info-line

    &__header {
        position: relative;
        flex-shrink: 0;
        height: 60px;
        @include border(bottom);
        @include flexCenter;

        @include xs-block() {
            top: 0;
            right: 0;
            left: 0;
            height: 50px;
            padding: 0 10px;
        }

        &_title {
            display: block;
            width: 100%;
            margin: 0;
            padding: 0;
            color: $gray--darkest;
            font-size: 28px;
            font-weight: 300;
            font-weight: 300;
            text-align: center;

            @include xs-block() {
                font-size: 18px;
                font-weight: 400;
                line-height: 50px;
            }
        } //.aplication__header_title
    } //.aplication__header

    &__wrap {
        display: flex;
        flex-grow: 1;
        overflow: hidden;

        @include xs-block() {
            display: block;
            width: 100%;
            scroll-behavior: smooth;
            @include scroll;
        }
    } //.aplication__wrap

    &__footer {
        @include border(top);

        .aplication__btns {
            border-top: 0;
        } //.aplication__footer .aplication__btns
    } //.aplication__footer

    &__title {
        width: 100%;
        margin-bottom: 25px;
        font-size: 26px;
        font-weight: 500;

        @include xs-block() {
            color: $gray--darken;
        }
    } //.aplication__title

    &__subtitle {
        margin-bottom: 15px;
        padding-top: 20px;
        color: $gray--darken;
        font-size: 22px;
        font-weight: 500;
    } //.aplication__subtitle

    &__close {
        @include btnBlockClose;
    } //.aplication__close

    &__more {
        right: 0;
        width: 50px;
        height: 50px;
        cursor: pointer;
        @include centre('y');

        .icon-dots {
            width: 25px;
            height: 25px;
            fill: $gray--dark;
        }
    } //.aplication__more

    &__top-line {
        flex-shrink: 0;
        margin-bottom: 0;
        padding: $gutter $gutter * 1.5;

        @include sm-block() {
            align-items: initial;
            margin-top: 5px;
            margin-bottom: 0;
            padding: 0;
            border-top: 1px solid $color-border--lightest;
            border-bottom: 1px solid $color-border--lightest;
            background-color: #fff;
        }

        .top-line {
            &__sorting {
                @include sm-block() {
                    margin-right: 0;
                    padding: $gutter / 2;
                }
            }

            &__select {
                @include md-block() {
                    margin: 0;
                }
            }
        }

        .mobile-item {
            margin: 0;

            &--client {
                @include border(left);
            }
        }
    } //.aplication__top-line

    &__user {
        visibility: hidden;
        opacity: 0;
        display: none;
        padding: 10px 25px;
        transition: transform 500ms cubic-bezier(0.23, 1, 0.32, 1),
            opacity 500ms cubic-bezier(0.23, 1, 0.32, 1);
        cursor: pointer;
        @include tX(-10px);

        &:hover {
            background-color: $color-bg--hover;
        }

        & + & {
            border-top: 1px solid $color-border--lightest;
        }

        &.is-visible {
            display: flex;
        }

        &.is-animate {
            visibility: visible;
            opacity: 1;
            @include tX(0);
        }
    } //.aplication__user

    &__text {
        margin-top: $gutter * 1.5;
        padding-left: 28px;

        @include sm-block() {
            padding-left: 0;
        }
    } //.aplication__text

    &__date {
        position: relative;
        width: auto;
        margin-left: 30px;
        font-size: 23px;
        font-weight: 300;

        @include sm-block() {
            margin-left: 0;
        }

        .bb-input {
            &__icon {
                right: auto;
                left: -10px;
            } //.aplication__date .bb-input__icon
        } //.aplication__date .bb-input
    } //.aplication__date

    &__btns {
        display: block;
        text-align: center;
        @include footerBtn;

        .text {
            i {
                font-size: 15px;
            }

            & ~ .button-box {
                margin-top: 10px;
            }
        } //.aplication__btns .text
    } //.aplication__btns

    &__btn {
        flex-shrink: 0;

        &--link {
            justify-content: center;
            padding: 15px 0;
            border-bottom: 1px solid $color-border--lightest;
            border-radius: 0;
            font-size: 15px;
        } //.aplication__btn--link
    } //.aplication__btn

    .top-line {
        &__select {
            margin-right: 0;
            margin-left: 205px;

            @include max(lg) {
                margin-left: 20px;
            }
        } //.aplication .top-line__select
    } //.aplication .top-line

    .move-block-item {
        position: relative;
        padding: 20px 20px 5px 0;
        border-top: 0;
        border-bottom-color: $color-border--light;

        &__right {
            margin-left: auto;

            i {
                margin-right: 5px;
                color: $color-icon;
                font-size: 12px;
            }
        } //.aplication .move-block-item__right

        &__icon {
            position: absolute;
            top: auto;
            right: 0;
            bottom: 8px;
            width: 10px;
            height: 10px;
            color: $color-icon;
        } //.aplication .move-block-item__icon

        & + .move-block-item {
            margin-top: 10px;
        }
    } //.aplication .move-block-item

    .empty {
        padding: 50px 20px;
        font-size: 14px;

        &__text {
            text-align: center;
        } //.aplication .empty__text
    } //.aplication .empty

    &.is-scrolled {
        .aplication {
            &__header {
                background-color: #fff;
                @include border(bottom);

                &_title {
                    opacity: 1;
                } //.aplication.is-scrolled .aplication__header_title
            } //.aplication.is-scrolled .aplication__header
        } //.aplication.is-scrolled .aplication
    } //.aplication.is-scrolled

    &--scroll {
        .aplication {
            &__header {
                @include xs-block() {
                    border-bottom: 0;
                }

                &_title {
                    @include xs-block() {
                        opacity: 0;
                        @include tr;
                    }
                } //.aplication--scroll .aplication__header_title
            } //.aplication--scroll .aplication__header
        }
    } //.aplication--scroll

    &--layout {
        .aplication {
            &__inner {
                padding: 50px 0;

                @include md-block() {
                    padding: 30px 20px;
                }

                @include xs-block() {
                    height: 100%;
                    padding: 15px;
                }
            } //.aplication--layout .aplication__inner

            &__left {
                .aplication__inner {
                    @include xs-block() {
                        padding-bottom: 0;
                    }
                }
            } //.aplication--layout .aplication__left

            &__right {
                .aplication__inner {
                    @include xs-block() {
                        padding-top: 0;
                    }
                } //.aplication--layout .aplication__right .aplication__inner
            } //.aplication--layout .aplication__right
        } //.aplication--layout .aplication

        .bb-input {
            margin-bottom: 25px;
            @include flexColumn;

            @include sm-block() {
                width: 100%;
            }

            @include xs-block() {
                margin-bottom: 25px;
            }

            &__row {
                @include sm-block() {
                    flex-wrap: wrap;
                }
            } //.aplication--layout .bb-input__row

            & + .bb-input {
                @include sm-block() {
                    margin-left: 0;
                }
            } //.aplication--layout .bb-input+.bb-input
        } //.aplication--layout .bb-input

        .tab {
            margin-top: 10px;

            @include xs-block() {
                margin-top: 0;
            }

            &__title {
                // width: auto;
                // margin: 0 20px -1px;

                &:first-child {
                    margin-left: 0;
                }

                &:last-child {
                    margin-right: 0;
                }
            } //.aplication--layout .tab__title

            &__link {
                padding: 5px 0 6px;

                @include xs-block() {
                    font-size: 16px;
                    text-transform: initial;
                }

                &.js-move-block--show {
                    display: block;
                } //.aplication--layout .tab__link.js-move-block--show
            } //.aplication--layout .tab__link

            &__inner {
                padding: 0;

                @include md-block() {
                    padding: 0;
                }

                @include xs-block() {
                    display: flex;

                    .widget--transparent-mobile .widget__body {
                        height: 100%;
                        overflow-y: auto;
                    }
                }

                .request-item {
                    @include md-block() {
                        width: 100%;
                        margin-left: 0;
                    }
                }

                &.move-block__body {
                    padding: 15px;
                }
            } //.aplication--layout .tab__inner

            &__content {
                padding: 25px 0;

                @include xs-block() {
                    padding: 0;
                    background-color: #fff;
                }
            } //.aplication--layout .tab__content
        }
    } //.aplication--layout

    &--new {
        .user {
            flex-direction: row;
            @include flexCenter;

            &__avatar {
                margin-right: 20px;
                margin-bottom: 0;
            } //.aplication--new .user__avatar

            &__avatar-add {
                right: -5px;
                bottom: 0;
                width: 22px;
                height: 22px;
            } //.aplication--new .user__avatar-add

            &__img {
                width: 50px;
                height: 50px;
            } //.aplication--new .user__img
        } //.aplication--new .user

        .item-with-desc {
            margin-bottom: 10px;
            margin-left: 25px;
        } //.aplication--new .item-with-desc
    } //.aplication--new

    &--edit {
        &.is-focus {
            .user {
                &__arrow {
                    display: none;
                }

                &__info-block {
                    display: block;
                }
            }

            .aplication-success__tab {
                @include tX(0);
            }
        } //.aplication--edit.is-focus

        .user {
            &__info-block {
                display: none;
            } //.aplication--edit .user__info-block
        } //.aplication--edit .user

        .aplication {
            &__date {
                margin-left: 0;
            } //.aplication--edit .aplication__date
        } //.aplication--edit .aplication

        .aplication-success {
            position: relative;
            overflow: hidden;

            &__tab {
                position: absolute;
                top: 91px;
                right: 0;
                bottom: 0;
                width: 100%;
                transition: $trs;
                background-color: #fff;
                @include tX(200%);
            } //.aplication--edit .aplication-success__tab

            &__user {
                &:hover {
                    background-color: #fbfbfb;
                }
            } //.aplication--edit .aplication-success__user

            .tab {
                &__content {
                    position: static;
                } //.aplication--edit .tab__content

                &__inner {
                    position: absolute;
                    top: 53px;
                    bottom: 0;
                    left: 0;
                    width: 100%;
                    padding: 20px;
                } //.aplication--edit .tab__inner
            }
        } //.aplication--edit .aplication-success
    } //.aplication--edit

    &--info {
        @include xs-block() {
            .aplication__left,
            .aplication__right {
                background-color: $color-bg--lighten;
            }

            .aplication__wrap {
                background-color: $color-bg--lighten;
            }
        }

        .aplication__left {
            background-color: $color-bg--lighten;

            .aplication__inner--small {
                max-width: 420px;
            } //.aplication--info .aplication__left aplication__inner--small
        } //.aplication--info .aplication__left

        &.aplication--layout {
            @include xs-block() {
                .aplication__inner {
                    padding: 10px;
                }
            }
        } //.aplication--info.aplication--layout
    } //.aplication--info
} //.aplication

.aplication-item {
    position: relative;
    padding: 20px;
    @include box;

    @include md-block() {
        padding: 15px 20px 10px;
    }

    & + & {
        margin-top: 30px;

        @include sm-block() {
            margin-top: 20px;
        }
    }

    &__wrap {
        padding-left: 28px;
        border-left: 2px dashed $color-border--lightest;

        @include sm-block() {
            padding-left: 0;
            border-left: 0;
        }
    } //.aplication-item__wrap

    &__row {
        position: relative;
        margin-bottom: $gutter / 2;
        @include row-flex;

        @include sm-block() {
            flex-direction: column;

            &:first-child {
                .aplication-item__col {
                    &:first-child {
                        order: 1;
                        margin-top: 10px;
                    }
                }
            }
        }
    } //.aplication-item__row

    &__col {
        @include col;
        @include flexCenter;

        &:first-child {
            flex: 1;
            @include size(7);
            @include size-sm(12);
        }

        &:last-child {
            @include size(5);
            @include size-sm(12);

            .bb-input {
                &--select {
                    max-width: 143px;

                    @include md-block() {
                        max-width: 100%;
                    }
                }
            }
        }

        .bb-input {
            width: 100%;

            @include xs-block() {
                &:not(.move-select) {
                    margin-bottom: 10px;
                }
            }

            &__wrap {
                display: flex;
                align-items: flex-start;
                width: 100%;

                @include max(i5) {
                    flex-wrap: wrap;

                    .bb-input {
                        margin-left: 0;
                    }

                    .bb-select {
                        width: 100%;
                    }
                }
            } //.aplication-item__col .bb-input__wrap
        } //.aplication-item__col .bb-input
    } //.aplication-item__col

    &__service {
        .aplication-item__col {
            @include size-md(12);

            & + .aplication-item__col {
                @include md-block() {
                    margin-top: $gutter / 2;
                }
            }
        }
    } //.aplication-item__service

    &__btn {
        &:hover {
            text-decoration: none;
        }

        &--edit {
            margin-top: 27px;
            padding: 12px 0;
            color: $gray--light;
            font-size: 14px;

            @include md-block() {
                margin: 0 auto -10px;
            }

            @include sm-block() {
                margin-top: 0;
            }

            &:hover {
                color: $gray;
            }
        } //.aplication-item__btn--edit

        &--add {
            margin-top: 10px;
            font-size: 13px;
        } //.aplication-item__btn--add

        &--reset {
            position: absolute;
            top: 5px;
            right: 6px;

            &:hover {
                .btn {
                    opacity: 1;
                    transform: rotate(-90deg);
                    text-decoration: none;
                }
            }

            .btn {
                opacity: 0.6;
                padding: 5px;
                font-size: 16px;
                @include tr;

                i {
                    margin-right: 0;
                }
            }
        } //.aplication-item__btn--reset
    } //.aplication-item__btn

    &__dell {
        position: absolute;
        top: -3px;
        left: -15px;
        border: 0;
        background-color: transparent;
        cursor: pointer;

        &:hover {
            i {
                opacity: 1;
                transform: rotate(90deg);
            }
        }

        i {
            opacity: 0.6;
            color: $color-danger;
            @include tr;
        }
    } //.aplication-item__dell

    &__counter {
        position: absolute;
        top: 52px;
        left: -42px;
        justify-content: center;
        width: 25px;
        height: 25px;
        border: 2px solid $color-accent;
        border-radius: 50%;
        background-color: #fff;
        color: $color-accent;
        @include flexCenter;

        @include sm-block() {
            display: none;
        }
    } //.aplication-item__counter

    &--short {
        border: 1px solid $color-border--lightest;
        background-color: $color-bg--lightest;
        box-shadow: none;

        .aplication-item {
            &__btn {
                &--add {
                    display: none;
                }
            }

            &__counter {
                border-color: #e4e4e4;
            }
        }
    } //.aplication-item--short
} //.aplication-item

.aplication-success {
    position: relative;
    overflow-y: hidden;
    background-color: #fff;

    &__user {
        padding: 25px 20px;
        @include border(bottom);

        &:hover {
            .user__arrow {
                color: $gray--light;
            }
        }

        .user__name {
            @include text-overflow;
        }
    } //.aplication-success__user

    &__status {
        display: flex;
        width: 100%;
        @extend .search-client__form;

        .select2-container {
            max-width: 285px;
            margin: auto;
        }

        .select2-container--default .select2-selection--single {
            background-color: #fff;
        }
    } //.aplication-success__stauts

    &__tab {
        padding: 5px 20px 0;
    } //.aplication-success__tab

    .tab {
        &__inner {
            padding: 20px 20px 0;
        } //.aplication-success .tab__inner

        &__title {
            font-size: 14px;
            font-weight: 500;
        } //.aplication-success .tab__title

        &__link {
            padding: 10px;
        } //.aplication-success .tab__link

        &__content {
            // position: absolute;
            // top: 128px;
            // right: 0;
            // bottom: 0;
            // left: 0;
            padding: 0;
            @include flexColumn;
        } //.aplication-success .tab__content
    } //.aplication-success .tab

    .user {
        &__arrow {
            margin-left: auto;
            color: $gray--lighten;
            font-size: 25px;
        } //.aplication-success .user__arrow
    } //.aplication-success .user

    .info-block {
        &__link {
            font-weight: 500;
        }
    } //.aplication-success .info-block
} //.aplication-success

.aplication-info-table {
    &__list {
        @include flexColumn;
    } //.aplication-info-table__list

    &__item {
        display: flex;
        margin-bottom: 20px;
    } //.aplication-info-table__item

    &__text {
        flex: 1;
        line-height: 1.7;
        @include flexColumn;
    } //.aplication-info-table__text

    &__title {
        font-weight: 500;

        &--comment {
            margin-top: 15px;
        } //.aplication-info-table__title--comment
    } //.aplication-info-table__title

    &__desc,
    &__place {
        color: $gray--light;
        font-size: 15px;
    } //.aplication-info-table__desc

    &__time,
    &__price {
        flex: 0 0 100px;
        max-width: 100px;
        font-weight: 500;

        i {
            color: $color-icon;
            font-size: 12px;
        }
    }

    &__price {
        text-align: right;
    } //.aplication-info-table__price

    &__result {
        display: flex;
        padding-top: 20px;
        padding-left: 100px;
        @include border(top);

        .aplication-info-table {
            &__desc {
                flex: 1;
                color: $gray--light;
                font-size: 17px;
            } //.aplication-info-table__desk
        } //.aplication-info-table
    } //.aplication-info-table__result
} //.aplication-info-table

.search-client {
    @include flexColumn;

    &__form {
        padding: 25px;
        border-bottom: 1px solid $color-border--lightest;
        background-color: $color-bg--lightest;

        .search {
            &__input {
                padding-top: 13px;
                border: 1px solid $color-border--lightest;
                border-radius: $border-radius;
                background-color: #fff;

                &:hover {
                    border-color: $color-border--light;
                }
            } //.search-client__form .search__input
        } //.search-client__form .search
    } //.search-client__form
} //.search-clien

.new-client {
    z-index: z(fixed);
    background-color: #fff;
    will-change: transform;

    @include bb-scroll--thin;
    @include coverdiv;
    @include tr;
    @include tX(1000%);

    &__form {
        padding: $gutter $gutter * 1.5;
    } //.new-client__form

    &__title {
        margin-bottom: $gutter / 2;
        color: $gray--darkest;
        font-size: 18px;
        font-weight: 500;
    } //.new-client__title

    &__btn {
        display: inherit;
        margin: auto;
    } //.new-client__btn

    &.is-open {
        @include tX(0);
    }

    .bb-input {
        width: 100%;
        margin-bottom: $gutter;

        &__input {
            width: 100%;
        }
    } //.new-client .bb-input

    .bb-text {
        margin-bottom: $gutter * 2;
    } //.new-client .bb-text
} //.new-client

.new-aplication {
    .select2-container--default
        .select2-selection--single
        .select2-selection__arrow
        b {
        right: 3px;

        &::before {
            color: $color-accent;
        }
    }
} // .new-aplication
