.select2 {
    &__icon,
    .fal {
        margin-right: 5px;
    }
}

.select2-container {
    width: 100% !important;
    min-width: 105px;
}

.select2-dropdown {
    z-index: 150;
    border-radius: $border-radius;

    .select2-search__field {
        font-size: 14px;
        @extend %textLight;
    }
}

.select2-container--default .select2-results > .select2-results__options {
    max-height: 193px;
}

.select2-container--default .select2-selection--single {
    height: $base-btn-height;
    border-radius: $border-radius;
    border-color: $input-color-border;
    outline: none;
    line-height: $base-btn-height;
}

.select2-container--default .select2-selection__placeholder {
    font-weight: 400;
    font-style: italic;
    font-size: 14px;

    @include max(xs) {
        font-size: 16px;
    }
} //.select2-container--default .select2-selection__placeholder

.select2-container--default
    .select2-selection--single
    .select2-selection__rendered {
    height: 40px;
    line-height: 39px;
}

.select2-container--default
    .select2-selection--single
    .select2-selection__arrow {
    top: 0;
    right: 10px;
    width: auto;
    height: auto;
}

.select2-container--default
    .select2-selection--single
    .select2-selection__arrow
    b {
    position: absolute;
    top: 16px;
    right: 8px;
    left: auto;
    margin-top: auto;
    margin-left: auto;
    border: 0;

    &::before {
        content: '';
        display: block;
        width: 10px;
        height: 10px;
        transform: rotate(90deg);
        background-image: $base64-icon-angle;
        fill: $color-icon;
    } //.select2-container--default .select2-selection--single .select2-selection__arrow b::before
} //.select2-container--default .select2-selection--single .select2-selection__arrow b

.select2-container--default {
    &.select2-container--open {
        .select2-selection--single,
        .select2-selection--multiple {
            border-color: $input-color-border;
            box-shadow: $bxs-box;

            .select2-selection__arrow {
                b {
                    &::before {
                        transform: rotate(-90deg);
                    }
                }
            }
        }
    }
}

.select2-container--open .select2-dropdown--below {
    z-index: 1010;
    border-color: $input-color-border;
    box-shadow: $bxs-box;
}

.select2-results__option {
    position: relative;
    height: 32px;
    padding-left: 10px;

    font-size: 14px;
    line-height: 20px;
    @include text-overflow;

    &[aria-disabled='true'] {
        color: $color-text !important;

        // &::before {
        //     content: '';
        //     display: block;
        //     width: 3px;
        //     height: 100%;
        //     background-color: $color-accent;
        //     position: absolute;
        //     left: 0;
        //     top: 0;
        // }
    }

    //.select2-results__option[aria-disabled=true]

    .metro-icon {
        &::before {
            margin-right: 9px;
        }
    }
} //.select2-results__option

.select2-container--default .select2-results > .select2-results__options {
    @include bb-scroll--thin;
}

.select2-container--default .select2-results__option[aria-disabled='true'] {
    color: $gray--light;
}

.select2-dropdown,
.select2-container--default .select2-search--dropdown .select2-search__field {
    border-color: $input-color-border;
    box-shadow: none;

    &:focus {
        box-shadow: none;
    }
}

.custom-select__results {
    padding-right: 22px;
    @include flexCenter;

    span {
        margin-right: 10px;

        &:nth-child(1) {
            flex-grow: 1;
            text-overflow: ellipsis;
            overflow-x: hidden;
        }

        &:nth-child(2) {
            margin-left: auto;
        }

        &:nth-child(3) {
            min-width: $base-btn-height;
            margin-right: 0;
            text-align: right;
        }
    }
}

.select2-container .select2-selection--single .select2-selection__rendered {
    padding: 0 25px 0 10px;

    font-size: 16px;

    .custom-select__results {
        padding-right: 8px;

        span {
            &:not(:first-child) {
                color: $gray--light;
                font-weight: 400;
                font-style: italic;
            }

            &:nth-child(1) {
                flex-grow: 0;
                margin-right: 10px;
            }

            &:nth-child(2) {
                margin-right: 10px;
                // margin-left: 0;
            }

            &:nth-child(3) {
                min-width: $base-btn-height;
                margin-right: 0;
            }
        }

        //.select2-container .select2-selection--single .select2-selection__rendered .custom-select__results span
    }

    //.select2-container .select2-selection--single .select2-selection__rendered .custom-select__results
} //.select2-container .select2-selection--single .select2-selection__rendered

.select2-container--default {
    .select2-results__option {
        &[aria-selected='true'] {
            position: relative;
            background-color: transparent;
            color: $color-text;

            &::after {
                content: '\f00c';
                right: 5px;
                color: #bbb;
                font-size: 14px;
                @include fa('light');
                @include centre('y');
            }

            //.select2-container--default .select2-results__option[aria-selected=true]::after
        }

        //.select2-container--default .select2-results__option[aria-selected='true']

        &--highlighted {
            &[aria-selected] {
                background-color: $color-bg--lighten;
                color: #000;
            } //.select2-container--default .select2-results__option--highlighted[aria-selected]
        }

        //.select2-container--default .select2-results__option--highlighted
    } //.select2-container--default .select2-results__option

    .select2-selection--multiple,
    .select2-selection--single {
        transition: border 0.3s ease;
        border: 1px solid $input-color-border;
        background-color: #fff;

        &[aria-expanded='false'] {
            &:hover {
                border-color: $input-color-border--hover;
            }
        }
    }

    //.select2-container--default .select2-selection--multiple, .select2-container--default .select2-selection--single

    .select2-selection--multiple {
        padding: 3px 0 1px;
        @include flexCenter;

        .select2-selection__choice {
            margin: 1px 5px 3px 0;
        }

        //.select2-container--default .select2-selection--multiple .select2-selection__choice
    }

    //.select2-container--default .select2-selection--multiple

    .select2-selection--single {
        height: $base-btn-height;
    } //.select2-container--default .select2-selection--single

    .select2-container--default
        .select2-selection--single
        .select2-selection--multiple {
        height: auto;
        border-radius: $border-radius;
        line-height: 1;

        .select2-selection__rendered {
            height: auto;
            max-height: 170px;
            padding: 0 10px;
            line-height: 1;
            @include bb-scroll;
        }

        //.select2-container--default .select2-selection--multiple .select2-selection__rendered

        .select2-selection__choice {
            position: relative;
            height: auto;
            margin-top: 8px;
            padding: 0 20px 0 10px;
            border: 0;
            border-radius: 50px;
            background-color: #5897fb;
            color: #fff;
            font-size: 13px;
            line-height: 21px;

            &__remove {
                position: absolute;
                top: 0;
                right: 3px;
                color: #fff;
                font-size: 18px;
            }

            //.select2-container--default .select2-selection--multiple .select2-selection__choice__remove
        }

        //.select2-container--default .select2-selection--multiple .select2-selection__choice
    }

    //.select2-container--default .select2-selection--multiple
} //.select2-container--default

.select2-container--default.select2-container--focus
    .select2-selection--multiple {
    border-color: $input-color-border;
}

.select2-selection__rendered {
    height: 100%;
}

.select2-container--default .select2-search--inline .select2-search__field {
    height: 32px;
    margin-top: 2px;
    font-size: 13px;
    line-height: 30px;
}

.select2-selection .select2-selection--multiple {
    height: 32px;
    line-height: 32px;
}

.select2-container--open .select2-dropdown--above {
    z-index: z(modal);
}

.select2-container--default.select2-container--disabled
    .select2-selection--single {
    border-color: $gray--lighten !important;
    background-color: $gray--lighten !important;

    b::before {
        color: $gray--light;
    }
}

.select2-container--default
    .select2-selection--multiple
    .select2-selection__rendered {
    padding: 0 10px;
}

.select2-container--default
    .select2-selection--single
    .select2-selection__clear {
    right: 6px;
    z-index: 1;
    justify-content: center;
    width: 18px;
    height: 18px;
    border-radius: 50%;
    background-color: $gray--light;
    color: #fff;
    font-size: 15px;
    line-height: 19px;
    text-align: center;
    @include flexCenter;
    @include centre('y');

    i {
        background-color: #fff;
        font-size: 14px;
    }
} //.select2-container--default .select2-selection--single .select2-selection__clear

.js-select--multiple {
    &.no-search {
        & + .select2-container--default {
            .select2-search--inline {
                .select2-search__field {
                    display: none;
                }
            }

            .select2-selection--multiple {
                min-height: 40px;
            }
        }
    }
}
