.u-border-bottom--xs {
    @include xs-block() {
        @include border(bottom);
    }
}

.text-color {
    @each $theme, $color in $text-colors {
        &--#{$theme} {
            color: $color;
        }
    }

    &--black {
        color: #000;
    }
} //.text-color

.text-light {
    color: $color-text--light;
} //.text-light

.text-italic {
    font-style: italic;
} //.text-italic

.text-info {
    display: inline-block;
    color: $color-text--light;
    line-height: 1.4;

    @include xs-block() {
        font-size: 14px;
    }
} //.text-info

.text-shadow {
    @include textShadow;
} //.text-shadow

.text-up {
    text-transform: uppercase;
} //.text-up

.text-center {
    text-align: center;
} //.text-center

.align-center {
    align-items: center;
    justify-content: center;
} //.align-center

.text-instagram {
    background: -webkit-linear-gradient(
        135deg,
        #4c68d7 0%,
        #d321a4 50%,
        #ffef63 100%
    );
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
} //.text-instagram

.instagram-overlay {
    background-image: $color-bg--instagram;

    &::before {
        display: none !important;
    }
} //.instagram-overlay

.instagram-border {
    border-width: 15px;
    border-style: solid;
    // не удалять киты, без них градиент не работает
    -moz-border-image: -moz-linear-gradient(
        top right,
        #962fbf 0%,
        #d62976 35%,
        #fa7e1e 70%,
        #ffdc3e 100%
    );
    -webkit-border-image: -webkit-linear-gradient(
        top right,
        #962fbf 0%,
        #d62976 35%,
        #fa7e1e 70%,
        #ffdc3e 100%
    );
    border-image: linear-gradient(
        to bottom left,
        #962fbf 0%,
        #d62976 35%,
        #fa7e1e 70%,
        #ffdc3e 100%
    );
    border-image-slice: 1;

    @include max(md) {
        border-width: 10px;
    }
} //.instagram-border
