.dashboard {
    .header__top {
        @include xs-block() {
            display: none;
        }
    }
} //.dashboard

.chart__wrapper {
    line-height: 0;
} //.chart__wrapper

.domain {
    stroke: transparent;
} //.domain

.tick line {
    stroke: transparent;
} //.tick

.textLegend {
    font-size: 15px;

    @include min(sm) {
        font-weight: 300;
    }

    @include max(sm) {
        font-size: 16px;
    }
} //.textLegend

.pieValue {
    font-size: 14px;

    @include max(xs) {
        font-weight: 500;
    }
} //pieVale

.pieText {
    font-size: 14px;

    @include max(xs) {
        font-weight: 500;
    }
}

.axis--x .tick {
    &:hover {
        color: $color-accent;
        cursor: pointer;
    }
} //.axis--x
