#nprogress {
    .bar {
        background: $color-accent;
    }

    .peg {
        box-shadow: 0 0 10px $color-accent, 0 0 5px $color-accent;
    }

    .spinner-icon {
        border-top-color: $color-accent;
        border-left-color: $color-accent;
    }

    .spinner {
        display: none;
    }
} // #nprogress
