.bb-accordeon {
    @include nosel;

    &__item,
    & > div {
        //@include box;
        // box-shadow: $bxs;
        // border: 1px solid $color-border--lightest;
        @extend %boxLight;

        @include xs-block() {
            box-shadow: none;
        }

        & + .bb-accordeon__item {
            margin-top: 10px;
        } //.bb-accordeon__item + .bb-accordeon__item

        &.is-open {
            .bb-accordeon {
                &__title {
                    &:hover {
                        box-shadow: none;
                    } //.bb-accordeon__item.is-open .bb-accordeon__title:hover

                    // &::before {
                    //     content: '\f106';
                    // }

                    .icon-angle--bold {
                        transform: translateY(-50%) rotate(-90deg);
                    } //.bb-accordeon__item.is-open .bb-accordeon__title .icon-angle--bold
                } //.bb-accordeon__item.is-open .bb-accordeon__title
            } //.bb-accordeon__item.is-open .bb-accordeon
        } //.bb-accordeon__item.is-open
    } //.bb-accordeon__item

    &__title {
        position: relative;
        padding: 18px 20px;
        border-radius: $border-radius;
        color: $gray--darkest;
        font-size: 15px;
        font-weight: 500;
        cursor: pointer;
        @include flexCenter;

        @include max(sm) {
            &:hover {
                box-shadow: none;
            }
        }

        &:hover {
            @include min(xs) {
                box-shadow: 0 2px 4px 0 rgba(0, 0, 0, 0.05),
                    inset 0 0 0 2px $color-accent;
            }
        }

        &-count {
            margin-left: 7px;
            color: $gray--light;
            font-size: 14px;
            font-weight: 300;
        } //.bb-accordeon__title-count

        .icon-angle--bold {
            position: absolute;
            top: 50%;
            right: 10px;
            width: 10px;
            height: 10px;
            transform: translateY(-50%) rotate(90deg);
            fill: $color-icon--light;
        } //.bb-accordeon__title .icon-angle--bold
    } //.bb-accordeon__title

    &--other {
        .bb-accordeon {
            &__item {
                &.is-open {
                    .bb-accordeon__title {
                        &::before {
                            content: '';
                            transform: rotate(90deg);
                        }

                        &::after {
                            opacity: 0;
                            transform: rotate(90deg);
                        }
                    }
                }
            } //.bb-accordeon--other .bb-accordeon__item

            &__title {
                margin: 0;
                padding: 15px 50px 15px 15px;

                &::before {
                    content: '';
                    position: absolute;
                    top: 50%;
                    right: 10px;
                    display: block;
                    width: 1px;
                    height: 11px;
                    margin-top: 0.5px;
                    transform: translateY(-50%);
                    transform-origin: 50% 50%;
                    background-color: $color-accent;
                    will-change: transform;
                    @include tr;
                }

                &::after {
                    content: '';
                    position: absolute;
                    top: 50%;
                    right: 10px;
                    display: block;
                    width: 11px;
                    height: 1px;
                    transform: translateX(46%);
                    transform-origin: 50% 50%;
                    background-color: $color-accent;
                    will-change: transform;
                    @include tr;
                }
            } //.bb-accordeon--other .bb-accordeon__title

            &__content {
                width: 100%;
                margin-top: 10px;
            } //.bb-accordeon--other .bb-accordeon__content
        } //.bb-accordeon--other .bb-accordeon
    } //.bb-accordeon--other

    &--mobile {
        .bb-accordeon {
            &__item {
                @include border(bottom, #eee);

                &:first-child {
                    @include border(top, #eee);
                }

                &:last-child {
                    border-bottom: 0;
                }

                & + .bb-accordeon__item {
                    margin-top: 0;
                }
            } //.bb-accordeon--mobile .bb-accordeon__item

            &__title {
                display: block;
                width: 100%;

                &::before {
                    right: 20px;
                    left: auto;
                    margin-right: 0;
                    color: $gray--light;
                    @include fa('light');
                    @include centre('y');
                }
            } //.bb-accordeon--mobile .bb-accordeon__title

            &__content {
                padding: 0 10px 7px;
                font-size: 14px;

                a {
                    padding: 5px 0;
                }
            } //.bb-accordeon--mobile .bb-accordeon__content
        }
    } //.bb-accordeon--mobile

    &--dark {
        .bb-accordeon {
            &__item {
                background-color: $gray--darken;
            } //.bb-accordeon--dark .bb-accordeon__item

            &__title {
                color: rgba(#fff, 0.6);

                &::before {
                    color: rgba(#fff, 0.6);
                }
            } //.bb-accordeon--dark .bb-accordeon__title
        }
    } //.bb-accordeon--dark

    &--light {
        .bb-accordeon {
            &__item {
                box-shadow: none;
            } //.bb-accordeon--light .bb-accordeon__item

            &__title {
                box-shadow: none;

                &::before {
                    right: 20px;
                    margin-right: 0;
                    @include centre('y');
                }

                &:hover {
                    box-shadow: none;
                }
            } //.bb-accordeon--light .bb-accordeon__title
        }
    } //.bb-accordeon--light

    &.no-border {
        .bb-accordeon {
            &__item,
            & > div {
                border: 0;
            }
        }
    } //.bb-accordeon.no-border

    &--custom {
        .bb-accordeon {
            &__item,
            & > div {
                margin-top: 0;
                padding: 10px;
                border-right: 0;
                border-bottom: 0;
                border-left: 0;
                border-radius: 0;
                background-color: transparent;

                @include sm-block() {
                    box-shadow: none;
                }

                &:first-of-type {
                    border-top: 0;
                }

                & + .bb-accordeon__item {
                    margin-top: 0;
                }
            }

            &__title {
                padding: 10px 50px 10px 0;
            } //.bb-accordeon--custom .bb-accordeon__title
        } //.bb-accordeon--custom .bb-accordeon
    } //.bb-accordeon.border-top
} //.bb-accordeon
