$widget-colors: (
    new: #a9b1b8,
    work: $color-warning,
    done: #90bb61,
    abort: #e44a66,
    waiting: $color-warning,
);

.widget {
    & + .widget {
        .request-item {
            &:first-child:not(.request-item--notfilled) {
                @include xs-block() {
                    border-top: 0;
                }
            }
        }
    }
}

.request {
    &__inner {
        align-items: flex-start;
        @include row-flex;

        @include md-block() {
            margin: 0;
        }

        .widget {
            margin-bottom: 0;
            padding: 0;
            border: 0;
            background-color: transparent;

            @include col;
            @include size(3);

            @include min(md) {
                max-height: calc(100vh - 190px);
            }

            @include md-block() {
                width: 100%;
                margin: 0 0 20px;
                border: 0;
            }

            @include xs-block() {
                width: 100%;
                margin: 0;
                border-right: 0;
                border-left: 0;
                box-shadow: none;
            }

            &__body {
                margin-top: 10px;
                padding: 0;

                @include min(md) {
                    padding-bottom: 3px;
                    @include scroll;
                    @include customScroll;
                }

                @include xs-block() {
                    border: 0;
                    // background-color: #eef0f2;
                }

                .ui-sortable-placeholder {
                    margin-bottom: 10px;

                    @include xs-block() {
                        border: 0;
                    }
                }
            } //.request__inner .widget__body
        } //.request__inner .widget
    } //.request__inner

    &__widget {
        @each $theme, $color in $widget-colors {
            &--#{$theme} {
                .widget {
                    &__header {
                        background-color: $color;
                        color: #fff;
                    }
                }

                .request-item {
                    &__icon {
                        color: $color;
                    }
                }
            }
        }

        .widget {
            &__header {
                flex-shrink: 0;
                justify-content: center;
                border-bottom: 0;
                text-align: center;

                @include md-block() {
                    margin-bottom: 10px;
                    border-radius: $border-radius;
                }

                @include xs-block() {
                    display: none;
                }
            } //.request__widget__header

            &__body {
                @include min(lg) {
                    @include flexColumn;
                }

                @include md-block() {
                    padding-top: 0;
                    padding-bottom: 0;
                    border: 0;
                    box-shadow: none;
                    @include row-flex;
                }

                @include xs-block() {
                    justify-content: center;
                    margin: 0;
                    padding: 0;
                }
            } //.request__widget .widget__body
        }
    } //.request__widget

    &__tab {
        @include min(xs) {
            align-items: flex-start;
            @include row-flex;
        }

        @include xs-block() {
            margin-right: -10px;
            margin-left: -10px;
        }

        .request__widget {
            margin-bottom: 0;

            @include col;
            @include size(3);

            @include max-screen(1199) {
                width: 100%;
            }

            @include xs-block() {
                width: 100%;
                margin-right: 0;
                margin-left: 0;
                border: 0;
                background-color: transparent;
                box-shadow: none;
            }
        } //.request__tab .request__widget
    } //.request__tab

    &__empty {
        width: 100%;

        @include md-block() {
            margin-right: 10px;
            margin-left: 10px;
        }

        @include xs-block() {
            width: 100%;
            margin: 0 10px 20px;
            padding: 0 20px;
        }

        .empty {
            &__icon {
                margin-right: $gutter;
            }
        }
    } //.request__empty

    .tab {
        &__titles {
            margin-bottom: $gutter;

            @include min(mdX) {
                display: none;
            }

            @include min(xs) {
                @include col;
                @include size(12);
            }

            @include xs-block() {
                margin: 0 10px 20px;
            }
        } //.request .tab__titles

        &__title {
            margin: 0 7px;
            @each $theme, $color in $widget-colors {
                &--#{$theme} {
                    .tab {
                        &__icon {
                            color: $color;
                        }
                    }

                    &.ui-tabs-active,
                    &:hover {
                        .tab {
                            &__link {
                                border-bottom-color: $color;
                            }

                            &__icon {
                                color: $color;
                            }
                        }
                    }
                }
            }

            &:first-child {
                margin-left: 0;
            }

            &:last-child {
                margin-right: 0;
            }
        } //.request .tab__title

        &__link {
            font-size: 15px;
            font-weight: 500;
        } //.request .tab__link
    } //.request .tab

    .bb-date {
        width: 100%;

        &--controls {
            .bb-date {
                &__btn {
                    &--today {
                        display: block;
                    }
                }
            }
        } //.request .bb-date--controls
    } //.request .bb-date

    .top-line {
        &__sorting {
            @include xs-block() {
                & > .top-line__select {
                    margin-top: 10px;
                }

                .control-box {
                    margin-left: 0;
                }

                .top-line__select {
                    margin-bottom: 0;
                }
            }
        } //.request .top-line__sorting
    } //.request .top-line

    .ui-sortable-placeholder {
        margin-bottom: 10px;
    } //.request .ui-sortable-placeholder
} //.request

.request-item {
    position: relative;
    display: block;
    flex-shrink: 0;
    margin-bottom: 10px;
    padding: $gutter / 2;
    transition: initial;
    border: 1px solid $color-border--lightest;
    border-radius: $border-radius;
    background-color: #fff;
    cursor: pointer;

    @include size-md(6);
    @include size-xs(12);

    @include md-block() {
        @include col;
    }

    @include xs-block() {
        margin-bottom: 0;
        padding: 0;
        border-radius: 0;
        box-shadow: none;

        &:not(.request-item--mobile):not(.request-item--notfilled) {
            border-right: 0;
        }

        & + .request-item {
            border-top: 0;
        }
    }

    @each $theme, $color in $widget-colors {
        &--#{$theme} {
            border-left: 3px solid $color;
        }
    }

    @each $theme, $color in $widget-colors {
        &.is-#{$theme} {
            border-left: 3px solid $color;

            .request-item {
                &__status {
                    color: $color;
                }
            }
        }
    }

    &:first-child {
        margin-top: 0;
    }

    &:last-child {
        margin-bottom: 0;
    }

    &:last-child:not(.empty) {
        margin-bottom: 0;
    }

    &__inner {
        margin-right: 40px;

        @include xs-block() {
            display: flex;
            flex-direction: column;
            margin-left: 0;
            padding: 10px;
            border-left: transparent;
        }
    } //.request-item__inner

    &__link {
        color: $color-text;
    } //.request-item__link

    &__title {
        margin-bottom: 0;
        color: $gray--darkest;
        font-size: 15px;
        font-weight: 500;
        @include text-overflow;
    } //.request-item__title

    &__phone {
        display: block;
        color: $color-text;
        font-size: 12px;
    } //.request-item__phone

    &__service {
        display: block;
        margin-top: 5px;
        font-size: 14px;
        font-style: italic;
        font-weight: 300;
        line-height: 1;

        &.service-combo-text {
            span {
                max-width: 60%;
                @include text-overflow;
            }
        }
    } //.request-item__service

    &__master {
        display: block;
        margin: 7px 0 3px;
        color: $color-text;
        font-size: 14px;
        line-height: 1;

        @include xs-block() {
            margin-bottom: 0;
            @include text-overflow;
        }
    } //.request-item__master

    &__place {
        color: $color-text;
        font-style: italic;
        @extend .request-item__master;
    } //.request-item__place

    &__info {
        margin-top: 10px;
        font-size: 14px;
        @include flexCenter;
    } //.request-item__info

    &__price {
        position: relative;
        color: $color-text;
        font-size: 14px;
        font-weight: 600;

        &::before {
            content: '/';
            margin: 0 5px 0 8px;
            color: $color-icon;
        }

        i {
            margin-left: 3px;
            color: $color-icon;
            font-size: 10px;
        }
    } //.request-item__price

    &__date {
        color: $color-text;
        font-size: 13px;
        font-style: italic;
    } //.request-item__date

    &__icon {
        position: absolute;
        top: 11px;
        right: 10px;
        line-height: 1;

        .fa-info-circle {
            color: $color-danger;
        }
    } //.request-item__icon

    &__avatar {
        position: absolute;
        top: 10px;
        right: 10px;
        width: 35px;
        height: 35px;
        overflow: hidden;
        border-radius: 50%;

        img {
            border-radius: 50%;
        }
    } //.request-item__avatar

    &__controls {
        position: absolute;
        top: 35px;
        right: 9px;
        margin: 0;
    } //.request-item__controls

    &__status {
        font-size: 14px;
        font-style: italic;
        font-weight: 400;

        @include xs-block() {
            margin-right: 45px;
        }
    } //.request-item__status

    &.no-offset {
        flex-shrink: 0;
        width: 100%;
        margin: 0 -10px;
    } //.request-item.no-offset

    &--new {
        &:not(.request-item--mobile):not(.request-item--notfilled) {
            background-color: #f5f5f5;
        }
    } //.request-item--new

    &--notfilled {
        border-left: 3px solid $color-danger;

        & + .request-item--notfilled {
            @include xs-block() {
                margin-top: 1px;
                border-top: 0;
            }
        }
    } //.request-item--notfilled

    &--user {
        .request-item {
            &__date {
                color: #969696;
            }
        }
    } //.request-item--user

    &--mobile {
        position: relative;
        display: block;
        margin-bottom: 0;
        border-top: 1px solid $color-border--lightest;
        border-right: 1px solid $color-border--lightest;
        border-bottom: 1px solid $color-border--lightest;
        border-radius: 0;
        box-shadow: none;

        @include md-block() {
            width: 100%;
            margin: 0;
        }

        @include xs-block() {
            width: 100%;
            margin-left: 0;
            border-top: 1px solid $color-border--lightest;
            border-right: 1px solid $color-border--lightest;

            & + .request-item:not(.request-item--notfilled) {
                border-top: 1px solid $color-border--lightest;
            }
        }

        &:hover {
            .button-box {
                display: block;
            }
        }

        &:first-child {
            margin-top: 30px;
            border-top: 1px solid $color-border--lightest;
        } //.request-item--mobile:first-child

        & + .request-item--mobile {
            margin-top: 40px;
            border-top: 1px solid $color-border--lightest !important;
        }

        .request-item {
            &__inner {
                margin-right: 55px;
                padding-right: 0;
                border-left: transparent;
            } //.request-item--mobile .request-item__inner

            &__date {
                position: absolute;
                top: -25px;
                bottom: auto;
                left: 0;
                display: flex;
                flex-direction: column;
                align-items: center;
                justify-content: center;
                padding: 0;
                background-color: transparent;
                color: $gray--darkest;
                font-size: 14px;
                text-align: center;

                span {
                    display: block;
                    padding: 0;

                    &:last-child {
                        &:not(:only-child) {
                            border-top: 1px solid $color-border--light;
                        }

                        span {
                            display: none;
                        }
                    }
                }

                i {
                    display: none;
                }
            } //.request-item--mobile .request-item__date

            &__price {
                position: absolute;
                right: 11px;
                bottom: 7px;
            } //.request-item--mobile .request-item__price

            &__master {
                margin-right: 65px;
                margin-bottom: 0;
                @include text-overflow;
            } //.request-item--mobile .request-item__master

            &__service {
                white-space: normal;

                @include xs-block() {
                    overflow: initial;
                    text-overflow: initial;
                    white-space: normal;
                }
            } //.request-item--mobile .request-item__service
        } //.request-item--mobile

        .button-box {
            display: none;
            margin-right: 15px;

            .btn {
                & + .btn {
                    margin-left: 10px;
                }
            }
        } //.request-item--mobile .button-box
    } //.request-item--mobile

    &--icon {
        .request-item {
            &__inner {
                margin-right: 55px;
                padding-right: 0;
                padding-left: 0;
            } //.request-item--icon .request-item__inner

            &__service {
                margin-top: 4px;
                font-size: 12px;
            } //.request-item--icon .request-item__service
        } //.request-item--icon .request-item
    } //.request-item--icon

    &.drag-sort {
        transform: rotate(3deg);
        border: 0;
        box-shadow: 0 3px 10px 0 rgba(0, 0, 0, 0.3);
        cursor: grab;
    } //.request-item.drag-sort

    &.ui-sortable-placeholder {
        border: 2px solid #000 !important;
    }

    .color-line {
        position: absolute;
        top: 0;
        left: 0;
    } //.request-item .color-line

    .info-icon {
        position: absolute;
        right: 10px;
        bottom: 5px;
    }
} //.request-item

.aplication {
    .request-item {
        &:last-child {
            margin-bottom: 20px;
        } //.aplication .request-item:last-child
    } //.aplication .request-item
}
