.form {
    position: relative;
    width: 100%;
    max-width: 420px;
    margin: auto;
    padding: 15px 30px 30px;
    overflow: hidden;

    @include xs-block {
        width: 100%;
        max-width: 650px;
        padding: 15px;
    }

    @include max(i5) {
        width: 100%;
        max-width: 330px;
    }

    &__header {
        margin-bottom: 25px;
        text-align: center;
    } //.form__header

    &__body {
        width: 100%;
    } //.form__body

    &__inner {
        position: relative;
        width: 100%;
        @include flexColumn;
    } //.form__inner

    &__inner-horizontal {
        position: relative;
        display: flex;
        flex-direction: row;
        width: 100%;
    } //.form__inner-horizontal

    &__box {
        display: flex;
        margin-bottom: 20px;

        &--role {
            margin-bottom: 12px;

            .bb-checkbox {
                & + .bb-checkbox {
                    margin-left: $gutter;
                }
            }
        } //.form__box--role
    } //.form__box

    &__title {
        display: inline-block;
        margin-top: 5px;
        color: $gray--darkest;
        font-size: 20px;

        &--page {
            font-size: 23px;
        }
    } //.form__title

    &__subtitle {
        margin-top: 15px;
        font-size: 14px;

        span {
            font-weight: 500;
        }
    } //.form__subtitle

    &__reg {
        display: flex;
        flex-direction: column;
    } //.form__reg

    &__register {
        margin-top: $gutter;
        text-align: center;

        span {
            font-size: 14px;
        }
    } //.form__register

    &__rememberme {
        display: inline-block;
    } //.form__rememberme

    &__link {
        display: inline-block;
        color: $gray;
        font-size: 13px;
        font-weight: 300;

        &:hover {
            color: $color-accent;
        } //.form__link

        &--recover {
            margin-left: auto;
            @extend .form__link;
        } //.form__link--recover

        &--verify {
            margin-top: 10px;
            font-size: 15px;
        } //.form__link--verify
    } //.form__link

    &__info {
        margin-top: 30px;
        color: $gray--light;
        font-size: 13px;
        text-align: center;

        a {
            display: inline-block;

            &:hover {
                text-decoration: underline;
            }
        }
    } //.form__info

    &__option {
        justify-content: center;
        margin-top: 15px;
        color: $gray--darken;
        font-size: 14px;
        text-align: center;
        @include flexCenter;

        @include max(xs) {
            .btn-link {
                font-size: 14px;
            }
        }

        span {
            margin-right: 10px;
        }
    } //.form__option

    &__devider {
        width: 100%;
        margin: 20px 0;
    } //.form__devider

    &__success,
    &__confirm {
        z-index: z(success);
        padding: 30px;
        transition: all 500ms cubic-bezier(0.645, 0.045, 0.355, 1);
        background-color: #fff;
        text-align: center;
        @include coverdiv;
        @include tY(-100%);
    } //.form__success

    &__buttons {
        text-align: center;
    } //.form__buttons

    &__btn {
        display: block;
        width: 100%;
        margin: 0 auto;
        // padding: 9px 0;

        @include xs-block() {
            padding: 0 35px;
        }
    } //.form__btn

    &__social {
        display: flex;

        .btn {
            & + .btn {
                margin-left: 10px;
            }
        }
    } //.form__social

    &--horizontal {
        display: flex;
        max-width: 100%;
        padding: 0;

        @include xs-block() {
            flex-direction: column;
        }

        .form {
            &__inner {
                max-width: 420px;
                padding: 0;
            }
        } //.form--horizontal .form

        // .devider {

        //     &--vertical {
        //         margin: 0 50px;

        //         @include xs-block() {
        //             position: relative;
        //             display: table;
        //             width: 100%;
        //             margin: 30px 0;
        //             overflow: hidden;
        //             white-space: nowrap;
        //         }

        //         &::before,
        //         &::after {
        //             @include xs-block() {
        //                 position: relative;
        //                 top: 0.8em;
        //                 left: auto;
        //                 display: table-cell;
        //                 width: 42%;
        //                 height: initial;
        //                 border-top: 1px solid $input-color-border;
        //                 background-color: transparent;
        //                 min-height: initial;
        //             }
        //         }

        //         &::before {
        //             @include xs-block() {
        //                 left: 0;
        //             }
        //         }

        //         &::after {
        //             @include xs-block() {
        //                 right: 0;
        //                 left: auto;
        //             }
        //         }
        //     }
        // }
    } //.form--horizontal

    &--center {
        align-items: center;
        justify-content: center;
        @include flexColumn;
    } //.form--center

    &--clipped {
        overflow: hidden;

        @include xs-block() {
            max-height: 100vh;
        }
    }

    .form {
        padding: 0;
    } //.form .form

    &.is-success,
    &.is-confirm,
    &.choose-role {
        & > .modal-close {
            z-index: -1;
        }
    }

    &.is-success {
        .form-success {
            @include activeSuccess;
        }

        &--email {
            .form-success--email {
                @include activeSuccess;

                @include xs-block() {
                    position: fixed;
                    height: 100%;
                }
            }
        } //.form.is-success--email

        &--phone {
            .form-success--phone {
                @include activeSuccess;

                @include xs-block() {
                    position: fixed;
                    height: 100%;
                }
            }
        } //.form.is-success--phone

        .form-success--master,
        .form-success--confirm {
            @include tY(-200%);
        }

        .fancybox-close-small {
            z-index: z(successBtnClose);
            display: block;
        }
    } //.form.is-success

    [class^='is-success'] {
        @include xs-block() {
            min-height: 100vh;
            margin: 0;
        }
    }

    &.is-master.is-success {
        .form-success--master {
            @include activeSuccess;
        }
    } //.form.is-master.is-success

    &.is-confirm {
        .form-success--confirm {
            @include activeSuccess;
        }

        .fancybox-close-small {
            // z-index: z(successBtnClose);
            display: block;
        }

        & ~ .modal-footer:not(.show-all) {
            visibility: hidden;
            transition: 0.2s;
        }
    } //.form.is-confirm

    &.choose-role {
        height: 100%;

        .form-success--role {
            z-index: z(success);
            padding: 0;
            @include tY(0);

            &.form-success--blur {
                visibility: visible;
                opacity: 1;
            }
        }
    } //.form.choose-role

    &.no-overflow {
        overflow: visible;
    } //.form.no-overflow

    &.no-offset {
        padding: 0;
    } //.form.no-offset

    &.has-min-height {
        min-height: 425px;

        @include xs-block() {
            min-height: 100%;
        }
    } //.form.has-min-height

    .button-social,
    .btn-social {
        width: 100%;

        @include xs-block() {
            padding: 0;
        }

        .button-social + .button-social,
        .btn-social + .btn-social {
            margin-left: 10px;
        }
    } //.form .button-social, .form .button-social .btn-social

    .form-success {
        &__btn {
            display: inline-block;
            margin: 30px auto 0;
        } //.form .form-success__btn
    } //.form .form-success

    .bb-input {
        width: 100%;
        margin: 0 0 $gutter;

        &__input {
            width: 100%;
        } //.form .bb-input__input
    } //.form .bb-input

    .bb-text {
        margin-bottom: $gutter;
        @include flexColumn;
    } //.form .bb-text

    .bb-checkbox {
        &__title {
            font-size: 13px;
            font-weight: 300;
        }
    } //.form .bb-checkbox
} //.form

.form-success,
.form-confirm {
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    z-index: z(dropdown);
    transform: translateY(-300%);
    border-radius: $border-radius;
    background-color: #fff;
    text-align: center;
    @include tr;
    @include flexColumn;

    @include xs-block() {
        transform: translateY(-100vh);
    }

    &__inner {
        width: 100%;
        height: 100%;
        padding: 44px;
        // margin: auto;
        @include flexColumn;

        @include xs-block() {
            padding: 30px;
        }

        &--sm-offset {
            @include xs-block() {
                padding: 15px;
            }
        }

        &--center {
            align-items: center;
            justify-content: center;

            .form-success {
                &__header,
                &__body,
                &__footer {
                    width: 100%;
                }
            }
        } //.form-success__inner--center
    } //.form-success__inner

    &__header {
        .form-success__title {
            margin-bottom: 0;
        }
    } //.form-success__header

    &__body {
        // flex: 1;

        &.no-padding {
            padding: 0;
        } //.form-success__body.no-padding
    } //.form-success__body

    &__footer {
        @include xs-block() {
            margin-top: 30px;
        }
    }

    &__title {
        display: block;
        margin-bottom: 20px;
        color: $gray--dark;
        font-size: 20px;
        font-weight: 600;
        line-height: 1.5;
    } //.form-success__title

    &__subtitle {
        margin-bottom: 20px;
        color: $color-text--darkest;
        font-size: 15px;
        line-height: 1.5;

        strong {
            font-weight: 500;
        }
    } //.form-success__subtitle

    &__list {
        text-align: left;

        li {
            position: relative;
            margin-bottom: 5px;
            padding-left: 15px;
            color: $color-text--darkest;
            font-size: 14px;
            font-weight: 500;

            &::before {
                content: '';
                position: absolute;
                top: 10.5px;
                left: 0;
                display: block;
                width: 8px;
                height: 2px;
                background-color: $color-accent;
            }
        }
    } //.form-success__list

    &__info {
        display: block;
        margin-bottom: 50px;
        font-weight: 300;
    } //.form-success__info

    &__btn {
        display: block;
        width: 100%;
        margin-bottom: 15px;
    } //.form-success__btn

    &__btn-done {
        margin: 0 auto 20px;
    } //.form-success__btn-done

    &__back {
        position: fixed;
        top: 0;
        left: 0;
    } //.form-success__back

    &--bg-lightest {
        background-color: $color-bg--lightest;
    }

    &.is-active {
        z-index: z(dropdown);
        transform: translateY(0);

        &--blur {
            visibility: visible;
            opacity: 1;

            & ~ * {
                opacity: 0.5;
                @include blur(4px);
            }
        } //.form-success.is-active--blur
    } //.form-success.is-active

    &--confirm {
        .form-success {
            &__footer {
                margin-top: 30px;
            } //.form-success--confirm .form-success__footer
        }
    } //.form-success--confirm

    &--blur {
        visibility: hidden;
        opacity: 0;
        transform: translateY(0);
        background-color: transparent;

        & ~ * {
            @include tr(0.5s);
        }

        &.is-active {
            visibility: visible;
            opacity: 1;

            & ~ * {
                opacity: 0.5;
                @include blur(4px);
            }
        }

        .form {
            &__subtitle {
                color: $color-text--darkest;
            }
        }
    } //.form-success--blur

    &--role {
        // padding: 20px 50px;

        .form-success {
            &__header {
                position: relative;
                padding: 10px;
                @include border(bottom);
            } //.form-success--role .form-success__header

            &__roles {
                @include flexColumn;

                &.is-error {
                    overflow: hidden;

                    .form-success {
                        &__role {
                            display: block;
                            margin-top: 0;
                            border: 1px solid $color-danger;
                            box-shadow: none;
                            @include tr;

                            &:hover {
                                box-shadow: none;
                                cursor: default;
                                @include tY(0);
                            }

                            &.move-out {
                                transform: translateY(-200%);
                            }
                        } //.form-success__role

                        &__verify {
                            visibility: visible;
                            transform: translateY(0);
                            @include tr;
                        } //.form-success__verify
                    }
                }
            } //.form-success--role .form-success__roles

            &__body {
                height: calc(100% - 50px - 70px);
                @include scroll;
            } //.form-success--role .form-success__body

            &__footer {
                padding: 15px;
                @include border(top);

                & > .btn {
                    width: 100%;
                }

                &--no-margin-top {
                    margin-top: 0;
                } //.form-success--role .form-success__footer .form-success__footer--no-margin-top
            } //.form-success--role .form-success__footer

            &__close {
                position: absolute;
                top: 0;
                right: 0;
                bottom: 0;
                display: flex;
                align-items: center;
                justify-content: center;
                width: 40px;
                border: 0;
                background: transparent;
            } //.form-success--role .form-success__close

            &__icon-close {
                width: 12px;
                height: 12px;
            } //.form-success--role .form-success__icon-close

            &__role {
                padding: 15px;
                background-color: #fff;
                font-weight: 500;

                & + .form-success__role {
                    margin-top: 15px;
                }

                &.is-selected {
                    border: 1px solid $color-border--light;
                } //.form-success--role .form-success__role.is-selected
            } //.form-success--role .form-success__role

            &__verify {
                position: absolute;
                top: 0;
                right: 0;
                bottom: 0;
                left: 0;
                z-index: 51;
                visibility: hidden;
                display: flex;
                flex-direction: column;
                justify-content: center;
                padding: 15px;
                transform: translateY(100%);
                background-color: #fff;
                @include tr;

                .form-success__error-close {
                    position: absolute;
                    top: 0;
                    right: 0;
                    bottom: 0;
                    z-index: 51;
                    display: flex;
                    align-items: center;
                    justify-content: center;
                    width: 40px;
                    height: 40px;
                    border: 0;
                    background: transparent;
                }

                .form-success__error {
                    margin-bottom: 30px;
                    font-size: 26px;
                    line-height: 1.2;
                    text-align: left;
                }

                .form__btn {
                    width: 100%;
                    margin-top: 15px;
                } //.form-btn
            } //.form-success--role .form-success__verify
        } //.form-success--role .form-success

        .bb-checkbox {
            &__wrap {
                align-items: flex-start;
                width: fit-content;
                margin: auto;
                @include flexColumn;
            }

            &__title {
                color: $gray;
                font-size: 15px;
                font-weight: 500;
            }
        } //.form-success--role .bb-checkbox

        .bb-input {
            margin-bottom: 0;

            & + .bb-input {
                margin-top: 20px;
            }
        } //.form-success--role .bb-input

        &.is-error {
            .form-success__verify {
                visibility: visible;
                transform: translateY(0);
            } //.form-success--role.is-error .form-success__verify
        } //.form-success--role.is-error
    } //.form-success--role

    &--master {
        z-index: z(success);
    } //.form-success--master

    .bb-checkbox {
        &--master {
        } //.form-success .bb-checkbox--master
    } //.form-success .bb-checkbox

    .auth-form__title {
        text-transform: uppercase;
    } //.form-success .auth-form__title

    .empty {
        margin: auto;
        padding: 44px;

        @include xs-block() {
            padding: 30px;
        }
    } //.form-success .empty
} //.form-success

.success {
    z-index: 999;
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 18px;
    font-weight: 600;
    text-transform: uppercase;
    @include coverdiv;

    @include max(xs) {
        text-align: center;
    }

    span {
        margin: auto;
    }

    &--blur {
        visibility: hidden;
        opacity: 0;

        & ~ * {
            @include tr(0.5s);
        }

        &.is-active {
            visibility: visible;
            opacity: 1;

            & ~ * {
                opacity: 0.5;
                @include blur(4px);
            }
        }
    } //.success--blur
} //.success

.phone-confirm {
    &__inner {
        font-size: 14px;
    } //.phone-confirm__inner

    &__text {
        margin-bottom: 20px;
        color: $color-text;
    } //.phone-confirm__text

    &__send {
        strong {
            color: $color-text;
        }
    } //.phone-confirm__send

    &__repeat {
        margin: 15px auto 0;

        &:hover {
            text-decoration: none;
        }
    } //.phone-confirm__repeat

    &__timer {
        display: block;
        margin-top: 15px;
        font-size: 15px;
        font-weight: 300;
        text-align: center;

        span {
            color: $color-text;
            font-size: 16px;
            font-weight: 500;
        }
    } //.phone-confirm__timer

    &__input {
        max-width: 90px;
        margin-right: 15px;
        font-size: 20px;
        letter-spacing: 1px;
        line-height: 1;
        text-align: center;
    } //.phone-confirm__input

    &__buttons {
        justify-content: center;
        margin-top: 20px;
        text-align: center;
        @include flexColumn;

        .bb-input {
            width: 105px;
            margin-bottom: 0 !important;
            text-align: left;

            &__input {
                margin-right: 0;
            }

            &__error {
                overflow: inherit;
            }
        }
    } //.phone-confirm__buttons

    &__box {
        justify-content: center;
        margin-bottom: $gutter;
        @include flexCenter;
    } //.phone-confirm__box

    &__btn {
    } //.phone-confirm__btn

    &__btn-repeat {
        display: block;
        width: 100%;
        text-align: center;
    } //.phone-confirm__btn-repeat

    &__skip {
        display: flex;
        justify-content: center;
        color: $gray--light;
        text-align: center;

        &:hover {
            color: $gray--light;
            text-decoration: none;
        }
    } //.phone-confirm__skip
} //.phone-confirm
