.chat {
    overflow: hidden;
    background-color: #fff;
    @include flexColumn;

    &__body {
        flex: 1;
        // position: absolute;
        // top: 0;
        // right: 0;
        // bottom: 70px;
        // left: 0;
        // background-image: url('../files/img/bg/chat-bg.png');
        // @include cover;
        padding: 0 20px;
        @include scroll;

        @include xs-block() {
            padding: 0 15px;
        }
    } //.chat__body

    &__footer {
        // position: absolute;
        // right: 0;
        // bottom: 0;
        // left: 0;
        justify-content: center;
        border-top: 1px solid $color-border--lightest;
        background-color: $color-bg--lightest;
        @include flexCenter;
    } //.chat__footer

    &__back {
        display: inline-block;
        align-items: center;
        padding: 0 25px 0 35px;
        color: $gray--light;
        font-size: 15px;
        font-weight: 300;
        line-height: 50px;
        cursor: pointer;
        @include tr;
        @include flexCenter;

        @include max(xs) {
            span {
                display: none;
            }
        }

        &:hover {
            color: $gray;
            @include gp(to right, #eaeaea, #fff);
        }

        i {
            left: 15px;
            margin-right: 7px;
            color: $gray--light;
            font-size: 27px;
            line-height: 1;
            @include centre('y');
        }
    } //.chat__back

    &__user {
        text-align: center;
        @include centre;

        &-name {
            display: block;
            margin-bottom: 5px;
            color: $gray--darkest;
            font-size: 14px;
            line-height: 1;
        }

        &-status {
            display: block;
            color: $gray--light;
            font-size: 12px;
            line-height: 1;
            @extend %textLight;
        }
    } //.chat__user

    &__form {
        display: flex;
        align-items: center;
        justify-content: space-around;
        width: 100%;
        margin-top: auto;
        padding: 15px 20px;

        textarea {
            width: 80%;
            height: 40px;
            min-height: initial;
            max-height: 103px;
            padding: 12px 10px 5px;
            border-collapse: collapse;
            border: 1px solid $color-border--lighten;
            border-radius: $border-radius;
            color: $gray--darkest;
            font-size: 13px;
            line-height: 1;
            resize: none;
            @include bb-scroll--thin;
            @include placeholder {
                font-size: 13px;
            }

            &:focus {
                font-size: 13px;
            }
        } //.chat__form textarea
    } //.chat__form

    &__buttons {
        justify-content: center;
        height: 55px;
        text-align: center;
        @include flexCenter;
        @include max(md) {
            width: 100%;
            text-align: center;
        }
        @include max(xs) {
            height: auto;

            .button {
                padding: 4px 20px 5px;
                font-size: 15px;
            }
        }

        .button {
            width: 150px;
            height: 32px;
            padding: 0;
            border-radius: 0;
            font-size: 14px;
            line-height: 1;
            text-align: center;
            @include max(xs) {
                width: 50%;
            }
        }
    } //.chat__buttons

    &__btn {
        display: inline-block;
        width: 40px;
        height: 40px;
        padding: 0;
        border: 1px solid $color-border--lighten;
        border-radius: $border-radius;
        background-color: transparent;
        box-shadow: none;
        text-align: center;
        cursor: pointer;
        @include tr;

        &::before {
            content: '\f1d8';
            position: relative;
            top: 4px;
            color: $gray--light;
            font-size: 20px;
            @include fa('solid');
            @include tr;
        }

        &:hover,
        &:focus {
            border-color: $gray--light;
            background-color: transparent;
            box-shadow: none;

            &::before {
                color: $gray--light;
            }
        }

        &_list {
            display: none;
            width: 38px;
            height: 38px;
            margin-right: 15px;
            margin-left: 15px;
            @include max-screen(1024) {
                display: inline-block;
            }
            @include max-screen(375) {
                width: 32px;
                height: 32px;

                &::before {
                    top: -10px;
                    font-size: 18px;
                }
            }

            &::before {
                content: '\f27a';
                position: relative;
                top: -8px;
                color: $gray--light;
                font-size: 20px;
                @include fa('solid');
                @include tr;
            }
        }

        input {
            display: none;
        }
    } //.chat__btn

    &__order {
        position: relative;
        padding: 12px 15px;
        border-bottom: 1px solid $color-border--lightest;
        background-color: #fff;
    } //.chat__order

    &__unread {
        display: none;
        width: 6px;
        height: 6px;
        margin-left: 10px;
        border-radius: 50%;
        background-color: $color-accent;
    } //.chat__unread

    &__empty {
        height: 100%;
        font-size: 14px;
    } //.chat__empty
} //.chat

.message {
    position: relative;
    padding: 15px 0 20px;
    @include flexColumn;

    &__inner {
        position: relative;
        display: flex;
        margin-bottom: 20px;

        &:last-child {
            margin-bottom: 0;
        }
    } //.message__item

    &__text {
        &--top {
            margin-bottom: 5px;
            @include flexCenter;
        }
    } //.message__text

    &__author {
        color: $gray--darken;
        font-size: 14px;
        font-weight: 600;
        line-height: 1;
    } //.message__author

    &__time {
        margin-left: 7px;
        color: $gray--light;
        font-size: 13px;
        line-height: 1;
        @extend %textLight;
    } //.message__time

    &__dialog {
        padding: 10px 15px;
        border-radius: 0 8px 8px;
        background-color: #fff;
        box-shadow: 1px 1px 15px #ddd;

        &-item {
            margin-bottom: 10px;
            color: #000;
            font-size: 13px;

            &:last-child {
                margin-bottom: 0;
            }
        }
    } //.message__dialog

    &.is-income {
        .message__dialog {
            background-color: #a6cef1;
            box-shadow: 1px 1px 15px #a7a7a7;

            &-item {
                color: #fff;
            }
        }
    }

    &.is-confirm {
        .message__dialog {
            background-color: $color-success;
            box-shadow: 1px 1px 15px #a7a7a7;

            &-item {
                color: #fff;
            }
        }
    }

    &.is-reject {
        .message__dialog {
            background-color: $color-danger--light;
            box-shadow: 1px 1px 15px #a7a7a7;

            &-item {
                color: #fff;
            }
        }
    }

    .user {
        &__img {
            width: 40px;
            height: 40px;
        }
    }
} //.message
