//Custom Webkit Scroll

@mixin customScroll {
    @include scrollbar {
        width: 4px;
    }

    @include scrollbar-track {
        width: 4px;
    }

    @include scrollbar-thumb {
        background-color: $gray--lighten;
    }
}

@mixin bb-scroll {
    @include scroll;

    @include scrollbar {
        width: 5px;
    }

    @include scrollbar-track {
        width: 5px;
    }

    @include scrollbar-thumb {
        background-color: $gray--light;
    }
}

@mixin bb-scroll--thin {
    @include scroll;

    @include scrollbar {
        width: 3px;
    }

    @include scrollbar-track {
        width: 3px;
    }

    @include scrollbar-thumb {
        border-radius: 100px;
        background-color: $gray--lighten;
    }
}

