@mixin flex {
    display: flex;
    justify-content: space-between;
}

@mixin flexCenter {
    display: flex;
    align-items: center;
}

@mixin flexColumn {
    display: flex;
    flex-direction: column;
}

@mixin flexColumnCentered {
    display: flex;
    flex-direction: column;
    align-content: center;
    justify-content: center;
}

@mixin sectionOverlay($opa: 0.7) {
    position: relative;

    &::before {
        content: '';
        display: block;
        background-color: rgba($color-bg, $opa);
        @include tr;
        @include coverdiv;
    }

    & > * {
        z-index: z(default);
    }

    .container {
        position: relative;
    }
}

@mixin border($direction, $color: $color-border--lighten) {
    @if $direction == 'top' {
        border-top: 1px solid $color;
    }
    @if $direction == 'left' {
        border-left: 1px solid $color;
    }
    @if $direction == 'right' {
        border-right: 1px solid $color;
    }
    @if $direction == 'bottom' {
        border-bottom: 1px solid $color;
    }
    @if $direction == 'all' {
        border: 1px solid $color;
    }
}

@mixin listIcon($positionTop: 12px, $color: $color-accent) {
    position: relative;
    padding-left: 15px;

    &::before {
        content: '';
        position: absolute;
        top: $positionTop;
        left: 0;
        display: block;
        width: 8px;
        height: 2px;
        background-color: $color;
    }
} //ul li &::before (-)

@mixin checkIcon(
    $color: #fff,
    $width: 6px,
    $height: 11px,
    $top: 0,
    $left: 4px
) {
    content: '';
    position: absolute;
    top: $top;
    left: $left;
    width: $width;
    height: $height;
    transform: rotate(45deg);
    border: solid $color;
    border-width: 0 2px 2px 0;
}

//Icon angle svg base64
@mixin iconAngle(
    $position: relative,
    $right: 10px,
    $direction: bottom,
    $width: 10px,
    $height: 10px,
    $icon: $base64-icon-angle
) {
    position: $position;

    &::before {
        content: '';
        position: absolute;
        top: 50%;
        right: $right;
        z-index: z(fixed) - 2;
        display: block;
        width: $width;
        height: $height;
        background-image: $icon;
        fill: $color-icon;

        @if $direction == bottom {
            transform: translateY(-50%) rotate(90deg);
        }

        @if $direction == right {
            transform: translateY(-50%);
        }
    }
} //@include iconAngle($$position: relative, $right: 10px, $direction: bottom, $width: 10px, $height: 10px, $icon: $base64-icon-angle);

@mixin size($width, $height: $width) {
    width: $width;
    height: $height;
}

@mixin dropdownResponseWidth {
    @include max(xs) {
        width: 427px;
        max-width: 427px;
    }
    @include max(i8) {
        width: 416px;
    }
    @include max(i7) {
        width: 377px;
    }
    @include max(i5) {
        width: 322px;
    }
}

@mixin responseWidth {
    @include max(xs) {
        width: calc(425px - 20px);
        max-width: 425px;
    }
    @include max(i8) {
        width: calc(414px - 20px);
    }
    @include max(i7) {
        width: calc(375px - 20px);
    }
    @include max(i5) {
        width: calc(320px - 20px);
    }
}

@mixin itemGutter($g: $gutter) {
    margin-bottom: $g;

    &:last-child {
        margin-bottom: 0;
    }
}

//Позиционирует блок фиксированно и скрывает его транслейтом .is-open - показывает блок
@mixin visibleBlock {
    position: fixed;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    z-index: z(dropdown) - 1;
    display: none;
    flex-direction: column;
    background-color: #fff;

    @include max(md) {
        opacity: 0;
        display: flex;
        transform: translate(0, 200%);
        transition: $trs;
    }

    &.is-open {
        z-index: z(dropdown);
        display: flex;

        @include max(md) {
            opacity: 1;
            transform: translate(0);
        }
    }
}

//Накладывает на блок оверлей и делает его не кликабельным
@mixin itemDisabled {
    position: relative;
    cursor: not-allowed;
    pointer-events: none;
    touch-action: none;

    &::after {
        content: '';
        z-index: z(fixed);
        display: block;
        background-color: rgba(#fff, 0.75);
        @include coverdiv;
    }
}

@mixin textShadow($shadow: 2px 2px 4px rgba(0, 0, 0, 0.7)) {
    text-shadow: $shadow;
}

@mixin config-icon-colors($prefix, $colors...) {
    @each $i in $colors {
        .#{$prefix}#{nth($i, 1)} {
            color: nth($i, 2);
        }
    }
}

