@mixin boxShadow($color: #000) {
    box-shadow: 0 2px 4px 0 rgba($color, 0.1), 0 0 4px 0 rgba($color, 0.1);
}

@mixin bS($color: #a4adba) {
    box-shadow: 0 2px 5px 0 rgba($color, 0.25);
}

@mixin box {
    border-radius: $border-radius;
    background-color: #fff;
    @include bS;
    // @include boxShadow;
    // @include border(all, $color-border-very-light);
}

@mixin boxReset {
    border-radius: 0;
    box-shadow: none;
    @include border(top);
    @include border(bottom);
}

//Reset box style in mobile device
@mixin resetBox {
    margin: 0 -10px 20px;
    border-right: 0;
    border-left: 0;
    border-radius: 0;
    box-shadow: none;
    @include border(top);
    @include border(bottom);
}

//Set Widrh & Height like vars
@mixin baseSize {
    width: $base-header-size;
    height: $base-header-size;

    @include max(xs) {
        width: $base-header-size--small;
        height: $base-header-size--small;
    }
}

@mixin blockHeight {
    height: $base-header-size;
    line-height: $base-header-size;

    @include max(xs) {
        height: $base-header-size--small;
        line-height: $base-header-size--small;
    }
}

@mixin btnBlockClose {
    right: 0;
    width: 50px;
    height: 50px;
    cursor: pointer;
    @include centre('y');

    @include max(xs) {
        right: auto;
        left: 0;
    }

    &:hover {
        .icon {
            fill: $gray--darkest;
        }
    }

    &--right {
        right: 0;
        left: auto;
    }

    &--left {
        right: auto;
        left: 0;
    }
}

@mixin itemOffset($offset: 10px) {
    & > :not(:first-child) {
        margin-left: $offset;

        @include xs-block() {
            margin-top: $offset;
            margin-left: 0;
        }
    }

    & > :not(:last-child) {
        margin-right: $offset;

        @include xs-block() {
            margin-right: 0;
        }
    }
}

@mixin footerBtn {
    flex-shrink: 0;
    padding: 20px;
    background-color: #fff;
    @include border(top);

    @include xs-block() {
        padding: 15px;
    }

    &--small {
        border-radius: 0 0 $border-radius $border-radius;
    }

    .button-box {
        width: 100%;
        @include flexCenter;
    }

    .btn {
        width: 100%;
        min-width: 145px;
        max-width: 180px;
        font-weight: 600;
        font-size: 14px;
        text-transform: uppercase;

        @include xs-block() {
            min-width: 136px;
            max-width: 100%;
        }
    }

    .btn-animate {
        @include xs-block() {
            min-width: auto;
        }
    }

    .btn-error {
        @include xs-block() {
            flex-basis: 50%;
        }

        & ~ .btn-animate,
        & ~ .btn-gray {
            @include xs-block() {
                flex-basis: 50%;
                margin-left: 10px !important;
            }
        }
    }
}

@mixin activeSuccess {
    z-index: z(success);
    @include tY(0);
}

%img {
    display: block;
    max-width: 100%;
    margin: 0;
}

%boxLight {
    border: 1px solid $color-border--lightest;
    border-radius: $border-radius;
    background-color: #fff;
    box-shadow: 0 2px 5px 0 rgba(164, 173, 186, 0.25);
}

%styleInput {
    height: $base-btn-height;
    padding: 9px 10px;
    transform: translateZ(0);
    transition: border 0.3s ease;
    border: 1px solid $input-color-border;
    border-radius: $border-radius;
    background-color: #fff;
    color: $gray--darkest;
    font-size: 16px;
    line-height: 1;

    @include placeholder {
        color: $gray--light;
        font-weight: 400;
        font-style: italic;
        font-size: 14px;
        line-height: 23px;

        @include max(xs) {
            font-size: 16px;
        }
    }

    &:hover {
        border-color: $input-color-border--hover;
    }

    &:focus {
        border-color: darken($input-color-border--hover, 10%);
        font-size: 16px;
    }

    &:disabled {
        border-color: $gray--lighten !important;
        background-color: $gray--lighten !important;
    }

    @include max(xs) {
        height: $base-btn-height--xs;
    }
}

%badge {
    justify-content: center;
    min-width: 20px;
    height: 19px;
    padding: 3px 5px;
    overflow: hidden;
    border-radius: 500px;
    background-color: $color-accent;
    color: #fff;
    font-weight: 600;
    font-size: 10px;
    text-align: center;
    @include flexCenter;
}

%textLight {
    font-weight: 300;
    font-weight: 300;
}

%styleBlockHeader {
    display: block;
    max-width: 73%;
    margin: 0;
    padding: 0;
    color: $gray--darkest;
    font-weight: 300;
    font-size: 28px;
    text-align: center;
    white-space: nowrap;
    @include text-overflow;
    @include centre;

    @include max(xs) {
        width: 100%;
        padding: 0 15px;

        font-size: 18px;
        line-height: 50px;
    }
}
