.bb-slider {
    position: relative;

    &__arrow {
        z-index: z(default);
        display: inline-block;
        outline: none;
        cursor: pointer;
        @include centre('y');
        // @include iconAngle($position: absolute, $right: 18px, $width: 13px, $height: 13px);

        &::before {
            opacity: 0.3;
            color: $gray--darkest;
            font-size: 42px;
            @include fa('light');
        }

        &:hover {
            &::before {
                opacity: 1;
            }
        } //.bb-slider__arrow

        &.slick-disabled {
            opacity: 0.4;
            display: none !important;
        }

        &--prev {
            left: -20px;
            @include max(xs) {
                left: 43%;
            }

            &::before {
                content: '\f104';
            }
        } //.bb-slider__arrow--prev

        &--next {
            right: -20px;
            @include max(xs) {
                right: 43%;
            }

            &::before {
                content: '\f105';
            }
        } //.bb-slider__arrow--next
    } //.bb-slider__arrow

    &__preview {
        .slick-slide {
            position: relative;

            &::after {
                content: '';
                position: absolute;
                bottom: 0px;
                left: 0;
                display: none;
                width: 100%;
                height: 2px;
                background-color: $color-accent;
            }

            &.slick-current {
                &::after {
                    display: block;
                }
            }
        }
    }

    &__dots {
        position: absolute;
        top: 10px;
        right: 10px;
        padding: 2px 3px 1px 6px;
        border-radius: 500px;
        background-color: rgba(0, 0, 0, 0.55);
        color: #fff;
        font-size: 10px;
        font-weight: 600;
        letter-spacing: 3px;
    } //.bb-slider__dots

    &__slide {
        &.catalog-item,
        &.catalog-item--small {
            margin: 0 0 4px;

            &:last-child {
                margin: 0 0 4px;
            }
        }
    } //.bb-slider__slide

    &--second {
        .bb-slider {
            &__arrow {
                top: 50%;
                bottom: auto;
                display: block;
                width: 50px;
                height: 50px;
                transform: translateY(-50%);
                border-radius: 50%;
                background-color: #fff;
                box-shadow: 0 2px 4px 0 rgba(0, 0, 0, 0.1);

                @include sm-block() {
                    width: 40px;
                    height: 40px;
                    border-radius: 0;
                }

                &::before {
                    opacity: 1;
                    color: $color-accent;
                    font-size: 22px;
                    line-height: 28px;
                    @include centre('y');
                }

                &:hover {
                    background-color: $color-accent;

                    &::before {
                        color: #fff;
                    }
                }

                &--prev {
                    left: -25px;

                    @include sm-block() {
                        left: 5px;
                    }

                    &::before {
                        content: '\f104';
                        left: 40%;
                    }
                } //.bb-slider--second .bb-slider__arrow--prev

                &--next {
                    right: -25px;

                    @include sm-block() {
                        right: 5px;
                    }

                    &::before {
                        content: '\f105';
                        left: 45%;
                    }
                } //.bb-slider--second .bb-slider__arrow--next
            } //.bb-slider--second .bb-slider__arrow

            &__dots {
                @include min(xs) {
                    display: none;
                }
            } //.bb-slider--second .bb-slider__dots
        }
    } //.bb-slider--second

    &--main {
        .bb-slider {
            &__slides {
                position: relative;
                overflow: hidden;

                @include max(xs) {
                    overflow: initial;
                }
            }
        }

        .slick-slider {
            &:hover {
                .slick-prev {
                    left: 0;
                }

                .slick-next {
                    right: 0;
                }
            }
        }

        .slick-slide {
            margin: 0;
        }

        .slick-arrow {
            position: absolute;
            top: 3px;
            bottom: 0;
            width: 44px;
            height: 100%;
            transition: $trsMenu;

            &::before {
                display: none;
            }

            &.slick-disabled {
                // display: none !important;
                opacity: 0.4;
            }
        }

        .slick-prev {
            left: -44px;
        }

        .slick-next {
            right: -44px;
        } //.bb-slider--main .slick-arrow
    } //.bb-slider--main

    &--promo {
        .bb-slider {
            &__arrow {
                @include max(xs) {
                    display: none;
                }
            }
        }
    } //.bb-slider--promo

    &--adv {
        .bb-slider {
            &__item {
                margin-bottom: 20px;
            }
        }
    } //.bb-slider--adv

    &--zoom {
        .slick-slide {
            transition-delay: 0.2s;
            @include scale(0.7);
            @include tr(0.5s);

            &.slick-current {
                @include scale(1);
            }
        }
    } //.bb-slider--zoom

    &--triumph {
        height: 100%;
        background-color: #fbfbfb;

        .bb-slider {
            &__slides {
                display: flex;
                justify-content: center;

                @include max(sm) {
                    display: block;
                }
            } //.bb-slider__slides

            &__slide {
                display: flex;
                flex-direction: column;
                align-items: center;
                width: 255px;

                & + .bb-slider__slide {
                    border-left: 1px solid $color-border--lightest;

                    @include max(sm) {
                        border: 0;
                    }
                }

                &--left {
                    padding-right: 30px;

                    @include max(sm) {
                        padding-right: 0;
                    }
                } //.bb-slider__slide--left

                &--right {
                    padding-left: 30px;

                    @include max(sm) {
                        padding-left: 0;
                    }
                } //.bb-slider__slide--right
            } //.bb-slider__slide
        } //.bb-slider

        .slick-slide {
            margin: 0;
        }

        .slick-dots {
            bottom: 29px;
        }
    } //.bb-slider--triumph

    &--fullscreen-slider {
        width: 100%;
        height: 100%;

        .bb-slider {
            &__slides {
                width: 100%;
                height: 100%;
            } //.bb-slider__slides

            &__slide {
                justify-content: center;
                width: 100%;
                height: 100%;
                background-repeat: no-repeat;
                background-position: center;

                background-size: cover;

                @include sectionOverlay;
                @include flex;

                &--video {
                    @include sectionOverlay(0.3);
                }
            } //.bb-slider__slide
        } //.bb-slider
    } //.bb-slider--fullscreen

    .slick-arrow {
        display: flex;

        &::after {
            content: '';
            z-index: z(fixed);
            display: block;
            margin: auto;
        }

        &.slick-disabled {
            display: none !important;
        }
    } //.bb-slider .slick-arrow

    .slick-prev,
    .slick-next {
        &::after {
            content: '';
            display: block;
            width: 32px;
            height: 32px;
        }

        &:hover {
            background-color: rgba(#fff, 0.15);
        }
    }

    .slick-prev {
        &::before {
            left: 0;
        }

        &::after {
            background-image: url('data:image/svg+xml;base64,');
        }
    } //.bb-slider .slick-prev

    .slick-next {
        &::before {
            right: 0;
        }

        &::after {
            background-image: url('data:image/svg+xml;base64,');
        }
    } //.bb-slider .slick-next
} //.bb-slider

.c-slider {
    position: relative;

    .swiper-button-next {
        right: -20px;
    }

    .swiper-button-prev {
        left: -20px;
    }
} //.c-slider

.mockup-slider {
    position: relative;
    width: 80%;

    @include min(sm) {
        width: 30%;
    }

    .bb-slider {
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        padding: 22% 10.2% 26% 9%;
    } //.mockup-slider .bb-slider

    .slick-slide {
        margin: 0;
    } //.mockup-slider .slick-slide
} //.mockup-slider

.fullscreen-slider {
    &__video {
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        overflow: hidden;

        video {
            position: absolute;
            top: 0;
            left: 0;
            width: auto;
            height: 100%;
            @include sm-block() {
                transform: translateX(-50%);
            }
        }
    } //.fullscreen-slider__video

    .slick {
        &-track {
            height: 100%;
        } //.slick-track

        &-list {
            height: 100%;
        } //.slick-list

        &-slide {
            margin: 0;

            div {
                height: 100%;
            }
        } //.slick-slide
    } //.slick
} //.fullscreen-slider
