.progressbar {
    width: 100%;

    &__wrap {
    } //.progressbar__wrap

    &__list {
        align-items: center;
        justify-content: space-between;
        @include flexCenter;
    } //.progressbar__list

    &__item {
        position: relative;
        width: 100%;
        text-align: center;

        &::before,
        &::after {
            content: '';
            position: absolute;
            top: 14px;
            display: block;
            width: 50%;
            height: 2px;
            background-color: #e8e8e8;
        }

        &::before {
            left: 15px;
        }

        &::after {
            right: -15px;
        }

        &:first-child {
            &::before {
                display: none;
            }
        }

        &:last-child {
            &::after {
                display: none;
            }
        }

        &.is-checked {
            &::after {
                background-color: $color-success;
            }

            .progressbar__num {
                background-color: $color-success;

                &::before {
                    content: '\f00c';
                    color: #fff;
                    font-size: 17px;
                    @include fa();
                }
            }

            & + .progressbar__item {
                &::before {
                    background-color: $color-success;
                }
            }
        }

        &.is-invalid {
            .progressbar__num {
                background-color: $color-danger;
                color: #fff;
            }
        }

        &.is-current {
            .progressbar__num {
                border: 3px solid $color-success;
                background-color: #fff;
            }

            .progressbar__title {
                color: $gray--darkest;
            }
        }
    } //.progressbar__item

    &__num {
        position: relative;
        justify-content: center;
        width: 30px;
        height: 30px;
        margin: auto;
        overflow: hidden;
        border-radius: 50%;
        background-color: $gray--lighten;
        color: #000;

        font-size: 15px;
        @include flexCenter;
    } //.progressbar__num

    .progressbar__title {
        margin-top: 8px;
        color: #cecece;
        font-size: 14px;
        font-weight: 500;

        @include max(i5) {
            display: none;
        }
    }
} //.progressbar

.progress-block {
    &__title {
        flex: 1;
        @include text-overflow;
    } //.progress-block__title

    &__pecentage {
        flex-shrink: 0;
    } //.progress-block__pecentage

    &__info {
        margin-bottom: 5px;
        color: $gray--dark;
        font-size: 15px;
        font-weight: 500;
        @include flexCenter;
    }
} //.progress-block

.progress-line {
    position: relative;
    display: block;
    height: 6px;
    border-radius: 15px;
    background-color: $color-bg--lightest;

    &__inner {
        position: absolute;
        top: 0;
        left: 0;
        width: 0;
        height: 100%;
        transition: width 1s;
        border-radius: 15px;
        background-color: $color-accent;
    } //.progress-line__inner
} //.progress-line

.add-progress {
    margin-top: 30px;

    &__title {
        color: $gray--darken;
        font-size: 16px;
        font-weight: 500;
    } //.add-progress__title

    &__desc {
        font-size: 14px;
        font-weight: 300;
        line-height: 1.7;
    } //.add-progress__desc

    &__line {
        max-width: 150px;
        margin: 20px auto auto;
    } //.add-progress__line

    &__counter {
        margin-top: 10px;
        font-size: 15px;
        font-weight: 500;
    } //.add-progress__counter
} //.add-progress
