.graph {
    // background-color: #fff;

    &__date {
        margin-left: auto;

        @include max(sm) {
            margin-right: 0;
        }

        @include max(xs) {
            width: calc(100% - 50px);
            margin: 0 37px 0 0;
        }
    } //.graph__date

    &__sorting {
        @include max(xs) {
            flex-direction: column;
        }
    } //.graph__sorting

    .top-line {
        @include landscape {
            margin-bottom: 20px;
        }

        &__select {
            @include landscape {
                width: 100%;
                max-width: 423px;
                margin-bottom: 0;
                // margin-left: 0;
                // margin-top: 10px;
            }
        } //.graph .top-line__select

        // .select2-container {
        //     @include min(md) {
        //         min-width: 250px;
        //     }
        // } //.graph .top-line .select2-container
    } //.graph .top-line
} //.graph

.graph-table {
    // border-radius: $border-radius;
    // background-color: #fff;
    // @include bS;
    @include box;
    // @include flexColumn;

    @include xs-block() {
        // margin: 0 -15px;
        box-shadow: none;
        @include border(top);
        @include border(bottom);
    }

    &__inner {
        display: flex;
    } //.graph__inner

    &__static {
        flex-basis: 20%;
        flex-shrink: 0;

        @include min(sm) {
            flex-basis: 25%;
        }

        @include sm-block {
            flex-basis: 150px;
        }

        @include xs-block() {
            @include border(left);
        }

        table {
            width: 100%;
            // border: 1px solid $color-border--lighten;
        }

        tr,
        td {
            font-weight: 600;
            font-weight: 500;
            text-align: center;
            vertical-align: middle;

            @include max(sm) {
                vertical-align: top;
            }
        }

        th,
        td {
            border-right: 0;
        }

        td {
            padding: 5px 10px;
            cursor: default;
            @include border(right);
        }

        th {
            a {
                color: $color-text;

                &:hover {
                    color: $gray--darkest;
                }
            }
        }
    } //.graph-table__static

    &__move {
        flex-basis: 80%;
        flex-grow: 1;
        flex-shrink: 1;
        overflow-x: auto;

        @include min(sm) {
            flex-basis: 75%;
        }

        @include sm-block {
            flex-basis: calc(100% - 150px);
            overflow-x: auto;

            table {
                table-layout: auto;
            }
        }

        table {
            min-width: 100%;
        }

        thead {
            @include xs-block() {
                @include border(right);
            }

            th {
                &:last-child {
                    border-right: 0;
                }
            }
        }

        tbody {
            @include xs-block() {
                @include border(right);
            }
        }

        th {
            letter-spacing: 0;
            text-transform: initial;
        } //.graph-table__move th

        tr {
            td,
            th {
                &:first-child {
                    border-left: 0;
                }
            }
        } //.graph-table__move tr

        td {
            position: relative;
            cursor: pointer;
            @include border(right);

            &::after {
                content: '+';
                opacity: 0;
                display: block;
                transition: opacity 0.25s ease-in-out;
                color: $gray--light;
                font-size: 2em;

                @include max(xs) {
                    opacity: 0.2;
                }
            }

            &:hover {
                &::after {
                    opacity: 1;
                }
            }

            &:last-child {
                border-right: 0;
            }
        } //.graph-table__move td
    } //.graph-table__move

    table {
        // width: 100%;
        table-layout: fixed;
        border-collapse: collapse;
        user-select: none;
    } //.graph table

    thead {
        th {
            @include border(right);
        }
    }

    tr {
        &:last-child {
            td {
                border-bottom: 0;
            }
        }
    }

    th,
    td {
        min-width: 152px;
        height: 53px;
        // border: 1px solid $color-border--lighten;
        text-align: center;
        vertical-align: middle;
        @include border(bottom);

        @include max(sm) {
            height: 48px;
        }
    }

    th {
        height: 45px;
        padding: 0;
        font-size: 14px;
        font-weight: 600;
        font-weight: 500;
        text-align: center;
        text-transform: uppercase;
        vertical-align: middle;
    } //.graph th

    &.is-empty {
        .graph-table {
            &__static,
            &__move {
                tbody {
                    display: none;
                }
            }
        }

        .empty {
            display: flex;
        }
    }

    .user {
        position: relative;

        &__img {
            flex-shrink: 0;
            width: 40px;
            height: 40px;

            @include max(sm) {
                display: none;
            }
        } //.graph-table user__img

        &__text {
            flex-grow: 1;
            width: 100%;
            margin-top: 4px;
            font-size: 14px;
            line-height: 1.2;
            text-align: left;
            @include text-overflow;
        } //.graph-table user__text

        &__name {
            font-size: 14px;
            @include text-overflow;

            @include max(sm) {
                font-size: 13px;
            }
        } //.graph-table user__name

        &__time {
            border-radius: 15px;

            font-size: 12px;
            line-height: 1;
            text-align: center;

            @include max(sm) {
                top: auto;
                right: auto;
                bottom: -15px;
                left: 0;
                padding: 0;
                background-color: transparent;
                color: $gray;
                text-align: left;
            }
        } //.graph-table user__time
    } //.graph-table user

    .range {
        padding: 3px;

        &::after {
            display: none;
        }
    } //.graph-table .range

    .empty {
        display: none;
        padding-top: 35px;
    } //.graph-table .empty

    &--scroll {
        th,
        td {
            text-transform: none;
        } //.graph-table--scroll th, .graph-table--scroll td

        th {
            height: 60px;
        } //.graph-table--scroll th

        td {
            height: 48px;
            max-height: 48px;
            padding: 0 10px;
            overflow: hidden;
            vertical-align: middle;
            white-space: nowrap;
        } //.graph-table--scroll td

        .graph-table {
            &__static {
                td {
                    @include sm-block() {
                        vertical-align: middle;
                    }
                } //.graph-table--scroll .graph-table__static td
            } //.graph-table--scroll .graph-table__static

            &__move {
                overflow: auto;

                table {
                    width: auto;
                    min-width: 100%;

                    thead {
                        th {
                            padding-right: 10px;
                            padding-left: 10px;
                        }
                    }
                } //.graph-table--scroll .graph-table__move table

                .graph-table {
                    &__value {
                        white-space: nowrap;

                        &::after {
                            display: none;
                        }
                    } //.graph-table--scroll .graph-table__move .graph-table__value

                    &__total {
                        font-weight: 500;
                    } //.graph-table--scroll .graph-table__move .graph-table__total
                }
            } //.graph-table--scroll .graph-table__move
        }
    } //.graph-table--scroll

    &--empty {
        @include flexColumn;

        td,
        th {
            height: 40px !important;
        } //.graph-table--empty td

        .empty {
            padding-top: 35px;
        } //.graph-table--empty .empty
    } //.graph-table--empty
} //.graph-table

.graph-item {
    padding: 3px 0;
    border-radius: $border-radius;
    background-color: #8a8a8a;
    color: #fff;
    font-size: 14px;

    &:hover {
        background-color: #a9a9a9;
    }

    @each $theme, $color in $text-colors {
        &.#{$theme} {
            background-color: $color;

            &:hover {
                background-color: lighten($color, 5%);
            }
        }
    }

    & + & {
        margin-top: 3px;
    }
} //.graph-item
