$services-color-bg: #eef0f2;

.order-item {
    position: relative;
    margin-bottom: 10px;
    // padding: 15px 20px;
    border: 1px solid $color-border--lightest;
    border-radius: $border-radius;
    background-color: #fff;

    @include sm-block() {
        // padding: 15px 10px;
    }

    &:last-child {
        margin-bottom: 0;
    }

    &__header {
        position: relative;
        display: flex;
        flex-direction: column;
        padding: 15px;

        .button-box {
            justify-content: flex-start;
            margin-top: 5px;

            .btn-link--invite {
                padding: 3px 15px;
            }
        }
    } //.order-item__header

    &__status-line {
        position: absolute;
        top: 0;
        left: 0;
        width: 3px;
        height: 100%;
        border-radius: 0 3px 3px 0;

        &.gray {
            background-color: #a9b1b8;
        } //.order-item__status-line.gray

        &.red {
            background-color: #c9302c;
        } //.order-item__status-line.red

        &.green {
            background-color: #8cc152;
        } //.order-item__status-line.green
    } //.order-item__status-line

    &__status {
        &.green {
            color: $color-success;
        } //.order-item__status.green

        &.red {
            color: $color-danger;
        } //.order-item__status.red

        &.gray {
            color: $gray--light;
        } //.order-item__status.gray
    } //.order-item__status

    &__title,
    &__text {
        font-size: 16px;
    } //.order-item__title, .order-item__text

    &__title {
        font-weight: 500;
    }

    &__header-box {
        justify-content: space-between;
        width: 100%;
        @include flexCenter;
    } //.order-item__header-box

    &__date {
        color: $color-text--light;
        font-size: 15px;
    } //.order-item__date

    &__small-text {
        font-size: 14px;
        font-weight: 300;
    } //.order-item__small-text

    &__cost {
        font-size: 18px;
        font-weight: 500;
        text-align: right;
        white-space: nowrap;
    } //.order-item__cost

    &__content {
        background-color: $services-color-bg;
        // padding: 0 15px;
        // margin-top: 15px;
        @include xs-block() {
            @include border(top);
        }

        .market-product-item {
            padding: 20px 15px;

            @include xs-block() {
                padding: 20px 10px;
            }

            // &:first-of-type {
            //     @include border(top);
            // }

            & + .order-item__footer {
                @include border(top);
            }
        }
    } //.order-item__content

    &__footer {
        display: flex;
        align-items: center;
        justify-content: space-between;
        padding: 15px;
        background-color: #fff;

        @include xs-block() {
            flex-direction: column;
        }

        .button-box {
            @include xs-block() {
                order: 2;
                width: 100%;
                margin-top: 20px;
            }

            @include i5-block() {
                flex-direction: column;
            }

            .btn {
                @include xs-block() {
                    width: 100%;
                    margin-right: 0;
                    margin-left: 0;
                }

                & + .btn {
                    @include i5-block() {
                        margin-top: 10px;
                        margin-left: 0;
                    }
                }
            }
        }

        .order-item__cost {
            // display: flex;
            // justify-content: flex-end;
            @include xs-block() {
                order: 1;
                width: 100%;
            }
        }
    } //.order-item__footer

    &__category {
        color: $gray--light;
        font-size: 13px;
    } //.order-item__category

    &__check {
        position: absolute;

        & ~ .order-item__header-box {
            margin-left: 30px;
        }

        &.xs-hide {
            & ~ .order-item__header-box {
                @include xs-block() {
                    margin-left: 0;
                }
            }
        }
    } //.order-item__check

    & > .market-product-item--horizon {
        position: static;
        padding: 0;
    } //.order-item > .market-product-item--horizon

    .market-product-item {
        &__del {
            @include xs-block() {
                top: 0;
                right: 0;
            }
        }
    } //.order-item .market-product-item

    .cabinet-product__item--header {
        background-color: $color-bg--lighten;
        @include border(top);
    } //.order-item .cabinet-product__item--header

    // &:not(.is-open) {
    //     .order-item__header {
    //         .btn-link--invite {
    //             display: none;
    //         }
    //     }
    // }

    &.is-open {
        .order-item {
            &__header {
                padding-bottom: 15px;
            }

            &__header-box {
                .order-item__cost {
                    display: none;
                }
            } //.order-item.is-open .order-item__header-box

            &__content {
                // @include border('top');
            }
        }
    } //.order-item.is-open

    &.offset {
        padding: 15px;
    }

    &.no-offset {
        padding-right: 0;
        padding-left: 0;

        .order-item__header {
            padding-right: 15px;
            padding-left: 15px;
        }

        &.is-open {
            padding-bottom: 0;
        }
    } //.order-item.no-offset

    &.no-border {
        @include xs-block() {
            border-right: 0;
            border-left: 0;
            border-radius: 0;
        }
    } //.order-item.no-border
} //.order-item

.order-grid-table {
    &__thead,
    &__tbody,
    &__order,
    &__tr {
        display: flex;
    } //.order-grid-table__thead, .order-grid-table__body, .order-grid-table__order, .order-grid-table__tr

    &__thead {
        padding: 10px 0;
    } //.order-grid-table__thead

    &__tbody,
    &__order,
    &__tr--column {
        flex-direction: column;
    } //.order-grid-table__body, .order-grid-table__order

    &__tr {
        position: relative;
        padding: 15px 0;

        @include mdX-block() {
            flex-wrap: wrap;
            justify-content: flex-start;
        }

        @include md-block() {
            justify-content: space-between;
        }

        & > .order-grid-table__td {
            @include mdX-block() {
                flex-basis: 20%;
                flex-grow: 1;
            }

            @include sm-block() {
                &:nth-child(odd) {
                    text-align: right;
                }
            }

            &--big {
                @include mdX-block() {
                    flex-basis: 20%;
                }

                @include md-block() {
                    flex-basis: auto;
                }
            }

            &--small {
                @include mdX-block() {
                    flex-basis: 10%;
                }

                @include md-block() {
                    flex-basis: auto;
                }
            }
        }

        &--column {
            padding: 0;

            .box {
                border-right: 0;
                border-left: 0;
                border-radius: 0;

                &:last-child {
                    border-bottom: 0;
                }

                .market-product-item {
                    &:last-child {
                        border-bottom: 0;
                    }

                    &__articul {
                        margin-top: auto;
                    }
                } //.order-grid-table__tr--column .box .market-product-item
            } //.order-grid-table__tr--column .box
        } //.order-grid-table__tr--column
    } //.order-grid-table__tr

    &__order {
        border-radius: $border-radius;
        background-color: #fff;
        @include border(all);

        &:not(:first-child) {
            margin-top: 10px;
        }

        .order-grid-table__tr {
            @include sm-block() {
                flex-wrap: wrap;
                // flex-direction: column;
            }

            & > .order-grid-table__td {
                @include sm-block() {
                    flex-basis: 100%;
                    align-self: center;
                    max-width: 50%;
                    margin: 0;

                    &:not(:nth-last-child(-n + 2)) {
                        margin-bottom: 10px;
                    }
                }

                &--big {
                    @include xs-block() {
                        &:nth-last-child(-n + 2) {
                            max-width: 100%;
                            margin-bottom: 10px;
                        }

                        &:last-child {
                            margin-bottom: 0;
                        }
                    }
                }
            }
        }
    } //.order-grid-table__order

    &__th,
    &__td {
        flex-basis: 20%;
        flex-grow: 1;
        flex-shrink: 0;
        max-width: 20%;
        margin: auto;
        padding-right: 15px;
        padding-left: 15px;
        font-size: 14px;

        &--small {
            flex-basis: 10%;
            max-width: 10%;
            white-space: nowrap;
        } //.order-grid-table__th--small, .order-grid-table__td--small

        &--big {
            flex-basis: 20%;
        } //.order-grid-table__th--big, .order-grid-table__td--big
    } //.order-grid-table__th, .order-grid-table__td

    &__th {
        color: $gray--light;
    }

    &__td {
        .bb-select--round {
            &::before {
                right: 13px;
            } //.order-grid-table__td .bb-select--round::before

            select {
                height: 40px;
            } //.order-grid-table__td .bb-select--round select
        } //.order-grid-table__td .bb-select--round

        .bb-input--select-round {
            .select2-container--default .select2-selection--single {
                height: 40px;

                .select2-selection__arrow b {
                    top: 15px;
                    right: 13px;
                }
            } //.order-grid-table__td .bb-input--select-round .select2-container--default .select2-selection--single
        }

        &--big {
            white-space: wrap;
        } //.order-grid-table__td--big
    } //.order-grid-table__td

    &__status {
        position: absolute;
        top: 0;
        left: 0;
        width: 3px;
        height: 100%;
        border-radius: 0 3px 3px 0;
        background-color: #a9b1b8;

        &.red {
            background-color: #c9302c;
        }

        &.green {
            background-color: #8cc152;
        }
    } //.order-grid-table__status
} //.order-grid-table

.order-detail {
    display: flex;
    flex-direction: column;
    @include border(top);

    @include xs-block() {
        border-top: 0;
    }

    &__status {
        padding: 0 15px;
        padding-top: 10px;

        .bb-input--select-round
            .select2-container--default
            .select2-selection--single {
            @include xs-block() {
                height: 42px;
            }

            .select2-selection__rendered {
                @include xs-block() {
                    height: 100%;
                }
            } //.order-detail__status .bb-input--select-round .select2-container--default .select2-selection--single .select2-selection__rendered
        } //.order-detail__status .bb-input--select-round .select2-container--default .select2-selection--single

        .bb-select--round
            .select2-container--default
            .select2-selection--single
            .select2-selection__arrow
            b {
            @include xs-block() {
                top: 16px;
                right: 13px;
            }
        } //.order-detail__status .bb-select--round .select2-container--default .select2-selection--single .select2-selection__arrow b

        .bb-select::before {
            @include xs-block() {
                right: 13px;
            }
        } //.order-detail__status .bb-select::before
    } //.order-detail__status

    &__detail {
        display: flex;
        flex-direction: column;

        .prop-value {
            flex-basis: 100%;
            flex-grow: 1;

            span {
                &:first-child {
                    min-width: 20ch;
                    color: $color-text--light;
                }

                &:last-child {
                    max-width: 60ch;

                    @include md-block() {
                        max-width: 40vw;
                    }
                }

                &.mark {
                    color: $color-info;
                }

                &.bold {
                    font-weight: 500;
                }
            }
        }
    } //.order-detail__detail

    &__block {
        position: relative;
        padding: 15px;

        &:not(:last-child) {
            @include border(bottom);
        }

        .prop-value {
            @include xs-block() {
                display: flex;
                flex-direction: column;
                margin-bottom: 10px;

                span {
                    &:last-child {
                        max-width: none;
                        margin-left: 0;
                        font-size: 1.2em;
                    }
                }
            }
        }
    } //.order-detail__block

    &__more {
        position: absolute;
        right: 15px;
        @include centre('y');

        &:hover {
            @include centre('y');
        }
    } //.order-detail__more

    &__goods {
        display: flex;
        flex-wrap: wrap;

        & > * {
            flex-basis: 15%;

            &:not(:first-child) {
                margin-left: 10px;
            }

            &:nth-child(n + 6) {
                display: none;
            }
        }
    } //.order-detail__goods

    &__title {
        & + .order-detail__goods {
            margin-top: 10px;
        }
    } //.order-detail__title
} //.order-detail
