.c-swiper-container {
    @include xs-block() {
        overflow: hidden;
        position: relative;
        visibility: hidden;
    }

    &__inner {
        @include xs-block() {
            overflow: hidden;
            position: relative;
        }

        .request-info {
            &__item {
                margin-bottom: 10px;
                padding: 10px;
                border-top: 1px solid $gray--lightest;
            }
        }
    } //.c-swiper-container__inner

    &__item {
        @include xs-block() {
            float: left;
            position: relative;
            width: 100%;
        }
    } //.c-swiper-container__item
} //.c-swiper-container
