$services-color-bg: #eef0f2;

.services {
    margin-top: 20px;

    @include xs-block() {
        margin: 0 -10px;
        // padding-bottom: 30px;
        // padding-top: 70px;
    }

    &__item {
        position: relative;

        &.bb-accordeon__item {
            border: 0;
            // border-bottom: 1px solid $color-border--lightest;
            border-radius: 0;
            background-color: $services-color-bg;
            box-shadow: none;

            &:first-child {
                @include border(top);
            } //.services__item.bb-accordeon__item:first-child

            &:not(.is-open) {
                @include border(bottom);
            }

            .item-services__list {
                border-bottom: 0;
            } //.services__item.bb-accordeon__item .item-services__list
        } //.services__item.bb-accordeon__item

        &.is-open {
            border-bottom: 0;
        }

        & + .services__item.bb-accordeon__item {
            margin-top: 0;
            // margin: 0;

            // .item-services__header {
            //     // margin-top: 15px;
            // }
        } //.services__item+.services__item

        .empty {
            padding: 30px 0;

            &__icon {
                width: 50px;
                height: 50px;
            } //.services__item .empty__icon

            &__title {
                color: $gray--light;

                font-size: 18px;
            } //.services__item .empty__title
        } //.services__item .empty

        &--rubber {
            @include border(bottom);

            .item-services__title {
                display: block;
                margin: 10px;
                margin-left: 0;
                font-weight: 500;
                pointer-events: all;

                &-icon {
                    width: 10px;
                    height: 10px;
                    transform: rotateZ(90deg);
                    transform-origin: center;
                } //.services__item--rubber .item-services__title-icon
            } //.services__item--rubber .item-services__title

            &.is-open {
                .item-services__title-icon {
                    transform: rotateZ(-90deg);
                } //.services__item--rubber.is-open .item-services__title-icon
            } //.services__item--rubber.is-open

            .item-category-services__time {
                min-width: auto;
            }
        } //.services__item--rubber
    } //.services__item

    .ui-sortable-placeholder {
        border: 0;
        background-color: $services-color-bg;
    } //.services .ui-sortable-placeholder

    .item-category-services {
        &__name {
            padding-left: 32px;
        }
    }
} //.services

.category-services {
    @include flexColumn;

    &__item {
        width: 100%;
    } //.category-services__item

    &__color {
        position: absolute;
        top: 0;
        left: 0;
        width: 4px;
        height: 100%;
    } //.category-services__color

    .color-line {
        position: absolute;
        top: 0;
        left: 0;
        width: 4px;
        height: 100%;
    } //.category-services .color-line

    .icon--drag {
        position: relative;
        top: -1px;
        width: 32px;
        height: 32px;
        padding: 10px;

        &:hover {
            cursor: grab;
        }
    } //.category-services .icon--drag
} //.category-services

.item-services {
    &:not(:first-child) {
        .item-services {
            &__header {
                padding-top: 30px;

                &::before {
                    top: 57%;
                }
            }
        }
    }

    &__header {
        left: 0;
        padding: 18px 15px 18px 4px;
        background-color: $color-bg--lightest;
        color: $gray--darkest;
        font-size: 15px;
        font-weight: 500;
        cursor: pointer;
        @include flexCenter;

        @include xs-block() {
            padding-right: 10px;
            // padding-left: 13px;
            // padding-left: 37px;
        }

        &::before {
            display: none;
            // @include fa('light');
            // color: $color-icon;
            // margin-right: 0;
            // @include centre;
            // right: auto;

            // @include xs-block() {
            //     left: 13px;
            // }
        }

        &:hover {
            box-shadow: none;
            cursor: pointer;
        }

        .icon--drag {
            @include xs-block() {
                margin-left: -3px;
                padding: 9px;
            }
        }

        .bb-dropdown {
            &__list {
                min-width: 210px;
            } //.category-services__header .bb-dropdown__list
        } //.item-services__header .bb-dropdown
    } //.item-services__header

    &__title {
        color: $gray--dark;
        font-size: 18px;
        line-height: 1.4;
        pointer-events: none;

        @include xs-block() {
            width: 100%;
        }
    } //.item-services__title

    &__list {
        border: 1px solid $color-border--lightest;
        border-bottom: 0;
        background-color: $services-color-bg;
        // position: relative;

        @include xs-block() {
            border-right: 0;
            border-left: 0;
        }

        &--rubber {
            border: 0;

            .item-category-services {
                &:first-child {
                    border-top: 1px solid $color-border--lightest;
                } //.item-services__list--rubber .item-category-services:first-child

                &:last-child {
                    border-bottom: 1px solid $color-border--lightest;
                } //.item-services__list--rubber .item-category-services:last-child
            }

            .item-category-services__inner {
                padding: 15px 0;
            }
        } //.item-services__list--rubber
    } //.item-services__list

    &__controls {
        margin-left: auto;
    } //.item-service__controls

    &__dell {
        position: absolute;
        top: 0;
        right: 0;
    } //.item-service__dell
} //.item-service

.item-category-services {
    position: relative;
    border-bottom: 1px solid $color-border--lightest;
    background-color: #fff;
    @include border(bottom);

    &:hover {
        background-color: $color-bg--hover;
        cursor: pointer;

        .item-category-services {
            &__controls {
                visibility: visible;
                opacity: 1;
            }
        }
    } //.item-category-services:hover

    &:last-child {
        border-bottom: 0;
    } //.item-category-services:last-child

    &__inner {
        position: relative;
        align-items: center;
        padding: 12px 15px 15px 4px;
        @include flexCenter;

        @include xs-block() {
            flex-wrap: wrap;
            align-items: flex-start;
            padding: 7px 0;
        }

        .item-category-services {
            &__name {
                &:not(:only-child) {
                    margin-top: 3px;
                }
            } //.item-category-services__inner .item-category-services__name
        } //.item-category-services__inner .item-category-services

        .bb-checkbox__box {
            top: 18px;
            left: 3px;

            @include xs-block() {
                top: 17px;
            }
        } //.item-category-services__inner .bb-checkbox__box
    } //.item-category-services__inner

    &__name {
        display: flex;
        flex: 1;
        flex-direction: column;
        align-items: flex-start;
        align-self: flex-start;
        justify-content: flex-start;
        // margin-top: 5px;
        padding-left: 9px;

        @include xs-block() {
            margin-right: 10px;
            padding-left: 32px;
        }
    } //.item-category-services__name

    &__title {
        margin-bottom: 0;
        color: $gray--dark;
        font-size: 15px;
        font-weight: 500;

        @include xs-block() {
            width: 100%;
        }

        span {
            @include md-block() {
                display: inline-block;
            }

            & + span {
                margin-left: 3px;
                line-height: 1.5;

                &::before {
                    content: '\f067';
                    margin-right: 5px;
                    color: $color-icon;
                    font-size: 13px;
                    @include fa;
                }
            }
        } //.item-category-services__title span
    } //.item-category-services__title

    &__price {
        // padding-right: 18px;
        flex-shrink: 0;
        justify-content: flex-end;
        min-width: 200px;
        color: $gray--dark;
        font-weight: 500;
        text-align: right;
        @include flexCenter;

        @include sm-block() {
            min-width: 150px;
            font-size: 15px;
        }

        @include xs-block() {
            min-width: 65px;
            padding-right: 0;
        }

        @include i5-block() {
            min-width: 70px;
        }

        &--rubber {
            min-width: auto;
        } //.item-category-services__price--rubber

        .price {
            &__old {
                margin-right: auto;
                padding-right: 18px;
                color: $gray--light;

                font-size: 15px;
                font-weight: 500;
                text-align: left;
                text-decoration: line-through;

                @include xs-block() {
                    margin-right: 0;
                    font-size: 12px;
                }

                .icon {
                    width: 11px;
                    height: 11px;
                    @include xs-block() {
                        width: 8px;
                        height: 8px;
                    }
                }
            } //.item-category-services__price__old

            span {
                margin-right: auto;
                padding-right: 18px;
                color: $gray--light;

                font-size: 15px;
                font-weight: 500;
                text-align: left;
                text-decoration: line-through;

                @include xs-block() {
                    position: absolute;
                    right: 0;
                    bottom: -13px;
                    margin-right: 0;
                    font-size: 12px;

                    &::after {
                        right: 4px;
                        display: none;
                        font-size: 10px;
                    }
                }
            } //.item-category-services__price span
        }
    } //.item-category-services__price

    &__time {
        flex-shrink: 0;
        min-width: 170px;
        margin-right: 20px;
        color: $gray--light;

        @include sm-block() {
            min-width: 125px;
        }

        @include xs-block() {
            margin-right: 0;
            font-size: 15px;
        }
    } //.item-category-services__time

    &__controls {
        visibility: hidden;
        opacity: 0;
        margin-left: auto;

        @include xs-block() {
            visibility: visible;
            opacity: 1;
        }

        .info-block {
            position: absolute;
            top: 10px;
            right: 10px;
        }
    } //.item-category-services__controls

    &__status {
        position: absolute;
        top: 0;
        left: 0;
        z-index: z(fixed);
        visibility: hidden;
        opacity: 0;
        justify-content: center;
        width: 100%;
        height: 100%;
        padding: 0 5px;
        // display: none;

        background-color: rgba(#fff, 0.9);
        // color: $color-success;
        color: $gray--dark;
        font-size: 15px;
        text-align: center;
        // border-radius: $border-radius;
        // @include centre('y');
        // right: 330px;
        @include tr;
        @include flexCenter;

        // @include max(xs) {
        //     left: auto;
        //     right: 70px;
        //     top: 20px;
        //     transform: translate(0, 0);
        // }

        span {
            margin: auto;
        }
    } //.item-category-services__status

    &__overlay {
        justify-content: center;
        background-color: rgba(#fff, 0.9);
        font-size: 14px;
        font-weight: 300;
        text-align: center;
        @include coverdiv;
        @include flexCenter;
    } //.item-category-services__overlay

    &__info {
        @include flexCenter;

        @include xs-block() {
            width: 100%;
        }

        ul {
            @include xs-block() {
                width: 100%;
            }
        }

        li {
            padding: 3px 0;
            @include flexCenter;

            @include xs-block() {
                justify-content: space-between;
                margin: 0 10px 0 31px;
            }

            & + li {
                @include xs-block() {
                    border-top: 1px solid $color-border--lightest;
                }
            }
        } //.item-category-services__info li

        &--rubber {
            li {
                @include xs-block() {
                    margin: 0 0 0 31px;
                }
            } //.item-category-services__info--rubber li
        } //.item-category-services__info--rubber
    } //.item-category-services__info

    &.is-saved {
        .item-category-services {
            &__status {
                visibility: visible;
                opacity: 1;
            }
        }
    } //.item-category-services.is-saved

    .icon--drag {
        position: absolute;
        top: 8px;

        @include xs-block() {
            top: 3px;
            margin-right: 0;
        }
    } //.item-category-services .icon--drag

    .control {
        &__item {
            font-size: 13px;
            font-weight: 500;
        } //.item-category-services .control__item

        &__icon {
            font-size: 13px;
        } //.item-category-services .control__icon
    } //.item-category-services .control

    .bb-input {
        width: 100%;
    } //.item-category-services .bb-input

    .bb-checkbox {
        &__title {
            @extend .item-category-services__title;
        }
    } //.item-category-services .bb-checkbox

    &.is-disabled {
        &::before {
            content: '';
            position: absolute;
            top: 0;
            right: 0;
            bottom: 0;
            left: 0;
            z-index: 1;
            background: rgba($color: #fff, $alpha: 0.7);
        }

        .icon--drag {
            z-index: 2;
        }
    }
} //.item-category-services

.price-box {
    margin-bottom: 30px;

    &__item {
        position: relative;
        padding: 15px 25px;
        border: 1px solid $color-bg--light;
        border-radius: $border-radius;
        background-color: rgba($color-bg--lightest, 0.8);

        @include xs-block() {
            padding: 0;
            border: 0;
            border-radius: 0;
            background-color: transparent;
        }
    } //.price-box__item

    &__dell {
        position: absolute;
        top: 0;
        right: -32px;
        // width: 10px;
        // height: 10px;
        // background-color: transparent;
        // border: 1px solid #000;
        // fill: $color-accent;
        cursor: pointer;
        @include tr;

        &:hover {
            .icon {
                @include rotate(90);
            }
        }

        .icon {
            width: 10px;
            height: 10px;
            fill: $color-accent;
            @include tr;
        } //.price-box__dell .icon
    } //.price-box__dell

    &__add {
        position: relative;
        width: 100%;
        margin-top: 20px;
        padding: 0 10px;
        border: 1px solid rgba($color-bg--lightest, 0.8);
        background: rgba($color-bg--lightest, 0.8);
        color: $gray;
        font-size: 14px;

        @include xs-block() {
            display: block;
            margin-top: 10px;
            font-size: 16px;
            text-align: left;
        }

        &:hover,
        &:active {
            // border-color: rgba($color-bg--lightest, 0.8);
            // background-color: rgba($color-bg--lightest, 0.8);
            color: $gray--dark;

            @include xs-block() {
                color: $color-text;
                text-decoration: none;
            }

            .icon {
                fill: $gray--dark;
                @include rotate(135);
            }
        }

        .icon {
            position: absolute;
            top: 40%;
            right: 5px;
            width: 10px;
            height: 10px;
            margin-right: 5px;
            fill: $color-icon;
            @include rotate(45);
            @include tr;

            @include xs-block() {
                position: absolute;
                width: 9px;
                height: 9px;
            }
        } //.price-box__add .icon
    } //.price-box__add

    & + .price-box {
        margin-top: 30px;
    }

    .bb-input {
        @include xs-block() {
            width: 100%;
            margin: 0 0 25px;
        }

        &__row {
            @include xs-block() {
                margin: 0;
            }
        } //.price-box .bb-input__row

        &__icon {
            font-size: 12px;
        } //.price-box .bb-input__icon
    } //.price-box .bb-input
} //.price-box

.service-select {
    position: relative;
    height: 100%;
    min-height: inherit;
    overflow: hidden;
    background-color: $color-bg--lightest;
    @include flexColumn;

    &__title {
        padding: 20px 15px 10px;
        background: $color-bg--lightest;
        color: $gray;

        font-size: 12px;
        letter-spacing: 0.5px;
        text-transform: uppercase;
        @include border(bottom);
        @include flexCenter;
    } //.service-select__title

    &__list {
        background-color: #fff;
    } //.service-select__list

    &__picker {
        position: absolute;
        top: 0;
        right: 0;
        bottom: 0;
        left: 0;
        z-index: z(fixed);
        transition: $trs;
        background-color: #fff;
        box-shadow: $bxs-with-overlay;
        @include tX(110%);
    } //.service-select__picker

    &__overlay {
        @extend .overlay;
    } //.service-select__overlay

    &__btn-select {
        position: fixed;
        top: 0;
        right: 0;
        left: 0;
    } //.service-select__btn-select

    &.has-category {
        .service-select {
            &__picker {
                @include tX(0);
            }
        }
    } //.service-select.has-category
} //.service-select

.service-select-picker {
    overflow: hidden;
    @include flexColumn;
    // background-color: $color-bg--lightest;

    &__list {
        flex-grow: 1;
        @include flexColumn;
        @include scroll;
    } //.service-select-picker__list

    &__btn {
        width: 100%;
        padding: 15px 20px;
        border-top: 0;
        border-right: 0;
        border-left: 0;
        outline: none;
        background-color: #fff;
        font-size: 14px;
        font-weight: 500;
        text-transform: uppercase;
        @include border(bottom);
        @include flexCenter;

        .icon {
            margin-left: auto;
            fill: $gray--dark;
        }
    } //.service-select-picker__btn

    &__title {
        @extend .service-select__title;
    } //.service-select-picker__title

    &.has-add-btn {
        padding-top: 70px;
    }
} //.service-select-picker

.service-select-picker-item {
    position: relative;
    flex-shrink: 0;
    padding: 17px 15px;
    border-bottom: 1px solid $color-border--lightest;
    background-color: #fff;
    cursor: pointer;
    @include flexCenter;

    &:hover {
        background-color: $color-bg--hover;
    }

    &:last-child {
        border-bottom: 0;
    }

    &__title {
        margin-right: 20px;
        color: $gray--darkest;
        font-size: 15px;
        font-weight: 500;
    } //.service-select-picker-item__title

    &__icon {
        right: 15px;
        width: 28px;
        height: 28px;
        margin-left: auto;
        fill: #c3c7c5;
        @include centre('y');

        &.icon-check--fill {
            width: 25px;
            height: 25px;
        }
    } //.service-select-picker-item__icon

    &__checkbox {
        position: absolute;
        width: 1px;
        height: 1px;
        margin: -1px;
        padding: 0;
        overflow: hidden;
        clip: rect(0 0 0 0);
        border: 0;
        white-space: nowrap;
        clip-path: inset(50%);

        &:checked ~ .service-select-picker-item__icon {
            &.icon-check--fill {
                fill: $gray--dark;
            }
        }
    }

    &.is-checked {
        .service-select-picker-item {
            &__icon {
                &.icon-check--fill {
                    fill: $gray--dark;
                }
            }
        }
    }

    .color-line {
        width: 23px;
        height: 23px;
        margin-right: 18px;
        margin-left: -1px;
        border-radius: 50%;
    }
} //.service-select-picker-item
