$o: 10px;
$ob: 15px;
$om: 2px;

.bb-upload {
    position: relative;

    &__home {
        position: absolute;
        bottom: 0;
        left: 0;
        width: 100%;
        padding: 3px 0;
        background-color: rgba(0, 0, 0, 0.4);
        color: #fff;
        font-weight: 300;
        text-align: center;
        @include tr;
    } //.bb-upload__home

    &__button {
        position: relative;
        width: 150px;
        height: 100px;
        margin-bottom: 0;
        border: 1px solid #000;
        border-radius: 5px;
        cursor: pointer;

        &::before {
            content: '';
            position: absolute;
            top: 50%;
            left: 50%;
            display: block;
            width: 25%;
            height: 2px;
            transform: translateX(-50%);
            background-color: #000;
        }

        &::after {
            content: '';
            position: absolute;
            top: 50%;
            left: 50%;
            display: block;
            width: 25%;
            height: 2px;
            transform: translateX(-50%) rotate(90deg);
            background-color: #000;
        }

        .fa {
            font-size: 28px;
            cursor: pointer;
        }

        input {
            display: none;
        }
    } //.bb-upload__button

    &__year {
        margin-top: 15px;
        color: $gray--light;
        font-size: 14px;
        font-weight: 600;
    } //.bb-upload__time

    &__list {
        margin-top: 5px;
        padding: 0;
        list-style-type: none;
        @include row-flex();

        .bb-upload__item {
            @include col();
            @include size(4);
            @include size-sm(6);
            @include size-xs(12);

            &--icon {
                @include size(2);
                @include size-lg(3);
                @include size-md(4);
                @include size-sm(4);
                @include size-xs(6);
                @include size-i7(12);
            }
        }

        &.column-2 {
            @include row-flex;

            @include xs-block() {
                margin-right: 5px;
                margin-left: 5px;
                @include u-row-flex($o);
            }

            .bb-upload {
                &__item {
                    @include col;
                    @include size(6);
                    @include xs-block() {
                        margin-bottom: $o;
                        @include u-col($o);
                        @include u-size(6, 12, $o);
                    }
                }
            }
        }

        &.column-4 {
            @include row-flex;

            @include xs-block() {
                margin-right: 1px;
                margin-left: 1px;
                @include u-row-flex($o);
            }

            .bb-upload {
                &__item {
                    border-radius: 0;
                    @include col;
                    @include size(3);
                    @include xs-block() {
                        margin-bottom: $o;
                        @include u-col($o);
                        @include u-size(3, 12, $o);
                    }
                }
            }
            $o: 1px;
        }

        &.photo-block {
            margin: 7px 5px 5px;

            label {
                margin-bottom: $ob;
                @include xs-block() {
                    margin-bottom: $om;
                }
                @include u-col($ob);
                @include xs-block() {
                    @include u-col($om);
                }
                @include u-size(3, 12, $ob);
                @include xs-block() {
                    @include u-size(4, 12, $om);
                }

                &::before {
                    content: '';
                    padding-top: 100%;
                    float: left;
                }

                .bb-upload__img {
                    border-radius: 0;
                }
            }
        }
    } //.bb-upload__list

    &__item {
        position: relative;
        display: inline-block;
        height: auto;
        max-height: 185px;
        margin-bottom: $gutter;
        overflow: hidden;
        cursor: pointer;

        @include max(i7) {
            .fa {
                &.fa-undo {
                    bottom: 0;
                    left: 0;
                }

                &.fa-trash-o {
                    right: 0;
                    bottom: 0;
                }
            }
        }

        &:hover {
            .bb-upload {
                &__overlay {
                    visibility: visible;
                    opacity: 1;
                    pointer-events: auto;
                    filter: none;
                }
            }
        }

        &:first-child {
            .bb-upload__btn--first {
                display: none;
            }
        }

        img {
            @include box;
        }
    } //.bb-upload__item

    &__img {
        display: block;
        max-width: 100%;
        height: 100%;
        object-fit: cover;
    } //.bb-upload__img

    &__count {
        position: absolute;
        top: -8px;
        left: -8px;
        z-index: 99;
        display: block;
        width: 17px;
        height: 17px;
        border-radius: 50%;
        background-color: #000;
        color: #fff;
        font-size: 11px;
        font-weight: bold;
        text-align: center;
    } //.bb-upload__count

    &__overlay {
        position: absolute;
        top: 10px;
        right: 10px;
        left: 10px;
        visibility: hidden;
        opacity: 0;
        justify-content: center;
        overflow: hidden;
        border-radius: $border-radius;
        @include flexCenter;
        @include max(sm) {
            visibility: visible;
            opacity: 1;
        }

        i {
            opacity: 0.7;
            display: flex;
            align-items: center;
            justify-content: center;
            width: 28px;
            height: 28px;
            transition: all 0.35s ease;
            border: 0;
            background-color: #000;
            color: #fff;
            font-size: 16px;
            cursor: pointer;

            &:hover {
                opacity: 1;
            }
        }
    } //.bb-upload__overlay

    &__loader {
        background-color: rgba(#fff, 0.9);

        .loader--scroll {
            @include centre;
            @include max(xs) {
                top: 20%;
            }
        }
    } //.bb-upload__loader

    &__btn {
        opacity: 0.7;
        justify-content: center;
        width: 28px;
        height: 28px;
        transition: none;
        border: 0;
        background-color: #000;
        color: #fff;
        font-size: 14px;
        cursor: pointer;
        @include flexCenter;

        &:hover {
            opacity: 1;
            color: #fff;
        }

        &:nth-child(2) {
            margin-left: auto;
            border-radius: $border-radius 0 0 $border-radius;
        }

        .icon-arrow-to-top {
            width: 15px;
            height: 15px;
        }

        .icon-trash {
            width: 16px;
            height: 16px;
        }

        &--wide {
            width: auto;
            padding: 0 8px;
            font-size: 13px;
            white-space: nowrap;
        } //.bb-upload__btn--wide

        &--first {
            margin-right: 10px;
            border-radius: $border-radius;

            @include max(xs) {
                margin-right: auto;
            }
        } //.bb-upload__btn--first
    } //.bb-upload__btn

    &__icon {
        fill: #fff;
    } //.bb-upload__icon

    &__empty {
        margin-bottom: $gutter;
    } //.bb-upload__empty

    &--icon {
        .bb-upload {
            &__item {
                border: 0;
            } //.bb-upload--icon .bb-upload__item

            &__overlay {
                top: auto;
                right: 0;
                bottom: 0;
                left: auto;
                visibility: visible;
                opacity: 1;
                transform: translate(0);
            } //.bb-upload--icon .bb-upload__overlay

            &__btn {
                opacity: 1;
                width: 50px;
                height: 50px;
                border-radius: $border-radius;
                background-color: rgba($color-bg--darken, 0.3);
                font-size: 20px;

                .icon {
                    fill: #fff;
                }
            } //.bb-upload--icon .bb-upload__btn
        } //.bb-upload--icon .bb-upload
    } //.bb-upload--icon

    &--picker {
        @include scroll;

        .bb-upload {
            &__list {
                margin-bottom: 5px;
            }
        }
    } //.bb-upload--picker
} //.bb-upload

.img-control {
    position: absolute;
    top: 10px;
    right: 10px;
    left: 10px;
    visibility: hidden;
    opacity: 0;
    justify-content: center;
    overflow: hidden;
    border-radius: $border-radius;
    @include flexCenter;
    @include max(sm) {
        visibility: visible;
        opacity: 1;
    }

    @include xs-block() {
        top: 0;
        right: 0;
        bottom: 0;
        left: 0;
        border-radius: 0;
    }

    &__wrap {
        &:hover {
            .img-control {
                visibility: visible;
                opacity: 1;
            }
        }
    }

    &__btn {
        opacity: 0.7;
        justify-content: center;
        width: 28px;
        height: 28px;
        transition: none;
        border: 0;
        background-color: #000;
        color: #fff;
        font-size: 14px;
        cursor: pointer;
        @include flexCenter;

        @include min(sm) {
            &:nth-child(2) {
                margin-left: auto;
                border-radius: $border-radius 0 0 $border-radius;
            }
        }

        @include xs-block() {
            width: 32px;
            height: 32px;
            border-radius: 0;
        }

        &:hover {
            opacity: 1;
            color: #fff;
        } //.img-control__btn:hover

        .icon-arrow-to-top {
            width: 15px;
            height: 15px;
        } //.img-control__btn .icon-arrow-to-top

        &--wide {
            width: auto;
            padding: 0 8px;
            font-size: 13px;
            white-space: nowrap;
        } //.img-control__btn--wide

        &--first {
            margin-right: 10px;
            border-radius: $border-radius;

            @include max(xs) {
                margin-right: auto;
            }
        } //.img-control__btn--first

        &--up {
            @include xs-block() {
                position: absolute;
                top: 0;
                left: 0;
            }
        } //.img-control__btn--up

        &--trash,
        &--crop,
        &--right,
        &--left {
            @include xs-block() {
                position: absolute;
                top: auto;
                bottom: 0;
            }
        }

        &--trash {
            @include xs-block() {
                left: 96px;
            }
        } //.img-control__btn--trash

        &--right {
            @include xs-block() {
                left: 64px;
            }
        } //.img-control__btn--right

        &--left {
            @include xs-block() {
                left: 32px;
            }
        } //.img-control__btn--left

        &--crop {
            @include xs-block() {
                left: 0;
            }
        } //.img-control__btn--crop
    } //.img-control__btn

    &__icon {
        opacity: 0.7;
        display: flex;
        align-items: center;
        justify-content: center;
        width: 20px;
        height: 20px;
        transition: all 0.35s ease;
        border: 0;
        background-color: #000;
        fill: #fff;
        font-size: 16px;
        cursor: pointer;
    } //.img-control .icon
} //.img-control
