.add-form {
    &:last-child:not(:first-child) {
        .add-form {
            &__btn {
                &--add {
                    display: block;
                }

                &--remove {
                    display: block;
                    width: auto;
                    min-width: 140px;
                }
            }
        }
    }

    &:first-child {
        .add-form {
            &__btn {
                &--remove {
                    display: none;
                }
            }
        }
    }

    &__form {
        border-radius: $border-radius;
        @include border('all', $input-color-border);
    } //.add-form__form

    &__props {
        display: flex;
        flex-wrap: wrap;
        align-items: center;
        justify-content: flex-start;
        padding: 5px;
        padding-bottom: 0;

        &:empty {
            padding: 0;
        }
    } //.add-form__props

    &__title {
        display: block;
        margin-bottom: 5px;
        font-size: 14px;
    } //.add-form__title

    &__prop {
        position: relative;
        display: flex;
        flex-shrink: 0;
        align-items: center;
        justify-content: center;
        margin-right: 5px;
        margin-bottom: 5px;
        padding-left: 30px;
        border-radius: $border-radius;
        background-color: $gray--lighten;

        &:last-child {
            margin-right: 0;
        }
    } //.add-form__prop

    &__value {
        cursor: default;
    } //.add-form__value

    &__add-prop {
        position: relative;
        display: flex;
    } //.add-form__add-prop

    &__input {
        width: 100%;
        padding-right: 35px;
        border: 0;
    } //.add-form__input

    &__submit {
        position: absolute;
        right: 0;
        bottom: 0;
        display: flex;
        align-items: center;
        justify-content: center;
        padding: 10px;
        border: 0;
        background: transparent;

        .icon {
            width: 20px;
            height: 20px;
        } //.add-form__submit .icon
    } //.add-form__submit

    &__copy,
    &__btn {
        margin-top: 35px;

        &--add {
            margin-left: 10px;
        } //.add-form__btn--add

        &--remove {
            margin-right: 10px;
        } //.add-form__btn--remove
    } //.add-form__btn

    &__btn {
        display: block;

        @include xs-block() {
            padding-right: 15px;
            padding-left: 15px;
        }

        &--add {
            margin-left: 0;
        } //.add-form__btn--add

        &--remove {
            display: block;
            width: auto;
            min-width: 140px;
        } //.add-form__btn--remove
    } //.add-form__btn

    &__drag-icon {
        position: absolute;
        top: 0;
        left: 0;
        display: inline-flex;
        width: 30px;
        height: 40px;
        cursor: grab;

        .icon {
            width: 13px;
            height: 13px;
            margin: auto;
        }
    } //.add-form__drag-icon

    &__footer {
        @include flexCenter;
    } //.add-form__footer

    &.is-cloned {
        &:last-child:not(:first-child) {
            .add-form__btn--remove {
                margin-right: 10px;
            }
        }

        &:only-child {
            .add-form {
                &__btn {
                    &--add {
                        display: block;
                    } //.add-form.is-cloned:only-child .add-form__btn--add

                    &--remove {
                        display: none;
                        // width: auto;
                        // margin-right: 10px;
                    } //.add-form.is-cloned:only-child .add-form__btn--remove
                } //.add-form.is-cloned:only-child .add-form__btn
            }
        } //.add-form.is-cloned:only-child

        .add-form {
            &__btn {
                &--add {
                    // display: block;
                    display: none;
                }

                &--remove {
                    // display: none;
                    display: block;
                    width: 100%;
                    margin-right: 0;
                } //.add-form.is-cloned .add-form__btn--remove
            } //.add-form.is-cloned .add-form__btn
        }
    } //.add-form.is-cloned
} //.add-form
